const popupMemberCount = document.querySelector('.js-popup-members-count');
import generateName from '../household/generate-name';

export default function initPopupMembersCount() {
  const closePopup = popupMemberCount.querySelectorAll('.js-popup-members-count-close');
  const content = popupMemberCount.querySelector('.js-popup-members-count-content');
  let rowTemplate = document.createElement('div');
  let openPopup = true;

  User.rooms[0].households.forEach(household => {
    if ( household.h_household_member_count !== household.members.length ) {
      openPopup = false;
    }
  });

  if ( !openPopup ) {
    popupMemberCount.classList.add('is-show');
    content.innerHTML = '';
    User.rooms[0].households.forEach(household => {
      if ( household.h_household_member_count !== household.members.length ) {
        let row = rowTemplate.cloneNode();
        row.classList.add('popup-count-members__row');
        row.innerHTML = generateName(household.h_household_number)+`: Додана кількість членів домогосподарства (${household.members.length}) не відповідає зазначеній у запитанні <b>П15. (Опис домогосподарств)</b> кількості членів домогосподарства (${household.h_household_member_count})`;
        content.appendChild(row);
      }


    });

    closePopup.forEach(btn => {
      btn.removeEventListener('click', (e) => {
        popupMemberCount.classList.remove('is-show');
      });
    });

    closePopup.forEach(btn => {
      btn.addEventListener('click', (e) => {
        popupMemberCount.classList.remove('is-show');
      });
    });
  }

  return openPopup;
}
