export default function adjustHomeless() {
  console.log(User.rooms[0].r_building_id);
  let tab1 = document.querySelector("a[href='#s_member-1']").parentElement;

  if ( +User.rooms[0].r_building_id === 13 ) {
    tab1.classList.remove('ok', 'warning', 'error');
    tab1.classList.add('disabled');

    document.querySelectorAll('input[name=m_temp]').forEach(el => {
      el.disabled = true;
      el.checked = false;
    });
    // document.querySelectorAll('input[name=m_planning_leave_place]').forEach(el => {
    //   el.disabled = true;
    //   el.checked = false;
    // });

    // other.value = '';
    // other.disabled = true;
  } else {
    tab1.classList.remove('disabled');
    document.querySelectorAll('input[name=m_temp]').forEach(el => {
      el.disabled = false;
    });
  }
}
