export default function parseDataForRoom(room) {
  // Прибавляю 0 для соответствия вводу - если 2 и больше символа не прибавляю
  (room.r_improve_toilet_id.length >= 2) ? (true) : (room.r_improve_toilet_id = '0' + room.r_improve_toilet_id);

  room.r_datasource = util.CODE_CONTROL_TASK;

  room.households.forEach(households => {

    households.h_household_number = +households.h_household_number;
    (households.h_temp_residents === '0') ? (households.h_temp_residents = false) : (households.h_temp_residents = true);
    households.h_household_member_count = +households.h_household_member_count;
    (households.edit === '1') ? (households.edit = true) : (households.edit = false);
    (households.h_household_room_parts === '1') ? (households.h_household_room_parts = true) : (households.h_household_room_parts = false);
                
    households.members.forEach(member => {
      (member.m_lived_another_country_year === null) ? (member.m_lived_another_country_year = '') : (true);
      (member.m_national_refusal === '0') ? (member.m_national_refusal = false) : (member.m_national_refusal = true);
      (member.m_live_settlement_year === null) ? (member.m_live_settlement_year = '') : (true);
      (member.m_live_birth_continuously_year === null) ? (member.m_live_birth_continuously_year = '') : (true);
      (member.m_live_birth_continuously_month === null) ? (member.m_live_birth_continuously_month = '') : (true);
      (member.m_left_settlement === null) ? (member.m_left_settlement = '') : (true);
      (member.m_job_settlement === null) ? (member.m_job_settlement = '') : (true);
      (member.m_arrival_month === null) ? (member.m_arrival_month = '') : (true);
      (member.m_absence_period === null) ? (member.m_absence_period = '') : (true);
      (member.m_number_children_born === null) ? (member.m_number_children_born = '') : (true);
      (member.edit === '1') ? (member.edit = true) : (member.edit = false);
      (member.is_first === '1' || member.is_first === true) ? (member.is_first = true) : (member.is_first = false);
    });
  });
}
