import Household from '../../classes/Household';
import householdTabClick from './household-tab-click';
import householdMembersNumChange from './members-num-change';
import {householdGenerateId,householdMaxNum} from './services';
import generateName from './generate-name';
import checkHouseholdDel from './check-household-del';

export function roomHouseholdNumChange() {
  this.value = this.value.replace(/([^\d]*)/g, '');
  let num = parseInt( this.value );

  if ( isNaN(num) || num < 1 ) {
    num = 1;
  }

  if ( num <= 5 ) {
    document.querySelector(".radio-num__input.js-room-households[value='"+num+"']").checked = true;
    if ( this.type === 'radio' ) {
      document.querySelector('.js-room-households_text').value = '';
    }
  } else {
    document.querySelectorAll('.radio-num__input.js-room-households').forEach(el => {
      el.checked = false;
    });
  }

  checkHouseholdDel( num ).then(res => {
    updateHouseholdsArray( num );
  }).catch(err => {
    // пользователь передумал удалять домохозяйства, возвращаем предыдущее количество
    let orig = window.User.rooms[0].households.length;
    if ( orig <= 5 ) {
      document.querySelector(".radio-num__input.js-room-households[value='"+orig+"']").checked = true;
    } else {
      document.querySelectorAll('.radio-num__input.js-room-households').forEach(el => {
        el.checked = false;
      });
      document.querySelector('.js-room-households_text').value = orig;
    }
  });
}


function updateHouseholdsArray( num ) {
  let curNum = window.User.rooms[0].households.length;
  if(num > curNum) {
    let index = householdMaxNum() + 1;
    while(num > window.User.rooms[0].households.length) {
      let h = new Household();

      //h.h_id = householdGenerateId();
      h.h_household_number = index;
      h.h_room_id = window.User.rooms[0].r_id;

      window.User.rooms[0].households.push( h );
      index++;
    }
  } else if(num < curNum) {
    window.User.rooms[0].households.splice(num);
  }

  window.User.rooms[0].r_households_count = num;
  window.needSave = true;
  updateHouseholdTabs();
}


export function updateHouseholdTabs() {
  const tabs = document.querySelector('.js-room-household-tabs');
  const buttons = document.querySelector('.js-room-household-details');

  let num = window.User.rooms[0].households.length;
  let curNum = tabs.querySelectorAll('li').length;

  if ( num > curNum ) {
    while ( num > tabs.querySelectorAll('li').length ) {
      let h_index  = tabs.querySelectorAll('li').length;
      let index = window.User.rooms[0].households[h_index].h_household_number;

      let newTab = document.createElement( 'li' );
      let newA = document.createElement( 'a' );

      newA.href = '#household-' + index;
      newA.innerHTML = generateName(index);
      /*
      if(window.User.rooms[0].households[h_index].h_name === null) {
	      newA.innerHTML = 'Домогосподарство ' + index;
      } else {
	      newA.innerHTML = window.User.rooms[0].households[h_index].h_name;
      }
*/
      newA.dataset.household = index;

      newTab.appendChild( newA );
      newTab.dataset.household = index;
      newTab.addEventListener('click', householdTabClick);
      tabs.appendChild( newTab );

      let newContainer = document.createElement( 'div' );
      let newInput = document.createElement( 'input' );
      let members;
      if( window.User.rooms[0].households[h_index].h_household_member_count ) {
        members = window.User.rooms[0].households[h_index].h_household_member_count;
      } else {
        members = 1;
      }

      newContainer.classList.add( 'form-household-count__flex' );

      for(let i=1; i<=5; i++) {
        let newButton = document.createElement( 'div' );
        let newRadio = document.createElement( 'input' );
        let newLabel = document.createElement( 'label' );
        let id = 'household' + index + '_member_count-' + i;

        newButton.classList.add( 'radio-num' );

        newRadio.classList.add('radio-num__input', 'js-household-members');
        newRadio.type = 'radio';
        newRadio.id = id;
        newRadio.value = i;
        newRadio.name = 'h_household' + index + '_member_count';
        newRadio.dataset.household = index;

        if(i === members) {
          newRadio.checked = true;
        }
        newRadio.addEventListener('change', householdMembersNumChange);

        newLabel.innerHTML = i;
        newLabel.classList.add('form__subtitle', 'radio-num__label');
        newLabel.htmlFor = id;

        newButton.appendChild( newRadio );
        newButton.appendChild( newLabel );
        newContainer.appendChild( newButton );
      } // for(let i=1; i<=5; i++)

      newInput.type = 'text';
      newInput.id = 'household' + index + '_member_count-text';
      newInput.classList.add('input-rect', 'js-household-members-text');
      newInput.placeholder = 'Інше';
      newInput.maxLength = 2;
      if(members > 5) {
	      newInput.value = members;
      }
      newInput.addEventListener('keyup', householdMembersNumChange);
      newInput.dataset.household = index;

      newContainer.appendChild( newInput );
      buttons.appendChild( newContainer );
      index++;
    } // while(num > tabs.querySelectorAll("li").length)
  } else if ( num < curNum ) {
    let deleted = [];
    while ( num < tabs.querySelectorAll('li').length ) {
      deleted.push( tabs.lastChild.dataset.household );
      tabs.removeChild( tabs.lastChild );
      buttons.removeChild( buttons.lastChild );
    }

    // Проверяем, не было ли удалено текущее домохозяйство
    if ( deleted.indexOf(window.User.rooms[0].current_household) !== -1 ) {
      // Удалили current_household, нужно переключиться на другой
      let household = householdMaxNum(); // берём последний
      window.User.rooms[0].current_household = household;
      document.querySelector(".js-room-household-tabs > li[data-household='" + household + "']").click();
    }

  }


//console.log( buttons );
}
