import setValues from './set-values';
import { getCurrentHouseholdIndex } from '../components/household/services';
import checkRoom from '../components/room/controls';
import checkHousehold from '../components/household/controls';
import checkMember from '../components/member/controls';
import help from './help';
import myMember from '../components/member/handlers/my-member';
import updateProgressMemberInForm from './update-progress-member-in-form';
import parseDataForRoom from './parse-data-for-room';

const LZString = require('../helpers/lz-string.js');

(function() {
  const ESC_KEYCODE = 27;
  const ENTER_KEYCODE = 13;

  window.util = {
    isEscEvent: (evt, action) => {
      if ( evt.keyCode === ESC_KEYCODE ) {
        action(evt);
      }
    },

    isEnterEvent: (evt, action) => {
      if ( evt.keyCode === ENTER_KEYCODE ) {
        action(evt);
      }
    },

    objectLengthAll: (object) => {
      let length = 0;
      for( var key in object ) {
        if ( object.hasOwnProperty(key) ) {
          ++length;
        }
      }
      return length;
    },

    objectLengthFilled: (object) => {
      let length = 0;
      for( var key in object ) {
        if ( object.hasOwnProperty(key) && object[key] !== null && object[key] !== '99' && object[key] !== '999' && object[key] !== '' ) {
          ++length;
        }
      }
      return length;
    },

    uint8arrayToStringMethod: (myUint8Arr) => {
      return String.fromCharCode.apply(null, myUint8Arr);
    },

    base64ToArrayBuffer: ( base64 ) => {
      var binary_string = window.atob(base64);
      var len = binary_string.length;
      var bytes = new Uint8Array( len );
      for (var i = 0; i < len; i++) {
        bytes[i] = binary_string.charCodeAt(i);
      }
      return new Uint8Array( bytes.buffer );
    },

    arrayBufferToBase64: ( buffer ) => {
      var binary = '';
      var bytes = new Uint8Array( buffer );
      var len = bytes.byteLength;
      for (var i = 0; i < len; i++) {
        binary += String.fromCharCode( bytes[ i ] );
      }
      return window.btoa( binary );
    },

    LZStringToCompress: (obj) => {
      return LZString.compress( JSON.stringify(obj) );
    },

    LZStringToDecompress: (string) => {
      return LZString.decompress(string);
    },

    decompressFromUTF16: (string) => {
      return LZString.decompressFromUTF16(string);
    },

    compressToUTF16: (string) => {
      return LZString.compressToUTF16(string);
    },

    removeCoordinateInTasks: (array, role) => {
      array.staffusers_census[0].staffusers_instructor.forEach(instructor => {
        instructor.staffusers_counting.forEach(counting => {
          if ( counting.t_datasource === 2 && role !== 3 ) {
            counting.housestatus.forEach(house => {
              house.hs_latitude_check = null;
              house.hs_longitude_check = null;
            });
          }
        });
      });
      // console.log(array);
      return array;
    },

    setValues: setValues,
    household: getCurrentHouseholdIndex,

    checkRoom: checkRoom,
    checkHousehold: checkHousehold,
    checkMember: checkMember,
    myMember: myMember, // возвращает члена по id или текущего
    updateProgressMemberInForm: updateProgressMemberInForm,

    MIN_AGE_FOR_MARRIAGE: 15,
    MIN_AGE_FOR_PARENT: 15,
    MIN_AGE_FOR_GRAND: 30,
    MAX_LENGTH_PROGRESS: 22,
    DEFAULT_VALUE_REGION_COUNTRY: '999',
    DEFAUL_VALUE_NATIONALITY: '999',
    DEFAULT_VALUE_LANGUAGE: '999',
    selectsMember: [],
    months: ['січень', 'лютий', 'березень', 'квітень', 'травень', 'червень', 'липень', 'серпень', 'вересень', 'жовтень', 'листопад', 'грудень'],
    maxDays: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],

    help: help,
    // role
    CODE_MANAGER: 1,
    CODE_INSTRUCTOR: 2,
    CODE_COPYIS: 3,
    // datasource
    CODE_HOMELESS: 0,
    CODE_INET: 1,
    CODE_TASK: 2,
    CODE_STATIONARY: 3,
    CODE_CONTROL_TASK: 4,
    // преобразование данных при получении с бека
    parseDataForRoom: parseDataForRoom,

    CODE_ABSENT_COUNT_MAX: 5,

    DOMEN: 'https://census.cf'
  };
})();
