const man = '1';
const woman = '2';

export function sortGrandChildrenMarriage(children) {
  let newArr = [];
  
  if ( children.length > 0 ) {
    children.forEach(child => {
              
      if ( child.m_sib_id === '9' && child.m_gender_id === man ) {
        newArr.push(child);
                  
        if ( child.m_spouse_id !== null ) {
          for ( let i = 0; i < children.length; i++ ) {
            if ( child.m_spouse_id === children[i].m_id ) {
              newArr.push(children[i]);
              children.splice(i, 1);
            }
          }
        }
      } else if ( child.m_sib_id === '6' && child.m_gender_id === man ) {
        newArr.push(child);
                  
        if ( child.m_spouse_id !== null ) {
          for ( let i = 0; i < children.length; i++ ) {
            if ( child.m_spouse_id === children[i].m_id ) {
              newArr.push(children[i]);
              children.splice(i, 1);
            }
          }
        }
      } else {
        if ( newArr.indexOf(child) === -1 ) {
          newArr.push(child);
        }
      }
    });
  
    return newArr;
  } else {
    return newArr;
  }
  
}
