export function closePopupMember(e) {
  const popupMembers = document.querySelectorAll('.js-popup-member');

  if ( e.target === this || e.type === 'keyup') {
    popupMembers.forEach(popup => {
      popup.classList.remove('is-show');
      popup.setAttribute('member-id', '');
    });
  }
}

export function handlerClickWithoutPopupMember(e) {
  e.stopPropagation();
  const parents = document.querySelectorAll('.js-members .js-open-member');
  let target = e.target;

  parents.forEach(parent => {
    let child = parent.querySelector('.js-popup-member');
    let itsChild = target === child || child.contains(target);
    let itsParent = target === parent || parent.contains(target);
    let childIsShow = child.classList.contains('is-show');

    if (!itsChild && !itsParent && childIsShow ) {
      child.classList.remove('is-show');
    }

  });
    
}
