const popup = document.querySelector('.js-popup-info-ok');

export default function popupInfoOk(title, btnQuit) {
  const closePopup = popup.querySelectorAll('.js-popup-info-ok-close');
  const btnQuitPopup = popup.querySelector('.js-popup-info-ok-quit');
  const titlePopup = popup.querySelector('.js-popup-info-ok-title');

  popup.classList.add('is-show');

  titlePopup.innerHTML = '';
  titlePopup.innerHTML = title;

  closePopup.forEach(btn => {
    btn.removeEventListener('click', fnClosePopup);
  });

  closePopup.forEach(btn => {
    btn.addEventListener('click', fnClosePopup);
  });
  console.log(btnQuit);
  if ( btnQuit ) {
    btnQuitPopup.classList.add('is-show');

    closePopup.forEach(btn => {
      if ( btn.classList.contains('btn') ) {
        btn.innerText = 'Продовжити';
      }
    });

    btnQuitPopup.removeEventListener('click', reload);
    btnQuitPopup.addEventListener('click', reload);
  } else {
    btnQuitPopup.classList.remove('is-show');

    closePopup.forEach(btn => {
      btn.innerText = 'Ок';
    });
  }
}

function fnClosePopup() {
  popup.classList.remove('is-show');
}

function reload() {
  location.reload();
}
