import popupInfoOk from '../popup/popup-info-ok';

export default function updateIconErrorFamily() {
  const tabTreeFamily = document.querySelector('.js-members li[data="tree"]');
  const tabTreeHousehold = document.querySelector('.js-members li[data="household"]');
  const btnsHouseholds = document.querySelectorAll('.js-member-household-tabs li');
  const households = window.User.rooms[0].households;
  //   let checkHousehold = checkHouseholdObj();
  let currentHouseholdNum = +window.User.rooms[0].current_household;
  console.log('update family');
  if ( (tabTreeFamily.classList.contains('is-active') && tabTreeFamily.classList.contains('error')) || 
       (tabTreeFamily.classList.contains('is-active') && tabTreeFamily.classList.contains('warning')) ||
       (tabTreeFamily.classList.contains('is-active') && tabTreeFamily.classList.contains('ok')) ) {
    console.log('ok');
  } else if ( tabTreeFamily.classList.contains('is-active') && !tabTreeHousehold.classList.contains('is-active') ) {
    console.log('message');
    popupInfoOk('Додайте всіх членів домогосподарства. Потім заповніть по кожному члену домогосподарства персональну анкету.');
  }
  households.forEach((household, i) => {

    let errorClass;
    let errorProgressMember = 'ok';

    for ( let i = 0; i < household.members.length; i++ ) {
      if ( +household.members[i].progress !== 22 ) {
        errorProgressMember = 'warning';
        break;
      }
    }
    // console.log(tabTreeHousehold.getAttribute('class').indexOf('ok'));

    if ( +household.h_household_member_count !== household.members.length ) {
      errorClass = 'error';
    } else {
      errorClass = errorProgressMember; 
    }
    
    // если уже редактировали домогосподарство, навешиваю класс ошибки
    if ( household.edit ) {
      
      if ( errorClass === 'ok' ) {

        if ( !tabTreeHousehold.classList.contains('error') && !tabTreeHousehold.classList.contains('warning') ) {
          btnsHouseholds[i].classList.add(errorClass);
                
        } else {

        //   let errorClassForMainTab;

        //   if ( tabTreeHousehold.getAttribute('class').indexOf('warning') !== -1 ) {
        //     errorClassForMainTab = 'warning';
        //   } else if ( tabTreeHousehold.getAttribute('class').indexOf('error') !== -1 ) {
        //     errorClassForMainTab = 'error';
        //   }

          btnsHouseholds[i].classList.remove('ok', 'warning', 'error');
          btnsHouseholds[i].classList.add(household.h_error_class);
        }
      } else {
        console.log(btnsHouseholds[i]);
        btnsHouseholds[i].classList.remove('ok', 'warning', 'error');
        btnsHouseholds[i].classList.add(errorClass);
      }
    

      if ( household.h_household_number === currentHouseholdNum ) {
        tabTreeFamily.classList.remove('ok', 'warning', 'error');
        tabTreeFamily.classList.add(errorClass);
      }
    }

  });
  
}
