export default function householdTabClick(event) {
  event.preventDefault();

  const tabs = document.querySelector('.js-room-household-tabs');
  tabs.querySelectorAll('li').forEach(el => {
    el.classList.remove( 'is-active' );
  });

  this.classList.add( 'is-active' );
  window.User.rooms[0].current_household = this.dataset.household;

}
