import Member from '../Member';
import renderTreeMembers from '../render-tree-members';
import sortMembersForRenderTree from '../sort-members-for-render-tree';
import updateIconErrorFamily from '../update-icon-error-family';
import updateIconErrorHousehold from '../../household/update-icon-error-houshold';

export default function createNewMember(currentForm) {
  const data = { m_id: `${new Date().getTime()}`, progress: 2 };
  const selects = currentForm.querySelectorAll('select');
  const inputs = currentForm.querySelectorAll('input');
  const selectSpouse = currentForm.querySelector('select[name="m_spouse_id"]');
  const selectParent = currentForm.querySelector('select[name="m_parent_id"]');
  const selectsAge = currentForm.querySelectorAll('select[data="age"]');
  const momOrDad = '4';
  const currentAge = document.querySelector('.js-member-age').innerText.replace(/[^\/0-9]/g, '');
  const selectedChildren = currentForm.querySelectorAll('select[multiple] option:checked');
  const children = Array.from(selectedChildren).map(child => child.value);

  // записываю значения с формы
  selects.forEach(select => {
    if ( select.getAttribute('multiple') === null ) {
      data[select.name] = select.value;
    } else {

      if ( children.length > 0 ) {
        data[select.name] = children;
      } else {
        data[select.name] = [];
      }

    }
  });

  if ( selectParent.value !== '' ) {
    for ( let i = 0; i < window.members.length; i++ ) {
      if ( +window.members[i].m_id === +selectParent.value ) {
        window.members[i].m_children.push(data.m_id);
      }
    }
  }
  console.log(window.members);
  inputs.forEach(input => {
    if (input.type === 'radio' && input.checked) {
      data[input.name] = input.value;
    } else if (input.type !== 'radio' && input.type !== 'checkbox') {
      data[input.name] = input.value;
    } else if ( input.type === 'checkbox' ) {
      data[input.name] = input.checked;
    }
  });
  
  // если массив пустой, записываю идентификатор в обьекте что это первый член домохозяйства
  if ( members.length === 0 ) {
    data.is_first = true;
    data.m_sib_id = '1';

  }

  // возраст - полных лет
  data.m_age = parseInt(currentAge);

  // let currentDate = new Date(2019, 1, 12);
  // let currentMonth = currentDate.getMonth() + 1;
  // let currentYear = currentDate.getFullYear();

  // if ( currentMonth >= +data.m_birth_month ) {
  //   data.m_age = currentYear - +data.m_birth_year;
  // } else {
  //   data.m_age = currentYear - +data.m_birth_year - 1;
  // }

  let households = window.User.rooms[0].households;
  let currentNumberHousehold = +window.User.rooms[0].current_household;

  // записываю в обьект id текущего домохозяйства для нового члена
  households.forEach(house => {
    if ( currentNumberHousehold === +house.h_household_number ) {
      data.m_h_id = house.h_id;
    }
  });

  // создаю обьект нового члена домохозяйства
  const currentMember = new Member(data);
  members.push(currentMember);
  currentForm.reset();

  // обнуляю валидность ввода возраста для следущего члена домохозяйства
  selectsAge.forEach(select => {
    select.setAttribute('valid', 'false');
  });

  // обнуляю выбор муж/жена
  [...selectSpouse.children].forEach(option => {
    if ( option.value !== '' ) {
      option.parentNode.removeChild(option);
    }
  });
  // обнуляю выбор папа/мама
  [...selectParent.children].forEach(option => {
    if ( option.value !== '' ) {
      option.parentNode.removeChild(option);
    }
  });
  // запрещаю доступ к выбору связей/муж-жена/папа-мама для следущего члена домохозяйства
  // selectRelationship.disabled = true;
  selectSpouse.disabled = true;
  selectParent.disabled = true;

  currentForm.classList.remove('is-show');

  // проверяю если выбрана связь уже с женатым/замужней удаляю у них эти связи
  if ( currentMember.m_spouse_id ) {
    let prevMarriageId;

    members.forEach(member => {

      if ( member.m_id === currentMember.m_spouse_id ) {
        prevMarriageId = member.m_spouse_id;
        member.m_spouse_id = null;
        console.log(member);
      }

      members.forEach(mem => {
        if ( mem.m_id === prevMarriageId ) {
          mem.m_spouse_id = null;
          console.log(mem);
        }
      });
      
      
    });
  }
  
  // проверяю еcли выбрана связь с членом домохозяйства - устанавливаю второму
  members.forEach(member => {
    let currentId;
    if ( member.m_spouse_id ) {
      members.forEach(member_connection => {
        if ( +member_connection.m_id === +member.m_spouse_id ) {
          member_connection.m_spouse_id = member.m_id;
          currentId = member.m_id;
        }
      });
    }

    if ( member.m_spouse_id === currentId ) {
      member.m_spouse_id = null;
    }

    // устанавливаю связь с мамой/папой первому члену д/х
    if ( currentMember.m_sib_id === momOrDad ) {
      members.forEach(member => {
        if ( member.is_first ) {
          member.m_parent_id = currentMember.m_id;
        }
      });
    }

    // дети
    children.forEach(childId => {
      
      if ( +member.m_id === +childId ) {
        let currentChild = member;
        
        // если нет родителя, ставлю id 
        if ( currentChild.m_parent_id === null ) {
          currentChild.m_parent_id = currentMember.m_id;

        } else {
          // иначе ищу родителя для удаления из его масива детей
          for ( let i = 0; i < members.length; i++ ) {
            if ( members[i].m_id === currentChild.m_parent_id ) {
              let removeChildren = members[i].m_children; // масив детей из которого нужно удалить
              let indexRemoveChildId = null;

              for ( let j = 0; j < removeChildren.length; j++ ) {
                // ищу ребенка в масиве для удаления
                if ( removeChildren[j] === childId ) {
                  indexRemoveChildId = j;
                  break;
                }
              }
              removeChildren.splice(indexRemoveChildId, 1);
            }
          } // end for
          // 
          currentChild.m_parent_id = currentMember.m_id;
        }
        

      }
    });

  });

  // очищаю поле дети
  if ( window.selectChildren ) {
    console.log('create remove select children');
    window.selectChildren.destroy();
    window.selectChildren = null;
    currentForm.querySelector('[select]').innerHTML = '';
  }
  
  // перерисовываю дерево 
  let sortMembers = sortMembersForRenderTree(members);
  renderTreeMembers(sortMembers);
  
  updateIconErrorHousehold();
  updateIconErrorFamily();
}
