import saveInfo from '../../handlers/save-info';
import initRoom from '../room/init-room';
import { removeMember } from '../member/clone-member';
import initHousehold from '../household/init-household';
import updateHouseholds from '../household/update-households';
import checkMemberData from '../member/check-member-data';
import renderTreeMembers from '../member/render-tree-members';
import sortMembersForRenderTree from '../member/sort-members-for-render-tree';
import initMembers from '../member/init-member';
import householdUpdateTabs2 from '../household/household-update-tabs2';
import editHousehold from '../household/edit-household';
import checkRoomData from '..//room/check-room-data';
import sendData from '../../handlers/send-data';
import updateIconErrorFamily from '../member/update-icon-error-family';

const root = document.querySelector('.js-root');
const allSections = root.querySelectorAll('[data=section]');

export function initNavigation() {
  const btnSave = root.querySelector('.js-btn_save');
  const btnRoom = root.querySelector('.js-btn-room');
  const btnSendData = root.querySelector('.js-btn-send-data');
  const btnHouseholds = root.querySelector('.js-btn-households');

  btnSave.addEventListener('click', saveInfo);

  btnRoom.addEventListener('click', showRoom);

  btnHouseholds.addEventListener('click', showHouseholds);

  btnSendData.addEventListener('click', sendData);
}

export function showRoom() {
  
  if ( root.getAttribute('data-section') === 'family' ) {
    const activeTabFamilyTop = document.querySelector('.js-tabs-title-family li.is-active');
  
    if ( activeTabFamilyTop.getAttribute('data') === 'household' ) {
      let householdIndex = util.household();
      util.setValues(window.User.rooms[0].households[householdIndex], document.querySelector('.js-household-form'));
    }
  
  } else if ( root.getAttribute('data-section') === 's_member' ) {
    checkMemberData().then(() => {
      let currentMemberId = document.querySelector('.js-user-block .js-member').getAttribute('data-id');
      let currentMember;
      let formMember =  document.querySelector('.js-form-member');
      let formMemberTab =  document.querySelectorAll('.js-form-member .js-tabs-contains-disabled li');
      let householdIndex = util.household();
      let arrProgress = [];
      
      window.User.rooms[0].households[householdIndex].members.forEach(member => {
        if ( currentMemberId === member.m_id ) {
          currentMember = member;
        }
      });
      
      formMemberTab.forEach(tab => {
        if ( tab.classList.contains('ok') || tab.classList.contains('disabled') ) {
          arrProgress.push('ok');
        }
      });
      
      currentMember.progress = arrProgress.length + 2;
      util.setValues(currentMember, formMember);
      
      
      allSections.forEach(el => {
        if ( el.classList.contains('room') ) {
          el.removeAttribute('hidden');
        } else {
          el.setAttribute('hidden', true);
        }
      });
      
      root.setAttribute('data-section', 'room');
                
      tabsRoomStartingPosition();
      initRoom();
      removeMember();
      initHousehold();
      updateHouseholds();
  
    }).catch(() => {
      return;
    });
  }
  
  if ( root.getAttribute('data-section') !== 's_member' && root.getAttribute('data-section') !== 'room' ) {

    tabsRoomStartingPosition();
    initRoom();
    removeMember();
    initHousehold();
    updateHouseholds();
  
    allSections.forEach(el => {
      if ( el.classList.contains('room') ) {
        el.removeAttribute('hidden');
      } else {
        el.setAttribute('hidden', true);
      }
    });
  
    root.setAttribute('data-section', 'room');
  
    if ( window.User.rooms[0].first_time === false ) {
      checkRoomData().then(() => {});
      document.querySelector('.js-popup-check-continue').click();
    }
      
    if ( window.User.rooms[0].first_time === true ) {
      window.User.rooms[0].first_time = false;
    }
  }
    
  document.querySelector('.js-household-label').innerText = 'Домогосподарства';
}
  
function showHouseholds() {
  const allTabsTitleTop = root.querySelectorAll('.family li[data]');
  const allTabsContent = root.querySelectorAll('.family #members-1 .js-tab-content');
      
  if ( root.getAttribute('data-section') === 's_member' ) {
    checkMemberData().then(() => {
      let currentMemberId = document.querySelector('.js-user-block .js-member').getAttribute('data-id');
      let currentMember;
      let formMember =  document.querySelector('.js-form-member');
      let formMemberTab =  document.querySelectorAll('.js-form-member .js-tabs-contains-disabled li');
      let householdIndex = util.household();
      let arrProgress = [];
        
      window.User.rooms[0].households[householdIndex].members.forEach(member => {
        if ( currentMemberId === member.m_id ) {
          currentMember = member;
        }
      });
        
      formMemberTab.forEach(tab => {
        if ( tab.classList.contains('ok') || tab.classList.contains('disabled') ) {
          arrProgress.push('ok');
        }
      });
        
      currentMember.progress = arrProgress.length + 2;
      util.setValues(currentMember, formMember);
  
      initHousehold();
        
      // Делаем ссылку на текущий массив members в глобальной области видимости
      window.members = window.User.rooms[0].households[householdIndex].members;
  
      // Отрисовываем вкладки домохозяйств на секции family
      householdUpdateTabs2();
      updateIconErrorFamily();
  
      editHousehold();
  
      initMembers();
  
      // Перерисовуем дерево семейных отношений
      let sortMembers = sortMembersForRenderTree(members);
      renderTreeMembers(sortMembers);
  
      removeMember();
  
      allSections.forEach(el => {
        if ( el.classList.contains('family') ) {
          el.removeAttribute('hidden');
        } else {
          el.setAttribute('hidden', true);
        }
      });
  
      root.setAttribute('data-section', 'family');
  
      document.querySelector('.js-household-label').innerText = 'Домогосподарства';
        
      // вкладка (Родине дерево) делаю активной
      allTabsTitleTop.forEach((tab, i) => {
        if ( i === 1 ) {
          allTabsContent[i].classList.add('is-active');
          allTabsTitleTop[i].classList.add('is-active');
        } else {
          allTabsContent[i].classList.remove('is-active');
          allTabsTitleTop[i].classList.remove('is-active');
        }
        
      });
  
        
    }).catch(() => {
      return;
    });
  } else if ( root.getAttribute('data-section') === 'room' ) {
    checkRoomData().then(() => {
      // Переписываем значения с формы form-room в глобальный объект window.User.rooms[0]
      util.setValues(window.User.rooms[0], document.querySelector('.js-form-room'));
        
      initHousehold();
        
      // Делаем ссылку на текущий массив members в глобальной области видимости
      let householdIndex = util.household();
      window.members = window.User.rooms[0].households[householdIndex].members;
  
      // Отрисовываем вкладки домохозяйств на секции family
      householdUpdateTabs2();
      updateIconErrorFamily();
  
      editHousehold();
  
      initMembers();
      
      // Перерисовуем дерево семейных отношений
      let sortMembers = sortMembersForRenderTree(members);
      renderTreeMembers(sortMembers);
    
      allSections.forEach(el => {
        if ( el.classList.contains('family') ) {
          el.removeAttribute('hidden');
        } else {
          el.setAttribute('hidden', true);
        }
      });
            
      root.setAttribute('data-section', 'family');
        
      // вкладка (Опис домогосподарства) делаю активной
      allTabsTitleTop.forEach((tab, i) => {
        if ( i === 0 ) {
          allTabsContent[i].classList.add('is-active');
          allTabsTitleTop[i].classList.add('is-active');
        } else {
          allTabsContent[i].classList.remove('is-active');
          allTabsTitleTop[i].classList.remove('is-active');
        }
        
      });
  
      document.querySelector('.js-household-label').innerText = 'Домогосподарства';
  
    }).catch(err => {
      console.log(err);
      return;
    });;
  }
  
}
  
export function tabsRoomStartingPosition() {
  const tabs = document.querySelector('.js-tabs-room');
  const titles = tabs.querySelectorAll('li[data-info]');
  const contents = tabs.querySelectorAll('.js-tab-content');
  const tabsHouseholdCountMembers = document.querySelector('.js-room-household-tabs');
    
  titles.forEach(title => {
    title.classList.remove('is-active');
  });
  
  contents.forEach(content => {
    content.classList.remove('is-active');
  });
  
  titles[0].classList.add('is-active');
  contents[0].classList.add('is-active');
  
  for ( let i = 0; i < tabsHouseholdCountMembers.children.length; i++ ) {
    let tabTitle = tabsHouseholdCountMembers.children[i];
  
    if ( +tabTitle.getAttribute('data-household') === 1 ) {
      tabTitle.click();
    }
    break;
  }
  
}
