import generateName from '../household/generate-name';
import { controlMatrixInet, controlMatrixTablet } from './control-matrix';
let controlMatrix = null;

export default function checkMemberObj() {
  let h = window.User.rooms[0].households;
  let warning = false;
  let error = false;
  let buf = [];
  let name, h_name, res;
  let frame, style;

  User.rooms[0].r_datasource === 1 ? controlMatrix = controlMatrixInet : controlMatrix = controlMatrixInet;

  for ( let i = 0; i < h.length; i++ ) {
    h_name = generateName( h[i].h_household_number );

    for ( let j = 0; j < h[i].members.length; j++ ) {

      res = checkOne( h[i], h[i].members[j] );

      let currentCode = 0;
      res.forEach(data => {
        if ( data.code > currentCode ) {
          currentCode = data.code;
        }
      });
      
      buf.push( {errors: res, m_name: h[i].members[j].m_name, h_name: h_name, m_id: h[i].members[j].m_id, m_h_id: h[i].members[j].m_h_id } );

      // Навешиваем стиль на соотв. члена
      switch(currentCode) {
        case 0:
          style = 'ok';
          break;
        case 1:
          style = 'warning';
          break;
        case 2:
          style = 'error';
          break;
      }

      frame = document.querySelector('.member.js-open-member.js-member[data-id="'+h[i].members[j].m_id+'"]');

      if( frame ) {
        frame.classList.remove('ok', 'warning', 'error');
        frame.classList.add( style );
      }

    } // for(let j=0; j<h[i].members.length; j++) - члены

  } // for(let i=0; i<h.length; i++) - домохозяйства

  return buf;
}


