import createSelect from './create-select';
import { cityName } from '../../../dictionary/names';

export default function initSelectSettlement(formMember, currentMember) {
  const selectSettlement = formMember.querySelectorAll('[data-select-settlement]');
  // создаю выпадающий список для языков
  //const selectSettlementName = selectSettlement.getAttribute('data-select-settlement');
  //const selectSettlementClass = selectSettlement.getAttribute('data-class');
  //const selectSettlementPlaceholder = selectSettlement.getAttribute('data-placeholder');
  //const dataForSelectSettlement = [selectSettlementPlaceholder, selectSettlementName, selectSettlementClass, selectSettlement];
  const catalogSettlement = [];

  // готовлю данные для выпадающих списков городов
  [...selectSettlement].forEach(select => {
    let selectData = [];

    selectData.push(select.getAttribute('data-placeholder'));
    selectData.push(select.getAttribute('data-select-settlement'));
    selectData.push(select.getAttribute('data-class'));
    selectData.push(select);
    catalogSettlement.push(selectData);
  });


  catalogSettlement.forEach(dataSelect => {

    let catalog = [];
    let selectName = dataSelect[1];
    let region;
    let selected;

    switch(selectName) {
      case 'm_job_settlement':
        region = parseInt( currentMember.m_job_region );
        if ( ! isNaN( region ) ) {

          if ( window.cityDictionary !== undefined ) {

            for ( let i = 0; i < window.cityDictionary.length; i++ ) {
              // Добавляем только те населённые пункты, которые относятся к этому региону
              if ( window.cityDictionary[i].region_id === region ) {

                if ( +currentMember[selectName] === +window.cityDictionary[i].terson_id && currentMember[selectName] !== util.DEFAULT_VALUE_REGION_COUNTRY ) {
                  catalog.push( {value: window.cityDictionary[i].terson_id, label: cityName(window.cityDictionary[i])} );
                  selected = window.cityDictionary[i].terson_id;
                } else {
                  catalog.push( {value: window.cityDictionary[i].terson_id, label: cityName(window.cityDictionary[i])} );
                }
              } // if(window.cityDictionary[i].region_id === region)
            } // for(i=0; i<cityDictionary.length; i++)
          } // if(window.cityDictionary !== undefined)
        } // if( ! isNaN( parseInt(currentMember.m_job_region) ) )
        break;

      case 'm_left_settlement':
        region = parseInt( currentMember.m_left_region );
        if ( !isNaN( region ) && region < 100 ) {
          if ( window.cityDictionary !== undefined) {

            for ( let i = 0; i < window.cityDictionary.length; i++ ) {
              // Добавляем только те населённые пункты, которые относятся к этому региону
              if ( window.cityDictionary[i].region_id === region ) {

                if ( +currentMember[selectName] === +window.cityDictionary[i].terson_id && currentMember[selectName] !== util.DEFAULT_VALUE_REGION_COUNTRY ) {
                  catalog.push( {value: window.cityDictionary[i].terson_id, label: cityName(window.cityDictionary[i])} );
                  selected = window.cityDictionary[i].terson_id;
                } else {
                  catalog.push( {value: window.cityDictionary[i].terson_id, label: cityName(window.cityDictionary[i])} );
                }

              } // if(window.cityDictionary[i].region_id === region)
            } // for(i=0; i<cityDictionary.length; i++)
          } // if(window.cityDictionary !== undefined)
        }
        break;
    } // switch(selectName)

    // инициализирую выпадающее списки по собраным данным
    createSelect( dataSelect, catalog, false, selected );

  });

}
