

export default function getCoordinate(currentHousestatus) {
  return new Promise((resole, reject) => {
    const options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0
    };
        
    let housestatus;
    console.log(currentHousestatus);

    function success(crd) {

      window.cryptoWrapper.encrypt(JSON.stringify(crd.latitude))
        .then(cryptLatitude => {
          currentHousestatus.hs_latitude_check = cryptLatitude;
          window.cryptoWrapper.encrypt(JSON.stringify(crd.longitude))
            .then(cryptLongitude => {
              currentHousestatus.hs_longitude_check = cryptLongitude;
              currentHousestatus.hs_start_datetime = +new Date();
              housestatus = currentHousestatus;
              resole(housestatus);
            })
            .catch(error => {
              popupInfoOk( 'Помилка шифрування координвтів latitude: ' + err);
            });
        })
        .catch(error => {
          popupInfoOk( 'Помилка шифрування координвтів latitude: ' + err);
        });
    }
              
    function error(err) {
      console.log(`ERROR(${err})`);
      reject(err.message);
            
    }
    // location.get().then(res => {success(res);})
    //   .catch(e => {error(e);});

    location.get(success, error);
    // if(navigator.geolocation) {
    //   console.log('navigator.geolocation TRUE');
    //   navigator.geolocation.getCurrentPosition(success, error, options);
    // }else {
    //   console.log('navigator.geolocation FALSE');
    // }



  });
  // там ошибка ERROR(1): User denied Geolocation
}
