export default function mySelect( name ) {
  let obj = null;
  let BreakException = {};
  try {
    util.selectsMember.forEach(sel => {
      
      if ( name === 'm_speak_other_languages' && sel.length === undefined ) {
        if(sel.passedElement.element.name === name) {
          obj = sel;
          throw BreakException;
        }
      } else if ( sel.length !== undefined ) {
        if(sel[0].name === name) {
          obj = sel;
          throw BreakException;
        }
      }
      
    });
  } catch (e) {
    if (e !== BreakException) throw e;
  }
  return obj;
}
