export default function getCryptoCert() {
  return new Promise(resolve => {

    // ноль из джавы не равен нолю из джс ? нулл у нас в джс nativeStorage.getData('cryptoFiles')); или мб ? а не и так да+
    if ( storage.get('cryptoFiles') !== null) {
      console.log('cryptoFiles have');
      resolve(true);
    } else {

      const downloadCert = document.querySelector('.js-cert-form');
      downloadCert.style.display = 'block';
      let publicCert = null;
      const inputs = document.querySelectorAll('.js-cert-form input');
      const form = document.querySelector('.js-cert-form');
      const cryptoFiles = {
        cert1: null,
        cert2: null,
        key: null,
        psw: null,
        public: null
      };

      query.post(`${util.DOMEN}/reg/synchro`,
        Base64.encode( JSON.stringify({
          content: JSON.stringify({
            type: 'get_open_key'
          })
        }))
      ).then(res => {
        console.log(res);
        const response = JSON.parse(res);
        publicCert = util.arrayBufferToBase64( new Uint8Array(response.data.FILE_PUBLIC_CERTIFICATE) );

      }).catch(err => alert('Помилка публічного сертифікату' + err));


      inputs.forEach(input => {
        input.addEventListener('change', function(event) {
          const self = this;

          var reader = new FileReader();
          console.log('FileReader start');
          reader.onload = function() {
            const file = new Uint8Array( reader.result );

            if ( self.getAttribute('data') === 'psw') {
              let psw = util.uint8arrayToStringMethod(file);
              cryptoFiles.psw = psw;

            } else {
              switch ( self.getAttribute('data') ) {
                case 'cert1':
                  cryptoFiles.cert1 = util.arrayBufferToBase64(file);

                  break;
                case 'cert2':
                  cryptoFiles.cert2 = util.arrayBufferToBase64(file);

                  break;
                case 'key':
                  cryptoFiles.key = util.arrayBufferToBase64(file);

                  break;
              }
            }
          };

          reader.readAsArrayBuffer( event.target.files[0] );

        });
      });

      form.addEventListener('submit', function(e) {
        e.preventDefault();

        cryptoFiles.public = publicCert;
        storage.set('cryptoFiles', JSON.stringify(cryptoFiles));
        console.log('add all cert');
        resolve(false);
      });
    }
  });
}
