import openPopupCheck from '../popup/popup-check';

export default function checkHouseholdData() {
  return new Promise(function(resolve, reject) {
    let res = util.checkHousehold();
    let activeTabInFamily = document.querySelector('.js-tabs-title-family li[data].is-active').getAttribute('data');
    console.log(res);
    if ( activeTabInFamily === 'tree' ) {
      
      resolve(0);
      return;
    }

    if ( res.code === 0 ) {
	    resolve(0);
      return;
      
    } else {
	  // открываем popup
      openPopupCheck(1, res.text, resolve, reject);

    } // if(res.code === 0) ... else

  }); // new Promise()
}
