export default function membersUpdate() {
  const form = document.querySelector('.js-household-members2');
  let num = window.User.rooms[0].current_household;
  let household = window.User.rooms[0].households[util.household()];

  form.querySelectorAll('.js-household-member-count2').forEach(el => {
    el.checked = false;
    el.dataset.household = num;
  });
  form.querySelector('.js-household-member-count2-text').dataset.household = num;

  if( household.h_household_member_count <= 5 ) {
    document.querySelector(".radio-num__input.js-household-member-count2[value='" +
			household.h_household_member_count + "']").checked = true;
    document.querySelector('.js-household-member-count2-text').value = '';
  } else {
    document.querySelector('.js-household-member-count2-text').value = household.h_household_member_count;
  }

}
