import householdTabClick2 from './household-tab-click2';
import membersUpdate from './members-update';
import memberCountBinds from './member-count-binds';
import generateName from './generate-name';
import updateIconErrorHousehold from './update-icon-error-houshold';
import updateIconErrorFamily from '../member/update-icon-error-family';

export default function householdUpdateTabs2() {
  const tabs = document.querySelector('.js-member-household-tabs');

  // Убираем существующие закладки
  while (tabs.firstChild) {
    tabs.removeChild(tabs.firstChild);
  }

  let households = window.User.rooms[0].households;
  for ( let i = 0; i < households.length; i++ ) {
    let newTab = document.createElement( 'li' );
    let newA = document.createElement( 'a' );
    let num = households[i].h_household_number;
    
    newTab.classList.add('circle');
    newA.href = '#household-' + num;
    newA.innerHTML = generateName(households[i].h_household_number);
    
    newTab.appendChild( newA );
    newTab.dataset.household = num;

    if ( +num === +window.User.rooms[0].current_household ) {
      newTab.classList.add( 'is-active' );
    }

    newTab.addEventListener('click', householdTabClick2);
    tabs.appendChild( newTab );
  }
  
  if ( tabs.children.length > 6 ) {
    tabs.classList.add('is-scroll');
  } else {
    tabs.classList.remove('is-scroll');
  }
  
  updateIconErrorHousehold();

  // Указываем кол-во членов текущего домохозяйства и проставляем всем inputs правильный data-household
  //membersUpdate();

  // Привязываем обработчик на изменение кол-ва членов домохозяйства
  //memberCountBinds();
}
