window.checkInitHousehold = false; // передаем в функцию initHousehold, чтоб не навешивать повторно оброботчики

export default function openAllForms() {
  const btnOpenAllContent = document.querySelector('.js-open-all-content');
  btnOpenAllContent.addEventListener('click', checkTask );
}

function checkTask() {
  const popupIndivRoom = document.querySelector('.js-popup-indiv-room');
  const indexTask = popupIndivRoom.getAttribute('current-index-task');
  const housestatusID = popupIndivRoom.getAttribute('current-hs-id');
  let housestatus = JSON.parse(storage.get('housestatus'));
  const popupRoomRewritten = document.querySelector('.js-popup-room-rewritten');
  const popupRoomRewrittenResolve = document.querySelector('.js-popup-room-rewritten-resolve');
  const popupRoomRewrittenReject = document.querySelector('.js-popup-room-rewritten-reject');
  const self = this;

  if ( self.getAttribute('stationary') === 'true' ) {
    handlerOpenAllForm(self);
    return;
  }

  if ( self.parentElement.parentElement.getAttribute('data-source') === '3' || self.parentElement.parentElement.getAttribute('data-source') === '4' ) {
    housestatus = JSON.parse(storage.get('currentHousestatus'));
  }
  console.log(self);
  console.log(JSON.stringify(housestatus));
  for ( let i = 0; i < housestatus.length; i++ ) {
    if ( +housestatus[i].hs_id === +housestatusID ) {

      if ( housestatus[i].tasks[indexTask].t_code !== null && housestatus[i].tasks[indexTask].t_datasource !== util.CODE_CONTROL_TASK ) {
        popupRoomRewritten.classList.add('is-show');

        showPopupRoomRewritten(JSON.parse(housestatus[i].tasks[indexTask].t_code))
          .then(
            resolve => {
              if ( resolve === 'rewritten' ) {

                popupRoomRewritten.classList.remove('is-show');
                handlerOpenAllForm(self);
              } else if ( resolve === 'resolve' ) {

                popupRoomRewritten.classList.remove('is-show');
                popupRoomRewrittenResolve.classList.add('is-show');

                showPopupRoomRewrittenResolve()
                  .then(
                    changeStatus => {
                      popupRoomRewrittenResolve.classList.remove('is-show');

                      const poemId = document.querySelector('.js-popup-room-rewritten-code input:checked').value;
                      const currentHsId = self.parentElement.parentElement.getAttribute('current-hs-id');
                      const currentIndexTask = self.parentElement.parentElement.getAttribute('current-index-task');
                      const housestatus = storage.get('housestatus') ? JSON.parse(storage.get('housestatus')) : JSON.parse(storage.getItem('currentHousestatus'));
                      let nameLocalStorage = storage.get('housestatus') ? 'housestatus' : 'currentHousestatus';

                      housestatus.forEach(house => {
                        if ( +house.hs_id === +currentHsId ) {
                          house.tasks[+currentIndexTask].t_status_id = +changeStatus;
                          house.tasks[+currentIndexTask].t_finish_datetime = +new Date();
                          house.tasks[+currentIndexTask].t_r_id = +poemId;

                          for ( let i = 0; i < house.tasks.length; i++ ) {
                            if ( +house.tasks[i].t_status_id <= 2 ) {
                              house.hs_status_id = 2;
                              break;
                            } else if ( +house.tasks[i].t_status_id >= 3 ) {
                              house.hs_status_id = 3;
                            }
                          }
                        }
                      });
                      console.log(+currentHsId);
                      console.log(currentHsId);
                      storage.set(nameLocalStorage, JSON.stringify( housestatus ));
                      document.querySelector('.js-popup-indiv-room-close').click();
                      renderTableRooms(+currentHsId);
                    },
                    rewritten => {
                      popupRoomRewrittenResolve.classList.remove('is-show');
                      handlerOpenAllForm(self);
                    }).catch(error => console.log(error));
              }
            },
            reject => {
              popupRoomRewritten.classList.remove('is-show');
              popupRoomRewrittenReject.classList.add('is-show');

              showPopupRoomRewrittenReject()
                .then(
                  cansel => {
                    popupRoomRewrittenReject.classList.remove('is-show');
                  },
                  rewritten => {
                    popupRoomRewrittenReject.classList.remove('is-show');
                    handlerOpenAllForm(self);
                  });
            }).catch(error => console.log(error));

      } else if ( housestatus[i].hs_datasource === util.CODE_CONTROL_TASK && housestatus[i].tasks[indexTask].t_data4check !== null ) {
        window.currentControlTaskId = housestatus[i].hs_id;
        // расшифровка помещений паролем
        const protectRooms = (rooms, taskId, houseId) => {
          const indexLastRoom = rooms.length - 1;
          rooms.forEach((room, i) => {
            if ( room.task_id === taskId ) {
              let sourceData = JSON.stringify( room.room );
              let buf = new TextEncoder('utf-8').encode( sourceData );
              console.log(room.room);
              window.cryptoWrapper.protect( buf )
                .then(res => {
                  rooms[i].room = res;
                  if ( i === indexLastRoom ) {
                    storage.set('rooms', JSON.stringify( rooms ));

                    console.log(rooms);
                    renderTableControlTasks(taskId, houseId);
                    document.querySelector('.js-popup-indiv-room').classList.remove('is-show');
                  }
                }).catch(err => {
                  console.log( 'Помилка шифрування: ' + err);
                  return;
                });
            }
          });


        };
        // расшифровка сертификатом при получении помещений с бека
        window.cryptoWrapper.decrypt(window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_data4check).then(res => {
          const decryptRoom = String.fromCharCode.apply(null, res.data);
          const decryptRoomToUTF8 = iconv.decode(decryptRoom, 'utf8');
          const controlRooms = JSON.parse(decryptRoomToUTF8).rooms;
          const rooms = storage.get('rooms') ? JSON.parse( storage.get('rooms') ) : [];
          let taskId;
          let houseId = housestatus[i].hs_id;
          if ( controlRooms.length > 1 ) {

            if ( housestatus[i].tasks[indexTask].t_status_id === 1 ) {
              housestatus[i].tasks[indexTask].t_status_id = 2;
              controlRooms.forEach(room => {
                room.r_task_id = housestatus[i].tasks[indexTask].t_id;
                room.r_hs_id = housestatus[i].hs_id;
                room.r_datasource_inherit = room.r_datasource;
                room.r_datasource = util.CODE_CONTROL_TASK;
                taskId = housestatus[i].tasks[indexTask].t_id;

                rooms.push({
                  room: room,
                  status: housestatus[i].tasks[indexTask].t_status_id,
                  task_created_date: housestatus[i].tasks[indexTask].t_created_date,
                  task_id: housestatus[i].tasks[indexTask].t_id,
                  room_id: room.r_id
                });
              });
              storage.set('currentHousestatus', JSON.stringify( housestatus ));
              storage.set('controlTasks', JSON.stringify( housestatus ));
              let currentAllData = window.allData ? window.allData : JSON.parse(storage.get('allData'));
              let currentInstructor = JSON.parse(storage.get('currentInstructor'));
              let counting = currentInstructor.staffusers_counting;
              for ( let i = 0; i < counting.length; i++ ) {
                if ( counting[i].t_datasource === util.CODE_CONTROL_TASK ) {
                  counting[i].housestatus = housestatus;
                  break;
                }
              }

              storage.set('currentInstructor', JSON.stringify( currentInstructor ));
              currentAllData.staffusers_census[0].staffusers_instructor[0] = currentInstructor;
              window.allData = currentAllData;
              storage.set('allData', JSON.stringify( currentAllData ));
              protectRooms(rooms, taskId, houseId);
              popupInfoOk('Дане приміщення було переписано декілька разів. Перевірте та оберіть запис приміщення, в якому вказана достовірна інформація.');
            } else {
              renderTableControlTasks(housestatus[i].tasks[indexTask].t_id, housestatus[i].hs_id);
              document.querySelector('.js-popup-indiv-room').classList.remove('is-show');
            }

          } else {
            handlerOpenAllForm(self);
          }

        });
      } else {
        handlerOpenAllForm(self);
      }
      break;
    }
  }
}

function showPopupRoomRewritten(arrayPoems) {
  return new Promise((resolve, reject) => {
    const popupRoomRewritten = document.querySelector('.js-popup-room-rewritten');
    const btnResolve = popupRoomRewritten.querySelector('[data="resolve"]');
    const btnReject = popupRoomRewritten.querySelector('[data="reject"]');
    const btnRoomRewritten = popupRoomRewritten.querySelector('[data="rewritten"]');
    const code = popupRoomRewritten.querySelector('.js-popup-room-rewritten-code');
    const templatePoem = (obj) => {
      return `<div class="radio ">
                <input class="radio__input" type="radio" id="poem${obj.id}" value="${obj.id}" name="poems">
                <label class="radio__label" for="poem${obj.id}">${obj.poem}</label>
                <p class="radio__subtitle">
                  <span>Кількість домогосподарств: ${obj.households_count}</span>
                  <span>Кількість респондентів: ${obj.member_count}</span>
                <p>
              </div>`;
    };

    btnResolve.classList.add('disabled');

    code.innerHTML = '';

    arrayPoems.forEach(poem => {
      const container = document.createElement('template');
      container.innerHTML = templatePoem(poem);
      code.appendChild(container.content);
    });

    if ( arrayPoems.length === 1 ) {
      code.querySelector('.radio input').checked = true;
      btnResolve.classList.remove('disabled');
    }

    code.querySelectorAll('.radio input').forEach(radio => {
      radio.addEventListener('change', (e) => {
        btnResolve.classList.remove('disabled');
      });
    });

    btnResolve.addEventListener('click', function(e) {
      resolve(this.getAttribute('data'));
    });
    btnReject.addEventListener('click', function(e) {
      reject(this.getAttribute('data'));
    });
    btnRoomRewritten.addEventListener('click', function(e) {
      resolve(this.getAttribute('data'));
    });
  });
}

function showPopupRoomRewrittenResolve() {
  return new Promise((changeStatus, rewritten) => {
    const popupRoomRewrittenResolve = document.querySelector('.js-popup-room-rewritten-resolve');
    const btnResolve = popupRoomRewrittenResolve.querySelector('[data="resolve"]');
    const btnRoomRewritten = popupRoomRewrittenResolve.querySelector('[data="rewritten"]');

    btnResolve.addEventListener('click', (e) => changeStatus(9) );
    btnRoomRewritten.addEventListener('click', (e) => rewritten());
  });
}

function showPopupRoomRewrittenReject() {
  return new Promise((cansel, rewritten) => {
    const popupRoomRewrittenReject = document.querySelector('.js-popup-room-rewritten-reject');
    const btnCancel = popupRoomRewrittenReject.querySelector('[data="cansel"]');
    const btnRoomRewritten = popupRoomRewrittenReject.querySelector('[data="rewritten"]');

    btnCancel.addEventListener('click', (e) => cansel() );
    btnRoomRewritten.addEventListener('click', (e) => rewritten());
  });
}

function handlerOpenAllForm(self) {
  const root = document.querySelector('.js-root');
  const popupIndivRoom = document.querySelector('.js-popup-indiv-room');
  const outParentBlock = document.querySelector('.out');
  const preloader = document.querySelector('.preloader');
  const allSections = root.querySelectorAll('[data=section]');
  const roomId = popupIndivRoom.getAttribute('current-room-id');
  const housestatusID = popupIndivRoom.getAttribute('current-hs-id');
  const stationary = self.getAttribute('stationary');
  const datasource = +self.parentElement.parentElement.getAttribute('data-source');

  self.setAttribute('stationary', 'false');
  console.log(datasource);
  popupIndivRoom.classList.remove('is-show');
  // показываем прелоадер пока грузим справочники
  preloader.classList.add('is-show');
  outParentBlock.classList.add('preloader-show');

  initDictionaries().then(res => {
    // Генерируем случайный ID
    let timestamp = + new Date();
    let rand = Math.floor( Math.random() * 10000 );

    if ( stationary === 'true') {
      const role = +document.body.getAttribute('role');
      const region = document.querySelector('#r_region_id');
      const city = document.querySelector('#r_terson_id');
      const terson_distr = document.querySelector('#r_terson_distr');
      const street = document.querySelector('#r_street_id');
      const building = document.querySelector('#r_house_number_id');
      const flatNum = document.querySelector('#r_flat_number');
      const flatNumNot = document.querySelector('#r_flat_without_number');
      let stationaryHouses = null;
      let currentStationaryHouse = null;
      let indexStationaryHouse = null;
      let currentTask;
      let currentDepartaments;
      let currentInstructor;
      let currentHousestatus;
      let currentAllData = window.allData ? window.allData : JSON.parse(storage.get('allData'));
      if ( role === util.CODE_MANAGER ) {

        currentAllData.staffusers_census[0].staffusers_instructor.forEach(instructor => {
          if ( instructor.su_instructor_department === 0 && currentAllData.user.su_id === instructor.su_id ) {
            console.log( instructor.staffusers_counting[0].housestatus );
            stationaryHouses = instructor.staffusers_counting[0].housestatus;
            currentDepartaments = instructor.staffusers_counting[0];
            currentInstructor = instructor;
            // currentStationaryHouse = currentInstructor.staffusers_counting[0].housestatus;
          }
        });
      } else if ( role === util.CODE_INSTRUCTOR ) {
        currentAllData.staffusers_census[0].staffusers_instructor[0].staffusers_counting.forEach(counting => {
          if ( counting.su_counting_department === 0 && counting.t_datasource === 3 ) {
            console.log( counting.housestatus );
            stationaryHouses = counting.housestatus;
            currentDepartaments = counting;
            currentInstructor = currentAllData.staffusers_census[0].staffusers_instructor[0];
            // currentStationaryHouse = currentInstructor.staffusers_counting[0].housestatus;
          }
        });
      } else if ( role === util.CODE_COPYIS ) {
        stationaryHouses = JSON.parse(storage.get('housestatus'));

        for ( let i = 0; i < stationaryHouses.length; i++ ) {
          if ( stationaryHouses[i].hs_datasource === util.CODE_HOMELESS ) {
            currentStationaryHouse = stationaryHouses[i];
            break;
          }
        }

        console.log(currentStationaryHouse);
        for ( let i = 0; i < currentStationaryHouse.tasks.length; i++ ) {
          if ( currentStationaryHouse.tasks[i].t_status_id === 1 ) { // 1 - не виконано
            currentTask = currentStationaryHouse.tasks[i];
            break;
          }
        }
        console.log(currentTask);
      }

      if ( role !== util.CODE_COPYIS ) {
        // ищу есть ли уже этот дом в стационарных участках
        for ( let i = 0; i < stationaryHouses.length; i++ ) {
          if ( stationaryHouses[i].hs_house_id === +building.value ) {
            indexStationaryHouse = i;
            currentStationaryHouse = stationaryHouses[i];
            const tasks = currentStationaryHouse.tasks;

            for ( let j = 0; j < tasks.length; j++ ) {
              if ( tasks[j].t_flat_num === null ) {
                currentTask = tasks[j];

                break;
              }
            }
            if (!currentTask) {
              preloader.classList.remove('is-show');
              outParentBlock.classList.remove('preloader-show');
              popupInfoOk('Ви досягли ліміту кількості переписних дільниць. Будь ласка натисніть кнопку "Оновити"');

              return false;
            }
            break;
          }
        }
        // если нету беру новый слот
        if ( currentStationaryHouse === null ) {
          for ( let i = 0; i < stationaryHouses.length; i++ ) {
            if ( stationaryHouses[i].hs_house_id === null ) {
              indexStationaryHouse = i;
              currentStationaryHouse = stationaryHouses[i];
              currentTask = currentStationaryHouse.tasks[0];
              break;
            }
          }
          if (!currentStationaryHouse) {
            preloader.classList.remove('is-show');
            outParentBlock.classList.remove('preloader-show');
            popupInfoOk('Ви досягли ліміту кількості переписних дільниць. Будь ласка натисніть кнопку "Оновити"');

            return false;
          }
          console.log(currentStationaryHouse);
          currentStationaryHouse.hs_house_id = +building.value;
          currentStationaryHouse.hs_street_name = street.textContent;
          currentStationaryHouse.hs_num = building.textContent;
          currentStationaryHouse.hs_settlement_name = city.textContent;

        }
      }

      window.User = new User({first_time: true});

      if ( role !== util.CODE_COPYIS ) {
        window.User.rooms[0] = new Room({
          r_task_id: currentTask.t_id,
          r_hs_id: currentTask.t_hs_id,
          r_id: timestamp.toString() + rand.toString(),
          r_region_id: region.value,
          r_terson_id: city.value,
          r_street_id: street.value,
          r_house_number_id: building.value,
          r_flat_number: flatNum.value,
          r_flat_without_number: flatNumNot.checked,
          r_datasource: util.CODE_STATIONARY,
          r_status_id: 10 //
        });

        for ( let i = 0; i < window.buildingDictionary.length; i++ ) {
          if ( +window.buildingDictionary[i].house_id === +building.value ) {
            window.User.rooms[0].r_terson_id_distr = window.buildingDictionary[i].house_terson_id_distr;
          }
        }

        currentTask.t_flat_num = flatNum.value;

      } else {
        window.User.rooms[0] = new Room({
          r_task_id: currentTask.t_id,
          r_hs_id: currentTask.t_hs_id,
          r_id: timestamp.toString() + rand.toString(),
          r_region_id: region.value,
          r_terson_id: city.value,
          r_terson_id_distr: city.value
        });

        if ( !isNaN(terson_distr.value) ) {
          window.User.rooms[0].r_terson_id_distr = terson_distr.value;
        }
        window.User.rooms[0].r_datasource = util.CODE_HOMELESS;
      }

      console.log(currentStationaryHouse);
      if ( currentInstructor ) {
        currentInstructor.staffusers_counting[0].housestatus[indexStationaryHouse] = currentStationaryHouse;
      }

      console.log(currentInstructor);
      currentTask.t_start_datetime = +new Date();
      if ( role !== util.CODE_COPYIS ) {
        storage.set('stationary', JSON.stringify( currentInstructor.staffusers_counting[0].housestatus ));
        storage.set('currentDepartaments', JSON.stringify( currentDepartaments ));
        storage.set('currentInstructor', JSON.stringify( currentInstructor ));
      } else {
        storage.set('housestatus', JSON.stringify(stationaryHouses) );
      }

      console.log(currentStationaryHouse);
      console.log(stationaryHouses);
      console.log(currentTask);
      console.log(window.User.rooms[0]);

      initAllForm();
    } else if ( +popupIndivRoom.getAttribute('data-source') === util.CODE_CONTROL_TASK ) {

      if ( window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_data4check === null && window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_status_id === 1 ) {
        let currentControlTask = window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')];
        let households = JSON.parse(storage.get('currentHousestatus'));
        let currentHousehold;
        households.forEach(household => {
          if ( household.hs_id === currentControlTask.t_hs_id ) {
            currentHousehold = household;
          }
        });
        window.User = new User({first_time: true});
        window.User.rooms[0] = new Room({
          r_task_id: currentControlTask.t_id,
          r_hs_id: currentControlTask.t_hs_id,
          r_id: timestamp.toString() + rand.toString(),
          r_region_id: currentHousehold.hs_region_id,
          r_terson_id: currentHousehold.hs_terson_id,
          r_terson_id_distr: currentHousehold.hs_terson_id_distr,
          r_flat_number: currentHousehold.hs_num,
          r_datasource: util.CODE_CONTROL_TASK
        });

        console.log(window.User.rooms[0]);
        currentControlTask.t_start_datetime = +new Date();

        initAllForm();
      } else if ( window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_data4check !== null && window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_status_id === 1 ) {
        window.User = new User();

        try {
          console.log(window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_data4check);
          console.log(JSON.parse(window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_data4check));

          window.User.rooms[0] = JSON.parse( window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_data4check ).rooms[0];
          window.User.rooms[0].r_task_id = +JSON.parse( window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_id );
          window.User.rooms[0].r_hs_id = +JSON.parse( window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_hs_id );

          console.log(window.User.rooms[0]);

          parseDataForRoom(window.User.rooms[0]);

          initAllForm();
        } catch (error) {
          console.log(error);
          window.cryptoWrapper.decrypt(window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_data4check).then(res => {
            console.log(res);
            let decryptRoom = String.fromCharCode.apply(null, res.data);
            console.log(decryptRoom);
            let decryptRoomToUTF8 = iconv.decode(decryptRoom, 'utf8');
            console.log(decryptRoomToUTF8);

            window.User.rooms[0] = JSON.parse( decryptRoomToUTF8 ).rooms[0];
            window.User.rooms[0].r_task_id = +JSON.parse( window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_id );
            window.User.rooms[0].r_hs_id = +JSON.parse( window.controlTasks[+popupIndivRoom.getAttribute('current-index-task')].t_hs_id );

            console.log(window.User.rooms[0]);

            parseDataForRoom(window.User.rooms[0]);

            initAllForm();
          }).catch(error => {
            console.log(error);
          });
        }

      } else {
        window.User = new User();
        const rooms = JSON.parse( storage.get('rooms') );
        const housestatus = JSON.parse(storage.get('currentHousestatus'));
        let currentTask = null;
        let haveRoom = false;
        console.log('test start control-task');
        if ( rooms ) {
          rooms.forEach((currentRoom,i) => {
            if ( +currentRoom.task_id === +roomId ) {
              haveRoom = true;
              window.cryptoWrapper.unprotect(currentRoom.room).then(res => {
                console.log(res);
                let decryptRoom = String.fromCharCode.apply(null, res);
                console.log(decryptRoom);
                let decryptRoomToUTF8 = iconv.decode(decryptRoom, 'utf8');
                console.log(decryptRoomToUTF8);
                currentRoom.room = JSON.parse(decryptRoomToUTF8);
                window.User.rooms[0] = currentRoom.room;

                initAllForm();
              }).catch(error => {
                for ( let i = 0; i < housestatus.length; i++ ) {
                  if ( housestatus[i].hs_house_id ) {
                    const tasks = housestatus[i].tasks;
                    for ( let j = 0; j < tasks.length; j++ ) {
                      if ( tasks[j].t_id === +roomId ) {
                        currentTask = tasks[j];

                        window.User = new User();
                        window.User.rooms[0] = new Room({
                          r_task_id: currentTask.t_id,
                          r_hs_id: currentTask.t_hs_id,
                          r_id: timestamp.toString() + rand.toString(),
                          r_flat_number: currentTask.t_flat_num,
                          r_region_id: housestatus[i].hs_region_id,
                          r_terson_id:  housestatus[i].hs_terson_id,
                          r_terson_id_distr:  housestatus[i].hs_terson_id_distr,
                          r_datasource: util.CODE_CONTROL_TASK
                        });

                        initAllForm();
                        console.log(currentTask);
                        console.log(window.User.rooms[0]);
                        break;
                      }
                    }
                  }
                }
                alert('Помилка розшифровки!\n' + error);
              });
            }
            console.log(i);
          });
        }
        console.log('test finish control-task');

        if ( !haveRoom ) {
          console.log('need new room control-task');

          for ( let i = 0; i < housestatus.length; i++ ) {
            if ( housestatus[i].hs_house_id ) {
              const tasks = housestatus[i].tasks;
              for ( let j = 0; j < tasks.length; j++ ) {
                if ( tasks[j].t_id === +roomId ) {
                  currentTask = tasks[j];

                  window.User = new User();
                  window.User.rooms[0] = new Room({
                    r_task_id: currentTask.t_id,
                    r_hs_id: currentTask.t_hs_id,
                    r_id: timestamp.toString() + rand.toString(),
                    r_flat_number: currentTask.t_flat_num,
                    r_region_id: housestatus[i].hs_region_id,
                    r_terson_id:  housestatus[i].hs_terson_id,
                    r_terson_id_distr:  housestatus[i].hs_terson_id_distr,
                    r_datasource: util.CODE_CONTROL_TASK
                  });

                  initAllForm();
                  console.log(currentTask);
                  console.log(window.User.rooms[0]);
                  break;
                }
              }
            }
          }
        }
      }

    } else if ( (stationary === 'false' && datasource === util.CODE_STATIONARY) || (stationary === 'false' && datasource === util.CODE_STATIONARY && storage.get('rooms') === null) ) {
      window.User = new User();
      const rooms = JSON.parse( storage.get('rooms') );
      const housestatus = JSON.parse(storage.get('currentHousestatus'));
      let currentTask = null;
      let haveRoom = false;
      console.log('test start');
      if ( rooms ) {
        rooms.forEach((currentRoom,i) => {
          if ( +currentRoom.task_id === +roomId ) {
            haveRoom = true;
            window.cryptoWrapper.unprotect(currentRoom.room).then(res => {
              console.log(res);
              let decryptRoom = String.fromCharCode.apply(null, res);
              console.log(decryptRoom);
              let decryptRoomToUTF8 = iconv.decode(decryptRoom, 'utf8');
              console.log(decryptRoomToUTF8);
              currentRoom.room = JSON.parse(decryptRoomToUTF8);
              window.User.rooms[0] = currentRoom.room;

              initAllForm();
            }).catch(error => {
              for ( let i = 0; i < housestatus.length; i++ ) {
                if ( housestatus[i].hs_house_id ) {
                  const tasks = housestatus[i].tasks;
                  for ( let j = 0; j < tasks.length; j++ ) {
                    if ( tasks[j].t_id === +roomId ) {
                      currentTask = tasks[j];

                      window.User = new User();
                      window.User.rooms[0] = new Room({
                        r_task_id: currentTask.t_id,
                        r_hs_id: currentTask.t_hs_id,
                        r_id: timestamp.toString() + rand.toString(),
                        r_flat_number: currentTask.t_flat_num,
                        r_region_id: housestatus[i].hs_region_id,
                        r_terson_id:  housestatus[i].hs_terson_id,
                        r_terson_id_distr:  housestatus[i].hs_terson_id_distr
                      });

                      initAllForm();
                      console.log(currentTask);
                      console.log(window.User.rooms[0]);
                      break;
                    }
                  }
                }
              }
              alert('Помилка розшифровки!\n' + error);
            });
          }
          console.log(i);
        });
      }
      console.log('test finish');

      if ( !haveRoom ) {
        console.log('need new room');

        for ( let i = 0; i < housestatus.length; i++ ) {
          if ( housestatus[i].hs_house_id ) {
            const tasks = housestatus[i].tasks;
            for ( let j = 0; j < tasks.length; j++ ) {
              if ( tasks[j].t_id === +roomId ) {
                currentTask = tasks[j];

                window.User = new User();
                window.User.rooms[0] = new Room({
                  r_task_id: currentTask.t_id,
                  r_hs_id: currentTask.t_hs_id,
                  r_id: timestamp.toString() + rand.toString(),
                  r_flat_number: currentTask.t_flat_num,
                  r_region_id: housestatus[i].hs_region_id,
                  r_terson_id:  housestatus[i].hs_terson_id,
                  r_terson_id_distr:  housestatus[i].hs_terson_id_distr
                });

                initAllForm();
                console.log(currentTask);
                console.log(window.User.rooms[0]);
                break;
              }
            }
          }
        }
      }
    } else if ( storage.get('rooms') === null || storage.get('rooms') === undefined || ( Array.isArray(JSON.parse( storage.get('rooms') )) && !JSON.parse( storage.get('rooms') ).length) ) {
      let housestatus = JSON.parse(storage.get('housestatus'));
      let currentTask;
      console.log(housestatus);
      console.log(housestatusID);
      console.log(roomId);
      for ( let i = 0; i < housestatus.length; i++ ) {
        if ( +housestatus[i].hs_id === +housestatusID ) {

          for ( let j = 0; j < housestatus[i].tasks.length; j++ ) {
            if ( +housestatus[i].tasks[j].t_id === +roomId ) {

              currentTask = housestatus[i].tasks[j];
              currentTask.t_start_datetime = +new Date();
              console.log(currentTask);
              window.User = new User();
              window.User.rooms[0] = new Room(
                {
                  r_task_id: roomId,
                  r_hs_id: housestatusID,
                  r_id: timestamp.toString() + rand.toString(),
                  r_flat_number: currentTask.t_flat_num,
                  r_flat_without_number: currentTask.t_flat_without_num !== 1 ? false : true,
                  r_region_id: housestatus[i].hs_region_id,
                  r_terson_id: housestatus[i].hs_terson_id,
                  r_terson_id_distr: housestatus[i].hs_terson_id_distr,
                  r_street_id: housestatus[i].hs_street_id,
                  r_house_number_id: housestatus[i].hs_house_id
                });
              storage.set('housestatus', JSON.stringify( housestatus ));
              break;
            }
          }

          break;
        }
      }

      initAllForm();

    } else {
      let housestatus = JSON.parse(storage.get('housestatus'));
      let currentHousestatus = null;
      let flatNumber;
      let flatWithoutNumber;
      for ( let i = 0; i < housestatus.length; i++ ) {
        if ( +housestatus[i].hs_id === +housestatusID ) {
          currentHousestatus = housestatus[i];
          break;
        }
      }

      for ( let i = 0; i < currentHousestatus.tasks.length; i++ ) {
        if ( currentHousestatus.tasks[i].t_id === +roomId ) {
          flatNumber = currentHousestatus.tasks[i].t_flat_num;
          flatWithoutNumber = currentHousestatus.tasks[i].t_flat_without_num;
          currentHousestatus.tasks[i].t_start_datetime = +new Date();
          break;
        }
      }

      for ( let i = 0; i < housestatus.length; i++ ) {
        if ( +housestatus[i].hs_id === +housestatusID ) {
          housestatus[i] = currentHousestatus;
          break;
        }
      }

      storage.set('housestatus', JSON.stringify( housestatus ));

      window.User = new User();
      const rooms = JSON.parse( storage.get('rooms') );


      searchRoom()
        .then(
          resolve => {
            initAllForm();
          },
          reject => {
            window.User.rooms[0] = new Room({
              r_task_id: +roomId,
              r_hs_id: +housestatusID,
              r_id: timestamp.toString() + rand.toString(),
              r_region_id: currentHousestatus.hs_region_id,
              r_terson_id: currentHousestatus.hs_terson_id,
              r_terson_id_distr: currentHousestatus.hs_terson_id_distr,
              r_flat_number: flatNumber,
              r_flat_without_number: flatWithoutNumber !== 1 ? false : true,
              r_street_id: currentHousestatus.hs_street_id,
              r_house_number_id: currentHousestatus.hs_house_id
            });

            initAllForm();
          });

      function searchRoom() {
        return new Promise((resolve, reject) => {
          for ( let i = 0; i < rooms.length; i++ ) {
            if ( +rooms[i].task_id === +roomId ) {
              window.cryptoWrapper.unprotect(rooms[i].room).then(res => {
                console.log(res);
                let x = String.fromCharCode.apply(null, res);
                console.log(x);
                let inp = iconv.decode(x, 'utf8');
                console.log(inp);
                rooms[i].room = JSON.parse(inp);
                window.User.rooms[0] = rooms[i].room;
                resolve();
              }).catch(error => {
                reject();
              });
              break;
            } else if ( i === rooms.length - 1 ) {
              reject();
            }
          }

        });
      }
    }

    allSections.forEach(el => {
      if ( el.classList.contains('room') ) {
        el.removeAttribute('hidden');
      } else {
        el.setAttribute('hidden', true);
      }
    });

    root.setAttribute('data-section', 'room');

    function initAllForm() {
      if ( window.User ) {
        addValuesAddresSection();
      }
      tabsRoomStartingPosition();
      initRoom(datasource);
      console.log(window.checkInitHousehold);
      initHousehold(true, window.checkInitHousehold);
      updateHouseholds();
      window.checkInitHousehold = true;

      if ( window.User.rooms[0].first_time === false ) {
        checkRoomData().then(() => {});
        document.querySelector('.js-popup-check-continue').click();
      } else {
        document.querySelectorAll('.js-tabs-room li.circle').forEach(li => {
          li.classList.remove('error', 'warning', 'ok');
        });
      }

      // скрываем прелоадер после загрузки справочников
      preloader.classList.remove('is-show');
      outParentBlock.classList.remove('preloader-show');
    }

  });
}

function addValuesAddresSection() {
  region.setChoiceByValue( +window.User.rooms[0].r_region_id );
  city.setChoiceByValue( +window.User.rooms[0].r_terson_id );
  street.setChoiceByValue( +window.User.rooms[0].r_street_id );
  building.setChoiceByValue( +window.User.rooms[0].r_house_number_id );
  document.querySelector('#r_flat_number').value = window.User.rooms[0].r_flat_number;
  document.querySelector('#r_flat_without_number').checked = window.User.rooms[0].r_flat_without_number;
}
