// Проверочная функция (контроль) для формы домохозяйства
export default function checkHousehold() {
  let tempResidents = document.querySelector('input[name=h_temp_residents]').checked;

  // Проверяем название домохозяйства
  const h_name = document.querySelector('input[name=h_name]');
  let name = h_name.value;
  // Проверяем количество комнат 
  const roomCheckbox = document.querySelector('input:checked[name=h_household_rooms]');
  const roomInput = document.querySelector('.js-household-rooms-text');
  const roomPart = document.querySelector('input:checked[name=h_household_room_parts]');

  if ( isNaN(+roomInput.value)) {
    let roomsVal = roomInput.value;
    roomInput.value = '';
    return {code: 2, text: `Невірно зазначено Кількість зайнятих членами домогосподарства житлових кімнат: "${roomsVal}" (має бути ціле число)`};
  }

  if ( name.trim() === '' ) {
    name = 'Домогосподарство ' + window.User.rooms[0].current_household;
    h_name.value = name;
  }

  // Проверяем количество комнат 
  if ( !roomCheckbox && !roomInput.value && !roomPart && !tempResidents && (+window.User.rooms[0].r_building_id <= 8 || +window.User.rooms[0].r_building_id === 99) ) {
    return {code: 1, text: 'Ви не зазначили кількість кімнат'};
  }

  // Проверяем тип собственности
  const h_ownership = document.querySelector('input:checked[name=h_ownership_id]');
  if ( h_ownership === null && !tempResidents && (+window.User.rooms[0].r_building_id <= 8 || +window.User.rooms[0].r_building_id === 99) ) {
    return {code: 1, text: 'Ви не зазначили тип володіння житловим приміщенням'};
  }

  // Проверяем наличие Интернет
  const h_internet = document.querySelector('input:checked[name=h_improve_internet_id]');
  if ( h_internet === null && tempResidents === false ) {
    return {code: 1, text: 'Ви не зазначили доступ до мережі Інтернет'};
  }

  return {code: 0, text: 'Ok'};
}
