export default function myMember( id ) { // возвращает член с заданным id или текущего члена
  let currentMember = null;
  let hIndex = util.household();
  let BreakException = {};

  if(id === undefined) {
    id = document.querySelector('.js-user-block .js-member').getAttribute('data-id');
  }

  try {
    window.User.rooms[0].households[hIndex].members.forEach(member => {
      if ( id === member.m_id ) {
        currentMember = member;
      }
    });
  } catch (e) {
    if (e !== BreakException) throw e;
  }
  return currentMember;
}
