export default function adjustLeave() {

  let block = false;
  let input = document.querySelector('input:checked[name=m_temp]');
  let tab = document.querySelector("a[href='#s_member-19']").parentElement;

  if(input !== null && +input.value === 3) {
    block = true;
  }

  if( block ) {
    const other = document.querySelector( 'input[name=m_planmigration_reason_another]' );
    tab.classList.remove('ok', 'warning', 'error');
    tab.classList.add('disabled');

    document.querySelectorAll('input[name=m_planmigration_reason_id]').forEach(el => {
      el.disabled = true;
      el.checked = false;
    });
    document.querySelectorAll('input[name=m_planning_leave_place]').forEach(el => {
      el.disabled = true;
      el.checked = false;
    });

    other.value = '';
    other.disabled = true;
  } else {
    tab.classList.remove('disabled');
    document.querySelectorAll('input[name=m_planning_leave_place]').forEach(el => {
      el.disabled = false;
    });
  }

}
