export function householdSearchById( id ) {
  let buf = window.User.rooms[0].households;
  for(let i=0; i<buf.length; i++) {
    if(buf[i].h_id === id) {
      return i;
    }
  }
  return -1;
}


export function householdGenerateId() {
  // Генерируем случайный ID
  let timestamp = + new Date();
  let rand = Math.floor( Math.random() * 10000 );
  return timestamp.toString() + rand.toString();
}


export function householdSearchByNum( num ) {
  // Поиск индекса элемента с заданным h_household_number в массиве домохозяйств
  let buf = window.User.rooms[0].households;

  for ( let i = 0; i < buf.length; i++ ) {
    if ( +buf[i].h_household_number === +num ) {
      return i;
    }
  }
  return -1;
}


export function householdMaxNum( num ) {
  // Поиск максимального h_household_number в массиве домохозяйств
  let buf = window.User.rooms[0].households;
  if(buf.length === 0) {
    return 0; // Если пока нет ни одного домохозяйства
  }
  let max = buf[0].h_household_number;
  for(let i=1; i<buf.length; i++) {
    if(buf[i].h_household_number > max) {
      max = buf[i].h_household_number;
    }
  }
  return max;
}


export function getCurrentHouseholdIndex() {
  let household = window.User.rooms[0].current_household;
  let index = householdSearchByNum( household );
  console.log(index);
  return index;
}


