import initRoom from '../../room/init-room';
import checkRoomData from '../../room/check-room-data';
import initHousehold from '../../household/init-household';
import { tabsRoomStartingPosition } from '../../core/navigation';
import { region, city, street, building, terson_distr } from '../init-address-form';
import updateHouseholds from '../../household/update-households';
import User from '../../../classes/User';
import initDictionaries from '../../dictionary/init-dictionaries';
import iconv from 'iconv-lite';
import parseDataForRoom from '../../../helpers/parse-data-for-room';

export default function renderTableControlTasks(taskId, houseId) {
  const indiv = document.querySelector('.js-indiv-main-section');
  const controlTasksTable = indiv.querySelector('.js-table-control-tasks');
  const titleAddress = indiv.querySelector('.js-title-address-control-tasks');
  const rooms = JSON.parse( storage.get('rooms') );
  const currentRooms = [];
  const housestatus = JSON.parse(storage.getItem('currentHousestatus'));
  let textAddress = '';


  console.log(rooms);

  for ( let i = 0; i < housestatus.length; i++ ) {
    if ( +housestatus[i].hs_id === +houseId ) {
      let tasks = housestatus[i].tasks;
      for ( let j = 0; j < tasks.length; j++ ) {
        if ( +tasks[j].t_id === +taskId ) {
          textAddress = `${housestatus[i].hs_settlement_name}, ${housestatus[i].hs_street_name}, ${housestatus[i].hs_num}, кв. ${tasks[j].t_flat_num}`;
          break;
        }
      }
      break;
    }
  }
  let currentRoomsLength = 0;
  for ( let i = 0; i < rooms.length; i++ ) {
    if ( +rooms[i].task_id === +taskId ) {
      currentRoomsLength++;
    }
  }
  console.log(taskId);
  console.log(currentRoomsLength);
  for ( let i = 0; i < rooms.length; i++ ) {
    if ( +rooms[i].task_id === +taskId ) {

      window.cryptoWrapper.unprotect(rooms[i].room).then(res => {
        let decryptRoom = String.fromCharCode.apply(null, res);
        console.log(decryptRoom);
        let decryptRoomToUTF8 = iconv.decode(decryptRoom, 'utf8');
        rooms[i].room = JSON.parse(decryptRoomToUTF8);
        currentRooms.push(rooms[i]);
        console.log(currentRooms);
        if ( currentRoomsLength === currentRooms.length ) {
          console.log('renderControlTask');
          renderControlTask(currentRooms, textAddress);
        }
      }).catch(error => {
        console.log('Помилка розшифровки!\n' + error);
      });

    }
  }

  function renderControlTask(currentControlRooms, textAddress) {
    console.log(currentControlRooms);

    titleAddress.innerHTML = textAddress;
    controlTasksTable.innerHTML = '';
    controlTasksTable.innerHTML = templateHeaderControlTasks();

    currentControlRooms.forEach((room, i) => {
      const container = document.createElement('template');
      container.innerHTML = templateRowControlTasks(room, i);
      controlTasksTable.appendChild(container.content);
    });
    const btnsOpenAllForm = controlTasksTable.querySelectorAll('.js-open-control-task');
    btnsOpenAllForm.forEach(btn => {
      btn.addEventListener('click', initAllForm);
    });

    const allIndivSection = document.querySelectorAll('.js-indiv-section');
    const controlTasksSection = document.querySelector('.js-indiv-control-tasks');

    allIndivSection.forEach(section => {
      section.setAttribute('hidden', true);
    });
    controlTasksSection.removeAttribute('hidden');
  }
}

function templateHeaderControlTasks() {
  return `<div class="indiv-control-tasks__table-row indiv-control-tasks__table-row-header">
              <div class="indiv-control-tasks__table-col">№</div>
              <div class="indiv-control-tasks__table-col">Джерело</div>
              <div class="indiv-control-tasks__table-col">Статус</div>
              <div class="indiv-control-tasks__table-col">Кількість домогосподарств</div>
              <div class="indiv-control-tasks__table-col">Кількість респондентів</div>
              <div class="indiv-control-tasks__table-col">Відсоток заповненості</div>
            </div>`;
}
function templateRowControlTasks(obj,i) {
  return `<a href="#" class="indiv-control-tasks__table-row js-open-control-task" data-id="${obj.room.r_id}" data-task-id="${obj.task_id}">
              <div class="indiv-control-tasks__table-col js-control-tasks-number">${i+1}</div>
              <div class="indiv-control-tasks__table-col js-control-tasks-datasource">${textDatasourceRoom(+obj.room.r_datasource_inherit)}</div>
              <div class="indiv-control-tasks__table-col js-control-tasks-status">${textStatusRoom(+obj.room.r_status_id)}</div>
              <div class="indiv-control-tasks__table-col js-control-tasks-count-households">${obj.room.households.length}</div>
              <div class="indiv-control-tasks__table-col js-control-tasks-count-members">${obj.room.r_member_count}</div>
              <div class="indiv-control-tasks__table-col js-control-tasks-percent">${obj.room.r_percent_filling}%</div>
            </a>`;
}

function initAllForm(e) {
  const root = document.querySelector('.js-root');
  const allSections = root.querySelectorAll('[data=section]');
  const outParentBlock = document.querySelector('.out');
  const preloader = document.querySelector('.preloader');
  const rooms = JSON.parse( storage.get('rooms') );

  window.User = new User();
  // показываем прелоадер пока грузим справочники
  preloader.classList.add('is-show');
  outParentBlock.classList.add('preloader-show');

  for ( let i = 0; i < rooms.length; i++ ) {
    window.cryptoWrapper.unprotect(rooms[i].room).then(res => {
      let decryptRoom = String.fromCharCode.apply(null, res);
      let decryptRoomToUTF8 = iconv.decode(decryptRoom, 'utf8');
      rooms[i].room = JSON.parse(decryptRoomToUTF8);
      if ( +rooms[i].task_id === +this.getAttribute('data-task-id') && +rooms[i].room.r_id === +this.getAttribute('data-id') ) {
        window.User.rooms[0] = rooms[i].room;

        parseDataForRoom(window.User.rooms[0], true);
        initDictionaries().then(res => {

          allSections.forEach(el => {
            if ( el.classList.contains('room') ) {
              el.removeAttribute('hidden');
            } else {
              el.setAttribute('hidden', true);
            }
          });

          root.setAttribute('data-section', 'room');

          if ( window.User ) {
            addValuesAddresSection();
          }
          tabsRoomStartingPosition();
          initRoom(window.User.rooms[0].r_datasource);
          initHousehold(true, window.checkInitHousehold);
          updateHouseholds();
          window.checkInitHousehold = true;

          if ( window.User.rooms[0].first_time === false ) {
            checkRoomData().then(() => {});
            document.querySelector('.js-popup-check-continue').click();
          } else {
            document.querySelectorAll('.js-tabs-room li.circle').forEach(li => {
              li.classList.remove('error', 'warning', 'ok');
            });
          }
          // скрываем прелоадер после загрузки справочников
          preloader.classList.remove('is-show');
          outParentBlock.classList.remove('preloader-show');
        });


      }
    }).catch(error => {
      console.log('Помилка розшифровки!\n' + error);
    });

  }


}

function addValuesAddresSection() {
  region.setChoiceByValue( +window.User.rooms[0].r_region_id );
  city.setChoiceByValue( +window.User.rooms[0].r_terson_id );
  street.setChoiceByValue( +window.User.rooms[0].r_street_id );
  building.setChoiceByValue( +window.User.rooms[0].r_house_number_id );
  document.querySelector('#r_flat_number').value = window.User.rooms[0].r_flat_number;
  document.querySelector('#r_flat_without_number').checked = window.User.rooms[0].r_flat_without_number;
}

const textStatusRoom = (statusId) => {
  const textStatus = JSON.parse( storage.get('r_status') );
  let text = '';

  for ( let i = 0; i < textStatus.length; i++ ) {
    if ( statusId === textStatus[i].t_status_id ) {
      text = textStatus[i].t_status_name;
      break;
    }
  }
  return text;
};

const textDatasourceRoom = (datasource) => {
  let text = '';
  switch(datasource) {
    case util.CODE_INET:
      text = 'Інтернет';
      break;
    case util.CODE_STATIONARY:
      text = 'Переписна дільниця';
      break;
    case util.CODE_TASK:
      text = 'Обхід';
      break;
  }
  return text;
};
