export default class Storage {
  constructor() {
  }

  get(name) {// String
    let x = nativeStorage.getData(name);
    if(x !== 'null') {
      console.log('its from nativeStorage ' + x);
      return x;
    }
    return null;

    // localStorage.getItem();
  }

  set(name, data) { // String, String
    nativeStorage.putData(name, data);
    //  вот єта да? +
  }

  remove(name) { // String
    nativeStorage.delData(name);
    // localStorage.removeItem(name);
  }
}
