export default function popupControlAll(text, resolve, reject) {
  const popup = document.querySelector('.js-popup-control-all');
  const content = popup.querySelector('.js-popup-control-all-content');

  popup.classList.add('is-show');
  document.body.classList.add('is-open-popup');
  content.innerHTML = text;

  popup.addEventListener('click', function closePopup(e) {
    let dataBtn = e.target.getAttribute('data');

    if ( dataBtn === 'return' ) {
      popup.classList.remove('is-show');
      document.body.classList.remove('is-open-popup');
      popup.removeEventListener('click', closePopup);
      reject(2);
    }

    if ( dataBtn === 'continue' ) {
      popup.classList.remove('is-show');
      document.body.classList.remove('is-open-popup');
      popup.removeEventListener('click', closePopup);
      resolve(0);
    }
  });
            
}
