const popup = document.querySelector('.js-popup-check');
const closePopup = popup.querySelector('.popup-check__close');
const content = popup.querySelector('.popup-check__content');

export default function openPopupCheck(mode, text, resolve, reject) {
  popup.classList.remove('error', 'warning');
  if(mode === 2) {
    popup.querySelector('.js-popup-check-title').innerHTML = 'Помилка';
    popup.querySelector('.js-popup-check-continue').parentElement.classList.remove( 'is-show' );
    popup.classList.add( 'error' );
  } else {
    popup.querySelector('.js-popup-check-title').innerHTML = 'Попередження';
    popup.querySelector('.js-popup-check-continue').parentElement.classList.add( 'is-show' );
    popup.classList.add( 'warning' );
  }

  content.innerHTML = text;
  document.querySelector('body').classList.add('is-open-popup');
  popup.classList.add('is-show');

  const continueButton = popup.querySelector('.js-popup-check-continue');
  popup.addEventListener('click', function clickDialog(e) {

    if ( e.target.tagName === 'BUTTON' || this === e.target ) {
      popup.removeEventListener('click', clickDialog);
      popup.classList.remove( 'is-show' );
      document.querySelector('body').classList.remove('is-open-popup');

      if (e.target === continueButton) { // пользователь хочет перейти дальше, несмотря на ошибку
        resolve( 0 );
      } else { // пользователь хочет вернуться и исправить ошибку
        reject( 2 );
      }
    }

  });

}
