import checkTabData from '../components/tabs/check-tab-data.js';
import focusInputInTabs from './focus-input-in-tabs';
import householdUpdateTabs2 from '../components/household/household-update-tabs2';
import updateIconErrorFamily from '../components/member/update-icon-error-family.js';

export default function initTabs() {
  const tabsLinks = document.querySelectorAll('.js-tabs > ul > li');

  function handlerClickTabLink(event) {
    event.preventDefault();
    let currentTarget = event.currentTarget;
    let currentTargetNumebr = currentTarget.getAttribute('data-info');
    let anchorReference = event.target;
    let activeTabContentId = anchorReference.getAttribute('href');
    let activeTabContent = document.querySelector(activeTabContentId);

    const tab = anchorReference.parentElement.parentElement.parentElement;
    
    const contents = [...tab.children[1].children];
    const links = [...tab.children[0].children];

    if ( this.classList.contains('disabled') ) {
      return;
    }
    
    checkTabData().then(function() {

      links.forEach(link => {
        link.classList.remove('is-active');
      });

      currentTarget.classList.add('is-active');

      contents.forEach(content => {
        content.classList.remove('is-active');
      });

      activeTabContent.classList.add('is-active');
      
      focusInputInTabs(currentTargetNumebr);
      
      if ( anchorReference.getAttribute('href') === '#family-2') {
        // при переходе на семейное дерево, перезаписываем текущий обьект household
        util.setValues(window.User.rooms[0].households[util.household()], document.querySelector('.js-household-form'));

        // Делаем ссылку на текущий массив members в глобальной области видимости
        let householdIndex = util.household();
        window.members = window.User.rooms[0].households[householdIndex].members;

        // Отрисовываем вкладки домохозяйств на секции family
        householdUpdateTabs2();
        updateIconErrorFamily();
      }

    }).catch(function(res) {

      console.log( res );
      // ignore unsuccessful result
      return;

    });

  }

  tabsLinks.forEach(link => {
    link.addEventListener('click', handlerClickTabLink);
  });

}
