export default function setValues(obj, form) {
  const selects = form.querySelectorAll('select');
  const inputs = form.querySelectorAll('input');
  
  if ( form.classList.contains('js-form-member')) {
    obj.m_livelihood_sources = [];
  }
  
  selects.forEach(select => {

    if ( select.multiple === true ) {

      const selected = document.querySelectorAll('select[multiple] option:checked');
      const values = Array.from(selected).map(el => el.value);

      if ( values.length > 0 ) {
        obj[select.name] = values;
      } else {
        obj[select.name] = ['999'];
      }

    } else if ( select.name === 'm_arrival_month' || select.name === 'm_live_birth_continuously_month' ) {

      obj[select.name] = select.value;

    } else {

      if ( select.name === 'm_language_id' && isNaN(select.value) ) {

        obj.m_language_another = select.value;
        obj[select.name] = '490';

      } else if ( select.name === 'm_language_id' && !isNaN(select.value) && select.value !== '' ) {

        obj.m_language_another = '';
        obj[select.name] = select.value;

      } else if ( select.name === 'm_national_id_original' && isNaN(select.value) ) {

        obj.m_national_another = select.value;
        obj[select.name] = '990';


      } else if ( select.name === 'm_national_id_original' && !isNaN(select.value) ) {

        obj.m_national_another = '';

        if ( select.value === '' ) {
          obj[select.name] = '999';
        } else {
          obj[select.name] = select.value;
        }

      } else if ( select.name === 'm_job_region' && select.value === '' || 
                  select.name === 'm_main_job_location_another_country' && select.value === '' ||
                  select.name === 'm_left_another_country' && select.value === '' ||
                  select.name === 'm_left_region' && select.value === '' ) 
      {

        obj[select.name] = '999';

      } else if ( select.value !== '' ) {

        obj[select.name] = select.value;
      } else if ( select.name === 'm_job_region' && select.value === '' ||
                  select.name === 'm_left_settlement' && select.value === '') 
      {
        obj[select.name] = '';
      }
    }

  });
  
  let nameInput = null;

  inputs.forEach(input => {
    
    if ( input.type === 'radio' && input.checked ) {

      obj[input.name] = input.value;
      nameInput = input.name;
  
    } else if ( input.type === 'radio' && !input.checked ) {

      if ( input.name !== nameInput && input.name !== 'h_household_rooms' ) {
        obj[input.name] = '99';
      }

    } else if ( input.type === 'checkbox' && input.checked ) {

      if ( Array.isArray(obj[input.name]) ) {
        obj[input.name].push(input.value);
      } else {
        obj[input.name] = true;
      }

    } else if ( input.type === 'checkbox' && !input.checked) {

      if ( !Array.isArray(obj[input.name]) ) {
        obj[input.name] = false;
      }

    } else if ( input.type === 'text' || input.type === 'number' ) {

      obj[input.name] = input.value;

    }
  });

  if ( obj.m_livelihood_sources !== undefined && obj.m_livelihood_sources.length === 0 ) {
    obj.m_livelihood_sources = ['99'];
  }

  if ( form.classList.contains('js-household-form') && !obj.edit ) {
    obj.edit = true;
  }

  console.log(obj);
  return obj;

}
