export default function renderTableRooms(id) {
  const indiv = document.querySelector('.js-indiv-main-section');
  const roomsTable = indiv.querySelector('.js-table-rooms');
  const popupIndivRoom = document.querySelector('.js-popup-indiv-room');
  const closePopupIndivRoom = document.querySelectorAll('.js-popup-indiv-room-close');
  const titleAddress = indiv.querySelector('.js-title-address');
  const titleCountRooms = indiv.querySelector('.js-count-rooms');
  let textAddress;
  let textCountRooms = 0;
  let housestatus;
  const role = +document.body.getAttribute('role');
  const minTimeForAbsentMember = 0; // 1000ms, 60sec, min, hour

  if ( storage.get('housestatus') && +role === util.CODE_COPYIS ) {
    housestatus = JSON.parse( storage.get('housestatus') );
  } else if ( storage.get('currentHousestatus') ) {
    housestatus = JSON.parse(storage.getItem('currentHousestatus'));
  }
  console.log(housestatus);
  console.log(id);

  roomsTable.innerHTML = '';
  roomsTable.innerHTML = templateHeaderRooms();

  for ( let i = 0; i < housestatus.length; i++ ) {
    if ( +housestatus[i].hs_id === +id ) {

      if ( +housestatus[i].hs_datasource === util.CODE_HOMELESS ) {
        textAddress = 'Безпритульні';
      } else {
        textAddress = `${housestatus[i].hs_settlement_name}, ${housestatus[i].hs_street_name}, ${housestatus[i].hs_num}`;
      }

      if ( housestatus[i].tasks[0].t_datasource === 4 ) {
        window.controlTasks = housestatus[i].tasks;
      }

      housestatus[i].tasks.forEach(task => {
        const container = document.createElement('template');

        if ( task.t_flat_num !== null ) {
          textCountRooms++;
          if ( task.t_status_id >= 3 && task.t_status_id !== 4 && task.t_status_id !== 10 && task.t_status_id !== 5 && task.t_status_id !== 8 ) {
            container.innerHTML = templateRowRoomsDisab(task);
          } else if ( task.t_status_id === 4 ) {

            if ( task.t_absent_count === util.CODE_ABSENT_COUNT_MAX ) {
              container.innerHTML = templateRowRoomsDisab(task);
            } else if ( task.t_absent_count < util.CODE_ABSENT_COUNT_MAX && (task.t_absent_date + minTimeForAbsentMember) > +new Date() ) {
              container.innerHTML = templateRowRoomsDisab(task);
            } else {
              container.innerHTML = templateRowRooms(task);
            }

          } else {
            container.innerHTML = templateRowRooms(task);
          }
        } else if ( task.t_status_id === 2 ) {
          container.innerHTML = templateRowRooms(task);
        } else if ( task.t_status_id > 2 ) {
          container.innerHTML = templateRowRoomsDisab(task);
        }

        roomsTable.appendChild(container.content);
      });

      closePopupIndivRoom.forEach(close => {
        close.addEventListener('click', (e) => {
          popupIndivRoom.setAttribute('current-room-id', '');
          popupIndivRoom.setAttribute('current-hs-id', '');
          popupIndivRoom.setAttribute('current-index-task', '');
          popupIndivRoom.setAttribute('data-source', '');
          popupIndivRoom.classList.remove('is-show');
          popupIndivRoom.querySelector('.js-id-room-text').innerText = '';
        });
      });

      break;
    }
  }

  titleAddress.innerText = textAddress;
  titleCountRooms.innerText = textCountRooms;

  const linksRooms = document.querySelectorAll('.js-open-popup-indiv-room');

  linksRooms.forEach((linkOpenForm, i) => {

    if ( role !== util.CODE_COPYIS && +linkOpenForm.getAttribute('data-source') !== 4 && +linkOpenForm.getAttribute('data-source') !== 3 ) {
      linkOpenForm.style.pointerEvents = 'none';
    }

    if ( role === util.CODE_MANAGER && +linkOpenForm.getAttribute('data-source') === 4 ) {
      linkOpenForm.style.pointerEvents = 'none';
    }

    linkOpenForm.addEventListener('click', function(e) {
      popupIndivRoom.setAttribute('current-room-id', linkOpenForm.getAttribute('data-id'));
      popupIndivRoom.setAttribute('current-hs-id', linkOpenForm.getAttribute('data-id-hs'));
      popupIndivRoom.setAttribute('current-index-task', i);
      popupIndivRoom.setAttribute('data-source', linkOpenForm.getAttribute('data-source'));
      popupIndivRoom.classList.add('is-show');
      if ( linkOpenForm.getAttribute('data-num') === 'null') {
        popupIndivRoom.querySelector('.js-id-room-text').innerText = 'Без номеру';
      } else {
        popupIndivRoom.querySelector('.js-id-room-text').innerText = linkOpenForm.getAttribute('data-num');
      }

    });
  });

  const indivRoomsSection = document.querySelector('.js-indiv-rooms');
  const indivControlTaskSection = document.querySelector('.js-indiv-control-tasks');
  indivControlTaskSection.setAttribute('hidden', true);
  indivRoomsSection.removeAttribute('hidden');

}
function templateHeaderRooms() {
  return `<div class="indiv-rooms__table-row indiv-rooms__table-row-header js-rooms-header">
            <div class="indiv-rooms__table-col"><span>№</span> приміщення</div>
            <div class="indiv-rooms__table-col">Статус</div>
            <div class="indiv-rooms__table-col">Кількість домогосподарств</div>
            <div class="indiv-rooms__table-col">Кількість респондентів</div>
          </div>`;
}
function templateRowRooms(obj) {
  return `<a href="#" class="indiv-rooms__table-row js-open-popup-indiv-room" data-id="${obj.t_id}" data-source="${obj.t_datasource}" data-id-hs="${obj.t_hs_id}" data-num="${obj.t_flat_num}">
            <div class="indiv-rooms__table-col js-room-number">${obj.t_flat_without_num !== 1 && obj.t_flat_num !== null ? obj.t_flat_num : 'Без номеру'}</div>
            <div class="indiv-rooms__table-col js-room-status">${textStatusRoom(obj)}</div>
            <div class="indiv-rooms__table-col js-count-household">${obj.t_household_count !== null ? obj.t_household_count : ''}</div>
            <div class="indiv-rooms__table-col js-count-members">${obj.t_member_count !== null ? obj.t_member_count : ''}</div>
          </a>`;
}
function templateRowRoomsDisab(obj) {
  return `<a href="#" class="indiv-rooms__table-row indiv-rooms__table-row--disabled js-open-popup-indiv-room" data-id="${obj.t_id}" data-id-hs="${obj.t_hs_id}">
            <div class="indiv-rooms__table-col js-room-number">${obj.t_flat_without_num !== 1 && obj.t_flat_num !== null ? obj.t_flat_num : 'Без номеру'}</div>
            <div class="indiv-rooms__table-col js-room-status">${textStatusRoom(obj)}</div>
            <div class="indiv-rooms__table-col js-count-household">${obj.t_household_count !== null ? obj.t_household_count : ''}</div>
            <div class="indiv-rooms__table-col js-count-members">${obj.t_member_count !== null ? obj.t_member_count : ''}</div>
          </a>`;
}
function textStatusRoom(obj) {
  const textStatus = JSON.parse( storage.get('r_status') );
  let text;
  let num = +obj.t_status_id;

  if ( num !== 4 ) {
    for ( let i = 0; i < textStatus.length; i++ ) {
      if ( num === textStatus[i].t_status_id ) {
        text = textStatus[i].t_status_name;
        break;
      }
    }
  } else {
    for ( let i = 0; i < textStatus.length; i++ ) {
      if ( num === textStatus[i].t_status_id ) {
        let textCount = obj.t_absent_count === 1 ? 'раз' : 'рази';
        if ( obj.t_absent_count === 5 ) {
          textCount = 'разів';
        }
        text = textStatus[i].t_status_name + ` (${obj.t_absent_count} ${textCount})`;
        break;
      }
    }
  }

  return text;
}
