export function addrName(obj) {
  let name = '';

  // Формируем строку адреса для заполнения квадратика слева
  let region = document.querySelector('#r_region_id');
  let city = document.querySelector('#r_terson_id');
  let street = document.querySelector('#r_street_id');
  let building = document.querySelector('#r_house_number_id');
  let apartment = document.querySelector('#r_flat_number');

  if (region.options[region.selectedIndex] !== undefined) {
    name = name + region.options[region.selectedIndex].text;
  }
  if (city.options[city.selectedIndex] !== undefined) {
    name = name + ', ' + city.options[city.selectedIndex].text;
  }
  if (street.options[street.selectedIndex] !== undefined) {
    name = name + ', ' + street.options[city.selectedIndex].text;
  }
  if (building.options[building.selectedIndex] !== undefined) {
    name = name + ', ' + building.options[building.selectedIndex].text;
  }
  if (apartment.value !== undefined && apartment.value !== '') {
    name = name + ', кв. ' + apartment.value;
  }

  return name;
}


export function cityName(obj) {
  name = obj.terson_name_ua;
  if(obj.terson_name_add !== null) {
    name = name + ' (' + obj.terson_name_add + ')';
  }

  return name;
}


export function streetName(obj) {
  name = obj.street_type_ua + ' ' + obj.street_name_ua;
  if(obj.street_nameold_ua !== '' &&
     obj.street_nameold_ua !== null &&
     obj.street_nameold_ua !== 'null') {
    name = name + ' (';
    if(obj.street_typeold_ua !== '' &&
       obj.street_typeold_ua !== null &&
       obj.street_typeold_ua !== 'null' &&
      obj.street_typeold_ua !== obj.street_type_ua) {
      name = name + obj.street_typeold_ua + ' ';
    }
    name = name + obj.street_nameold_ua + ')';
  }
  return name;
}


export function houseName(obj) {
  name = obj.house_number;
  if(obj.house_numberadd !== null) {
    name = name + ' корп. ' + obj.house_numberadd;
  }

  return name;
}
