export default function updateProgressMemberInForm() {
  let arrProgress = [];
  document.querySelectorAll('.js-form-member .js-tabs-contains-disabled li').forEach(tab => {
    if ( tab.classList.contains('ok') || tab.classList.contains('disabled') ) {
      arrProgress.push('ok');
    }
  });
  document.querySelector('.js-user-block .js-member .member__progress span').style.height = `${(arrProgress.length + 2)*100/util.MAX_LENGTH_PROGRESS}%`;
}
