  // Шестнадцатая закладка - непрерывность проживания
  function() {
    const tab = document.querySelector("a[href='#s_member-16']").parentElement;
    const mem = document.querySelector('#s_member-16');
    let err = clearClass( 16 );

    // Если этот таб отключен, значение проверять не нужно
    if( tab.classList.contains('disabled') ) {
      return {code:0, text:'Ok'};
    }

    // Если "тимчасово проживаючий" или жил тут с рождения, пропускаем этот вопрос
    let tmp = getRadioVal( 'm_temp' );
    let birth = getRadioVal( 'm_live_birth_continuously' );
    if(+tmp === 3 || +birth === 1) {
      tab.classList.add( 'ok' );
      return {code:0, text:'Ok'};
    }

    let year = parseInt( mem.querySelector('[name=m_live_settlement_year]').value );
    if( isNaN( year ) ) {
      tab.classList.add( err.class );
      return {code:err.code, text:'Ви не зазначили, з якого року респондент безперервно проживає в цьому населеному пункті', number: 16};
    }

    let member = util.myMember();
    let dt = new Date();
    if(year < member.m_birth_year || year > dt.getFullYear() ) {
      tab.classList.add( err.class );
      return {code:err.code, text:'Невірно вказано рік, з якого респондент безперервно проживає в цьому населеному пункті. Має бути з '+member.m_birth_year+' по '+dt.getFullYear(), number: 16};
    }

    let month = parseInt( mem.querySelector('select[name=m_arrival_month]').value );
    if(year === 2018) {
      if( ! isNaN( month ) && month !== 12) {
        tab.classList.add( 'error' );
        return {code:2, text:'Місяць має бути грудень 2018', number: 16};
      }
    } else if(year > 2018) {
      if( isNaN( month ) ) {
        tab.classList.add( 'error' );
        return {code:2, text:'Ви не зазначили місяць прибуття', number: 16};
      } else {
        if(month === 12) {
          tab.classList.add( 'error' );
          return {code:2, text:'Якщо зазначено 2019 рік, місяць має бути з січня по листопад', number: 16};
        }
      }
    }

    tab.classList.add( 'ok' );
    return {code:0, text:'Ok'};
  },

