import {roomHouseholdNumChange, updateHouseholdTabs} from './household-num-change';
import householdTabClick from './household-tab-click';
import Household from '../../classes/Household';
import householdMembersNumChange from './members-num-change';
import { householdGenerateId } from './services';
import householdRoomChange from './household-room-change';
import generateName from './generate-name';
import { setHandlers } from './handlers';
import checkHouseholdData from './check-household-data';

let household_initialised = false;

export default function initHousehold(reset, firstTime) {
  const openTooltipHousehold = document.querySelector('.js-open-tooltip-household');
  const closeTooltipHousehold = document.querySelector('.js-close-tooltip-household');
  const contentInfoHousehold = document.querySelector('.js-info-household-more');
  const scrollBlockHousehold = document.querySelector('.js-household-scroll');

  if ( reset ) {
    household_initialised = false;
  }
  // Проверяем, была ли инициализация уже проведена ранее
  if ( household_initialised ) {
	    return; // Это повторный вызов инициализации, сразу выходим
  }

  household_initialised = true; // на будущее

  if ( !firstTime ) { // проверяем или первая инициализация, чтоб не навешивать повторно обработчики
    openTooltipHousehold.addEventListener('click', (e) => {
      e.preventDefault();
      contentInfoHousehold.classList.add('is-full');
      scrollBlockHousehold.classList.add('is-small');
      openTooltipHousehold.classList.remove('is-show');
    });
    
    closeTooltipHousehold.addEventListener('click', (e) => {
      e.preventDefault();
      contentInfoHousehold.classList.remove('is-full');
      scrollBlockHousehold.classList.remove('is-small');
      openTooltipHousehold.classList.add('is-show');
    });
  
    // выделяю текст при фокусе
    document.querySelector('[name="h_name"]').addEventListener('focus', function(e) {
      this.select();
    });
  }
  
  console.log(window.User.rooms[0]);
  if ( window.User.rooms[0].households.length === 0 ) { // Данных ещё нет

    let h = new Household();

    //h.h_id = householdGenerateId(); // defined in components/household/services.js
    h.h_household_number = 1;
    h.h_room_id = window.User.rooms[0].r_id;

    window.User.rooms[0].households.push( h );
    window.User.rooms[0].current_household = h.h_household_number;
    window.User.rooms[0].r_households_count = window.User.rooms[0].households.length;

    // Обработчик клика на первом табе домохозяйства
    document.querySelector('.js-room-household-tabs > li').addEventListener('click', householdTabClick);

    // Привязываем обработчики изменения кол-ва членов первого домохозяйства
    document.querySelectorAll('input[name=h_household1_member_count]').forEach(el => { // на все радиокнопки
    		 el.addEventListener('change', householdMembersNumChange); // defined in components/room/members-num-change.js
    });
    document.querySelector('#household1_member_count-text').addEventListener('keyup', householdMembersNumChange); // на текстовый ввод
    document.querySelector('#households-count-1').click();
    updateHouseholdTabs();

  } else { // Данные есть

	  // Удаляем все табы с домохозяйствами
	  const tabs = document.querySelector('.js-room-household-tabs');
	  const buttons = document.querySelector('.js-room-household-details');

    while ( tabs.querySelectorAll('li').length ) {
      tabs.removeChild( tabs.lastChild );
      buttons.removeChild( buttons.lastChild );
    }

	  let h = window.User.rooms[0].households;
	  if ( h.length <= 5 ) {
	    document.querySelector(".radio-num__input.js-room-households[value='"+h.length+"']").checked = true;
	  } else {
	    document.querySelectorAll('.radio-num__input.js-room-households').forEach(el => {
	      el.checked = false;
	    });
      document.querySelector('.js-room-households_text').value = h.length;
	  }

	  updateHouseholdTabs();

    tabs.querySelector('li').firstChild.click();

  } // if(window.User.rooms[0].households.length === 0) ... else

  // Привязываем обработчики изменения кол-ва домохозяйств
  document.querySelectorAll('.js-room-households').forEach(el => { // на все радиокнопки
		 el.addEventListener('change', roomHouseholdNumChange); // defined in components/room/household-num-change.js
  });
  document.querySelector('.js-room-households_text').addEventListener('keyup', roomHouseholdNumChange); // на текстовый ввод

  // Привязываем обработчики изменения кол-ва комнат на форме домохозяйства
  document.querySelectorAll('.js-household-rooms').forEach(el => { // на все радиокнопки
		 el.addEventListener('change', householdRoomChange); // defined in components/room/household-num-change.js
  });
  document.querySelector('.js-household-rooms-text').addEventListener('keyup', householdRoomChange); // на текстовый ввод

  // Навешиваем обработчики на форму для солблюдения workflow
  setHandlers();

}
