import checkRoomData from '../../components/room/check-room-data';
import checkHouseholdData from '../../components/household/check-household-data';
import renderTreeMembers from '../../components/member/render-tree-members';
import sortMembersForRenderTree from '../../components/member/sort-members-for-render-tree';
import initMembers from '../../components/member/init-member';
import householdUpdateTabs2 from '../../components/household/household-update-tabs2';
import initHousehold from '../../components/household/init-household';
import editHousehold from '../../components/household/edit-household';
import updateIconErrorFamily from '../../components/member/update-icon-error-family';

export default function handlerFormRoom(direction) {
  const root = document.querySelector('.js-root');
  const allSections = root.querySelectorAll('[data=section]');

  if ( direction === 'back' ) {
    checkRoomData().then(() => {
      // Переписываем значения с формы form-room в глобальный объект window.User.rooms[0]
      util.setValues(window.User.rooms[0], document.querySelector('.js-form-room'));

      allSections.forEach(el => {
        if ( el.classList.contains('address') || el.classList.contains('auth') ) {
          el.removeAttribute('hidden');
        } else {
          el.setAttribute('hidden', true);
        }
      });
        
      root.setAttribute('data-section', 'auth');
    }).catch(err => {
      console.log(err);
      return;
    });;

  } else if ( direction === 'next' ) {

    // Переход с помещения на домохозяйство?
    checkRoomData().then(() => {

      // Переписываем значения с формы form-room в глобальный объект window.User.rooms[0]
      util.setValues(window.User.rooms[0], document.querySelector('.js-form-room'));

      initHousehold();
      
      // Делаем ссылку на текущий массив members в глобальной области видимости
      let householdIndex = util.household();
      window.members = window.User.rooms[0].households[householdIndex].members;

      // Отрисовываем вкладки домохозяйств на секции family
      householdUpdateTabs2();
      updateIconErrorFamily();

      editHousehold();

      initMembers();

      // Перерисовуем дерево семейных отношений
      let sortMembers = sortMembersForRenderTree(members);
      renderTreeMembers(sortMembers);

      // показываем нужную секцию 
      allSections.forEach(el => {
        if ( el.classList.contains('family') ) {
          el.removeAttribute('hidden');
        } else {
          el.setAttribute('hidden', true);
        }
      });
    
      // вешаем тукущюю секциию на root
      root.setAttribute('data-section', 'family');

      let allTabsTitleTop = root.querySelectorAll('.family li[data]');
      let allTabsContent = root.querySelectorAll('.family #members-1 .js-tab-content');

      allTabsTitleTop[0].classList.add('is-active');
      allTabsTitleTop[1].classList.remove('is-active');
      allTabsContent[0].classList.add('is-active');
      allTabsContent[1].classList.remove('is-active');
      
    }).catch(res => {
      console.log(res);
      return;
    });

  }
}
