const popup = document.querySelector('.js-popup-warn');
const content = popup.querySelector('.js-popup-warn-content');

export default function openPopupWarn(text, resolve, reject) {
  content.innerHTML = text;
  document.querySelector('body').classList.add('is-open-popup');
  popup.classList.add('is-show');

  const continueButton = popup.querySelector('.js-popup-warn-yes');
  popup.addEventListener('click', function clickDialog(e) {

    if ( e.target.tagName === 'BUTTON' || this === e.target ) {
      popup.removeEventListener('click', clickDialog);
      popup.classList.remove( 'is-show' );
      document.querySelector('body').classList.remove('is-open-popup');

      if (e.target === continueButton) { // пользователь подтвердил?
        resolve( 0 );
      } else {
        reject( 2 );
      }
    }

  });

}
