import checkMemberData from '../../components/member/check-member-data';
import renderTreeMembers from '../../components/member/render-tree-members';
import sortMembersForRenderTree from '../../components/member/sort-members-for-render-tree';
import { cloneMember, removeMember } from '../../components/member/clone-member';

export default function handlerFormMember() {
  checkMemberData().then(() => {
    const root = document.querySelector('.js-root');
    const allSections = root.querySelectorAll('[data=section]');

    let currentMemberId = document.querySelector('.js-user-block .js-member').getAttribute('data-id');
    let currentMember;
    let formMember =  document.querySelector('.js-form-member');
    let formMemberTab =  document.querySelectorAll('.js-form-member .js-tabs-contains-disabled li');
    let householdIndex = util.household();
    let arrProgress = [];

    window.User.rooms[0].households[householdIndex].members.forEach(member => {
      if ( currentMemberId === member.m_id ) {
        currentMember = member;
      }
    });

    formMemberTab.forEach(tab => {
      if ( tab.classList.contains('ok') || tab.classList.contains('disabled') ) {
        arrProgress.push('ok');
      }
    });

    currentMember.progress = arrProgress.length + 2;
    util.setValues(currentMember, formMember);

    let sortMembers = sortMembersForRenderTree(window.User.rooms[0].households[householdIndex].members);
    renderTreeMembers(sortMembers);

    removeMember();

    allSections.forEach(el => {
      if ( el.classList.contains('family') ) {
        el.removeAttribute('hidden');
      } else {
        el.setAttribute('hidden', true);
      }
    });

    root.setAttribute('data-section', 'family');
          
  }).catch(() => {
    return;
  });
}
