import initSelectNationality from './selects/init-select-nationality';
import initSelectsRegionCounry from './selects/init-selects-region-country';
import initSelectLanguage from './selects/init-select-language';
import initSelectLanguageMulti from './selects/init-select-language-multi';
import initSelectSettlement from './selects/init-select-settlement';
import { setHandlers, runHandlers, removeHandlers } from '../handlers';
import generateName from '../../household/generate-name';
import checkMemberData from '../check-member-data';
import { cloneMember, removeMember } from '../clone-member';

export default function openFormMember(e) {
  const root = document.querySelector('.js-root');
  const allSections = root.querySelectorAll('[data=section]');
  const formMember = document.querySelector('.js-form-member');
  const inputs = formMember.querySelectorAll('input');
  const inputsRadio = formMember.querySelectorAll('input[type="radio"]');
  const inputsCeckbox = formMember.querySelectorAll('input[type="checkbox"]');
  const inputsText = formMember.querySelectorAll('input[type="text"]');
  const inputsNumber = formMember.querySelectorAll('input[type="number"]');
  const inputsSelect = formMember.querySelectorAll('select.select--white');
  const selectsChange = formMember.querySelectorAll('.js-select-change');
  const allSelect = formMember.querySelectorAll('[select]');
  const nameSelectLanguages = 'm_speak_other_languages';
  const tabsMember = formMember.querySelector('.js-tabs');
  const wrapsTabTitle = tabsMember.querySelectorAll('.tabs__nav-title');
  const tabsContainers = tabsMember.querySelectorAll('.js-tabs-container');
  const fieldWifeOrHasband = document.querySelector('.js-name-wife-or-hasband');
  const fieldAbsencePeriod = document.querySelector('[name="m_absence_period"]');
  const fieldmAbsenceLessMonth = document.querySelector('[name="m_absence_less_month"]');
  const liveSettlementYear =  document.querySelector('[name="m_live_settlement_year"]');
  const livedAnotherCountryYear =  document.querySelector('[name="m_lived_another_country_year"]');
  let currentMember;
  let currentMemberId = this.parentNode.parentNode.getAttribute('member-id');
  let templateOptionYear = new Option('зазначте рік', '');
  templateOptionYear.setAttribute('default', '');

  // клонирую члена для левой панели
  cloneMember(currentMemberId);

  // показываем нужную секцию 
  allSections.forEach(el => {
    if ( el.classList.contains('s_member') ) {
      el.removeAttribute('hidden');
    } else {
      el.setAttribute('hidden', true);
    }
  });

  // вешаем тукущюю секциию на root
  root.setAttribute('data-section', 's_member');

  // ищу по id члена д/х для редактирования формы
  members.forEach(member => {
    if ( +member.m_id === +currentMemberId ) {
      currentMember = member;
    }
  });

  // Убираем обработчики workflow
  removeHandlers();

  // Очищаем объекты Choice
  util.selectsMember.forEach(select => {
    if ( select.length !== undefined ) {
      select[0].selectize.destroy();
    } else {
      select.destroy();
    }
    
  });
  // Обнуляем массив
  util.selectsMember = [];
  // сносим элементы select с div-ов
  [...allSelect].forEach(select => {
    if ( select.firstChild ) {
      select.innerHTML = '';
    }
  });

  // обнуляю все input`s
  inputs.forEach(input => {
    if ( input.type === 'radio' || input.type === 'checkbox' ) {
      input.checked = false;
    } else if ( input.type === 'text' || input.type === 'number' ) {
      input.value = '';
    }

  });

  fieldWifeOrHasband.innerHTML = '';
  if ( currentMember.m_spouse_id !== null ) {
    document.querySelector('.js-parent-name-marriage').classList.remove('is-hide');
    members.forEach(member => {
      if ( member.m_id === currentMember.m_spouse_id ) {
        fieldWifeOrHasband.innerText = member.m_name;
        fieldWifeOrHasband.setAttribute('title', member.m_name);
      }
    });
  } else {
    document.querySelector('.js-parent-name-marriage').classList.add('is-hide');
  }

  // убираем отметки 'ok', 'warning' и 'error' со всех табов
  document.querySelectorAll('a[href^="#s_member-"]').forEach(el => {
    el.parentElement.classList.remove('ok', 'warning', 'error');
  });

  // инициализация выпадающих списков
  initSelectsRegionCounry(formMember, currentMember);
  initSelectNationality(formMember, currentMember);
  initSelectLanguage(formMember, currentMember);
  initSelectLanguageMulti(formMember, currentMember);
  initSelectSettlement(formMember, currentMember);


  liveSettlementYear.innerHTML = '';
  // наполняю дропдаун 16-а
  for ( let i = +currentMember.m_birth_year; i < 2020; i++ ) {
    let option = new Option;
    option.setAttribute('value', i);
    option.innerText = i;
    liveSettlementYear.appendChild(option);
  }

  liveSettlementYear.insertBefore(templateOptionYear.cloneNode(true), liveSettlementYear.firstChild);

  livedAnotherCountryYear.innerHTML = '';
  // наполняю дропдаун A18 (У якому році)
  if ( +currentMember.m_birth_year <= 1991 ) {
    for ( let i = 1991; i < 2020; i++ ) {
      let option = new Option;
      option.setAttribute('value', i);
      option.innerText = i;
      livedAnotherCountryYear.appendChild(option);
    }
  } else {
    for ( let i = +currentMember.m_birth_year; i < 2020; i++ ) {
      let option = new Option;
      option.setAttribute('value', i);
      option.innerText = i;
      livedAnotherCountryYear.appendChild(option);
    }
  }

  livedAnotherCountryYear.insertBefore(templateOptionYear.cloneNode(true), livedAnotherCountryYear.firstChild);


  // устанавливаю значение в форме
  for ( let key in currentMember ) {

    inputsRadio.forEach(input => {
      if ( input.name === key && currentMember[key] !== null && !Array.isArray(currentMember[key]) ) {
        if ( input.value === currentMember[key] ) {
          input.checked = true;
        }
      }
    });

    inputsCeckbox.forEach(input => {
      if ( input.name === key && currentMember[key] !== false && !Array.isArray(currentMember[key]) ) {
        input.checked = true;
      }
    });

    if ( Array.isArray(currentMember[key]) && currentMember[key].length > 0 && key !== nameSelectLanguages ) {

      currentMember[key].forEach(value => {
        inputsCeckbox.forEach(input => {
          if ( input.value === value ) {
            input.checked = true;
          }
        });
      });
    }

    inputsText.forEach(input => {
      if ( input.name === key && currentMember[key] !== null ) {
        input.value = currentMember[key];
      }
    });

    inputsNumber.forEach(input => {
      if ( input.name === key && currentMember[key] !== null ) {
        input.value = currentMember[key];
      }
    });

    for ( let i = 0; i < inputsSelect.length; i++ ) {
      if ( inputsSelect[i].name === key && currentMember[key] !== '' ) {
        inputsSelect[i].classList.remove('is-empty');
        inputsSelect[i].value = currentMember[key];
        break;
      } else if ( inputsSelect[i].name === key && currentMember[key] === '' ) {
        inputsSelect[i].classList.add('is-empty');
        inputsSelect[i].value = '';
        break;
      }
    }
    
  }

  function changedSelectMonth(e) {
    if ( e.target.value !== '' ) {
      e.target.classList.remove('is-empty');
    } else {
      e.target.classList.add('is-empty');
    }
  }

  function changedFieldAbsencePeriod(e) {
    fieldmAbsenceLessMonth.checked = false;
    if ( fieldAbsencePeriod.value !== '' ) {
      fieldAbsencePeriod.classList.remove('is-empty');
    } else {
      fieldAbsencePeriod.classList.add('is-empty');
    }
  }

  function changedFieldmAbsenceLessMonth(e) {
    fieldAbsencePeriod.value = '';
    fieldAbsencePeriod.classList.add('is-empty');
  }

  fieldAbsencePeriod.removeEventListener('change', changedFieldAbsencePeriod);

  fieldAbsencePeriod.addEventListener('change', changedFieldAbsencePeriod);

  fieldmAbsenceLessMonth.removeEventListener('change', changedFieldmAbsenceLessMonth);

  fieldmAbsenceLessMonth.addEventListener('change', changedFieldmAbsenceLessMonth);

  selectsChange.forEach(select => {
    select.removeEventListener('change', changedSelectMonth);
  });

  selectsChange.forEach(select => {
    select.addEventListener('change', changedSelectMonth);
  });

  // обнуляю табы
  wrapsTabTitle.forEach(tabTitle => {
    [...tabTitle.children].forEach((title, i) => {
      let indexCurentActiveTab = +User.rooms[0].r_building_id === 13 ? 1 : 0;
      if ( i === indexCurentActiveTab ) {
        title.classList.add('is-active');
      } else {
        title.classList.remove('is-active');
      }
    });
  });

  tabsContainers.forEach(tabsContainer => {
    [...tabsContainer.children].forEach((content, i) => {
      let indexCurentActiveTab = +User.rooms[0].r_building_id === 13 ? 1 : 0;
      if ( i === indexCurentActiveTab ) {
        content.classList.add('is-active');
      } else {
        content.classList.remove('is-active');
      }
    });
  });

  // Меняем название домохозяйства на бирке слева
  document.querySelector('.js-household-label').innerHTML = generateName();
  // Навешиваем обработчики workflow
  setHandlers();
  // запускаем обработчики workflow на подгруженных данных
  runHandlers();

  // Если анкета открывается не впервые, проверяем ранее введённые данные
  if( currentMember.edit ) {
    checkMemberData( true );
  }
  currentMember.edit = true;

}
