import renderTreeMembers from '../render-tree-members';
import sortMembersForRenderTree from '../sort-members-for-render-tree';
import {clearFormCreateMember} from './open-form-create-member';

export default function updateMember(currentMember, form) {
  const selects = form.querySelectorAll('select');
  const inputs = form.querySelectorAll('input');
  const selectedChildren = form.querySelectorAll('select[multiple] option:checked');
  const children = Array.from(selectedChildren).map(child => child.value);
  const oldChildren = currentMember.m_children;
  // console.log(oldChildren);
  // console.log(children);
  
  if ( currentMember.m_parent_id !== null ) {
    let indexChild = null;
    for ( let i = 0; i < window.members.length; i++ ) {
      if ( +window.members[i].m_id === +currentMember.m_parent_id ) {
        window.members[i].m_children.forEach((childId, i) => {
          if ( +childId === +currentMember.m_id) {
            indexChild = i;
          }
        });
        window.members[i].m_children.splice(indexChild, 1);
        break;
      }
    }
  }

  selects.forEach(select => {
    if ( select.getAttribute('multiple') === null ) {

      if ( select.value !== '') {

        if ( select.name === 'm_parent_id' ) {
          for ( let i = 0; i < window.members.length; i++ ) {
            if ( +window.members[i].m_id === +select.value ) {
              window.members[i].m_children.push(currentMember.m_id);
            }
          }
        }

        currentMember[select.name] = select.value;
      }
  
      if ( select.name === 'm_spouse_id' && select.value === '') {
        currentMember[select.name] = null;
      }
  
      if ( select.name === 'm_parent_id' && select.value === '') {
        currentMember[select.name] = null;
      }

    }
  });

  inputs.forEach(input => {
    if (input.type === 'radio' && input.checked) {
      currentMember[input.name] = input.value;
    } else if (input.type === 'text') {
      currentMember[input.name] = input.value;
    }
  });

  // возраст - полных лет
  let currentDate = new Date();
  let currentMonth = currentDate.getMonth() + 1;
  let currentYear = currentDate.getFullYear();
  
  if ( currentMember.m_age !== '' ) {
    currentMember.m_age = +currentMember.m_age;
  } else if ( currentMonth >= +currentMember.m_birth_month ) {
    currentMember.m_age = currentYear - +currentMember.m_birth_year;
  } else {
    currentMember.m_age = currentYear - +currentMember.m_birth_year - 1;
  }

  // проверяю еcли выбрана связь с членом домохозяйства - устанавливаю второму 
  members.forEach(member => {

    // удаляю предыдущему
    if ( member.m_spouse_id === currentMember.m_id ) {
      member.m_spouse_id = null;
    }
    // устанавливаю
    if ( currentMember.m_spouse_id === member.m_id ) {

      if ( member.m_spouse_id !== null ) {
        members.forEach(m => {
          if ( member.m_spouse_id === m.m_id ) {
            m.m_spouse_id = null;
          }
        });
      }

      member.m_spouse_id = currentMember.m_id;
    }


    // дети
    children.forEach(childId => {
      
      if ( +member.m_id === +childId ) {
        let currentChild = member;
        
        // если нет родителя, ставлю id 
        if ( currentChild.m_parent_id === null ) {
          currentChild.m_parent_id = currentMember.m_id;

        } else {
          // иначе ищу родителя для удаления из его масива детей
          for ( let i = 0; i < members.length; i++ ) {
            if ( members[i].m_id === currentChild.m_parent_id ) {
              let removeChildren = members[i].m_children; // масив детей из которого нужно удалить
              let indexRemoveChildId = null;

              for ( let j = 0; j < removeChildren.length; j++ ) {
                // ищу ребенка в масиве для удаления
                if ( removeChildren[j] === childId ) {
                  indexRemoveChildId = j;
                  break;
                }
              }
              removeChildren.splice(indexRemoveChildId, 1);
            }
          } // end for
          // 
          currentChild.m_parent_id = currentMember.m_id;
        }
        

      }
    });

  });

  currentMember.m_children = children;
  console.log(oldChildren);
  let removeChildren = [];
  oldChildren.forEach(oldChild => {
    if ( children.indexOf(oldChild) === -1 ) {
      removeChildren.push(oldChild);
    }
  });
  console.log(removeChildren);
  removeChildren.forEach(childId => {
    for ( let i = 0; i < members.length; i++ ) {
      if ( +members[i].m_id === +childId ) {
        console.log(members[i]);
        members[i].m_parent_id = null;
        break;
      }
    }
  });

  // очищаю поле дети
  if ( window.selectChildren ) {
    console.log('update remove select children');
    window.selectChildren.destroy();
    window.selectChildren = null;
    form.querySelector('[select]').innerHTML = '';
  }

  form.classList.remove('is-show', 'is-edit', 'is-first');
  clearFormCreateMember();
  let sortMembers = sortMembersForRenderTree(members);
  renderTreeMembers(sortMembers);

}
