import membersUpdate from './members-update';
import sortMembersForRenderTree from '../member/sort-members-for-render-tree';
import renderTreeMembers from '../member/render-tree-members';
import editHousehold from './edit-household';
import householdUpdateTabs2 from './household-update-tabs2';
import checkHouseholdData from './check-household-data';
import updateIconErrorFamily from '../member/update-icon-error-family';

export default function householdTabClick2(event) {
  event.preventDefault();
  
  if ( this.classList.contains('is-active') ) {
    return;
  }

  checkHouseholdData().then(() => {
    const tabs = document.querySelector('.js-member-household-tabs');
    const allTabsTitleTop = document.querySelectorAll('.family li[data]');
    const allTabsContent = document.querySelectorAll('.family #members-1 .js-tab-content');
    const num = this.dataset.household;
    let previousIndexHousehold;

    tabs.querySelectorAll('li').forEach((el, i) => {
      if ( el.classList.contains( 'is-active' ) ) {
        previousIndexHousehold = i;
      }
    });

    tabs.querySelectorAll('li').forEach(el => {
      el.classList.remove( 'is-active' );
    });
    
    this.classList.add( 'is-active' );
    window.User.rooms[0].current_household = num;

    // Переключаем глобальный members на текущее домохозяйство
    window.members = window.User.rooms[0].households[util.household()].members;

    // Перерисовуем дерево семейных отношений
    let sortMembers = sortMembersForRenderTree(members);
    renderTreeMembers(sortMembers);

    
    util.setValues(window.User.rooms[0].households[previousIndexHousehold], document.querySelector('.js-household-form'));

    householdUpdateTabs2();
    
    editHousehold();

    updateIconErrorFamily();
    
    allTabsTitleTop[1].classList.remove('is-active');
    allTabsTitleTop[0].classList.add('is-active');
    allTabsContent[1].classList.remove('is-active');
    allTabsContent[0].classList.add('is-active');

  }).catch(err => {
    console.log(err);
    return;
  });

}
