export function closeFormCreateMember() {
  if ( formCreateMember.classList.contains('is-show') ) {
    warningCloseFormCreateMember()
      .then(
        ok => {
          clearFormCreateMember();
          formCreateMember.classList.remove('is-show', 'is-edit', 'is-first');
        },
        cansel => {
          console.log('cansel form');
        });
  }
}

function warningCloseFormCreateMember(detectForm) {
  return new Promise((ok, cansel) => {
    openPopupWarn(`<center><p><span class="popup-check__subtitle" style="font-size: 20px;margin-bottom: 20px;display: block;">Закрити вікно?</span></p>
                   <p><span class="popup-check__subtitle">Дані не будуть збережені. Для збереження даних натисніть "Ні" і потім натисніть "Додати" або "Зберегти"</span></p></center>`, ok, cansel );
  });
}

export function clearFormCreateMember() {
  [...formCreateMember.querySelectorAll('[name]')].forEach(el => {

    if ( el.getAttribute('type') === 'radio' ) {
      el.checked = false;
    } else if ( el.getAttribute('type') === 'text' || el.getAttribute('type') === 'number' ) {
      el.value = '';
    } else {
      // дата рождения - устанавливаю атрибут valid в значение false и значение по умолчанию
      if ( el.getAttribute('data') === 'age' ) {
        el.setAttribute('valid', 'false');
        [...el.children].forEach(option => {
          if ( option.value === '' ) {
            option.selected = true;
          }
        });
      }
      // если выбор связей то блокирую и ставлю значение по умолчанию
      if ( el.name === 'm_sib_id' ) {
        // el.disabled = true;
        [...el.children].forEach(option => {
          if ( option.value === '' ) {
            option.selected = true;
          }
        });
        // обнуляю имена муж/жена и папа/мама
      } else if ( el.getAttribute('data') !== 'age' ) {
        [...el.children].forEach(option => {
          if ( option.value !== '' ) {
            option.parentNode.removeChild(option);
          }
        });
      }
            
    }

    if ( window.selectChildren ) {
      console.log('close remove select children');
      window.selectChildren.destroy();
      window.selectChildren = null;
      formCreateMember.querySelector('[select]').innerHTML = '';
    }

    btnCreateMember.innerText = 'Додати';
  });
}
