export default function adjustContinuum() {

  let block = false;
  let input = document.querySelector('input:checked[name=m_temp]');
  if(input !== null && +input.value === 3) {
    block = true;
  } else {
    let birth = document.querySelector('input:checked[name=m_live_birth_continuously]');
    if (birth !== null && +birth.value === 1) {
      block = true;
    }
  }

  let tab = document.querySelector("a[href='#s_member-16']").parentElement;
  const year = document.querySelector('[name=m_live_settlement_year]');
  const month = document.querySelector('select[name=m_arrival_month]');
  if ( block ) {
    tab.classList.remove('ok', 'warning', 'error');
    tab.classList.add('disabled');
    year.disabled = true;
    year.value = '';
    month.disabled = true;
    month.value = '';
  } else {
    tab.classList.remove('disabled');
    year.disabled = false;
    month.disabled = false;
  }

}
