import openPopupCheck from '../popup/popup-check';

export default function checkTabData() {

  return new Promise(function(resolve, reject) {
    // Определяем текущую секцию по элементу root
    let sectionName = document.querySelector( '.js-root' ).dataset.section;
    // Определяем текущие табы
    let tabsTitle = document.querySelectorAll( '[href^="#' + sectionName + '"]' );
    let currentTab;
    // определяем активный таб на секции family
    let currentTabFamily = document.querySelector('.js-members li[data].is-active').getAttribute('data');

    // если переход на секции family с дерева на домохозяйство, не показываем предупреждения
    if ( sectionName === 'family' && currentTabFamily === 'tree' ) {
      console.log('bla');
      return resolve( 0 );
    }

    // ищем активный таб
    tabsTitle.forEach(link => {
      if ( link.parentElement.classList.contains('is-active') ) {
        currentTab = link.parentElement;
      }
    });

    if ( currentTab !== undefined ) {
      // Определяем номер вопроса по href из верхней закладки
      let chunks = currentTab.firstChild.href.split( /#/ );
      let regex = new RegExp( sectionName + '-' );
      let questionNumber = chunks[1].replace(regex, '');

      // Вызываем соответствующий контроль.
      let checks;
      switch(sectionName) {
        case 'room':
          checks = util.checkRoom;
          break;
        case 's_member':
          checks = util.checkMember;
          break;
        case 'family':
          checks = util.checkHousehold;
          break;
        default:
          checks = [];
      }

      if ( checks.length ) {
        // Контроль с именем checkName существует?
        if ( typeof checks[questionNumber] === 'function' ) {
          let res = checks[questionNumber](); // Вызываем контроль
          // Проверяем результат контроля
          if ( res['code'] === 0 ) { // Ошибок нет
            resolve( res['code'] );
          } else {
            // открываем popup
            openPopupCheck(res['code'], res['text'], resolve, reject);
          } // if(res['code'] === 0) ... else
        } 
      } else {
        let res = checks();
        // Проверяем результат контроля
        if ( res['code'] === 0 ) { // Ошибок нет
          resolve( res['code'] );
        } else {
          // открываем popup
          openPopupCheck(res['code'], res['text'], resolve, reject);
        } 
      }
      

    } else { // Контроля не существует, разрешаем переход на другой таб
      resolve( 0 );
    }
    
    

    // обновляю статус заполнения члена д/х ( progress line ) 
    if ( document.querySelector( '.js-root' ).getAttribute('data-section') === 's_member' ) {
      util.updateProgressMemberInForm();
    }

  }); // new Promise

}
