import myMember from './my-member';

export default function adjustWorksearch( member ) {

  if(member === undefined) {
    member = myMember();
  }

  if(member === null) {
    return;
  }

  let block = false;

  if (member.m_age < 15) {
    block = true;
  } else {
    let input = document.querySelector('input:checked[name=m_temp]');
    if ( input !== null && +input.value === 3 ) {
      block = true;
    } else {
      let working = document.querySelector('input:checked[name=m_have_week_paid_work]');
      if ( working !== null && +working.value === 1 ) {
        block = true;
      }
    }
  } // if(member.m_age < 15) ... else

  let tab = document.querySelector("a[href='#s_member-14']").parentElement;
  if( block ) {
    tab.classList.remove('ok', 'warning', 'error');
    tab.classList.add('disabled');
    document.querySelectorAll('input[name=m_last_month_looking_job]').forEach(el => {
      el.checked = false;
      el.disabled = true;
    });
    document.querySelectorAll('input[name=m_ready_job_two_weeks]').forEach(el => {
      el.disabled = true;
      el.checked = false;
    });

    // document.querySelectorAll('input[name=m_dontwork_id]').forEach(el => {
    //   el.disabled = true;
    //   el.checked = false;
    // });

    // let other = document.querySelector( 'input[name=m_refuse_job_reason_another]' );
    // other.disabled = true;
    // other.value = '';
  } else {
    tab.classList.remove('disabled');
    document.querySelectorAll('input[name=m_last_month_looking_job]').forEach(el => {
      el.disabled = false;
    });
  }
}
