import handlerFormRoom from './navigation-wizard/handler-form-room';
import renderTreeMembers from '../components/member/render-tree-members';
import sortMembersForRenderTree from '../components/member/sort-members-for-render-tree';
import { cloneMember, removeMember } from '../components/member/clone-member';

export default function linksControl() {
  const popupControl = document.querySelector('.js-popup-control-all');
  const links = document.querySelectorAll('.js-link-control');

  function handrelLinksControl(e) {
    const root = document.querySelector('.js-root');
    const allSections = root.querySelectorAll('[data=section]');
    let currentSection = this.getAttribute('data-section');
    let currentNumber = this.getAttribute('data-number');
    let currentName = this.getAttribute('data-name-household');
    
    console.log(currentSection);
    console.log(currentNumber);
    console.log(currentName);
    // закрываем попап с контролями
    popupControl.click();
    
    if ( currentSection === 'room' ) {

      let currentTabsTitle = document.querySelectorAll(`.${currentSection} li[data-info]`);
      let currentTabsContent = document.querySelectorAll(`.${currentSection} .js-tab-content`);

      removeMember();
      document.querySelector('.js-household-label').innerText = 'Домогосподарства';

      // показываем нужную секцию 
      allSections.forEach(el => {
        if ( el.classList.contains(currentSection) ) {
          el.removeAttribute('hidden');
        } else {
          el.setAttribute('hidden', true);
        }
      });
          
      // вешаем текущюю секциию на root
      root.setAttribute('data-section', currentSection);

      // показываю нужный таб
      for ( let i = 0; i < currentTabsTitle.length; i++ ) {
        if ( +currentNumber - 1 === i ) {
          currentTabsTitle[i].classList.add('is-active');
          currentTabsContent[i].classList.add('is-active');
        } else {
          currentTabsTitle[i].classList.remove('is-active');
          currentTabsContent[i].classList.remove('is-active');
        }
      }

      // если перешли на вкладку П2, делаю фокус на инпуте
      if ( currentTabsTitle[1].classList.contains('is-active') ) {
        document.querySelector('[name="r_total_area"]').focus();
      }
    }

    if ( currentSection === 'family' ) {
      let currentNameHousehold = currentName;

      removeMember();
      document.querySelector('.js-household-label').innerText = 'Домогосподарства';

      window.User.rooms[0].households.forEach(house => {
        if ( house.h_name === currentNameHousehold ) {
          window.User.rooms[0].current_household = `${house.h_household_number}`;
        }
      });

      handlerFormRoom('next');
      document.querySelector('.js-popup-check-continue').click();
    }
    
    if ( currentSection === 's_member' ) {
      removeMember();
      
      let currentTabsTitle = document.querySelectorAll(`.${currentSection} li[data-info]`);
      let currentTabsContent = document.querySelectorAll(`.${currentSection} .js-tab-content`);
      let currentMemberId = this.getAttribute('data-id');
      let currentNameHousehold = currentName;

      window.User.rooms[0].households.forEach(house => {
        if ( house.h_name === currentNameHousehold ) {
          window.User.rooms[0].current_household = `${house.h_household_number}`;
        }
      });

      let householdIndex = util.household();
      window.members = window.User.rooms[0].households[householdIndex].members;

      // Перерисовуем дерево семейных отношений
      let sortMembers = sortMembersForRenderTree(members);
      renderTreeMembers(sortMembers);

      let currentBtnOpenMemberForm = document.querySelector(`.js-member[data-id="${currentMemberId}"] .js-open-form-member`);
      let currentMember = document.querySelector(`.js-member[data-id="${currentMemberId}"]`);

      currentMember.click();
      currentBtnOpenMemberForm.click();

      // показываю нужный таб
      for ( let i = 0; i < currentTabsTitle.length; i++ ) {
        if ( +currentNumber - 1 === i ) {
          currentTabsTitle[i].classList.add('is-active');
          currentTabsContent[i].classList.add('is-active');
        } else {
          currentTabsTitle[i].classList.remove('is-active');
          currentTabsContent[i].classList.remove('is-active');
        }
      }
      // если перешли на вкладку А6, делаю фокус на инпуте
      if ( currentTabsTitle[5].classList.contains('is-active') ) {
        document.querySelector('[name="m_number_children_born"]').focus();
      }

    }

  }


  links.forEach(link => {
    link.removeEventListener('click', handrelLinksControl);
  });

  links.forEach(link => {
    link.addEventListener('click', handrelLinksControl);
  });

}
