export default function adjustMarriage( member ) {
  // Проверяем отношение к первому члену
  if(+member.m_sib_id === 6) {
    // Невестка/зять не может быть "никогда не был(а) в браке"
    document.querySelector("input[name=m_marriage_id][value='1']").disabled = true;
    document.querySelector("input[name=m_marriage_id][value='1']").checked = false;
  } else {
    document.querySelector("input[name=m_marriage_id][value='1']").disabled = false;
  }

  // Есть пара?
  if(member.m_spouse_id === null) {
    document.querySelectorAll('input[name=m_marriage_id]').forEach(el => {
      if(+el.value === 2 || +el.value === 3) { // Блокируем "в браке, остальные открываем"
        el.disabled = true;
        el.checked = false;
      } else {
        el.disabled = false;
      }
    });
  } else {
    // Разблокируем "в браке"
    document.querySelector("input[name=m_marriage_id][value='1']").disabled = false;
    document.querySelector("input[name=m_marriage_id][value='2']").disabled = false;
    let m = util.myMember( member.m_spouse_id );
    if(m !== null) {

      // Проверяем, одинаковое ли семейное положение для пары
      if(member.m_marriage_id !== m.m_marriage_id) {

        if(member.m_marriage_id !== null) { // для текущего члена задано семейное положение
          m.m_marriage_id = member.m_marriage_id;
        } else if(m.m_marriage_id !== null) { // для пары текущего члена задано семейное положение
          member.m_marriage_id = m.m_marriage_id;
        } else { // ни для кого не задано - принудительно женим
          member.m_marriage_id = 2;
          m.m_marriage_id = 2;
        }
        document.querySelector("input[name=m_marriage_id][value='"+member.m_marriage_id+"']").checked = true;

      }

      // сразу блокируем "никогда не был в браке" и "вдова"
      [1,4].forEach(i => {
        document.querySelector("input[name=m_marriage_id][value='"+i+"']").disabled = true;
        document.querySelector("input[name=m_marriage_id][value='"+i+"']").checked = false;
      });

      // Если наша пара - не отец/свекр/тесть первого члена, "в разводе" тоже блокируем
      if(+m.m_sib_id !== 4 && +m.m_sib_id !== 7) {
        [5,6].forEach(i => {
          document.querySelector("input[name=m_marriage_id][value='"+i+"']").disabled = true;
          document.querySelector("input[name=m_marriage_id][value='"+i+"']").checked = false;
        });
      }

    } // if(m !== null)
  } // if(member.m_spouse_id !== null)
}
