import mySelect from './my-select';

export default function adjustCountry() {

  let block = false;
  let input = document.querySelector('input:checked[name=m_temp]');

  if ( input !== null && +input.value === 3 ) {
    block = true;

  } else {

    const birth = document.querySelector('input:checked[name=m_live_birth_continuously]');

    if ( birth !== null && +birth.value === 1 ) {
      block = true;

    } else {
      let year = parseInt( document.querySelector('[name=m_live_settlement_year]').value );

      if ( year < 1991 ) {
        block = true;

      }
    } // if(birth !== null && +birth.value === 1) ... else
  } // if(input !== null && +input.value === 3) ... else

  let tab = document.querySelector("a[href='#s_member-18']").parentElement;
  if ( block ) {
    tab.classList.remove('ok', 'warning', 'error');
    tab.classList.add('disabled');

    document.querySelectorAll( 'input[name=m_is_lived_another_country]' ).forEach(el => {
      el.checked = false;
      el.disabled = true;
    });

    let country = mySelect( 'm_lived_another_country' );
    country[0].selectize.clear();
    country[0].selectize.disable();

    let year = document.querySelector( '[name=m_lived_another_country_year]' );

    year.value = '';
    year.disabled = true;

    document.querySelectorAll('input[name=m_reason_another_country]').forEach(el => {
      el.checked = false;
      el.disabled = true;
    });

    let another = document.querySelector( 'input[name=m_reason_another_country_another]' );
    another.value = '';
    another.disabled = true;
  } else {
    tab.classList.remove('disabled');
    document.querySelectorAll( 'input[name=m_is_lived_another_country]' ).forEach(el => {
      el.disabled = false;
    });
  }

}
