import createSelect from './create-select';

export default function initSelectLanguage(formMember, currentMember) {
  const selectLanguage = formMember.querySelector('[data-select-language]');
  // создаю выпадающий список для языков
  const selectLanguageName = selectLanguage.getAttribute('data-select-language');
  const selectLanguageClass = selectLanguage.getAttribute('data-class');
  const selectLanguagePlaceholder = selectLanguage.getAttribute('data-placeholder');
  const dataForSelectLanguage = [selectLanguagePlaceholder, selectLanguageName, selectLanguageClass, selectLanguage];
  const catalogLanguage = [];
  let selected;

  languageDictionary.forEach(language => {
    if ( language.language_id !== util.DEFAULT_VALUE_LANGUAGE ) {

      if ( +currentMember[selectLanguageName] === +language.language_id && currentMember[selectLanguageName] !== util.DEFAULT_VALUE_LANGUAGE ) {
        catalogLanguage.push( {value: language.language_id, label: language.language_name_ua} );
        selected = language.language_id;
      } else {
        catalogLanguage.push( {value: language.language_id, label: language.language_name_ua} );
      }
    }
  });

  createSelect( dataForSelectLanguage, catalogLanguage, false, selected );
}
