import { householdSearchByNum } from './services';

export default function householdMembersNumChange() {
  this.value = this.value.replace(/([^\d]*)/g, '');
  let num = parseInt( this.value );
  let household = this.dataset.household;
  let section = document.querySelector('.js-root').dataset.section;
  let selector, selectorText;

  selector = ".radio-num__input.js-household-members[data-household='" + household + "']";
  selectorText = ".js-household-members-text[data-household='" + household + "']";
  console.log(this.value);
  if ( isNaN(num) ) {
    num = 1;
  }

  if ( num <= 5 ) {
    document.querySelector( selector + "[value='" + num + "']" ).checked = true;
    if (this.type === 'radio') {
      document.querySelector( selectorText ).value = '';
    }
  } else {
    document.querySelectorAll( selector ).forEach(el => {
      el.checked = false;
    });
  }

  updateHouseholdMembers(household, num);
}

function updateHouseholdMembers(num, count) {
  let index = householdSearchByNum( num );
  let section = document.querySelector('.js-root').dataset.section;

  if ( index !== -1 ) {
    window.User.rooms[0].households[index].h_household_member_count = count;
    window.needSave = true;
    if ( section === 'room' ) { // переключаем закладку только на странице с домохозяйствами
      setActiveTab( num );
    }
  }
}


function setActiveTab( household ) {
  let tab = document.querySelector(".js-room-household-tabs > li[data-household='" + household + "']");
  tab.click();
}
