import initHandlersFormCreateMember from './form-create-member/update-form-create-member';
import { openFormCreateMember } from './form-create-member/open-form-create-member';
import sortMembersForRenderTree from './sort-members-for-render-tree';
import renderTreeMembers from './render-tree-members';
import { closeFormCreateMemberForOverlay, closeFormCreateMember } from './form-create-member/open-form-create-member';
import changedTextForValidity from './form-create-member/update-form-create-member/changed-text-for-validity';
import controlFormCreateMember from './form-create-member/control-form-create-member';
import popupInfoOk from '../popup/popup-info-ok';

let init = false;
export default function initMembers() {

  if ( init ) {
    return;
  }
    
  init = true;

  const formCreateMember = document.querySelector('.js-form-create-member');
  const btnsCloseFormCreateMember = formCreateMember.querySelectorAll('.js-close-form-create-member');
  const btnAddMember = document.querySelector('.js-add-member');
  const btnCreateMember = formCreateMember.querySelector('[type="submit"]');

  btnAddMember.addEventListener('click', checkLengthMembers);
  initHandlersFormCreateMember();

  btnCreateMember.addEventListener('click', () => {
    changedTextForValidity();
  });
  
  formCreateMember.addEventListener('submit', function(e) {
    e.preventDefault();
    controlFormCreateMember(this);
  });

  // Делаем ссылку на текущий массив members в глобальной области видимости
  let householdIndex = util.household();
  window.members = window.User.rooms[0].households[householdIndex].members;

  if( members.length ) {
    let sortMembers = sortMembersForRenderTree(members);
    renderTreeMembers(sortMembers);

    btnsCloseFormCreateMember.forEach(btnClose => {
      btnClose.addEventListener('click', closeFormCreateMember);
    });

    formCreateMember.addEventListener('click', closeFormCreateMemberForOverlay);
  }
}


function checkLengthMembers() {
  // return new Promise(resolve => {
  let households = User.rooms[0].households;
  let indexActiveHousehold = +document.querySelector('.js-member-household-tabs li.is-active').getAttribute('data-household') - 1;

  if ( +households[indexActiveHousehold].h_household_member_count <= households[indexActiveHousehold].members.length ) {
    popupInfoOk(`У запитанні П15. (Опис домогосподарств) зазначена кількість членів домогосподарства (${households[indexActiveHousehold].h_household_member_count}). 
                 Ви вже додали (${households[indexActiveHousehold].h_household_member_count}) членів домогосподарства. 
                 Після додавання нового члена домогосподарства поверніться до запитання П15. (Опис домогосподарств)
                 та відкорегуйте кількість членів домогосподарства.`);
    openFormCreateMember();
  } else {
    openFormCreateMember();
  }
  // })
}
