export default function householdRoomChange() {
  this.value = this.value.replace(/([^\d]*)/g, '');
  let num = parseInt( this.value );

  // if ( isNaN(num) ) {
  //   num = 1;
  // }

  if ( num <= 5 && num !== 0 ) {

    document.querySelector(".radio-num__input.js-household-rooms[value='" + num + "']").checked = true;

    if ( this.type === 'radio' ) {
      document.querySelector('.js-household-rooms-text').value = '';
    }

  } else {
    document.querySelectorAll('.radio-num__input.js-household-rooms').forEach(el => {
      el.checked = false;
    });
  }

  let i = util.household();
  if ( i !== -1 ) {
    window.User.rooms[0].households[i].h_household_rooms = num;
  }

  document.querySelector('#h_household_room_parts').checked = false;

}
