import { region, city, street, building, terson_distr, initAddressFormHomeless } from './init-address-form';
import initdictionaryList from '../dictionary/init-dictionaries';
import popupInfoOk from '../popup/popup-info-ok';

export default function handlerBtnHomeless() {
  const btnHomeless = document.querySelector('.js-homeless');
  const btnCreateRoomStationary = document.querySelector('.js-create-room-stationary');
  const btnCreateRoomHomeless = document.querySelector('.js-create-room-homeless');
  const btnBackSection = document.querySelector('.js-back-section');

  initAddressFormHomeless();

  btnHomeless.addEventListener('click', function(e) {
    let stationaryHouses = JSON.parse(storage.get('housestatus'));
    let currentTask = null;
    let currentStationaryHouse = null;

    for ( let i = 0; i < stationaryHouses.length; i++ ) {
      if ( stationaryHouses[i].hs_datasource === util.CODE_HOMELESS ) {
        currentStationaryHouse = stationaryHouses[i];
        break;
      }
    }
    console.log(currentStationaryHouse);
    for ( let i = 0; i < currentStationaryHouse.tasks.length; i++ ) {
      if ( currentStationaryHouse.tasks[i].t_status_id === 1 ) { // 1 - не виконано
        currentTask = currentStationaryHouse.tasks[i];
        break;
      }
    }
    console.log(currentTask);

    if (!currentTask) {
      popupInfoOk('Ви досягли ліміту кількості для перепису безпритульних. Будь ласка завітайте до переписного відділу та натисніть кнопку "Оновити"');
    } else {
      const indivSections = document.querySelectorAll('.js-indiv-section');
      const flatNumber = document.querySelector('#r_flat_number');
      const flatNumberNot = document.querySelector('#r_flat_without_number');

      btnCreateRoomHomeless.classList.add('disabled');

      indivSections.forEach(section => {
        if ( !section.getAttribute('hidden') ) {
          let prevSectionClass = null;

          section.getAttribute('class').split(' ').forEach(string => {
            if ( string.startsWith('js') && string.indexOf('js-indiv-section') === -1 && string !== 'js-indiv-address' ) {
              prevSectionClass = string;
              console.log(prevSectionClass);
              document.querySelector('.js-indiv-main-section').setAttribute('prev-section', prevSectionClass);

            }
          });

        }

        if ( section.classList.contains('js-indiv-address') ) {
          console.log(region);
          region.destroy();
          city.destroy();
          city.init();
          city.disable();
          terson_distr.destroy();
          terson_distr.init();
          terson_distr.disable();
          // street.destroy();
          // street.init();
          // street.disable();
          // building.destroy();
          // building.init();
          // building.disable();
          flatNumber.value = '';
          flatNumber.parentElement.parentElement.style.visibility = 'hidden';
          flatNumber.parentElement.parentElement.style.position = 'absolute';
          flatNumber.parentElement.parentElement.style.opacity = '0';
          flatNumber.setAttribute('disabled', 'disabled');
          flatNumberNot.checked = true;
          flatNumberNot.parentElement.style.display = 'none';
          flatNumberNot.parentElement.style.pointerEvents = 'none';
          flatNumberNot.parentElement.style.marginLeft = 'auto';
          flatNumberNot.parentElement.parentElement.parentElement.style.marginTop = '-60px';
          btnCreateRoomStationary.style.display = 'none';
          btnCreateRoomHomeless.style.display = 'flex';

          let buf = [];
          initdictionaryList().then(res => {
            for (let i = 0; i < window.regionDictionary.length; i++) {
              // Добавляем только те регионы, которые относятся к Украине
              if ( window.regionDictionary[i].region_parent_id === 0 ) {
                if ( window.regionDictionary[i].region_trial ) {
                  buf.push( {value: window.regionDictionary[i].region_id, label: window.regionDictionary[i].region_name_ua} );
                }
              }
            }

            region.init();
            region.setChoices(buf, 'value', 'label', false);
          });

          section.removeAttribute('hidden');
        } else {
          section.setAttribute('hidden', true);
        }
      });

      document.querySelector('.js-close-sidebar').click();
      document.querySelector('.js-nav-address').classList.add('is-show');

      if ( !btnBackSection.classList.contains('have-event') ) {

        btnBackSection.classList.add('have-event');

        btnBackSection.addEventListener('click', function(e) {
          const prevSectionClass = document.querySelector('.js-indiv-main-section').getAttribute('prev-section');
          const indivSections = document.querySelectorAll('.js-indiv-section');
          console.log(this);
          indivSections.forEach(section => {

            if ( section.classList.contains(prevSectionClass) ) {
              section.removeAttribute('hidden');
            } else {
              section.setAttribute('hidden', true);
            }
          });
        });
      }
    }

  });

  btnCreateRoomHomeless.addEventListener('click', function(e) {
    const btnOpenAllContent = document.querySelector('.js-open-all-content');
    btnOpenAllContent.setAttribute('stationary', true);
    btnOpenAllContent.parentElement.parentElement.setAttribute('data-source', util.CODE_HOMELESS);
    btnOpenAllContent.click();
  });
}
