import myMember from './my-member';
import mySelect from './my-select';

export default function adjustMigration( member ) {

  if(member === undefined) {
    member = myMember();
  }

  if(member === null) {
    return;
  }

  let block = false;


  let input = document.querySelector('input:checked[name=m_temp]');
  if(input !== null && +input.value === 3) {
    block = true;

  } // if(member.m_age < 15) ... else

  let tab = document.querySelector("a[href='#s_member-15']").parentElement;
  if( block ) {
    tab.classList.remove('ok', 'warning', 'error');
    tab.classList.add('disabled');
    document.querySelectorAll('input[name=m_live_birth_continuously]').forEach(el => {
      el.checked = false;
      el.disabled = true;
    });
    // document.querySelectorAll('input[name=m_armed_conflict]').forEach(el => {
    //   el.checked = false;
    //   el.disabled = true;
    // });
    // const year = document.querySelector('[name=m_live_birth_continuously_year]');
    // year.disabled = true;
    // year.value = '';
    // const month = document.querySelector('select[name=m_live_birth_continuously_month]');
    // month.disabled = true;
    // month.value = '';

    let region = mySelect( 'm_left_region' );
    if(region !== null) {
      region[0].selectize.clear();
      region[0].selectize.disable();
    }

    let city = mySelect( 'm_left_settlement' );
    if(city !== null) {
      city[0].selectize.clear();
      city[0].selectize.disable();
    }
  } else {
    tab.classList.remove('disabled');
    document.querySelectorAll('input[name=m_live_birth_continuously]').forEach(el => {
      el.disabled = false;
    });
  }
}
