export default class Location {
  constructor() { }

  get2() {
    return new Promise((resolve, reject) => {
      const result = nativeLocation.getLocation();

      try {
        if (result !== 'Error') {
          console.log(result);
          resolve(JSON.parse(result));

        } else {
          reject(result);
        }
      } catch (e) {
        console.log('query get catch ' + result);
      }

    });
  }
  get(success, error) {
    const result = nativeLocation.getLocation();
    try {
      if (result !== 'Error') {
        console.log(result);
        success(JSON.parse(result));
      } else {
        error(result);
      }
    } catch (e) {
      console.log('query get catch ' + result);
    }

  }
}
