export default function removeDictionarys() {
  storage.remove('lz_languageDictionary');
  storage.remove('lz_streetDictionary');
  storage.remove('lz_nationalityDictionary');
  storage.remove('lz_districtsDictionary');
  storage.remove('lz_buildingDictionary');
  storage.remove('lz_regionDictionary');
};
