import { addrName } from '../dictionary/names';

export default function checkRoomObj() {

  let error = false;
  let warning = false;
  // let name = addrName();
  let allErrors = [];

  // Прогоняем все контроли на форме
  for (let i = 0; i < util.checkRoom.length; i++) {

    if (typeof util.checkRoom[(i+1)] === 'function') {
      res = util.checkRoom[(i+1)]();

      allErrors.push({code: res.code , text: res.text, number: res.number});
      if (res.code === 1) {
        warning = true;
        continue;
      } else if (res.code === 2) {
        error = true;
        break;
      }
    } // if(typeof util.checkRoom[(i+1)] === 'function') ... else

  } // for(let i=0; i<util.checkRoom.length; i++)

  let res = 0;
  if ( error ) {
    res = 2;
  } else if ( warning ) {
    res = 1;
  }
  
  return [ {/*title: 'name',*/ content: allErrors } ];
}
