import createNewMember from './create-new-member';
import updateMember from './update-member';

const popupControlMember = document.querySelector('.js-popup-control-form-create-member');
const popupControlMemberContent = document.querySelector('.js-popup-control-form-create-member-content');
const popupControlMemberClose = document.querySelectorAll('.js-popup-control-form-create-member-close');
const popupControlMemberOk = document.querySelector('.js-popup-control-form-create-member-ok');


export default function controlFormCreateMember(form) {
  const btnSubmit = form.querySelector('button[type="submit"]');
  let promise = new Promise((resolve, reject) => { 
    const formCreateMember = form;
    const fieldConnection = formCreateMember.querySelector('[name="m_sib_id"]');
    const selectMonth = formCreateMember.querySelector('[name="m_birth_month"]');
    const selectYear = formCreateMember.querySelector('[name="m_birth_year"]');
    const fieldParent = formCreateMember.querySelector('[name="m_parent_id"]');
    const fieldSpouse = formCreateMember.querySelector('[name="m_spouse_id"]');
    const fieldGender = formCreateMember.querySelector('[name="m_gender_id"]:checked');
    const fieldParentValue = form.querySelector('[name="m_parent_id"]').value;
    const parentId = fieldParent.value;
    const month = selectMonth.value;
    const year = selectYear.value;
    const gender = fieldGender.value;
    const man = '1';
    const woman = '2';
    const wifeOrHasband = '2';
    const sonOrDaughterInLaw = '6';
    const sonOrDaughter = '3';
    const grandFatherOrMather = '8';
    const grandSonOrDaughter = '9';
    const matherOrFatherInLaw = '7';
    let fullAge;
    let firstMember;
    let wife;
    let ageFieldParent;
    let memberWifeOrHasband;
    let textGender;
    let textMarriage;
    let textAge;
    
    let btnCreateMember = btnSubmit.getAttribute('data-create');

    if ( members.length > 0 && btnCreateMember === '' ) {
      members.forEach(member => {
        if ( member.is_first ) {
          firstMember = member;
        }
        if ( member.m_id === fieldParentValue ) {
          ageFieldParent = member.m_age;
        }
        if ( member.m_id === fieldSpouse.value ) {
          memberWifeOrHasband = member;
          if ( memberWifeOrHasband.m_gender === '1' ) {
            textGender = 'він';
            textMarriage = 'одружений';
          } else {
            textGender = 'вона';
            textMarriage = 'одружена';
          }

          if ( +memberWifeOrHasband.m_age === 1 ) {
            textAge = 'рік';
          } else if ( +memberWifeOrHasband.m_age > 1 && +memberWifeOrHasband.m_age <= 4 ) {
            textAge = 'ріки';
          } else {
            textAge = 'років';
          }
        }
      });

      if ( firstMember.m_spouse_id !== null ) {
        members.forEach(member => {
          if ( member.m_spouse_id === firstMember.m_id ) {
            wife = member;
          }
        });
      }
  
      // возраст - полных лет
      let currentDate = new Date();
      let currentMonth = currentDate.getMonth() + 1;
      let currentYear = currentDate.getFullYear();
      if ( currentMonth >= +month ) {
        fullAge = currentYear - +year;
      } else {
        fullAge = currentYear - +year - 1;
      }

      if ( fieldConnection.value === wifeOrHasband && +fullAge < util.MIN_AGE_FOR_MARRIAGE ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = '“дружина, чоловік” вказується тільки особі, вік якої 15 років і старше';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === sonOrDaughterInLaw && +fullAge < util.MIN_AGE_FOR_MARRIAGE ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = '“невістка, зять” вказується тільки особі, вік якої 15 років і старше.';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === grandFatherOrMather && +fullAge < util.MIN_AGE_FOR_GRAND ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = '“баба, дід” вказується тільки особі, вік якої 30 років і старше';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === matherOrFatherInLaw && +fullAge - +firstMember.m_age < 15 ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = 'Різниця між віком записаного першим члена домогосподарства та віком “свекрухи, свекра, тещі, тестя” повинна бути не менше 15 років.';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === grandFatherOrMather && +fullAge - +firstMember.m_age < 30 ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = 'Записаний першим член домогосподарства не може бути молодший за “бабу (діда)” менш ніж на 30 років.';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === grandSonOrDaughter && +fullAge + 30 > +firstMember.m_age ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = '“Онука (онук)” не може бути молодший записаного першим члена домогосподарства менше ніж на 30 років';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === sonOrDaughter ) {
        if ( wife !== undefined ) {
          if ( firstMember.m_id !== parentId && wife.m_id !== parentId ) {
            popupControlMemberContent.innerHTML = '';
            let text = document.createElement('p');
            text.innerText = 'Якщо в полі “РОДИННI СТОСУНКИ З ОСОБОЮ, ЗАПИСАНОЮ ПЕРШОЮ” обрано - “донька/син”, то значення "мати" чи "батько" повинно посилатися на записаного першим члена домогосподарства або на його (її) дружину чи чоловіка.';
            popupControlMemberContent.appendChild(text);
      
            resolve(true);
            return;
          }
        } else {
          if ( firstMember.m_id !== parentId ) {
            popupControlMemberContent.innerHTML = '';
            let text = document.createElement('p');
            text.innerText = 'Якщо в полі “РОДИННI СТОСУНКИ З ОСОБОЮ, ЗАПИСАНОЮ ПЕРШОЮ” обрано "донька/син", то значення "мати" чи "батько" повинно посилатися на записаного першим члена домогосподарства або на його (її) дружину чи чоловіка.';
            popupControlMemberContent.appendChild(text);
      
            resolve(true);
            return;
          }
        }
        
      }

      if ( fieldConnection.value === wifeOrHasband && gender === woman && +firstMember.m_age > +fullAge + 25 ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = 'Якщо в полі “РОДИННI СТОСУНКИ З ОСОБОЮ, ЗАПИСАНОЮ ПЕРШОЮ” обрано - дружина. Перевірте: чоловік старший за дружину більше ніж на 25 років.';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === wifeOrHasband && gender === woman && +firstMember.m_age + 10 < +fullAge ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = 'Якщо в полі “РОДИННI СТОСУНКИ З ОСОБОЮ, ЗАПИСАНОЮ ПЕРШОЮ” обрано - дружина. Перевірте: дружина старша за чоловіка більше ніж на 10 років.';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === wifeOrHasband && gender === man && +firstMember.m_age > +fullAge + 10 ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = 'Якщо в полі “РОДИННI СТОСУНКИ З ОСОБОЮ, ЗАПИСАНОЮ ПЕРШОЮ” обрано - чоловік. Перевірте: дружина  старша за чоловіка більше ніж на 10 років.';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === wifeOrHasband && gender === man && +firstMember.m_age + 25 < +fullAge ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = 'Якщо в полі “РОДИННI СТОСУНКИ З ОСОБОЮ, ЗАПИСАНОЮ ПЕРШОЮ” обрано - чоловік. Перевірте: чоловік старший за дружину більше ніж на 25 років.';
        popupControlMemberContent.appendChild(text);

        resolve(true);
        return;
      }

      if ( fieldConnection.value === sonOrDaughter ) {
        if ( wife !== undefined ) {
          if ( +fullAge + 15 > +firstMember.m_age || +fullAge + 15 > +wife.m_age ) {
            popupControlMemberContent.innerHTML = '';
            let text = document.createElement('p');
            text.innerText = 'Якщо в полі “РОДИННI СТОСУНКИ З ОСОБОЮ, ЗАПИСАНОЮ ПЕРШОЮ” обрано - донька, син, то різниця між віком матері (батька) та дочки (сина) повинна бути більше 15 років.';
            popupControlMemberContent.appendChild(text);
      
            resolve(true);
            return;
          }
        } else {
          if ( +fullAge + 15 > +firstMember.m_age ) {
            popupControlMemberContent.innerHTML = '';
            let text = document.createElement('p');
            text.innerText = 'Якщо в полі “РОДИННI СТОСУНКИ З ОСОБОЮ, ЗАПИСАНОЮ ПЕРШОЮ” обрано - донька, син, то різниця між віком матері (батька) та дочки (сина) повинна бути більше 15 років.';
            popupControlMemberContent.appendChild(text);
      
            resolve(true);
            return;
          }
        }
        
        
      }

      if ( (+ageFieldParent - +fullAge) < util.MIN_AGE_FOR_PARENT ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = 'Різниця між віком матері (батька) та доньки (сина) повинна бути більше 15 років.';
        popupControlMemberContent.appendChild(text);
  
        resolve(true);
        return;
      }

      if ( +fullAge < util.MIN_AGE_FOR_MARRIAGE && fieldSpouse.value !== '' ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = 'Якщо вік респондента від 0 до 15 років, то поле "дружина, чоловік" не заповнюється.';
        popupControlMemberContent.appendChild(text);
  
        resolve(true);
        return;
      }
      
      if ( memberWifeOrHasband && +memberWifeOrHasband.m_age <= util.MIN_AGE_FOR_MARRIAGE ) {
        popupControlMemberContent.innerHTML = '';
        let text = document.createElement('p');
        text.innerText = `Вік респондента "${memberWifeOrHasband.m_name}" ${memberWifeOrHasband.m_age} ${textAge}, Вы впевнені що ${textGender} може бути ${textMarriage}?`;
        popupControlMemberContent.appendChild(text);
  
        resolve(true);
        return;
      }
      

      resolve(false);
      return;
      
    } else {
      resolve(false);
      return;
    }

  });

  promise
    .then(
      result => {
        console.log(result);
        if ( result ) {
          popupControlMember.classList.add('is-show');
          document.body.classList.add('warning');

          popupControlMemberClose.forEach(close => {
            close.removeEventListener('click', closePopupMemberControl);
          });

          popupControlMemberClose.forEach(close => {
            close.addEventListener('click', closePopupMemberControl);
          });

          popupControlMemberOk.removeEventListener('click', okPopupMemberControl);
          popupControlMemberOk.addEventListener('click', okPopupMemberControl);

        } else {

          const formCreateMember = form;
          const btnSubmit = formCreateMember.querySelector('button[type="submit"]');
          const itsFormNewMember = btnSubmit.getAttribute('member-id') === '' || btnSubmit.getAttribute('member-id') === null;
          
          popupControlMemberOk.innerText = 'Зберегти';
          popupControlMember.classList.remove('is-show');
          document.body.classList.remove('warning');

          if ( formCreateMember.getAttribute('first-time') === 'true' ) {
            formCreateMember.setAttribute('first-time', 'false');
        
            popupControlMemberContent.innerHTML = '<p>Відомості про стать та вік не можна буде виправити. Ви впевнені, що інформація заповнена вірно?</p>';
            popupControlMember.classList.add('is-show');
            document.body.classList.add('warning');
        
            popupControlMemberClose.forEach(close => {
              close.removeEventListener('click', closePopupMemberControl);
            });
        
            popupControlMemberClose.forEach(close => {
              close.addEventListener('click', closePopupMemberControl);
            });
        
            popupControlMemberOk.removeEventListener('click', okPopupMemberControl);
            popupControlMemberOk.addEventListener('click', okPopupMemberControl);
          } else {

            if ( itsFormNewMember ) {
              createNewMember(formCreateMember);
            } else {
              members.forEach(member => {
                if ( member.m_id === btnSubmit.getAttribute('member-id') ) {
                  updateMember(member, formCreateMember);
                }
              });
            }
          }

         
        }
      // console.log(result);
      },
      error => {
        console.log(error);
      }
    );

}

function closePopupMemberControl() {
  popupControlMember.classList.remove('is-show');
  document.body.classList.remove('warning');
}

function okPopupMemberControl() {
  const formCreateMember = document.querySelector('.js-form-create-member');
  const btnSubmit = formCreateMember.querySelector('.js-form-create-member button[type="submit"]');

  popupControlMemberOk.innerText = 'Все одно продовжити';
  popupControlMember.classList.remove('is-show');
  document.body.classList.remove('warning');

  if ( formCreateMember.getAttribute('first-time') === 'true' ) {
    formCreateMember.setAttribute('first-time', 'false');

    popupControlMemberOk.innerText = 'Зберегти';
    popupControlMemberContent.innerHTML = '<p>Відомості про стать та вік не можна буде виправити. Ви впевнені, що інформація заповнена вірно?"</p>';
    popupControlMember.classList.add('is-show');
    document.body.classList.add('warning');

    popupControlMemberClose.forEach(close => {
      close.removeEventListener('click', closePopupMemberControl);
    });

    popupControlMemberClose.forEach(close => {
      close.addEventListener('click', closePopupMemberControl);
    });

    popupControlMemberOk.removeEventListener('click', okPopupMemberControl);
    popupControlMemberOk.addEventListener('click', okPopupMemberControl);
  } else {

    popupControlMember.classList.remove('is-show');
    document.body.classList.remove('warning');
    btnSubmit.setAttribute('data-create', 'true');
    btnSubmit.click();
    btnSubmit.setAttribute('data-create', '');
  }
        
}
