import Choices from 'choices.js';
import myMember from '../../handlers/my-member';

export default function createSelect([placeholder, name, className, wrap], arrValue, multiCode = false, optionSelectedText = false ) {
  // создание select
  let customSelect;
  let currentMember = myMember( document.querySelector('.js-user-block .js-member').getAttribute('data-id') );
  const select = document.createElement('select');
  select.name = name;
  select.classList.add(className);
  wrap.appendChild(select);

  if ( multiCode ) {
    select.multiple = true;
    multiCode.forEach((value, i) => {
      if ( value !== util.DEFAULT_VALUE_LANGUAGE ) {
        const option = new Option();
        option.setAttribute('value', value);
        option.innerText = optionSelectedText[i];
        option.selected = true;
        select.appendChild(option);
      }
    });

    // Инициализация выпадающего списка multi с кнопкой удаления
    customSelect = new Choices(`.${className}`, {
      placeholder: true,
      placeholderValue: 'Зазначте мови, якими Ви володієте',
      noResultsText: 'Нічого не знайдено',
      itemSelectText: 'Натисніть, щоб вибрати',
      noChoicesText: 'Нема з чого вибрати',
      removeItemButton: true,
      maxItemCount: 3,
      searchResultLimit: 30,
      maxItemText: (maxItemCount) => {
        return `Не бiльше ${maxItemCount}-х мов`;
      },
    });

  } else {
    if ( name === 'm_language_id' || name === 'm_national_id_original' ) {
      // Инициализация выпадающего списка с созданием
      customSelect = $(` select.${className}`).selectize({
        valueField: 'value',
        labelField: 'label',
        searchField: 'label',
        options: arrValue,
        items: [optionSelectedText],
        create: true,
        createOnBlur: true,
        placeholder: placeholder
      });
      console.log(customSelect);
    } else {
      // Инициализация выпадающего списка с созданием
      customSelect = $(` select.${className}`).selectize({
        valueField: 'value',
        labelField: 'label',
        searchField: 'label',
        options: arrValue,
        items: [optionSelectedText],
        createOnBlur: true,
        placeholder: placeholder
      });
    }

  }

  if ( multiCode ) {
    customSelect.setChoices(arrValue, 'value', 'label', false);
  }

  if ( className === 'js-select-language' && currentMember.m_language_another !== '' ) {
    customSelect[0].selectize.createItem(currentMember.m_language_another);
  }

  if ( className === 'js-select-national' && currentMember.m_national_another !== '' ) {
    customSelect[0].selectize.createItem(currentMember.m_national_another);
  }


  util.selectsMember.push(customSelect);
}
