import generateName from './generate-name';

export default function updateHouseholds() {
  let households = window.User.rooms[0].households;

  for ( let i = 0; i < households.length; i++ ) {
    let tab = document.querySelector(".js-room-household-tabs > li[data-household='" + households[i].h_household_number + "']");

    // Исправляем названия
    tab.firstChild.innerHTML = generateName( households[i].h_household_number );
    // Исправляем активность/неактивность закладки
    if ( +households[i].h_household_number === +window.User.rooms[0].current_household ) {
      tab.classList.add( 'is-active' );
    } else {
      tab.classList.remove( 'is-active' );
    }

    // Исправляем количество членов
    if ( households[i].h_household_member_count <= 5 ) {
      document.querySelector(".radio-num__input.js-household-members[data-household='" + households[i].h_household_number + "'][value='" +
				households[i].h_household_member_count + "']").checked = true;
      document.querySelector(".js-household-members-text[data-household='" + households[i].h_household_number + "']").value = '';
    } else {
      document.querySelectorAll(".radio-num__input.js-household-members[data-household='" + households[i].h_household_number + "']").forEach(el => {
        el.checked = false;
      });
      document.querySelector(".js-household-members-text[data-household='" + households[i].h_household_number + "']").value = households[i].h_household_member_count;
    }

  } // for(let i=0; i<households.length; i++)

}
