import createSelect from './create-select';

export default function initSelectNationality(formMember, currentMember) {
  const selectNationality = formMember.querySelector('[data-select-nationality]');
  // создаю выпадающий список для национальности
  const selectNationalityName = selectNationality.getAttribute('data-select-nationality');
  const selectNationalityClass = selectNationality.getAttribute('data-class');
  const selectNationalityPlaceholder = selectNationality.getAttribute('data-placeholder');
  const dataForSelectNationality = [selectNationalityPlaceholder, selectNationalityName, selectNationalityClass, selectNationality];
  const catalogNationality = [];
  let selected;

  nationalityDictionary.forEach(nationality => {
    if ( nationality.national_id !== util.DEFAUL_VALUE_NATIONALITY ) {

      if ( +currentMember[selectNationalityName] === nationality.national_id && currentMember[selectNationalityName] !== util.DEFAUL_VALUE_NATIONALITY ) {
        catalogNationality.push( {value: +nationality.national_id, label: nationality.national_name_ua} );
        selected = nationality.national_id;
      } else {
        catalogNationality.push( {value: +nationality.national_id, label: nationality.national_name_ua} );
      }
    }
  });

  createSelect( dataForSelectNationality, catalogNationality, false, selected );
}
