import Choices from 'choices.js';
import {cityName, streetName, houseName} from '../dictionary/names';

const btnCreateRoomStationary = document.querySelector('.js-create-room-stationary');
const btnCreateRoomHomeless = document.querySelector('.js-create-room-homeless');

// Отслеживание введённых полей для формы адреса
const formStatus = {
  region: false,
  city: false,
  street: false,
  building: false,
  terson_distr: false
};

// Инициализация выпадающего списка регионов
export const region = new Choices('.js-select-region', {
  placeholder: true,
  placeholderValue: 'Оберіть регіон',
  noResultsText: 'Нічого не знайдено',
  itemSelectText: 'Натисніть, щоб вибрати',
  noChoicesText: 'Нема з чого вибрати',
  searchResultLimit: 30
});

// Инициализация выпадающего списка населённых пунктов
export const city = new Choices('.js-select-city', {
  placeholderValue: 'Оберіть населений пункт',
  noResultsText: 'Нічого не знайдено',
  itemSelectText: 'Натисніть, щоб вибрати',
  noChoicesText: 'Нема з чого вибрати',
  searchResultLimit: 30
});

// Инициализация выпадающего списка районов
export const terson_distr = new Choices('.js-select-terson_distr', {
  placeholderValue: 'Оберіть район',
  noResultsText: 'Нічого не знайдено',
  itemSelectText: 'Натисніть, щоб вибрати',
  noChoicesText: 'Нема з чого вибрати',
  searchResultLimit: 30
});

// Инициализация выпадающего списка улиц
export const street = new Choices('.js-select-street', {
  placeholderValue: 'Оберіть вулицю',
  noResultsText: 'Нічого не знайдено',
  itemSelectText: 'Натисніть, щоб вибрати',
  noChoicesText: 'Нема з чого вибрати',
  searchResultLimit: 30
});

// Инициализация выпадающего списка домов
export const building = new Choices('.js-select-building', {
  placeholderValue: 'Оберіть будинок',
  noResultsText: 'Нічого не знайдено',
  itemSelectText: 'Натисніть, щоб вибрати',
  noChoicesText: 'Нема з чого вибрати',
  searchResultLimit: 30,
  sortFn: function(a, b) {
    let first;
    let second;
    let firstLength = a.label.replace(/\s/g, '').length - 1;
    let secondLength = b.label.replace(/\s/g, '').length - 1;

    if ( a.label.length > 3 ) {
      first = a.label.replace(/\s/g, '').substring(0, firstLength);
    } else {
      first = a.label;
    }

    if ( b.label.length > 3 ) {
      second = b.label.replace(/\s/g, '').substring(0, secondLength);
    } else {
      second = b.label;
    }

    let one = first.replace(/[^\/0-9]/g, '');
    let two = second.replace(/[^\/0-9]/g, '');

    return one.replace(/\/.*/, '') - two.replace(/\/.*/, '');
  }
});

export function initAddressForm() {

  terson_distr.destroy();

  // Навешиваем обработчик, чтобы при изменении региона подгружались его населённые пункты
  const regionSelect = document.querySelector( '#r_region_id' );
  regionSelect.addEventListener('change', regionChange);

  const citySelect = document.querySelector( '#r_terson_id' );
  citySelect.addEventListener('change', cityChange);

  const tersonDistrSelect = document.querySelector( '#r_terson_distr' );
  tersonDistrSelect.parentElement.parentElement.style.display = 'none';

  const streetSelect = document.querySelector( '#r_street_id' );
  streetSelect.parentElement.parentElement.style.display = 'flex';
  streetSelect.addEventListener('change', streetChange);

  const buildingSelect = document.querySelector( '#r_house_number_id' );
  buildingSelect.parentElement.parentElement.style.display = 'flex';
  buildingSelect.addEventListener('change', buildingChange);

  const roomNotNumber = document.querySelector('#r_flat_without_number');
  roomNotNumber.parentElement.parentElement.style.display = 'flex';
  roomNotNumber.addEventListener('change', roomNotNumberChange);

  const roomNumber = document.querySelector('#r_flat_number');
  roomNumber.parentElement.parentElement.style.display = 'flex';
  roomNumber.addEventListener('input', checkForm);

  enableAddr();
}

export function initAddressFormHomeless() {
  street.destroy();
  building.destroy();
  
  // Навешиваем обработчик, чтобы при изменении региона подгружались его населённые пункты
  const regionSelect = document.querySelector( '#r_region_id' );
  regionSelect.addEventListener('change', regionChange);

  const citySelect = document.querySelector( '#r_terson_id' );
  citySelect.addEventListener('change', cityChange);

  const tersonDistrSelect = document.querySelector( '#r_terson_distr' );
  tersonDistrSelect.parentElement.parentElement.style.display = 'flex';
  tersonDistrSelect.addEventListener('change', tersonDistrChange);

  const streetSelect = document.querySelector( '#r_street_id' );
  streetSelect.parentElement.parentElement.style.display = 'none';

  const buildingSelect = document.querySelector( '#r_house_number_id' );
  buildingSelect.parentElement.parentElement.style.display = 'none';

  const roomNotNumber = document.querySelector('#r_flat_without_number');
  roomNotNumber.parentElement.parentElement.style.display = 'none';

  const roomNumber = document.querySelector('#r_flat_number');
  roomNumber.parentElement.parentElement.style.display = 'none';

  enableAddr();
}


function regionChange(event) {
  const role = +document.body.getAttribute('role');
  if ( role !== util.CODE_COPYIS ) {
    city.destroy();
    city.init();
    street.destroy();
    street.init();
    street.disable();
    building.destroy();
    building.init();
    building.disable();
  } else {
    city.destroy();
    city.init();
    terson_distr.destroy();
    terson_distr.init();
    terson_distr.disable();
  }
  

  let apartment = document.querySelector('#r_flat_number');
  apartment.value = '';
  apartment.disabled = true;

  // Выбрали регион?
  if ( +this.value !== 0 ) {
    formStatus.region = true;
    city.enable(); // разблокируем выбор населённого пункта
    // Справочник городов загружен?
    if ( window.cityDictionary !== undefined && window.cityDictionary !== null) {
      let buf = [];
      for ( let i = 0; i < window.cityDictionary.length; i++ ) {
        // Добавляем только те населённые пункты, которые относятся к этому региону
        if ( window.cityDictionary[i].region_id === +event.target.value ) {
          if ( window.cityDictionary[i].terson_trial ) {
            buf.push( {value: window.cityDictionary[i].terson_id, label: cityName(window.cityDictionary[i])} );
          }
        }
      } // for(i=0; i<cityDictionary.length; i++)
      city.setChoices(buf, 'value', 'label', false);
    } // if( cityDictionary )

  } else { // if(+event.target.value !== 0)
    city.disable(); // Если регион не выбран, блокируем выбор населённого пункта
    formStatus.region = false;
  } // if(+event.target.value !== 0) ... else

  checkForm(); // Запускаем проверку, активировать ли кнопку "Далі"
}


function cityChange(event) {
  const role = +document.body.getAttribute('role');
  console.log(role);
  if ( role !== util.CODE_COPYIS ) {
    street.destroy();
    street.init();
    building.destroy();
    building.init();
    building.disable();
  } else {
    terson_distr.destroy();
    terson_distr.init();
  }

  let apartment = document.querySelector('#r_flat_number');
  apartment.value = '';
  apartment.disabled = true;

  // Выбрали город?
  if ( +this.value !== 0 ) {
    formStatus.city = true;
    if ( role !== util.CODE_COPYIS ) {
      street.enable(); // разблокируем выбор улицы
      // Справочник улиц загружен?
      if ( window.streetDictionary !== null ) {
        let buf = [];
        for ( let i = 0; i < window.streetDictionary.length; i++ ) {
          // Добавляем только те улицы, которые относятся к этому городу
          if ( +window.streetDictionary[i].street_terson_id === +this.value ) {
            if ( window.streetDictionary[i].street_trial ) {
              buf.push( {value: window.streetDictionary[i].street_id, label: streetName(window.streetDictionary[i])} );
            }
          }
        } // for(i=0; i<streetDictionary.length; i++)
        street.setChoices(buf, 'value', 'label', false);
      } // if( streetDictionary )
    } else {
      terson_distr.enable(); // разблокируем выбор районов
      if ( window.districtsDictionary !== null ) {
        let buf = [];
        for ( let i = 0; i < window.districtsDictionary.length; i++ ) {
          // Добавляем только те улицы, которые относятся к этому городу
          console.log(+window.districtsDictionary[i].terson_parent_id);
          console.log(+this.value);
          if ( +window.districtsDictionary[i].terson_parent_id === +this.value ) {
            buf.push( {value: window.districtsDictionary[i].terson_id, label: window.districtsDictionary[i].terson_name_ua} );
          }
        } // for(i=0; i<streetDictionary.length; i++)
        if ( buf.length ) {
          terson_distr.setChoices(buf, 'value', 'label', false);
        } else {
          terson_distr.disable();
        }
        
      }
    }

  } else { // if(+event.target.value !== 0)
    street.disable(); // Если город не выбран, блокируем выбор улицы
    formStatus.city = false;

    terson_distr.disable();
    formStatus.terson_distr = false; // Если город не выбран, блокируем выбор районов
  } // if(+event.target.value !== 0) ... else

  checkForm(); // Запускаем проверку, активировать ли кнопку "Далі"
}

function tersonDistrChange(event) {

  if ( +this.value !== 0 ) {
    formStatus.terson_distr = true;
  }

  checkForm(); // Запускаем проверку, активировать ли кнопку "Далі"
}


function streetChange(event) {

  building.destroy();
  building.init();
  
  let apartment = document.querySelector('#r_flat_number');
  apartment.value = '';
  apartment.disabled = true;

  // Выбрали улицу?
  if ( +this.value !== 0 ) {
    formStatus.street = true;
    building.enable(); // разблокируем выбор дома

    // Справочник домов загружен?
    if ( window.buildingDictionary !== undefined && window.buildingDictionary !== null ) {

      let buf = [];
      for ( let i = 0; i < window.buildingDictionary.length; i++ ) {
        // Добавляем только те дома, которые есть на этой улице
        if ( +window.buildingDictionary[i].house_street_id === +this.value ) {
          if ( window.buildingDictionary[i].house_trial ) {
            buf.push( {value: window.buildingDictionary[i].house_id, label: houseName(window.buildingDictionary[i])} );
          }
        }
      } // for(i=0; i<streetDictionary.length; i++)
      building.setChoices(buf, 'value', 'label', false);

    } // if( buildingDictionary )

  } else { // if(+event.target.value !== 0)
    building.disable(); // Если улица не выбрана, блокируем выбор дома
    formStatus.street = false;
  } // if(+event.target.value !== 0) ... else

  checkForm(); // Запускаем проверку, активировать ли кнопку "Далі"
}


function buildingChange(event) {

  let apartment = document.querySelector('#r_flat_number');

  // Выбрали дом?
  if ( +this.value !== 0 ) {
    formStatus.building = true;
    apartment.disabled = false; // разблокируем ввод квартиры
    apartment.value = ''; // Очищаем квартиру
  } else {
    formStatus.building = false;
    apartment.disabled = true;
    apartment.value = ''; // Очищаем квартиру
  }

  checkForm(); // Запускаем проверку, активировать ли кнопку "Далі"
}

function roomNotNumberChange() {

  if ( this.checked ) {
    document.querySelector('#r_flat_number').value = '';
    document.querySelector('#r_flat_number').setAttribute('disabled', 'disabled');
  } else {
    document.querySelector('#r_flat_number').removeAttribute('disabled');
  }

  checkForm(); // Запускаем проверку, активировать ли кнопку "Далі"
}

export function resetAddrForm() {
  // очищаем и блокируем выбор квартиры
  let apartment = document.querySelector('#r_flat_number');
  apartment.value = '';
  apartment.disabled = true;

  // очищаем и блокируем выбор дома
  building.clearStore();
  building.disable();

  // очищаем и блокируем выбор улицы
  street.clearStore();
  street.disable();

  // очищаем и блокируем выбор города
  city.clearStore();
  city.disable();

  // очищаем и блокируем выбор региона
  region.clearStore();
  region.disable();

  checkForm(); // Запускаем проверку, активировать ли кнопку "Далі"
}


function checkForm() {
  const roomNumber = document.querySelector('#r_flat_number');
  const roomNotNumber = document.querySelector('#r_flat_without_number');
  const role = +document.body.getAttribute('role');
  const isDisabledTersonDistr = document.querySelector('#r_terson_distr').getAttribute('disabled') === '';
  // Проверяем, все ли поля введены
  if ( role !== util.CODE_COPYIS && formStatus.region && formStatus.city && formStatus.street && formStatus.building && (roomNotNumber.checked || roomNumber.value !== '') ) {
    btnCreateRoomStationary.classList.remove( 'disabled' ); // Разблокируем кнопку "Далі"
    btnCreateRoomHomeless.classList.remove( 'disabled' );
  } else if ( role === util.CODE_COPYIS && formStatus.region && formStatus.city && (formStatus.terson_distr || isDisabledTersonDistr) ) {
    btnCreateRoomStationary.classList.remove( 'disabled' ); // Разблокируем кнопку "Далі"
    btnCreateRoomHomeless.classList.remove( 'disabled' );
  } else {
    btnCreateRoomStationary.classList.add( 'disabled' );
    btnCreateRoomHomeless.classList.add( 'disabled' );
  }
}

function enableAddr() {

  if ( window.regionDictionary !== undefined && window.regionDictionary !== null ) {

    let buf = [];
    for (let i = 0; i < window.regionDictionary.length; i++ ) {
      // Добавляем только те регионы, которые относятся к Украине
      if ( window.regionDictionary[i].region_parent_id === 0 ) {
        if ( window.regionDictionary[i].region_trial ) {
          buf.push( {value: window.regionDictionary[i].region_id, label: window.regionDictionary[i].region_name_ua} );
        }
      }
    } // for(i=0; i<regionDictionary.length; i++)
    region.setChoices(buf, 'value', 'label', true);
  } // if(window.regionDictionary !== undefined && window.regionDictionary !== null)
  
}

function Event( event, params ) {
  params = params || { bubbles: false, cancelable: false, detail: undefined };
  var evt = document.createEvent( 'CustomEvent' );
  evt.initCustomEvent( event, params.bubbles, params.cancelable, params.detail );
  return evt;
}

