require('babel-polyfill');
import loadTerson from '../../helpers/load-terson';
import dictionaryList from './list';

// Загрузка библиотек сжатия
var LZString = require('../../helpers/lz-string.js');

export default async function initdictionaryList() {
  let name;
  let shortName;

  for ( let i = 0; i < dictionaryList.length; i++ ) {

    shortName = dictionaryList[i];
    name = shortName + 'Dictionary';

    // Этот справочник уже загружен?
    if (window[name] === undefined || window[name] === null) {
      let lzDictionary = storage.get( 'lz_' + name );
      // Справочник есть в local storage?
      if ( lzDictionary ) {
        let buf = LZString.decompress( lzDictionary );

        if ( buf ) { // local storage Ok
          try {
            window[name] = JSON.parse( buf );
          } catch(e) {
            storage.remove( 'lz_' + name );
            await loadTerson( shortName ); // битый JSON, загружаем заново
          }
        } else { // бывает, что в local storage "битая" версия
          storage.remove( 'lz_' + name );
          await loadTerson( shortName ); // загружаем с сервера
        }
      } else {
        await loadTerson( shortName ); // загружаем с сервера
      } // if( lzDictionary ) ... else

    } // if(window[name] === undefined)

  } // for(let i=0; i<dictionaryList.length; i++)

}
