export default function changedTextForValidity() {
  const formCreateMember = document.querySelector('.js-form-create-member');
  const genderMan = formCreateMember.querySelector('[name="m_gender_id"][value="1"]');
  const genderWoman = formCreateMember.querySelector('[name="m_gender_id"][value="2"]');
  const genderAll = formCreateMember.querySelectorAll('[name="m_gender_id"]');
  const name = formCreateMember.querySelector('[name="m_name"]');
  const year = formCreateMember.querySelector('[name="m_birth_year"]');
  const month = formCreateMember.querySelector('[name="m_birth_month"]');
  const day = formCreateMember.querySelector('[name="m_birth_day"]');
  // const age = formCreateMember.querySelector('[name="m_age"]');
  const connection = formCreateMember.querySelector('[name="m_sib_id"]');

  if ( !genderMan.checked && !genderWoman.checked ) {
    genderAll.forEach(input => {
      input.setCustomValidity('Оберіть один з варіантів');
    });
  } else {
    genderAll.forEach(input => {
      input.setCustomValidity('');
    });
  }

  if ( name.value === '' ) {
    name.setCustomValidity('Вкажіть ім`я респондента');
  } else {
    name.setCustomValidity('');
  }

  if ( year.value === '' ) {
    year.setCustomValidity('Оберіть рік');
  } else {
    year.setCustomValidity('');
  }

  if ( month.value === '' ) {
    month.setCustomValidity('Оберіть місяць');
  } else {
    month.setCustomValidity('');
  }

  if ( day.value === '' ) {
    day.setCustomValidity('Оберіть число');
  } else {
    day.setCustomValidity('');
  }

  // if ( age.value === '' ) {
  //   age.setCustomValidity('Вкажіть вік');
  // } else {
  //   age.setCustomValidity('');
  // }

  if ( connection.value === '' ) {
    connection.setCustomValidity('Оберіть ким респондент доводиться записаному першим');
  } else {
    connection.setCustomValidity('');
  }

}

