import initSelectChildren from '../create-select-children';

export default function changeRelationship() {
  const form = document.querySelector('.js-form-create-member');
  const selectRelationship = form.querySelector('select[name="m_sib_id"]');
  const selectSpouse = form.querySelector('select[name="m_spouse_id"]');
  const selectParent = form.querySelector('select[name="m_parent_id"]');
  const fieldChildren = form.querySelector('[data-select-children-multi="m_children"]');
  const optionTemplate = document.createElement('option');
  const wifeOrHusband = '2';
  const childrenCode = '3';
  const parent = '4';
  const man = '1';
  const woman = '2';

  selectRelationship.addEventListener('change', function(e) {
    const radioValue = form.querySelector('[name="m_gender_id"]:checked').value;
    let firstMember;
    let currentAge = document.querySelector('.js-member-age').innerText.replace(/[^\/0-9]/g, '');


    // очищаю поле дети
    if ( window.selectChildren ) {
      window.selectChildren.destroy();
      window.selectChildren = null;
      form.querySelector('[select]').innerHTML = '';
    }

    if ( !window.selectChildren ) {
      initSelectChildren(form, false, currentAge, selectRelationship.value);
      fieldChildren.parentElement.parentElement.classList.remove('is-disabled');
    }

    if ( this.value !== '' ) {
      this.classList.remove('is-empty');
    } else {
      this.classList.add('is-empty');
    }

    members.forEach(member => {
      if ( member.is_first ) {
        firstMember = member;
      }
    });
    // // удаляю с выбора муж/жена 
    // if ( selectSpouse.firstChild ) {
    //   [...selectSpouse.children].forEach(option => {
    //     if ( option.value !== '' ) {
    //       option.parentNode.removeChild(option);
    //     }
    //   });
    // }

    // если выбран пол женский
    if ( radioValue === woman ) {

      // обнуляю список муж/жена
      [...selectSpouse.children].forEach(option => {
        if ( option.value !== '' ) {
          option.parentNode.removeChild(option);
        }
      });

      // наполняю список выбора мужей
      members.forEach(member => {

        if ( member.m_gender_id === man && member !== members[0] && member.m_sib_id !== wifeOrHusband ) {
          let option = optionTemplate.cloneNode();
          option.setAttribute('value', member.m_id);
          option.innerText = `${member.m_name} (${member.m_age}р.)`;
          selectSpouse.appendChild(option);
        }
      });

      // если выбран пол мужской 
    } else {
      // обнуляю список муж/жена
      [...selectSpouse.children].forEach(option => {
        if ( option.value !== '' ) {
          option.parentNode.removeChild(option);
        }
      });
      // наполняю список выбора жен
      members.forEach(member => {
        if ( member.m_gender_id === woman && member !== members[0] && member.m_sib_id !== wifeOrHusband ) {
          let option = optionTemplate.cloneNode();
          option.setAttribute('value', member.m_id);
          option.innerText = `${member.m_name} (${member.m_age}р.)`;
          selectSpouse.appendChild(option);
        }
      });
    }

    // если выбрана связь муж/жена блокирую выбор поля муж/жена 
    if ( this.value === wifeOrHusband ) {
      selectSpouse.disabled = true;
      selectSpouse.parentElement.parentElement.classList.add('is-disabled');

      // автоматичести устанавливаю его имя 
      members.forEach(member => {

        if ( member.is_first ) {
          let option = optionTemplate.cloneNode();
          option.setAttribute('value', member.m_id);
          option.innerText = `${member.m_name} (${member.m_age}р.)`;
          option.selected = true;
          selectSpouse.appendChild(option);
        }
      });
      
    } else {

      // если возраст меньше 15лет, блокируем выбор поля муж/жена
      if ( +currentAge < util.MIN_AGE_FOR_MARRIAGE ) {
        selectSpouse.disabled = true;
        selectSpouse.parentElement.parentElement.classList.add('is-disabled');
      } else {
        selectSpouse.disabled = false;
        selectSpouse.parentElement.parentElement.classList.remove('is-disabled');
      }

      
    }

    // показываю выбор папа/мама
    selectParent.disabled = false;
    selectParent.parentElement.parentElement.classList.remove('is-disabled');

    // обнуляю список папа/мама
    [...selectParent.children].forEach(option => {
      if ( option.value !== '' ) {
        option.parentNode.removeChild(option);
      }
    });
    selectParent.classList.add('is-empty');
    // наполняю список выбора мама/папа
    members.forEach(member => {
      // для сина и дочки родители могут быть только первый и его (муж/жена)
      if ( this.value === childrenCode ) {
        if ( +currentAge < member.m_age && ( member.is_first || member.m_sib_id === wifeOrHusband ) ) {
          let option = optionTemplate.cloneNode();
          option.setAttribute('value', member.m_id);
          option.innerText = `${member.m_name} (${member.m_age}р.)`;
          selectParent.appendChild(option);
        }
      } else if ( +currentAge < member.m_age ) { // проверяю чтоб родители не были младше
        let option = optionTemplate.cloneNode();
        option.setAttribute('value', member.m_id);
        option.innerText = `${member.m_name} (${member.m_age}р.)`;
        selectParent.appendChild(option);
      }
    });
    
    if ( this.value !== childrenCode ) {

      [...selectParent.children].forEach(option => {
        if ( +option.value === +firstMember.m_id ) {
          option.parentNode.removeChild(option);
        }
      });
    } else {

      let currentParents = [];
      [...selectParent.children].forEach(option => {
        currentParents.push(option.value);
      });

      [...selectParent.children].forEach(option => {
        if ( !currentParents.includes(`${firstMember.m_id}`) ) {
          selectParent.appendChild( new Option( `${firstMember.m_name} (${firstMember.m_age})`, firstMember.m_id ) );
          currentParents.push(`${firstMember.m_id}`);
        }
      });
    }
    
  });
}
