export function setHandlers() {
  let tab;
  let func;

  for (let i=1; i<=14; i++) {
    tab = document.querySelector('#room-'+i);

    if ( eval('typeof handler'+i) === 'function') {

      tab.querySelectorAll('input').forEach(el => {

        if (el.type === 'text') {
          el.addEventListener('keyup', eval('handler'+i));
        } else {
          el.addEventListener('change', eval('handler'+i));
        }

      });
    } // if( eval('typeof handler'+i) === 'function')

  } // for(let i=1; i<3; i++)
}


export function runHandlers() {
  for (let i=1; i<=14; i++) {
    if ( eval('typeof handler'+i) === 'function') {
      eval( 'handler' + i + '()' );
    } // if( eval('typeof handler'+i) === 'function')
  }
}


