const man = '1';
const woman = '2';

export function sortSisterAndBrother(a,b) {
  if (a.m_sib_id > b.m_sib_id && a.m_sib_id !== '2') {
    return -1;
  }
  if (a.m_sib_id < b.m_sib_id) {
    return 1;
  }
  return 0;
}

export function sortMains(currentMembers) {
  let newArr = [];
  
  if ( currentMembers.length > 0 ) {
    currentMembers.forEach(child => {
      // если есть брат и его жена
      if ( child.m_sib_id === '5' && child.m_gender_id === man ) {
        newArr.push(child);
                
        if ( child.m_spouse_id !== null ) {
          for ( let i = 0; i < currentMembers.length; i++ ) {
            if ( child.m_spouse_id === currentMembers[i].m_id ) {
              newArr.push(currentMembers[i]);
              currentMembers.splice(i, 1);
            }
          }
        }
      }
    });

    currentMembers.forEach(child => {
      // если есть сестра и её муж
      if ( child.m_sib_id === '5' && child.m_gender_id === woman ) {
                
        if ( child.m_spouse_id !== null ) {
          for ( let i = 0; i < currentMembers.length; i++ ) {
            if ( child.m_spouse_id === currentMembers[i].m_id ) {
              newArr.push(currentMembers[i]);
              currentMembers.splice(i, 1);
            }
          }
        }
        newArr.push(child);

      } 
    });

    currentMembers.forEach(child => {
      // первый член д/х и его муж/жена
      if ( child.m_sib_id === '1' ) {
        newArr.push(child);

        if ( child.m_spouse_id !== null ) {
          currentMembers.forEach(childMarriage => {
            if ( child.m_spouse_id === childMarriage.m_id ) {
              newArr.push(childMarriage);
              newArr.push('fake');

            }
          });
        } else {
          currentMembers.forEach(childMarriage => {
            if ( child.m_sib_id === '2' ) {
              newArr.push(childMarriage);
              newArr.push('fake');
    
            }
          });
        }

        if ( newArr.indexOf('fake') === -1 ) {
          newArr.push('fake');
        }
                
      } 
    });


    currentMembers.forEach(child => {
      // если есть другой и его жена
      if ( child.m_sib_id === '10' ) {
        newArr.push(child);
                
        if ( child.m_spouse_id !== null ) {
          for ( let i = 0; i < currentMembers.length; i++ ) {
            if ( child.m_spouse_id === currentMembers[i].m_id ) {
              newArr.push(currentMembers[i]);
              currentMembers.splice(i, 1);
            }
          }
        }
      }
    });

    currentMembers.forEach(child => {
      // если есть несвязаный и его жена
      if ( child.m_sib_id === '11' ) {
        newArr.push(child);
                
        if ( child.m_spouse_id !== null ) {
          for ( let i = 0; i < currentMembers.length; i++ ) {
            if ( child.m_spouse_id === currentMembers[i].m_id ) {
              newArr.push(currentMembers[i]);
              currentMembers.splice(i, 1);
            }
          }
        }
      }
    });

    return newArr;
  } else {
    return [];
  }
}
