import { householdGenerateId } from './services';
import { runHandlers } from './handlers';

export default function editHousehold() {

  let householdIndex = util.household();
  let household;
  
  if ( householdIndex >= 0 ) { // Found?
    household = window.User.rooms[0].households[householdIndex];
  } else {
    return;
  }

  if ( household.h_id === null ) { // Нужно сгенерировать новый Id?
    household.h_id = householdGenerateId();
    window.User.rooms[0].households[householdIndex].h_id = household.h_id; // копируем новый Id в глобальный объект тоже
    household.h_name = 'Домогосподарство ' + window.User.rooms[0].current_household;
    // household.h_household_rooms = 1;
  }

  document.querySelector('#h_name').value = household.h_name;

  // Отмечаем временное проживание
  if ( household.h_temp_residents ) {
    document.querySelector('#h_temp_residents').checked = true;
  } else {
    document.querySelector('#h_temp_residents').checked = false;
  }

  // Отрисовываем количество комнат
  if ( household.h_household_rooms !== null && household.h_household_rooms <= 5 ) {

    document.querySelector(".radio-num__input.js-household-rooms[value='" + household.h_household_rooms + "']").checked = true;
    document.querySelector('.js-household-rooms-text').value = '';

  } else {
    document.querySelectorAll('.radio-num__input.js-household-rooms').forEach(el => {
      el.checked = false;
    });

    if ( household.h_household_rooms === null || isNaN(household.h_household_rooms) ) {
      document.querySelector('.js-household-rooms-text').value = '';
    } else {
      document.querySelector('.js-household-rooms-text').value = household.h_household_rooms;
    }
    
  }

  // Отмечаем частично занятые комнаты
  if ( household.h_household_room_parts ) {
    document.querySelector('#h_household_room_parts').checked = true;
  } else {
    document.querySelector('#h_household_room_parts').checked = false;
  }

  // Отмечаем тип собственности
  if ( household.h_ownership_id !== '99' ) {
    let ot = document.querySelector('#h_ownership_id-'+household.h_ownership_id);
    if ( ot !== null ) {
      ot.checked = true;
    }
  } else {
    document.querySelectorAll('[id^=h_ownership_id]').forEach(el => { el.checked = false; });
  }

  // Отмечаем наличие Интернет
  if ( household.h_improve_internet_id !== '99' ) {
    let inet = document.querySelector('#h_improve_internet_id-'+household.h_improve_internet_id);
    if ( inet !== null ) {
      inet.checked = true;
    }
  } else {
    document.querySelectorAll('[id^=h_improve_internet_id]').forEach(el => { el.checked = false; });
  }

  // Блокируем/разблокируем поля в зависимости от введённых значений
  runHandlers();
}
