import renderTableRooms from './render-table-rooms';
import renderTableManager from './render-table-manager';
import renderTableInstructor from './render-table-instructor';
import getCoordinate from '../get-coordinate';
import promisePopupGetCoordinate from '../promise-popup-get-coordinate';
import notHouse from '../not-house';

export default function renderTableHouses(countingDepartmentId, datasource) {
  const indiv = document.querySelector('.js-indiv-main-section');
  const fieldCountHouses = indiv.querySelector('.js-indiv-houses .js-indiv-task-count-house');
  const censusDepartment = indiv.querySelector('.js-indiv-houses .js-census-department');
  const instructorDepartment = indiv.querySelector('.js-indiv-houses .js-instructor-department');
  const readyPercent = indiv.querySelector('.js-indiv-houses .js-ready-percent');
  const finishHouses = indiv.querySelector('.js-indiv-houses .js-finish-houses');
  const allHouses = indiv.querySelector('.js-indiv-houses .js-all-houses');
  const countingDepartment = indiv.querySelector('.js-counting-department');
  const housesTable = document.querySelector('.js-houses-table');
  const indivRoomsSection = document.querySelector('.js-indiv-rooms');
  const indivHousesSection = document.querySelector('.js-indiv-houses');
  let housestatus = null;
  let user = null;
  const role = +document.body.getAttribute('role');
  console.log(countingDepartmentId);
  console.log(datasource);
  // if ( role !== util.CODE_COPYIS ) {
  //   instructorDepartment.classList.add('is-hide');
  // } else {
  //   instructorDepartment.classList.remove('is-hide');
  // }

  if ( countingDepartmentId || +countingDepartmentId === 0 ) { // если передали countingDepartmentId значит роль инструктор/заведущий, нужно найти по countingDepartmentId лічильну дільницу
    const counting = JSON.parse(storage.get('currentInstructor')).staffusers_counting;

    // if ( +datasource !== util.CODE_CONTROL_TASK && +datasource !== util.CODE_TASK ) {
    //   housestatus = JSON.parse(localStorage.getItem('currentHousestatus'));
    //   window.currentHousestatus = JSON.parse( localStorage.getItem('currentHousestatus') );
    // }
    // console.log(housestatus);

    for ( let i = 0; i < counting.length; i++ ) {
      if ( counting[i].su_counting_department === +countingDepartmentId && counting[i].t_datasource === +datasource ) {

        user = counting[i];
        storage.remove('currentDepartaments');
        storage.set('currentDepartaments', JSON.stringify( user ));

        // if ( !housestatus ) {
        housestatus = counting[i].housestatus;
        storage.remove('currentHousestatus');
        storage.set('currentHousestatus', JSON.stringify( housestatus ));
        window.currentHousestatus = JSON.parse(storage.get('currentHousestatus'));
        // }

      }
    }
  } else if ( storage.get('stationary') && role !== util.CODE_COPYIS ) {
    console.log(role);
    if ( role === util.CODE_MANAGER ) {
      const instructors = window.allData ? window.allData.staffusers_census[0].staffusers_instructor : JSON.parse(storage.get('allData')).staffusers_census[0].staffusers_instructor;

      for ( let i = 0; i < instructors.length; i++ ) {
        if ( instructors[i].su_instructor_department === 0 ) {
          user = instructors[i];
          housestatus = JSON.parse(storage.get('stationary'));
        }
      }
    } else if ( role === util.CODE_INSTRUCTOR ) {
      const countings = window.allData ? window.allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting : JSON.parse(storage.get('allData')).staffusers_census[0].staffusers_instructor[0].staffusers_counting;
      console.log(countings);
      for ( let i = 0; i < countings.length; i++ ) {
        console.log(countings[i].su_counting_department);
        console.log(countings[i].t_datasource);
        if ( countings[i].su_counting_department === 0 && countings[i].t_datasource === util.CODE_STATIONARY ) {
          user = countings[i];
          housestatus = JSON.parse(storage.get('stationary'));

        } else if ( countings[i].su_counting_department === 0 && countings[i].t_datasource === util.CODE_CONTROL_TASK ) {
          user = countings[i];
          housestatus = JSON.parse(storage.get('currentHousestatus'));
        }
      }
    }
    console.log(role);
    console.log(user);



  } else if ( storage.get('housestatus') ) { // еcли есть housestatus, значить роль переписувач
    document.querySelector('.js-add-room').classList.remove('is-show');
    housestatus = JSON.parse( storage.get('housestatus') );
    user = window.allData ? window.allData.user : JSON.parse(storage.get('allData')).user;

  } else if ( storage.get('currentHousestatus') ) {

    housestatus = JSON.parse(storage.get('currentHousestatus'));
    user = JSON.parse(storage.get('currentDepartaments'));
  }

  censusDepartment.innerText = user.su_census_department;
  instructorDepartment.innerText = user.su_instructor_department;
  instructorDepartment.parentElement.parentElement.setAttribute('data-instructor-department', user.su_instructor_department);
  countingDepartment.innerText = user.su_counting_department;

  housesTable.innerHTML = '';
  housesTable.innerHTML = templateHeaderHouses();


  let countFinishRoom = 0;
  let countAllRooms = 0;
  let countHouses = 0;
  console.log(housestatus);
  housestatus.forEach((currentHouse, i) => {
    let prevStatus = null;
    let statusHouse = null;
    for ( let i = 0; i < currentHouse.tasks.length; i++ ) {
      if ( currentHouse.tasks[i].t_flat_num !== null || +currentHouse.tasks[i].t_flat_without_num === 1 ) {
        if ( +currentHouse.tasks[i].t_status_id === 1 ) {
          statusHouse = 1;

          if ( statusHouse !== prevStatus && prevStatus !== null ) {
            statusHouse = 2;
            break;

          } else {
            prevStatus = 1;
          }

        } else if ( +currentHouse.tasks[i].t_status_id === 2 ) { // 2 - статус в работе

          statusHouse = 2;
          break;

        } else if ( +currentHouse.tasks[i].t_status_id >= 3 ) {
          if ( prevStatus === 1 || (+currentHouse.tasks[i].t_status_id === 4 && +currentHouse.tasks[i].t_absent_count < util.CODE_ABSENT_COUNT_MAX) ) {
            statusHouse = 2;
            break;
          } else {
            statusHouse = 3;
            prevStatus = 3;
          }
        } // end else if
      }
    } // end for
    // console.log(statusHouse);
    if ( currentHouse.hs_status_id === 1 || currentHouse.hs_status_id === 2 || currentHouse.hs_status_id === 3 ) {
      console.log(currentHouse);
      currentHouse.hs_status_id = statusHouse;
    }
  });

  housestatus.forEach((house, i) => {

    if ( house.hs_house_id !== null ) {
      countHouses++;
      if ( house.hs_status_id === 4 || house.hs_status_id === 5) { // 4 - статус нет дома
        const container = document.createElement('template');
        container.innerHTML = templateRowHousesDisabl(house, i);
        housesTable.appendChild(container.content);
      } else {
        const container = document.createElement('template');
        container.innerHTML = templateRowHouses(house, i);
        housesTable.appendChild(container.content);
      }


      house.tasks.forEach(task => {

        if ( task.t_flat_num !== null ) {
          countAllRooms++;
        }

        if ( task.t_status_id >= 3 && task.t_status_id !== 10 ) {
          countFinishRoom++;
        }
      });
    } else if ( +house.hs_datasource === util.CODE_HOMELESS ) {
      const container = document.createElement('template');
      container.innerHTML = templateHomeless(house);
      housesTable.appendChild(container.content);

    }

  });

  if ( housestatus[0].tasks[0].t_datasource === util.CODE_CONTROL_TASK || housestatus[0].tasks[0].t_datasource === util.CODE_STATIONARY ) {
    if ( housestatus[0].tasks[0].t_datasource === util.CODE_STATIONARY && (role === util.CODE_MANAGER || role === util.CODE_INSTRUCTOR) ) {
      document.querySelector('.js-btn-nav-counting-text').innerText = 'Переписна дільниця';
      if ( role === util.CODE_MANAGER ) {
        document.querySelector('.js-indiv-houses [data-instructor-department]').classList.add('is-hide');
      } else {
        document.querySelector('.js-indiv-houses [data-instructor-department]').classList.remove('is-hide');
      }
    } else {
      document.querySelector('.js-indiv-houses [data-instructor-department]').classList.remove('is-hide');
      document.querySelector('.js-btn-nav-counting-text').innerText = 'Контрольний обхід';
    }
    countingDepartment.innerText = '';
  } else {
    document.querySelector('.js-indiv-houses [data-instructor-department]').classList.remove('is-hide');
    document.querySelector('.js-btn-nav-counting-text').innerText = 'Лічильна дільниця: ';
    countingDepartment.innerText = user.su_counting_department;
  }

  fieldCountHouses.innerText = countHouses;

  if ( countAllRooms ) {
    readyPercent.innerText = `${Math.floor(countFinishRoom * 100 / countAllRooms)}%`;
  } else {
    readyPercent.innerText = '0%';
  }

  finishHouses.innerText = countFinishRoom;
  allHouses.innerText = countAllRooms;

  // перезаписываю в LoacalStorage housestatus с новыми статусами.
  storage.get('housestatus') ? storage.set('housestatus', JSON.stringify( housestatus )) : storage.set('currentHousestatus', JSON.stringify( housestatus ));

  const openRoom = document.querySelectorAll('.js-open-indiv-rooms');
  openRoom.forEach(link => {
    link.addEventListener('click', function(e) {
      const houseId = this.getAttribute('data-id');
      // const btnNotHouse = document.querySelector('.js-not-house');
      console.log(housestatus);
      for ( let i = 0; i < housestatus.length; i++ ) {
        if ( housestatus[i].hs_id === +houseId ) {
          if ( (role === util.CODE_COPYIS || (role === util.CODE_INSTRUCTOR && housestatus[i].hs_datasource === util.CODE_CONTROL_TASK) ) &&
                housestatus[i].hs_latitude_check === null &&
                housestatus[i].hs_longitude_check === null &&
                housestatus[i].hs_datasource !== util.CODE_HOMELESS &&
                housestatus[i].hs_datasource !== util.CODE_STATIONARY ) {

            const popupComment = document.querySelector('.js-popup-indiv-house-comment');
            const popupGetCoordinate = document.querySelector('.js-popup-get-coordinate');
            popupGetCoordinate.classList.add('is-show');
            let currentAllData = window.allData ? window.allData : JSON.parse(storage.get('allData'));
            promisePopupGetCoordinate()
              .then(result => {
                if ( result === true ) {
                  getCoordinate(housestatus[i])
                    .then(
                      result => {
                        housestatus[i] = result;
                        // перезаписываю в LoacalStorage housestatus с новыми статусами.
                        storage.get('housestatus') ? storage.set('housestatus', JSON.stringify( housestatus )) : storage.set('currentHousestatus', JSON.stringify( housestatus ) );

                        if ( role === util.CODE_INSTRUCTOR ) {
                          storage.set('controlTasks', JSON.stringify( housestatus ));
                          let currentInstructor = JSON.parse(storage.get('currentInstructor'));
                          let counting = currentInstructor.staffusers_counting;
                          for ( let i = 0; i < counting.length; i++ ) {
                            if ( counting[i].t_datasource === util.CODE_CONTROL_TASK ) {
                              counting[i].housestatus = housestatus;
                              break;
                            }
                          }
                          console.log(currentInstructor);
                          storage.set('currentInstructor', JSON.stringify( currentInstructor ));

                        }
                        console.log(housestatus);

                        alert('Координати успішно зафіксовано!');
                        setAllData(currentAllData, housestatus)
                          .then(resolve => {
                            renderTableRooms(houseId);
                          });

                        // btnNotHouse.classList.add('is-show');
                        indivHousesSection.setAttribute('hidden', true);
                        indivRoomsSection.removeAttribute('hidden');
                        document.querySelector('.js-not-house').classList.add('is-show'); // показываем кнопки (нет дома)
                        popupGetCoordinate.classList.remove('is-show');
                      },
                      error => {
                        alert('Координати не зафіксовано!\nСпробуйте ще!');
                        popupGetCoordinate.classList.remove('is-show');
                      }
                    );
                } else if ( result === 'not-house') {
                  notHouse(housestatus[i])
                    .then(
                      result => {
                        console.log(result);
                        housestatus[i] = result;
                        housestatus[i].tasks.forEach(task => {
                          task.t_status_id = 8;
                          task.t_start_datetime = +new Date();
                          task.t_finish_datetime = +new Date();
                        });
                        // перезаписываю в LoacalStorage housestatus с новыми статусами.
                        storage.get('housestatus') ? storage.set('housestatus', JSON.stringify( housestatus )) : storage.set('currentHousestatus', JSON.stringify( housestatus ));
                        console.log(housestatus);
                        popupGetCoordinate.classList.remove('is-show');
                        popupComment.classList.remove('is-show');
                        if ( role === util.CODE_INSTRUCTOR ) {
                          const countingDepartmentId = 0;

                          storage.set('controlTasks', JSON.stringify( housestatus ));
                          let currentInstructor = JSON.parse(storage.get('currentInstructor'));
                          let counting = currentInstructor.staffusers_counting;
                          for ( let i = 0; i < counting.length; i++ ) {
                            if ( counting[i].t_datasource === util.CODE_CONTROL_TASK ) {
                              counting[i].housestatus = housestatus;
                              break;
                            }
                          }
                          console.log(currentInstructor);
                          storage.set('currentInstructor',JSON.stringify( currentInstructor ));

                          setAllData(currentAllData, housestatus)
                            .then(resolve => {
                              renderTableHouses(countingDepartmentId, housestatus[i].hs_datasource);
                            });
                        } else {
                          renderTableHouses();
                        }

                      },
                      cansel => {
                        popupComment.classList.remove('is-show');
                        popupGetCoordinate.classList.remove('is-show');
                      });
                }
              },
              error => {
                console.log(error);
                popupGetCoordinate.classList.remove('is-show');
              });

            console.log(housestatus);

          } else {
            renderTableRooms(houseId);
            // btnNotHouse.classList.add('is-show');
            indivHousesSection.setAttribute('hidden', true);
            indivRoomsSection.removeAttribute('hidden');
            if ( role === util.CODE_COPYIS ) {
              document.querySelector('.js-not-house').classList.add('is-show'); // показываем кнопки (нет дома)
            } else {
              document.querySelector('.js-not-house').classList.remove('is-show'); // удаляем кнопки (нет дома)
            }

          }
          break;
        }
      }

    });
  });

  censusDepartment.parentElement.parentElement.removeEventListener('click', backCensusDepartment);
  censusDepartment.parentElement.parentElement.addEventListener('click', backCensusDepartment);

  instructorDepartment.parentElement.parentElement.removeEventListener('click', backInstructorDepartment);
  instructorDepartment.parentElement.parentElement.addEventListener('click', backInstructorDepartment);

  const btnsNavTablet = document.querySelectorAll('.js-btn-nav-tablet');

  if ( role === util.CODE_COPYIS || role === util.CODE_INSTRUCTOR ) { // 3 - это роль переписчика; 2 - роль инструктор;
    btnsNavTablet.forEach(btn => {
      btn.style.pointerEvents = 'none'; // если переписчик, блокируем клик по кнопкам ( Переписний відділ, Інструкторська дільниця )
      btn.querySelector('.js-arrow').style.display = 'none'; // удаляем стрелочку с кнопки

      if ( role === util.CODE_INSTRUCTOR && btn.getAttribute('data-instructor-department') ) { // если инструктор, включаем кнопку (Інструкторська дільниця)
        btn.style.pointerEvents = 'auto';
        btn.querySelector('.js-arrow').style.display = 'block';
      }
    });
  } else {
    btnsNavTablet.forEach(btn => {
      btn.style.pointerEvents = 'auto'; // если переписчик, блокируем клик по кнопкам (Переписний відділ, Інструкторська дільниця )
      btn.querySelector('.js-arrow').style.display = 'block'; // удаляем стрелочку с кнопки
    });
  }

}
function templateHeaderHouses() {
  return `<div class="indiv-houses__table-row indiv-houses__table-row-header js-houses-header">
            <div class="indiv-houses__table-col">№</div>
            <div class="indiv-houses__table-col">Населений пункт</div>
            <div class="indiv-houses__table-col">Вулиця</div>
            <div class="indiv-houses__table-col">Будинок</div>
            <div class="indiv-houses__table-col">Статус</div>
          </div>`;
}
function templateRowHouses(obj, inc) {
  return `<a href="#" class="indiv-houses__table-row js-open-indiv-rooms" data-id="${obj.hs_id}">
            <div class="indiv-houses__table-col js-house-increment">${inc+1}</div>
            <div class="indiv-houses__table-col js-house-region">${obj.hs_settlement_name}</div>
            <div class="indiv-houses__table-col js-house-street">${obj.hs_street_name}</div>
            <div class="indiv-houses__table-col js-house-number">${obj.hs_num}</div>
            <div class="indiv-houses__table-col js-house-status">${textStatusHouse(obj.hs_status_id)}</div>
          </a>`;
}
function templateRowHousesDisabl(obj, inc) {
  return `<a href="#" class="indiv-houses__table-row indiv-houses__table-row--disabled js-open-indiv-rooms" data-id="${obj.hs_id}">
            <div class="indiv-houses__table-col js-house-increment">${inc+1}</div>
            <div class="indiv-houses__table-col js-house-region">${obj.hs_settlement_name}</div>
            <div class="indiv-houses__table-col js-house-street">${obj.hs_street_name}</div>
            <div class="indiv-houses__table-col js-house-number">${obj.hs_num}</div>
            <div class="indiv-houses__table-col js-house-status">${textStatusHouse(obj.hs_status_id)}</div>
          </a>`;
}
function templateHomeless(obj) {
  return `<a href="#" class="indiv-houses__table-row js-open-indiv-rooms" data-id="${obj.hs_id}">
            <div class="indiv-houses__table-col">Безпритульні</div>
          </a>`;
}
function textStatusHouse(num) {
  const textStatus = JSON.parse( storage.get('h_status') );
  let text;

  for ( let i = 0; i < textStatus.length; i++ ) {
    if ( num === textStatus[i].h_status_id ) {
      text = textStatus[i].h_status_name;
      break;
    }
  }

  return text;
}

function backCensusDepartment(e) {
  const role = +document.body.getAttribute('role');
  const allIndivSection = document.querySelectorAll('.js-indiv-section');
  const managerSection = document.querySelector('.js-indiv-manager');

  renderTableManager(role);
  allIndivSection.forEach(section => {
    section.setAttribute('hidden', true);
  });
  managerSection.removeAttribute('hidden');

}

function backInstructorDepartment(e) {
  let numberInstructorDepartment;
  const role = +document.body.getAttribute('role');
  const allIndivSection = document.querySelectorAll('.js-indiv-section');
  const instructorSection = document.querySelector('.js-indiv-instructor');

  numberInstructorDepartment = +this.getAttribute('data-instructor-department');

  renderTableInstructor(role, numberInstructorDepartment);
  allIndivSection.forEach(section => {
    section.setAttribute('hidden', true);
  });
  instructorSection.removeAttribute('hidden');
}


function setAllData(allData, houses) {
  return new Promise(resolve => {

    for ( let i = 0; i < allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting.length; i++ ) {
      if ( allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting[i].su_counting_department === 0 &&
        allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting[i].t_datasource === util.CODE_CONTROL_TASK &&
        houses[0].hs_datasource === util.CODE_CONTROL_TASK ) {

        allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting[i].housestatus = houses;
        break;
      }
    }
    window.allData = allData;
    storage.set('allData', JSON.stringify( allData ));
    console.log(allData);
    resolve();
  });

}
