import renderTableRooms from './render-tables/render-table-rooms';

export default function btnAbsentRespondent() {
  const btnAbsentRespondent = document.querySelector('.js-absent-respondent');
  const popupIndivRoom = document.querySelector('.js-popup-indiv-room');

  btnAbsentRespondent.addEventListener('click', function(e) {
    const currentHsId = this.parentElement.parentElement.getAttribute('current-hs-id');
    const currentIndexTask = this.parentElement.parentElement.getAttribute('current-index-task');
    const status = this.getAttribute('status');
    const housestatus = storage.get('housestatus') ? JSON.parse(storage.get('housestatus')) : JSON.parse(storage.get('currentHousestatus'));
    let nameLocalStorage = storage.get('housestatus') ? 'housestatus' : 'currentHousestatus';
    let currentDatasouse = null;

    housestatus.forEach(house => {
      if ( +house.hs_id === +currentHsId ) {

        currentDatasouse = house.tasks[+currentIndexTask].t_datasource;

        house.tasks[+currentIndexTask].t_status_id = +status;
        if ( house.tasks[+currentIndexTask].t_start_datetime === null ) {
          house.tasks[+currentIndexTask].t_start_datetime = +new Date();
        }
        if ( house.tasks[+currentIndexTask].t_absent_count < util.CODE_ABSENT_COUNT_MAX ) {
          house.tasks[+currentIndexTask].t_absent_count += 1;
        }
        if ( house.tasks[+currentIndexTask].t_absent_count === util.CODE_ABSENT_COUNT_MAX ) {
          house.tasks[+currentIndexTask].t_finish_datetime = +new Date();
        }
        house.tasks[+currentIndexTask].t_absent_date = +new Date();

        console.log(house.tasks[+currentIndexTask]);

        for ( let i = 0; i < house.tasks.length; i++ ) {
          if ( +house.tasks[i].t_status_id <= 2 || (+house.tasks[i].t_status_id === 4 && +house.tasks[i].t_absent_count < util.CODE_ABSENT_COUNT_MAX) ) {
            house.hs_status_id = 2;
            break;

          } else if ( (+house.tasks[i].t_status_id >= 3 && +house.tasks[i].t_status_id !== 4) || (+house.tasks[i].t_status_id === 4 && +house.tasks[i].t_absent_count === util.CODE_ABSENT_COUNT_MAX) ) {
            house.hs_status_id = 3;
          } else {
            house.hs_status_id = 2;
          }
        }

        if ( house.hs_status_id === 3 ) {
          house.hs_finish_datetime = +new Date();
        }
      }
    });

    console.log(housestatus);
    if ( nameLocalStorage === 'currentHousestatus' ) {
      storage.set('currentHousestatus', JSON.stringify( housestatus ));
    } else {
      storage.set(nameLocalStorage, JSON.stringify( housestatus ));
    }

    if ( housestatus[0].hs_datasource === util.CODE_CONTROL_TASK ) {
      storage.set('controlTasks', JSON.stringify( housestatus ));
    }

    if ( currentDatasouse === util.CODE_CONTROL_TASK ) {
      let currentInstructor = JSON.parse(storage.get('currentInstructor'));
      let counting = currentInstructor.staffusers_counting;
      for ( let i = 0; i < counting.length; i++ ) {
        if ( counting[i].t_datasource === util.CODE_CONTROL_TASK ) {
          counting[i].housestatus = housestatus;
          break;
        }
      }
      console.log(currentInstructor);

      storage.set('currentInstructor', JSON.stringify( currentInstructor ));
    }

    renderTableRooms(+currentHsId);

    popupIndivRoom.setAttribute('current-room-id', '');
    popupIndivRoom.setAttribute('current-hs-id', '');
    popupIndivRoom.setAttribute('current-index-task', '');
    popupIndivRoom.setAttribute('data-source', '');
    popupIndivRoom.classList.remove('is-show');
    popupIndivRoom.querySelector('.js-id-room-text').innerText = '';
  });
}
