import Form from './Form';

export default class Household extends Form {
  constructor(arg = {}) {
    super();
    this.h_id = arg.h_id || null; // Унікальний id домогосподарства
    this.h_name				= arg.h_name || null; // Назва домогосподарства (наприклад, "Петренки")
    this.h_room_id = arg.h_room_id || null; // Посилання на id приміщення відповідного домогосподарства
    this.h_household_number = arg.h_household_number || null; // Номер домогосподарства
    this.h_household_member_count = arg.h_household_member_count || 1; // Кількість членів домогосподарства
    this.h_household_rooms = arg.h_household_rooms || null; // Кількість зайнятих домогосподарством житлових кімнат
    this.h_household_room_parts = arg.h_household_room_parts || false; // Кількість зайнятих домогосподарством частина кімнати
    this.h_ownership_id = arg.h_ownership_id || '99'; // Тип володіння житловим приміщенням (для одноквартирного будинку (індивідуального), частини одноквартирного будинку (індивідуального), окремої квартири, комунальної квартири)
    this.h_improve_internet_id = arg.h_improve_internet_id || '99'; // Доступ домогосподарства до мережі Інтернет (включаючи мобільний)
    //        this.members_household                = arg.members_household || null // Члени домогосподарства
    this.h_temp_residents = arg.h_temp_residents || false; // Домогосподарство тимчасово проживаючих
    this.members				= []; // члени
    this.current_member			= null;
    this.edit = false; 
    this.h_error_class = arg.h_error_class || '';
    this.h_indicators = arg.h_indicators || '';
  }
}
