import mySelect from './my-select';
import { cityName } from '../../dictionary/names';

export default function regionChange( name ) {
  const sel = document.querySelector( 'select[name=' + name + ']' );
  let city;

  if ( name === 'm_job_region') {
    city = mySelect( 'm_job_settlement' );

    // Очищаем выбор страны
    let country = mySelect( 'm_main_job_location_another_country' );
    if(country !== null) {
      country[0].selectize.clear();
    }

  } else if ( name === 'm_left_region') { // if(name === 'm_job_region')
    city = mySelect( 'm_left_settlement' );

    // Очищаем выбор страны
    let country = mySelect( 'm_left_another_country' );
    if ( country !== null ) {
      country[0].selectize.clear();
    }
  }

  let r_id = parseInt( sel.value );
  console.log(city[0].selectize);
  city[0].selectize.clear();
  city[0].selectize.clearOptions();
  if((! isNaN( r_id ) || r_id !== +util.DEFAULT_VALUE_REGION_COUNTRY) && r_id < 100) {
    // Подгружаем города этого региона
    city[0].selectize.enable();
    // Справочник городов загружен?
    if(window.cityDictionary !== undefined && window.cityDictionary !== null) {
      let buf=[];
      let str;
      for(let i=0; i<window.cityDictionary.length; i++) {
        // Добавляем только те населённые пункты, которые относятся к этому региону
        if(window.cityDictionary[i].region_id === r_id) {
          buf.push( {value: window.cityDictionary[i].terson_id, label: cityName(window.cityDictionary[i])} );
        }
      } // for(i=0; i<cityDictionary.length; i++)
      city[0].selectize.addOption(buf);
    } // if( cityDictionary )

    //    document.querySelector( 'select[name=m_job_region]' ).value = null;
  } else { // if( ! isNaN( r_id ) )
    city[0].selectize.disable();
  }
}
