const man = '1';
const woman = '2';

export function sortGrand(currentMembers) {
  let newArr = [];


  if ( currentMembers.length > 0 ) {
    currentMembers.forEach(child => {
      // если есть дед и его жена
      if ( child.m_sib_id === '8' ) {
        newArr.push(child);
                
        if ( child.m_spouse_id !== null ) {
          for ( let i = 0; i < currentMembers.length; i++ ) {
            if ( child.m_spouse_id === currentMembers[i].m_id ) {
              newArr.push(currentMembers[i]);
              currentMembers.splice(i, 1);
            }
          }
        }
      }
    });

    return newArr;
  } else {
    return [];
  }

    
    
}
