import { openPopupSelectionForm } from './open-popup-selection-form';
import deleteMember from './delete-member';
import updateIconErrorFamily from './update-icon-error-family';

const man = '1';
const woman = '2';
const first = '1';

let relationship = {
  '1': {
    '1': 'Записаний першим', // 1 - это мужской пол
    '2': 'Записана першою' // 2 - это женский пол
  }, 
  '2': {
    '1': 'чоловік',
    '2': 'дружина'
  }, 
  '3': {
    '1': 'син',
    '2': 'донька'
  }, 
  '4': {
    '1': 'батько',
    '2': 'мати'
  }, 
  '5': {
    '1': 'брат',
    '2': 'сестра'
  }, 
  '6': {
    '1': 'зять',
    '2': 'невістка'
  }, 
  '7': {
    '1': 'тесть',
    '2': 'теща',
    '3': 'свекор',
    '4': 'свекруха'
  }, 
  '8': {
    '1': 'дід',
    '2': 'баба'
  }, 
  '9': {
    '1': 'онук',
    '2': 'онука'
  }, 
  '10': {
    '1': 'інший ступінь рідства', // інший ступінь родинних стосунків (свояцтва)
    '2': 'інший ступінь рідства'
  }, 
  '11': {
    '1': 'непов’язаний родинними стосунками', // непов’язаний(на) родинними стосунками
    '2': 'непов’язана родинними стосунками'
  }, 
  '12': 'не вказали тип родинних стосунків з особою, що записана першою у  домогосподарстві'
};

export default function renderTreeMembers(obj) {
  const currentMembers = obj.members;
  const genderFirst = obj.genderFirst;
  const content = document.querySelector('.js-tree-members');
  const addMember = document.querySelector('.js-add-member');
  let removeRow = document.querySelectorAll('.js-row-members[data="5"]');
  let maxColForTree = 5;
  let maxCol = 0;
  let rowTree = document.createElement('div');
  rowTree.classList.add('family__tree-row', 'js-row-members');
  rowTree.setAttribute('data', '5');

  for ( let i = 0; i < removeRow.length; i++ ) {
    removeRow[i].parentElement.removeChild(removeRow[i]);
  }

  if ( currentMembers.length > 4 ) {
    document.querySelector('.js-row-members[data="4"]').style.marginBottom = '0';
    content.appendChild(rowTree);
  } else if ( currentMembers.length === 4 ) {
    document.querySelector('.js-row-members[data="4"]').style.marginBottom = '160px';
  } else {
    document.querySelector('.js-row-members[data="4"]').style.marginBottom = '0';
  }

  if ( currentMembers.length <= 4 && content.querySelector('[data="5"]') !== null ) {
    let lastRow = content.querySelector('[data="5"]');
    lastRow.parentNode.removeChild(lastRow);
  }

  // очищаю старое дерево членов домохозяйства
  [...content.children].forEach(row => {
    if ( row.tagName.toLowerCase() !== 'button' ) {
      row.innerHTML = '';
    }
  });

  if ( currentMembers !== undefined ) {
    currentMembers.forEach(row => {
      if (row.length + 1 > maxColForTree ) {
        maxColForTree = row.length + 1;
      }
      if ( Array.isArray(row) ) {
        if ( maxCol < row.length ) { 
          maxCol = row.length; 
        }
      } else {
        maxCol = 1;
      }
             
    });
        
    currentMembers.forEach((row, i) => {
      content.classList.remove('is-empty');
      let rowLength = Array.isArray(row) ? row.length : 1;
      let fakeCols = maxColForTree - rowLength;
      let num = i+1;

      if ( maxCol < maxColForTree ) {
        for (let i = 0; i < Math.floor(fakeCols/2); i++) {
          let fakeCol = document.createElement('div');
          fakeCol.classList.add('member', 'fake');
          content.querySelector(`.js-row-members[data="${num}"]`).appendChild(fakeCol);
        }
      }

      row.forEach(member => {
        if ( member !== 'fake' ) {
          const wrap = document.createElement('div');
          const name = document.createElement('p');
          const nameMask = document.createElement('span');
          const age = document.createElement('p');
          const connectionToFirst = document.createElement('p');
          const line = document.createElement('span');
          const linePair = document.createElement('span');
          const mainProgressLine = document.createElement('span');
          const childProgressLine = document.createElement('span');
          // let currentDate = new Date();
          // let currentMonth = currentDate.getMonth() + 1;
          // let currentYear = currentDate.getFullYear();
          // let ageMember;
          let iconMemberM = document.querySelector('.js-icon-man');
          let iconMemberW = document.querySelector('.js-icon-woman');
          let iconGenderM = document.querySelector('.js-icon-gender-man');
          let iconGenderW = document.querySelector('.js-icon-gender-woman');
          let iconMarriageTempl = document.querySelector('.js-icon-marriage');
          let iconMarriage = iconMarriageTempl.cloneNode(true);
          let btnClose = document.createElement('span');
          let popupMemberTemp = document.querySelector('.js-popup-member-templ');
          let popupMember = popupMemberTemp.cloneNode(true);
          let valid = document.createElement('div');
          let iconValid = document.createElement('span');
                    
          connectionToFirst.classList.add('member__connection');
          btnClose.classList.add('member__close', 'js-delete-member');
          btnClose.setAttribute('title', 'Видалити');
          wrap.classList.add('member', 'js-open-member', 'js-member');
          name.classList.add('member__text');
          age.classList.add('member__text', 'member__text--big');
          line.classList.add('line');
          linePair.classList.add('line-pair');
          mainProgressLine.classList.add('member__progress');
          popupMember.classList.remove('js-popup-member-templ');
          popupMember.classList.add('js-popup-member');
          linePair.appendChild(iconMarriage);
          iconValid.classList.add('member__valid-icon');
          valid.classList.add('member__valid');
          valid.appendChild(iconValid);
          childProgressLine.style.height = `${member.progress*100/util.MAX_LENGTH_PROGRESS}%`;
          name.innerText = member.m_name;
          name.setAttribute('title', member.m_name);
          name.appendChild(nameMask);
          age.innerHTML = member.m_age;

          if ( member.m_sib_id === '7' ) {
            if ( genderFirst === '1' ) {
              connectionToFirst.innerText = relationship[member.m_sib_id][member.m_gender_id];
            } else {
              let relationshipForWomanFirst = +member.m_gender_id + 2;
              connectionToFirst.innerText = relationship[member.m_sib_id][`${relationshipForWomanFirst}`];
            }
          } else {
            connectionToFirst.innerText = relationship[member.m_sib_id][member.m_gender_id];
          }

          if ( member.m_gender_id === man ) {
            let icon = iconMemberM.cloneNode(true);
            let iconGender = iconGenderM.cloneNode(true);

            icon.classList.add('member__icon');
            iconGender.classList.add('member__icon-gender');
            wrap.appendChild(icon);
            wrap.appendChild(iconGender);
            wrap.classList.add('member', 'js-open-member', 'member--man');

          } else {
            let icon = iconMemberW.cloneNode(true);
            let iconGender = iconGenderW.cloneNode(true);

            icon.classList.add('member__icon');
            iconGender.classList.add('member__icon-gender');
            wrap.appendChild(icon);
            wrap.appendChild(iconGender);
            wrap.classList.add('member', 'js-open-member', 'member--woman');
          }

          mainProgressLine.appendChild(childProgressLine);
          wrap.appendChild(name);
          wrap.appendChild(connectionToFirst);
          wrap.appendChild(age);
          wrap.appendChild(btnClose);
          wrap.appendChild(popupMember);
          wrap.appendChild(line);
          wrap.appendChild(linePair);
          wrap.appendChild(mainProgressLine);
          wrap.appendChild(valid);
          wrap.setAttribute('data-id', member.m_id);
          wrap.setAttribute('connection', member.m_spouse_id);
          wrap.setAttribute('parent-id', member.m_parent_id);
          if ( member.m_sib_id === first ) {
            wrap.setAttribute('data-first', 'true');
          }

          content.querySelector(`.js-row-members[data="${num}"]`).appendChild(wrap);

          // обработчик выбора формы
          const handlerOpenPopupSelectionForm = function(e) {
            let memberID = this.getAttribute('data-id');
            let currentPopup = this.querySelector('.js-popup-member');
            let btnOpenFormCreateMember = currentPopup.querySelector('button[data="connection"]');
            let btnOpenFormMember = currentPopup.querySelector('button[data="member"]');

            currentPopup.setAttribute('member-id', memberID);

            if ( e.target !== btnOpenFormCreateMember && e.target !== btnOpenFormMember ) {
              openPopupSelectionForm(e, memberID);
            }
          };

          wrap.removeEventListener('mouseenter', handlerOpenPopupSelectionForm);
          wrap.addEventListener('mouseenter', handlerOpenPopupSelectionForm);

          wrap.removeEventListener('click', handlerOpenPopupSelectionForm);
          wrap.addEventListener('click', handlerOpenPopupSelectionForm);

          // const btnOpenFormMember = wrap.querySelector('.js-open-form-member');

          btnClose.addEventListener('click', function(e) {
            e.stopPropagation();
            let idMember = this.parentNode.getAttribute('data-id');
            deleteMember(idMember);
          });

        } else {
          let fakeCol = document.createElement('div');
          fakeCol.classList.add('member', 'fake');
          content.querySelector(`.js-row-members[data="${num}"]`).appendChild(fakeCol);
        }


      });
            

      if ( maxCol < maxColForTree ) {
        for ( let i = 0; i < fakeCols/2; i++ ) {
          let fakeCol = document.createElement('div');
          fakeCol.classList.add('member', 'fake');
          content.querySelector(`.js-row-members[data="${num}"]`).appendChild(fakeCol);
        }
      }
    });


  } else {

    for ( let i = 0; i < content.children.length; i++ ) {
      for ( let j = 0; j < 8; j++ ) {
        let fakeCol = document.createElement('div');
        fakeCol.classList.add('member', 'fake');
        content.querySelector(`.js-row-members[data="${i+1}"]`).appendChild(fakeCol);
      }
    }
  }
    

  [...content.querySelectorAll('.js-row-members')].forEach(row => {
    [...row.children].forEach((child,i) => {
      child.setAttribute('data-col', i+1);
    });
  });

  [...content.querySelectorAll('.js-row-members')].forEach(row => {
    [...row.children].forEach((child,i) => {
      // проверяю не пустой ли елемент
      if ( child.children.length > 0 ) {
        // проверяю кто женат или замужем
        if ( child.getAttribute('connection') !== 'null' ) {

          child.classList.add('down');
          // проверяю положение на сетке
          let onePersonColId = child.getAttribute('data-col');
          let twoPersonColId = document.querySelector(`.js-tree-members [data-id="${child.getAttribute('connection')}"]`).getAttribute('data-col');
          let onePersonRowId = child.parentNode.getAttribute('data');
          let twoPersonRowId = document.querySelector(`.js-tree-members [data-id="${child.getAttribute('connection')}"]`).parentNode.getAttribute('data');

          if ( onePersonColId > twoPersonColId && onePersonRowId === twoPersonRowId ) {
            child.classList.add('pair-left');
          } else if ( onePersonRowId === twoPersonRowId ) {
            child.classList.add('pair-right');
          }
        }
        // проверяю родителей
        if ( child.getAttribute('parent-id') !== 'null' ) {
          child.classList.add('up');
          // проверяю положение на сетке
          let childColId = child.getAttribute('data-col');
          let parent = document.querySelector(`[data-id="${child.getAttribute('parent-id')}"]`);
          let parentColId = parent.getAttribute('data-col');

          if ( childColId > parentColId ) {

            if ( childColId - parentColId > 1) {
              let lineLength = childColId - parentColId;

              if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--man') ) {
                child.classList.add(`left-parent-pair${lineLength}`);
              } else if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--woman') ) {
                if ( parent.classList.contains('pair-left') ) {
                  child.classList.add(`left-parent-pair${lineLength+1}`);
                } else {
                  child.classList.add(`left-parent-pair${lineLength}`);
                }
                
              } else {
                child.classList.add(`left${lineLength}`);
              }

            } else {

              let lineLength = childColId - ( parentColId - 1 );

              // проверяю есть ли муж/жена
              if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--man') && parent.classList.contains('pair-left')) {
                child.classList.add(`left-parent-pair${lineLength}`);
              } else if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--woman') && parent.classList.contains('pair-right')) {
                child.classList.add('left-parent-pair');
              } else if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--woman') ) {
                child.classList.add(`left-parent-pair${lineLength}`);
              } else if ( parent.getAttribute('connection') !== 'null' ) {
                child.classList.add('left-parent-pair');
              } else {
                child.classList.add('left');
              }
            }

          } else if ( childColId < parentColId ) {

            if ( parentColId - childColId > 1) {
              let lineLength = parentColId - childColId;

              if ( parent.getAttribute('connection') !== 'null' ) {
                child.classList.add(`right-parent-pair${lineLength}`);
              } else {
                child.classList.add(`right${lineLength}`);
              }

            } else if ( parentColId - childColId === 1 ) {

              if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--man') ) {

                if ( parent.classList.contains('pair-left') ) {
                  child.classList.add('right-parent-pair');
                } else {
                  child.classList.add('right-parent-pair2');
                }
                
              } else if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--woman') ) {

                if ( parent.classList.contains('pair-left') ) {
                  child.classList.add('right-parent-pair');
                } else {
                  child.classList.add('right-parent-pair2');
                }
                
              } else {
                child.classList.add('right');
              }

            } else {
              
              // проверяю есть ли муж/жена
              if ( parent.getAttribute('connection') !== 'null' ) {
                child.classList.add('right-parent-pair');
              } else {
                child.classList.add('right');
              }
            }

          } else if ( childColId === parentColId ) {
            // проверяю есть ли муж/жена
            if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--woman') && parent.getAttribute('data-first') === 'true' ) {
              child.classList.add('right-parent-pair');

            } else if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--man') && parent.getAttribute('data-first') === 'true' ) {
              child.classList.add('right-parent-pair');

            } else if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--man') && parent.getAttribute('data-first') === null ) {

              if ( parent.classList.contains('pair-left') ) {
                child.classList.add('left-parent-pair');
              } else {
                child.classList.add('right-parent-pair');
              }
              
            } else if ( parent.getAttribute('connection') !== 'null' && parent.classList.contains('member--woman') ) {
              if ( parent.classList.contains('pair-right') ) {
                child.classList.add('right-parent-pair');
              } else {
                child.classList.add('left-parent-pair');
              }
            } else {
              child.classList.remove('up');
              child.classList.add('up-long');
            }
          }
                    
        }
      }
    });
  });

  // ставлю кнопку в нужное место
  if ( members.length > 0 ) {
    let colFirstMember = document.querySelector('[data-first="true"]').getAttribute('data-col');
    let parentNodeFirstMember = document.querySelector('[data-first="true"]').parentElement;
    let nextColFirstMember = parentNodeFirstMember.querySelector(`[data-col="${+colFirstMember+1}"]`);

    if ( nextColFirstMember.children.length === 0 ) {
      addMember.classList.add('is-small');
      addMember.querySelector('.js-add-member-text').innerText = 'додати';
      nextColFirstMember.appendChild(addMember);
    } else {
      addMember.classList.add('is-small');
      addMember.querySelector('.js-add-member-text').innerText = 'додати';
      parentNodeFirstMember.querySelector(`[data-col="${+colFirstMember+2}"]`).appendChild(addMember);
    }
  } else {
    addMember.classList.remove('is-small');
    addMember.querySelector('.js-add-member-text').innerText = 'додати особу';
    content.appendChild(addMember);
  }
  // console.log(members);

  updateIconErrorFamily();
}
