const formCreateMember = document.querySelector('.js-form-create-member');
const man = '1';
const woman = '2';
const grandfatherOrGrandmother = '8';

export function quantitiGrandFatherAndMother() {
  // считаю сколько дедов и баб для контроля
  let currentGrandfather = [];
  let currentGrandmother = [];
  
  members.forEach(member => {
    if ( member.m_sib_id === grandfatherOrGrandmother ) {
      if ( member.m_gender_id === man ) {
        currentGrandfather.push('man');
      } else {
        currentGrandmother.push('woman');
      }
    }
  });
  formCreateMember.setAttribute('data-grandfather', currentGrandfather.length );
  formCreateMember.setAttribute('data-grandmother', currentGrandmother.length );
}

export function disabledGrandFatherAndMother(genderValue) {
  const selectRelationship = formCreateMember.querySelector('select[name="m_sib_id"]');
  const quantitiGrandfather = formCreateMember.getAttribute('data-grandfather');
  const quantitiGrandmother = formCreateMember.getAttribute('data-grandmother');
  const maxGrandFatherOrGrandMother = '2';
  let currentGender; 

  if ( formCreateMember.querySelector('[name="m_gender_id"]:checked') !== null ) {
    currentGender = formCreateMember.querySelector('[name="m_gender_id"]:checked').value;
  } else {
    currentGender = genderValue;
  }

  if ( quantitiGrandfather === maxGrandFatherOrGrandMother && currentGender === man ) {
    [...selectRelationship.children].forEach(option => {
      if ( option.value === grandfatherOrGrandmother ) {
        option.disabled = true;
      }
    });
  } else if ( quantitiGrandmother < maxGrandFatherOrGrandMother && currentGender === man ) {
    [...selectRelationship.children].forEach(option => {
      if ( option.value === grandfatherOrGrandmother ) {
        option.disabled = false;
      }
    });
  }

  if ( quantitiGrandmother === maxGrandFatherOrGrandMother && currentGender === woman ) {
    [...selectRelationship.children].forEach(option => {
      if ( option.value === grandfatherOrGrandmother ) {
        option.disabled = true;
      }
    });
  } else if ( quantitiGrandmother < maxGrandFatherOrGrandMother && currentGender === woman ) {
    [...selectRelationship.children].forEach(option => {
      if ( option.value === grandfatherOrGrandmother ) {
        option.disabled = false;
      }
    });
  }
}
