import createSelect from './create-select';

export default function initSelectLanguageMulti(formMember, currentMember) {
  const selectLanguage = formMember.querySelector('[data-select-language-multi]');
  // создаю выпадающий список для языков
  const selectLanguageName = selectLanguage.getAttribute('data-select-language-multi');
  const selectLanguageClass = selectLanguage.getAttribute('data-class');
  const selectLanguagePlaceholder = selectLanguage.getAttribute('data-placeholder');
  const dataForSelectLanguage = [selectLanguagePlaceholder, selectLanguageName, selectLanguageClass, selectLanguage];
  const catalogLanguage = [];
  const optionSelectedText = [];
  const ukrainianLanguageCode = 1;
  
  languageDictionary.forEach(language => {

    if ( language.language_id !== +util.DEFAULT_VALUE_LANGUAGE && language.language_id !== ukrainianLanguageCode ) {
      catalogLanguage.push( {value: language.language_id, label: language.language_name_ua, selected: false} );
    }

    if ( currentMember[selectLanguageName].length > 0 ) {
      currentMember[selectLanguageName].forEach(languageCode => {
        if ( +languageCode === language.language_id ) {
          optionSelectedText.push(language.language_name_ua);

          let removeIndex = null;
          catalogLanguage.forEach((removeItem, index) => {
            if ( removeItem.value === languageCode ) {
              removeIndex = index;
            }
          });

          catalogLanguage.splice(removeIndex, 1);
        }
      });
    }
        
  });
  createSelect( dataForSelectLanguage, catalogLanguage, currentMember[selectLanguageName], optionSelectedText );
}
