import { sortCnildrenAndTheirLaw, sortChildrenMarriage } from './sort/sort-children';
import { sortGrandChildrenMarriage } from './sort/sort-grand-children';
import { sortSisterAndBrother, sortMains } from './sort/sort-mains';
import { sortGrand } from './sort/sort-grand';
import { sortParent } from './sort/sort-parents';

const man = '1';
const woman = '2';

export default function sortMembersForRenderTree(currentMembers) {
  let newArr = [];
  let genderFirst = null;
  console.log(currentMembers);
  let sortGender = function(a,b) {
    if (a.m_gender_id < b.m_gender_id) {
      return -1;
    }
    if (a.m_gender_id > b.m_gender_id) {
      return 1;
    }
    return 0;
  };

  let sortToFirst = function(a,b) {
    if (a.m_sib_id < b.m_sib_id) {
      return -1;
    }
    if (a.m_sib_id > b.m_sib_id) {
      return 1;
    }
    return 0;
  };

  let otherMembers = currentMembers.filter(el => {
    return el.m_sib_id === '10' ||
           el.m_sib_id === '11' ||
           el.m_sib_id === '6';
  });

  // дед/баба
  let grand = currentMembers.filter(el => {
    return el.m_sib_id === '8';
  });
  grand.sort(sortGender);
  grand.forEach(member => {
    if ( member.m_spouse_id !== null ) {
      otherMembers.forEach((marriage_member, i) => {
        if ( marriage_member.m_spouse_id === member.m_id ) {
          grand.push(marriage_member);
          otherMembers.splice(i,1);
        } 
      });
    }
  });
  
  let currentGrand = sortGrand(grand);
  if ( currentGrand.length > 0 ) {
    newArr.push(currentGrand);
  }
    
  // папа/мама теща/свекровь/свекр/тест
  let prnts = currentMembers.filter(el => {
    return el.m_sib_id === '4' ||
           el.m_sib_id === '7';
  });
  prnts.sort(sortGender);
  prnts.sort(sortToFirst);
  prnts.forEach(member => {
    if ( member.m_spouse_id !== null ) {
      otherMembers.forEach((marriage_member, i) => {
        if ( marriage_member.m_spouse_id === member.m_id ) {
          prnts.push(marriage_member);
          otherMembers.splice(i,1);
        } 
      });
    }
  });

  let currentParent = sortParent(prnts);
  if ( currentParent.length > 0 ) {
    newArr.push(currentParent);
  }

  // первый муж/жена брат/сестра без отношений к первому
  let mains = currentMembers.filter(el => {
    return el.is_first === true ||
           el.m_sib_id === '2' ||
           el.m_sib_id === '5' && el.m_gender_id === woman ||
           el.m_sib_id === '5' && el.m_gender_id === man; 
  });
  mains.forEach(member => {
    if ( member.m_spouse_id !== null ) {
      otherMembers.forEach((marriage_member, i) => {
        if ( marriage_member.m_spouse_id === member.m_id ) {
          mains.push(marriage_member);
          otherMembers.splice(i,1);
        } 
      });
    }
    if ( member.is_first ) {
      genderFirst = member.m_gender_id;
    }
  });
  let currentMains = sortMains(mains);
  // mains.sort(sortSisterAndBrother)
  if ( currentMains.length > 0 ) {
    newArr.push(currentMains);
  }
    
  // дети
  let children = currentMembers.filter(el => {
    return el.m_sib_id === '3';
  });
  children.forEach(member => {
    if ( member.m_spouse_id !== null ) {
      otherMembers.forEach((marriage_member, i) => {
        if ( marriage_member.m_spouse_id === member.m_id ) {
          children.push(marriage_member);
          otherMembers.splice(i,1);
        } 
      });
    }
  });
  children.sort(sortGender);
  let childrenMarriage = sortChildrenMarriage(children);

  if ( childrenMarriage.length > 0 ) {
    newArr.push(childrenMarriage);
  }
    
  // внуки
  let grandchildren = currentMembers.filter(el => {
    return el.m_sib_id === '9';
  });
  grandchildren.sort(sortGender);
  grandchildren.forEach(member => {
    if ( member.m_spouse_id !== null ) {
      otherMembers.forEach((marriage_member, i) => {
        if ( marriage_member.m_spouse_id === member.m_id ) {
          grandchildren.push(marriage_member);
          otherMembers.splice(i,1);
        }
      });
    }
  });
  let grandchildrenMarriage = sortGrandChildrenMarriage(grandchildren);
  if ( grandchildrenMarriage.length > 0 ) {
    newArr.push(grandchildrenMarriage);
  }

  let otherMembersConcat = [];
  // если есть (невістка, інший или неповьязаний)
  if ( otherMembers.length ) {
    let indexArr = [];
    otherMembers.forEach((member, indexMember) => {
      // если у (невістка, інший или неповьязаний) есть родитель
      if ( member.m_parent_id !== null ) {
        let allMembers = newArr;
        console.log(member);
        allMembers.forEach((rowMembers, indexRow) => {

          rowMembers.forEach(parent => {

            if ( parent !== 'fake' && parent.m_id === member.m_parent_id ) {

              if ( allMembers[indexRow+1] !== undefined ) {

                if ( allMembers[indexRow+1].indexOf('fake') === -1 ) {
                  console.log(member.m_spouse_id);
                  console.log(member.m_id);
                  allMembers[indexRow+1].unshift(member);
                  indexArr.unshift(indexMember);
                } else {
                  console.log(member.m_spouse_id);
                  console.log(member.m_id);
                  otherMembersConcat.push(member);
                  indexArr.unshift(indexMember);
                }
                

              } else {

                
                
                allMembers.push([member]);
                indexArr.push(indexMember);

                if ( member.m_spouse_id !== null ) {
                  for ( let i = 0; i < otherMembers.length; i++ ) {
                    if ( otherMembers[i].m_id === member.m_spouse_id ) {
                      console.log(otherMembers);
                      console.log(i);
                      allMembers[allMembers.length - 1].push(otherMembers[i]);
                      indexArr.push(i);
                      break;
                    }
                  }
                }

              }
            }
          });
        });
      }

    });

    indexArr.forEach(index => {
      console.log(index);
      delete otherMembers[index];
      console.log(otherMembers[index]);
    });

  }

  let childrenOther = [];
  let childrenOtherIndex = [];
  
  // если есть родители у (невістка, інший или неповьязаний), ложу их в новый массив
  otherMembers.forEach((member, index) => {
    if ( member.m_parent_id !== null ) {
      childrenOther.push(member);
      childrenOtherIndex.push(index);
    }
  });

  otherMembers = otherMembers.concat(otherMembersConcat);

  // удаляю детей с массива otherMembers
  if ( childrenOtherIndex.length ) {
    childrenOtherIndex.forEach(index => {
      delete otherMembers[index];
    });
  }

  let parentOther = [];
  let parentOtherIndex = [];

  // если в первом масиве (индекс 0) у членов д\х есть родитель, ищу его в (невістка, інший или неповьязаний) и пушу перед ним
  if ( newArr[0] ) {
    for (let i = 0; i < newArr[0].length; i++ ) {
      if ( newArr[0][i].m_parent_id ) {
        otherMembers.forEach((member, index) => {
          if ( member.m_children.length ) {
            parentOther.push(member);
            parentOtherIndex.push(index);
          }
        });
        console.log(parentOther);
        console.log(parentOtherIndex);
  
        if ( parentOtherIndex.length ) {
          parentOtherIndex.forEach(index => {
            delete otherMembers[index];
          });
        }
      }
    }
  }
  
  
  if ( parentOther.length ) {
    newArr.unshift(parentOther);
  }
  
  // ставлю в нужное место (невістка, інший или неповьязаний)
  newArr.forEach((arr, i) => {
    
    if ( arr[arr.length-1] === 'fake') {
      
      if ( otherMembers.length ) {
        let otherMembersMariage = [];
        let otherMembersRemoveIndex = [];

        
        otherMembers.forEach((member, index) => {
          if ( member.m_spouse_id !== null ) {
            console.log(member);
            otherMembersMariage.push(member);
            otherMembersRemoveIndex.push(index);
          }
        });

        if ( otherMembersRemoveIndex.length ) {
          otherMembersRemoveIndex.forEach(index => {
            delete otherMembers[index];
          });
        }

        let currentOtherMembers = otherMembers.filter(function(el) {
          return el != null;
        });
        
        // otherMembersMariage.sort(sortGender);
        currentOtherMembers.sort(sortGender);
        
        let allOtherMembers = otherMembersMariage.concat(currentOtherMembers);
        newArr.splice(i, 1, arr.concat(allOtherMembers));
      }
    }
  });

  // дети (невістка, інший или неповьязаний)
  if ( childrenOther.length ) {

    let indexArr = [];
    childrenOther.forEach((member, indexMember) => {
      if ( member.m_parent_id !== null ) {
        let allMembers = newArr;

        if ( allMembers.length > 1 ) {
          allMembers.forEach((rowMembers, indexRow) => {
            rowMembers.forEach(rowMember => {

              if ( rowMember !== 'fake' && rowMember.m_id === member.m_parent_id ) {

                if ( allMembers[indexRow+1] ) {
                  allMembers[indexRow+1].push(member);
                  indexArr.push(indexMember);
                } else {
                  allMembers.push([member]);
                  indexArr.push(indexMember);
                }
                
              }
            });
          });

        } else {
          allMembers.push([member]);
          indexArr.push(indexMember);
        }

      }
    });

    indexArr.forEach(index => {
      childrenOther.splice(index,1);
    });
  }

  return {members: newArr, genderFirst: genderFirst};
}
