import { runHandlers } from './handlers';

export default function loadRoom(datasource) {
  let room = window.User.rooms[0];

  // Пройдём по одинаковым (радиокнопки)
  let facilities = ['r_building_id', // Тип приміщення
    'r_improve_water_id', // Водопостачання
    'r_improve_toilet_id', // Туалет
    'r_toilet_other_households', // Туалет
    'r_improve_bath_id', // Ванна / душ
    'r_improve_bathhouse_id', // баня / сауна
    'r_improve_hotwater_id', // Гаряче водопостачання
    'r_improve_sewage_id', // Водовідведення (каналізація)
    'r_improve_kitchen_id', // Кухня або кухонний куток
    'r_improve_heating_id', // Опалення
    'r_improve_electricity_id', // Електропостачання
    'r_improve_electric_stove_id', // Електрична плита стаціонарна
    'r_improve_gas_id', // Газ
    'r_improve_garbage_id', // Видалення побутових відходів
    'r_period_id', // Період побудови будівлі
    'r_composition_id' // Матеріал зовнішніх стін будівлі
  ];
  console.log(datasource);
  for (let i = 0; i < facilities.length; i++) {
    if ( datasource === 0 && i === 0 ) {
      console.log(datasource === 0);
      console.log(datasource);
      document.querySelectorAll('[name='+facilities[i]+']').forEach(el => {
        el.parentElement.style.pointerEvents = 'none';
        if ( +el.value === 13 ) {
          el.checked = true;
        } else {
          el.checked = false;
        }
	    });
    } else {
      if (room[facilities[i]] === '99') {
        document.querySelectorAll('[name='+facilities[i]+']').forEach(el => {
          el.parentElement.style.pointerEvents = 'auto';
          el.checked = false;
        });
      } else {
        if ( window.User.rooms[0].r_datasource !== 0 ) {
          document.querySelectorAll('[name='+facilities[i]+']').forEach(el => {
            el.parentElement.style.pointerEvents = 'auto';
          });
        }
        document.querySelector('[name='+facilities[i]+'][value="'+room[facilities[i]]+'"]').checked = true;
      }
    }
	  
  } // for(let i=0; i<facilities.length; i++)

  // Додаткові поля для типу приміщення
  if (+room.r_building_id === 11) { // заклад охорони здоров’я, etc.
	  document.querySelector('[name=r_rolling_stock]').value = room.r_rolling_stock;
  } else {
	  document.querySelector('[name=r_rolling_stock]').value = '';
  }
  if (+room.r_building_id === 12) { // інше житлове приміщення
	  document.querySelector('[name=r_build_type_another]').value = room.r_build_type_another;
  } else {
	  document.querySelector('[name=r_build_type_another]').value = '';
  }

  // Площа приміщення
  document.querySelector('[name=r_total_area]').value = room.r_total_area;

  // Додаткове поле для видаленя відходів
  if (+room.r_improve_garbage_id === 65) { // інше житлове приміщення
	  document.querySelector('[name=r_garbage_another]').value = room.r_garbage_another;
  } else {
	  document.querySelector('[name=r_garbage_another]').value = '';
  }

  // Додаткове поле для матеріалу стін
  if (+room.r_composition_id === 6) { // інше житлове приміщення
	  document.querySelector('[name=r_composition_another]').value = room.r_composition_another;
  } else {
	  document.querySelector('[name=r_composition_another]').value = '';
  }

  // Запускаем обработчики workflow

  runHandlers();
}
