import openPopupCheck from '../popup/popup-check';

export default function checkRoomData() {
  return new Promise(function(resolve, reject) {
    let error = false;
    let warning = false;
    let res, code, text;
    let errText='<ol>', warnText='<ol>';

    // Прогоняем все контроли на форме
    for ( let i  =0; i < util.checkRoom.length; i++ ) {

      if ( typeof util.checkRoom[(i+1)] === 'function' ) {

        res = util.checkRoom[(i+1)]();
        if ( res.code === 1 ) {
          warnText = warnText + `<li><a href="#" class="js-link-control-room" data-section="room" data-number="${res.number}">${res.text} (П${res.number})</a></li>`;
          warning = true;
        } else if(res.code === 2) {
          errText = errText + `<li><a href="#" class="js-link-control-room" data-section="room" data-number="${res.number}">${res.text} (П${res.number})</a></li>`;
          error = true;
        }

      } else {
		  continue;
      } // if(typeof util.checkRoom[(i+1)] === 'function') ... else
    } // for(let i=0; i<util.checkRoom.length; i++)

    if( error ) {
      code = 2;
      text = '<span class="popup-check__subtitle">Дані щодо житлових умов заповнені некоректно.</span>' +
		errText + '</ol><span class="popup-check__subtitle">Будь-ласка, виправте помилки, перш ніж продовжити.</span>';
    } else if( warning ) {
      code = 1;
      text = '<span class="popup-check__subtitle">Дані щодо житлових умов заповнені некоректно.</span>' +
		warnText + '</ol><span class="popup-check__subtitle">Ви можете повернутися і уточнити дані або ж продовжити.</span>';
    } else {
      resolve(0);
      return;
    }

    // открываем popup
    openPopupCheck(code, text, resolve, reject);
    linkControlRoom();
  }); // new Promise()
}


function linkControlRoom() {
  const popupControl = document.querySelector('.js-popup-check');
  const links = document.querySelectorAll('.js-link-control-room');

  function handrelLinksControl(e) {
    const root = document.querySelector('.js-root');
    const allSections = root.querySelectorAll('[data=section]');
    let currentSection = this.getAttribute('data-section');
    let currentNumber = this.getAttribute('data-number');
    
    // закрываем попап с контролями
    popupControl.click();
    
    if ( currentSection === 'room' ) {

      let currentTabsTitle = document.querySelectorAll(`.${currentSection}  li[data-info]`);
      let currentTabsContent = document.querySelectorAll(`.${currentSection}  .js-tab-content`);

      // показываем нужную секцию 
      allSections.forEach(el => {
        if ( el.classList.contains(currentSection) ) {
          el.removeAttribute('hidden');
        } else {
          el.setAttribute('hidden', true);
        }
      });
          
      // вешаем текущюю секциию на root
      root.setAttribute('data-section', currentSection);

      // показываю нужный таб
      for ( let i = 0; i < currentTabsTitle.length; i++ ) {
        if ( +currentNumber - 1 === i ) {
          currentTabsTitle[i].classList.add('is-active');
          currentTabsContent[i].classList.add('is-active');
        } else {
          currentTabsTitle[i].classList.remove('is-active');
          currentTabsContent[i].classList.remove('is-active');
        }
      }
    }

  }

  links.forEach(link => {
    link.removeEventListener('click', handrelLinksControl);
  });

  links.forEach(link => {
    link.addEventListener('click', handrelLinksControl);
  });
}
