import renderTreeMembers from './render-tree-members';
import sortMembersForRenderTree from './sort-members-for-render-tree';
import popupInfoOk from '../popup/popup-info-ok';
const popupMember = document.querySelector('.js-popup-delete-member');


export default function deleteMember(idMember) {
  const popupMemberClose = popupMember.querySelectorAll('.js-popup-delete-member-close');
  const popupMemberOk = popupMember.querySelector('.js-popup-delete-member-ok');

  popupMember.classList.add('is-show');
  popupMember.setAttribute('id-member', idMember);

  popupMemberClose.forEach(btnClose => {
    btnClose.removeEventListener('click', closePopupMemberDelete);
  });

  popupMemberClose.forEach(btnClose => {
    btnClose.addEventListener('click', closePopupMemberDelete);
  });

  popupMemberOk.removeEventListener('click', okDataDeleteMember);
  popupMemberOk.addEventListener('click', okDataDeleteMember);
  
}

function closePopupMemberDelete() {
  popupMember.classList.remove('is-show');

}

function okDataDeleteMember() {
  let idMember = popupMember.getAttribute('id-member');
  let firstMember;
  popupMember.classList.remove('is-show');
  
  members.forEach(member => {
    if ( +member.is_first ) {
      firstMember = member;
    }
  });

  if ( members.length > 1 && +firstMember.m_id !== +idMember ) {

    // удаляю члена д/х по id
    members.forEach((member,i) => {
      if ( +member.m_id === +idMember) {
        members.splice(i,1);
      }
    });

    // удаляю у всех связь муж/жена, мама/папа, дети с удаленным членом д/х
    members.forEach(member => {
      if ( +member.m_spouse_id === +idMember ) {
        member.m_spouse_id = null;
      }

      if ( +member.m_parent_id === +idMember ) {
        member.m_parent_id = null;
      }

      let removeChildrenIndex = null;
      // ищу индекс ребенка которого удаляют в масиве детей
      member.m_children.forEach((child, index) => {
        if ( +child === +idMember ) {
          removeChildrenIndex = index;
        }
      });
      // если нашел етого ребенка в масиве, удаляю его из масива
      if ( removeChildrenIndex !== null ) {
        delete member.m_children[removeChildrenIndex];
      }

      member.m_children = member.m_children.filter(el => {return el != null;});

    });

  } else if ( +firstMember.m_id === +idMember && members.length === 1 ) {
    // удаляю члена д/х по id
    members.forEach((member,i) => {
      if ( +member.m_id === +idMember) {
        members.splice(i,1);
      }
    });
  } else {
    popupInfoOk('Не можна видалити першого члена домогосподарства, поки існують інші!');
  }

  // перерисовую дерево
  let sortMembers = sortMembersForRenderTree(members);
  renderTreeMembers(sortMembers);
}
