import renderTableHouses from './render-table-houses';
import renderTableManager from './render-table-manager';

export default function renderTableInstructor(role, id) {
  const indiv = document.querySelector('.js-indiv-main-section');
  const indivInstructorSection = indiv.querySelector('.js-indiv-instructor');
  const indivHousesSection = document.querySelector('.js-indiv-houses');
  const instructorTable = indiv.querySelector('.js-table-instructor');
  const numberDepartment = indiv.querySelector('.js-number-department');
  let dataInstructorDepartment = null;
  const textRole = indiv.querySelectorAll('.js-role-text');
  const censusDepartmentNum = indiv.querySelector('.js-indiv-instructor .js-census-department-num');
  const censusDepartment = indiv.querySelector('.js-indiv-instructor .js-census-department');
  const instructorDepartmentNum = indiv.querySelector('.js-indiv-instructor .js-instructor-department-num');
  const btnsFilterMembers = document.querySelectorAll('.js-indiv-instructor .js-filter-count-members');

  if ( role === util.CODE_MANAGER ) {
    const instructors = window.allData ? window.allData.staffusers_census[0].staffusers_instructor : JSON.parse(storage.get('allData')).staffusers_census[0].staffusers_instructor;

    for ( let i = 0; i < instructors.length; i++ ) {
      if ( instructors[i].su_instructor_department === +id ) {
        dataInstructorDepartment = instructors[i];
        storage.remove('currentInstructor');
        storage.set('currentInstructor', JSON.stringify( dataInstructorDepartment ));
        // localStorage.removeItem('currentInstructor');
        // localStorage.setItem('currentInstructor', util.LZStringToCompress(JSON.stringify( dataInstructorDepartment )) );
        break;
      }
    }

    textRole.forEach(text => {
      text.innerText = 'завідувача переписним відділом';
    });

    censusDepartment.style.pointerEvents = 'auto';
    censusDepartment.querySelector('.js-arrow').style.display = 'block';

  } else if ( role === util.CODE_INSTRUCTOR ) {

    dataInstructorDepartment = window.allData ? window.allData.staffusers_census[0].staffusers_instructor[0] : JSON.parse(storage.get('allData')).staffusers_census[0].staffusers_instructor[0];
    storage.remove('currentInstructor');
    storage.set('currentInstructor', JSON.stringify( dataInstructorDepartment ));

    textRole.forEach(text => {
      text.innerText = 'інструктора-контролера';
    });

    censusDepartment.style.pointerEvents = 'none';
    censusDepartment.querySelector('.js-arrow').style.display = 'none';
  }

  censusDepartmentNum.innerText = dataInstructorDepartment.su_census_department;
  instructorDepartmentNum.innerText = dataInstructorDepartment.su_instructor_department;
  instructorTable.innerHTML = '';
  instructorTable.innerHTML = templateHeaderTableInstructor();

  dataInstructorDepartment.staffusers_counting.forEach((el, i) => {
    if ( i === 0 ) {
      const container = document.createElement('template');
      container.innerHTML = templateRowTableInstructorAll();
      instructorTable.appendChild(container.content);
    }
    const container = document.createElement('template');
    container.innerHTML = templateRowTableInstructor(el);
    instructorTable.appendChild(container.content);
  });


  btnsFilterMembers.forEach(btn => {
    btn.removeEventListener('click', calcMembers);
    btn.addEventListener('click', calcMembers);
  });

  calcMembers();

  const openHouses = indiv.querySelectorAll('.js-open-houses');
  openHouses.forEach(link => {
    link.addEventListener('click', function(e) {
      const countDepartId = this.getAttribute('data-counting-department');
      const datasource = this.getAttribute('datasource');

      if ( +datasource === 3 || +datasource === 4 ) {
        if ( +datasource === 4 ) {
          document.querySelector('.js-btn-nav-counting-text').innerText = 'Контрольний обхід';
        } else if ( +datasource === 3 ) {
          JSON.parse(storage.get('currentInstructor')).staffusers_counting.forEach(counting => {
            if ( counting.t_datasource === 3 ) {
              storage.set('currentHousestatus', JSON.stringify( counting.housestatus ));
            }
          });
          document.querySelector('.js-btn-nav-counting-text').innerText = 'Переписна дільниця';
        }

        document.querySelector('.js-btn-nav-counting-text').style.width = '100%';
      } else {
        document.querySelector('.js-btn-nav-counting-text').innerText = 'Лічильна дільниця: ';
        document.querySelector('.js-btn-nav-counting-text').style.width = 'auto';
      }

      renderTableHouses(countDepartId, datasource);
      indivInstructorSection.setAttribute('hidden', true);
      indivHousesSection.removeAttribute('hidden');
    });
  });

  censusDepartment.removeEventListener('click', backCensusDepartment);
  censusDepartment.addEventListener('click', backCensusDepartment);

}

function templateHeaderTableInstructor() {
  return `<div class="indiv-instructor__table-row indiv-instructor__table-row-header">
            <div class="indiv-instructor__table-col">№ ЛД</div>
            <div class="indiv-instructor__table-col">Переписано</div>
            <div class="indiv-instructor__table-col">Кількість приміщень</div>
            <div class="indiv-instructor__table-col">Чоловіки</div>
            <div class="indiv-instructor__table-col">Жінки</div>
            <div class="indiv-instructor__table-col">Обидві статі</div>
          </div>`;
}
function templateRowTableInstructor(obj) {
  return `<a href="#" class="indiv-instructor__table-row js-open-houses" data-counting-department="${obj.su_counting_department}" datasource="${obj.t_datasource}">
            <div class="indiv-instructor__table-col js-number-department">${(obj.t_datasource === 3 || obj.t_datasource === 4) ? textDatasource(obj.t_datasource) : obj.su_counting_department}</div>
            <div class="indiv-instructor__table-col indiv-instructor__table-col-status">
              <span class="indiv-instructor__table-status"><span class="js-instuctor-statusbar"></span><span class="js-instuctor-statusbar-text"></span></span>
            </div>
            <div class="indiv-instructor__table-col"><span class="indiv-instructor__table-count js-finish-houses"></span><span class="indiv-instructor__table-count">з</span><span class="indiv-instructor__table-count js-all-houses"></span></div>
            <div class="indiv-instructor__table-col js-count-men"></div>
            <div class="indiv-instructor__table-col js-count-women"></div>
            <div class="indiv-instructor__table-col js-count-total"></div>
          </a>`;
}
function templateRowTableInstructorAll() {
  return `<a href="#" class="indiv-instructor__table-row indiv-instructor__table-row--all js-all-row-instructor">
            <div class="indiv-instructor__table-col js-number-department">Всього</div>
            <div class="indiv-instructor__table-col indiv-instructor__table-col-status">
              <span class="indiv-instructor__table-status"><span class="js-instuctor-statusbar"></span><span class="js-instuctor-statusbar-text"></span></span>
            </div>
            <div class="indiv-instructor__table-col"><span class="indiv-instructor__table-count js-finish-houses"></span><span class="indiv-instructor__table-count">з</span><span class="indiv-instructor__table-count js-all-houses"></span></div>
            <div class="indiv-instructor__table-col js-count-men"></div>
            <div class="indiv-instructor__table-col js-count-women"></div>
            <div class="indiv-instructor__table-col js-count-total"></div>
          </a>`;
}
function textDatasource(datasource) {
  if ( datasource === 3 ) {
    return 'ПД';
  } else {
    return 'КО';
  }
}
function backCensusDepartment(e) {
  const role = +document.body.getAttribute('role');
  const allIndivSection = document.querySelectorAll('.js-indiv-section');
  const managerSection = document.querySelector('.js-indiv-manager');

  renderTableManager(role);
  allIndivSection.forEach(section => {
    section.setAttribute('hidden', true);
  });
  managerSection.removeAttribute('hidden');

}
function calcMembers() {
  const indiv = document.querySelector('.js-indiv-main-section');
  const finishHouses = indiv.querySelectorAll('.js-indiv-instructor .js-finish-houses');
  const allHouses = indiv.querySelectorAll('.js-indiv-instructor .js-all-houses');
  const countMen = indiv.querySelectorAll('.js-indiv-instructor .js-count-men');
  const countWomen = indiv.querySelectorAll('.js-indiv-instructor .js-count-women');
  const countTotal = indiv.querySelectorAll('.js-indiv-instructor .js-count-total');
  const statusbar = indiv.querySelectorAll('.js-indiv-instructor .js-instuctor-statusbar');
  const statusbarText = indiv.querySelectorAll('.js-indiv-instructor .js-instuctor-statusbar-text');
  let markMembers = null;
  let dataInstructorDepartment = null;
  let currentInstructorId = document.querySelector('.js-indiv-instructor .js-instructor-department-num').innerText;
  const instructors = window.allData ? window.allData.staffusers_census[0].staffusers_instructor : JSON.parse(storage.get('allData')).staffusers_census[0].staffusers_instructor;
  const btnsFilterMembers = document.querySelectorAll('.js-indiv-instructor .js-filter-count-members');

  const rowAllStats = indiv.querySelector('.js-all-row-instructor');
  const rowAllFinishHouses = rowAllStats.querySelector('.js-finish-houses');
  const rowAllAllHouses = rowAllStats.querySelector('.js-all-houses');
  const rowAllCountMen = rowAllStats.querySelector('.js-count-men');
  const rowAllCountWomen = rowAllStats.querySelector('.js-count-women');
  const rowAllCountTotal = rowAllStats.querySelector('.js-count-total');
  const rowAllStatusbar = rowAllStats.querySelector('.js-instuctor-statusbar');
  const rowAllStatusbarText = rowAllStats.querySelector('.js-instuctor-statusbar-text');

  for ( let i = 0; i < instructors.length; i++ ) {
    if ( instructors[i].su_instructor_department === +currentInstructorId ) {
      dataInstructorDepartment = instructors[i];
      storage.set('currentInstructor', JSON.stringify( dataInstructorDepartment ));
      break;
    }
  }

  if ( this ) {
    markMembers = this.getAttribute('data');

    btnsFilterMembers.forEach(btn => {
      btn.classList.remove('is-active');
    });

    this.classList.add('is-active');
  }

  let countAllRoom = 0;
  let countAllFinishRoom = 0;

  let countAllMen = 0;
  let countAllWomen = 0;
  let countAllMembers = 0;

  let countTempMen = 0;
  let countTempWomen = 0;
  let countTempAllMembers = 0;

  let countAbsentTempMen = 0;
  let countAbsentTempWomen = 0;
  let countAbsentTempAllMembers = 0;

  dataInstructorDepartment.staffusers_counting.forEach((counting, i) => {
    let countAllHouses = 0;
    let countFinishHouses = 0;

    let men = 0;
    let women = 0;
    let allMembers = 0;

    let tempMen = 0;
    let tempWomen = 0;
    let tempAllMembers = 0;

    let tempAbsentMen = 0;
    let tempAbsentWomen = 0;
    let tempAbsentAllMembers = 0;

    counting.housestatus.forEach(housestatus => {

      housestatus.tasks.forEach(task => {

        if ( task.t_flat_num !== null ) {
          countAllHouses++;
        }

        if ( task.t_status_id >= 3 && task.t_status_id !== 10 && task.t_datasource !== util.CODE_HOMELESS ) {
          countFinishHouses++;
        }

        men += task.t_men_count;
        women += task.t_women_count;
        allMembers += task.t_member_count;

        tempMen += task.t_temp_men_count;
        tempWomen += task.t_temp_women_count;
        tempAllMembers += task.t_temp_count;

        tempAbsentMen += task.t_tempabsent_men_count;
        tempAbsentWomen += task.t_tempabsent_women_count;
        tempAbsentAllMembers += task.t_tempabsent_count;

        // if ( task.t_status_id > 3 ) {
        //   countFinishHouses++;
        // }

      });
    });

    switch (markMembers) {
      case null:
      case 'all':
        countAllRoom += countAllHouses;
        countAllFinishRoom += countFinishHouses;
        countAllMen += men;
        countAllWomen += women;
        countAllMembers += allMembers;

        finishHouses[i+1].innerText = countFinishHouses;
        allHouses[i+1].innerText = countAllHouses;
        countMen[i+1].innerText = men;
        countWomen[i+1].innerText = women;
        countTotal[i+1].innerText = allMembers;
        if ( countAllHouses === 0 ) {
          statusbarText[i+1].innerText = '0%';
          statusbar[i+1].style.width = '0%';
        } else {
          statusbarText[i+1].innerText = `${Math.floor(countFinishHouses * 100 / countAllHouses)}%`;
          statusbar[i+1].style.width = `${Math.floor(countFinishHouses * 100 / countAllHouses)}%`;
        }
        break;

      case 'temp':
        countAllRoom += countAllHouses;
        countAllFinishRoom += countFinishHouses;
        countTempMen += tempMen;
        countTempWomen += tempWomen;
        countTempAllMembers += tempAllMembers;

        finishHouses[i+1].innerText = countFinishHouses;
        allHouses[i+1].innerText = countAllHouses;
        countMen[i+1].innerText = tempMen;
        countWomen[i+1].innerText = tempWomen;
        countTotal[i+1].innerText = tempAllMembers;
        if ( countAllHouses === 0 ) {
          statusbarText[i+1].innerText = '0%';
          statusbar[i+1].style.width = '0%';
        } else {
          statusbarText[i+1].innerText = `${Math.floor(countFinishHouses * 100 / countAllHouses)}%`;
          statusbar[i+1].style.width = `${Math.floor(countFinishHouses * 100 / countAllHouses)}%`;
        }
        break;

      case 'tempabsent':
        countAllRoom += countAllHouses;
        countAllFinishRoom += countFinishHouses;
        countAbsentTempMen += tempAbsentMen;
        countAbsentTempWomen += tempAbsentWomen;
        countAbsentTempAllMembers += tempAbsentAllMembers;

        finishHouses[i+1].innerText = countFinishHouses;
        allHouses[i+1].innerText = countAllHouses;
        countMen[i+1].innerText = tempAbsentMen;
        countWomen[i+1].innerText = tempAbsentWomen;
        countTotal[i+1].innerText = tempAbsentAllMembers;
        if ( countAllHouses === 0 ) {
          statusbarText[i+1].innerText = '0%';
          statusbar[i+1].style.width = '0%';
        } else {
          statusbarText[i+1].innerText = `${Math.floor(countFinishHouses * 100 / countAllHouses)}%`;
          statusbar[i+1].style.width = `${Math.floor(countFinishHouses * 100 / countAllHouses)}%`;
        }
        break;
    }

  });

  switch (markMembers) {
    case null:
    case 'all':

      rowAllCountMen.innerText = countAllMen;
      rowAllCountWomen.innerText = countAllWomen;
      rowAllCountTotal.innerText = countAllMembers;
      if ( countAllRoom === 0 ) {
        rowAllStatusbarText.innerText = '0%';
        rowAllStatusbar.style.width = '0%';
      } else {
        rowAllStatusbarText.innerText = `${Math.floor(countAllFinishRoom * 100 / countAllRoom)}%`;
        rowAllStatusbar.style.width = `${Math.floor(countAllFinishRoom * 100 / countAllRoom)}%`;
      }
      break;

    case 'temp':

      rowAllCountMen.innerText = countTempMen;
      rowAllCountWomen.innerText = countTempWomen;
      rowAllCountTotal.innerText = countTempAllMembers;
      if ( countAllRoom === 0 ) {
        rowAllStatusbarText.innerText = '0%';
        rowAllStatusbar.style.width = '0%';
      } else {
        rowAllStatusbarText.innerText = `${Math.floor(countAllFinishRoom * 100 / countAllRoom)}%`;
        rowAllStatusbar.style.width = `${Math.floor(countAllFinishRoom * 100 / countAllRoom)}%`;
      }
      break;

    case 'tempabsent':

      rowAllCountMen.innerText = countAbsentTempMen;
      rowAllCountWomen.innerText = countAbsentTempWomen;
      rowAllCountTotal.innerText = countAbsentTempAllMembers;
      if ( countAllRoom === 0 ) {
        rowAllStatusbarText.innerText = '0%';
        rowAllStatusbar.style.width = '0%';
      } else {
        rowAllStatusbarText.innerText = `${Math.floor(countAllFinishRoom * 100 / countAllRoom)}%`;
        rowAllStatusbar.style.width = `${Math.floor(countAllFinishRoom * 100 / countAllRoom)}%`;
      }
      break;
  }

  rowAllFinishHouses.innerText = countAllFinishRoom;
  rowAllAllHouses.innerText = countAllRoom;
}
