export default function notHouse(currentHousestatus) {
  return new Promise((resolve, reject) => {
    const closePopupComment = document.querySelectorAll('.js-popup-indiv-house-comment-close');
    const formPopupComment = document.querySelector('.js-popup-indiv-house-comment form');
    const popupComment = document.querySelector('.js-popup-indiv-house-comment');
    console.log(currentHousestatus);
    popupComment.classList.add('is-show');

    formPopupComment.removeEventListener('submit', addComment);
    formPopupComment.addEventListener('submit', addComment);

    closePopupComment.forEach(btn => {
      btn.removeEventListener('click', cansel);
      btn.addEventListener('click', cansel);
    });

    let housestatus = null;

    function addComment(e) {
      e.preventDefault();
      currentHousestatus.hs_status_id = 4; // 4 - статус Виконано(відсутній)
      currentHousestatus.hs_start_datetime = +new Date();
      currentHousestatus.hs_finish_datetime = +new Date();
      currentHousestatus.hs_comments = formPopupComment.querySelector('input').value;
      housestatus = currentHousestatus;
      resolve(housestatus);
    }

    function cansel(e) {
      reject(false);
    }
  });
}
