import mySelect from './my-select';

export default function adjustPrev() {

  let block = false;
  let input = document.querySelector('input:checked[name=m_temp]');
  if(input !== null && +input.value === 3) {
    block = true;
  } else {
    let birth = document.querySelector('input:checked[name=m_live_birth_continuously]');
    if(birth !== null && +birth.value === 1) {
      block = true;
    }
  }

  let tab = document.querySelector("a[href='#s_member-17']").parentElement;
  const region = mySelect( 'm_previous_residence_region' );
  if( block ) {
    tab.classList.remove('ok', 'warning', 'error');
    tab.classList.add('disabled');
    console.log(region);
    region[0].selectize.clear();
    region[0].selectize.disable();

    document.querySelectorAll('input[name=m_previous_residence_type]').forEach(el => {
      el.disabled = true;
      el.checked = false;
    });
  } else {
    tab.classList.remove('disabled');
    region[0].selectize.enable();
    document.querySelectorAll('input[name=m_previous_residence_type]').forEach(el => {
      el.disabled = false;
    });
  }

}
