export function cloneMember(memberId) {
  const userBlock = document.querySelector('.js-user-block');
  const currentMemberTemp = document.querySelector(`.js-members .member[data-id="${memberId}"]`);
  const currentMember = currentMemberTemp.cloneNode(true);
  const popupMember = currentMember.querySelector('.js-popup-member');
  const prevMember = userBlock.querySelector('.js-member');

  currentMember.setAttribute('data-first', '');
  popupMember.parentNode.removeChild(popupMember);
  if ( prevMember ) {
    prevMember.parentNode.removeChild(prevMember);
  }
  userBlock.appendChild(currentMember);

}

export function removeMember() {
  const userBlock = document.querySelector('.js-user-block');
  const currentMember = userBlock.querySelector('.js-member');

  if ( currentMember !== null ) {
    userBlock.removeChild(currentMember);
  }
    

}
