const popup = document.querySelector('.js-popup-help');
const titlePopup = popup.querySelector('.js-popup-help-title');
const titlePopupHeigth = popup.querySelector('.js-popup-help-title-heigth');
const iconPopup = popup.querySelector('.js-popup-help-icon');
const contentPopup = popup.querySelector('.js-popup-help-content');
const closePopup = popup.querySelectorAll('.js-popup-help-close');
const warning = popup.querySelector('.js-popup-help-warning'); 

export function initPopupHelp(self) {
  let currentBtn = self.getAttribute('data');
  let currentSection = document.querySelector('.js-root').getAttribute('data-section');

  if ( currentBtn === 'census' ) {
    warning.innerHTML = util.help.census.warning;
    titlePopup.innerHTML = util.help.census.title;
    contentPopup.innerHTML = util.help.census.content;
    iconPopup.src = util.help.census.icon;
    
  } else if ( currentBtn === 'info' && currentSection === 'auth' ) {
    titlePopup.innerHTML = util.help.auth.title;
    contentPopup.innerHTML = util.help.auth.content;
    iconPopup.src = util.help.auth.icon;

  } else if ( currentSection === 'room' ) {
    let currentInfo = document.querySelector('.js-tabs-room li.is-active').getAttribute('data-info');

    titlePopup.innerHTML = util.help[currentInfo].title;
    contentPopup.innerHTML = util.help[currentInfo].content;
    iconPopup.src = util.help[currentInfo].icon;

  } else if ( currentBtn === 'info' && currentSection === 'household' ) {
    titlePopup.innerHTML = util.help.households.title;
    contentPopup.innerHTML = util.help.households.content;
    iconPopup.src = util.help.households.icon;
    
  } else if ( currentBtn === 'info-household' && currentSection === 'household' ) {
    titlePopup.innerHTML = util.help.household.title;
    contentPopup.innerHTML = util.help.household.content;
    iconPopup.src = util.help.household.icon;

  } else if ( currentBtn === 'info' && currentSection === 'family' ) {
    let currentInfo = document.querySelector('.js-tabs-title-family li[data].is-active').getAttribute('data');

    titlePopup.innerHTML = util.help[currentInfo].title;
    contentPopup.innerHTML = util.help[currentInfo].content;
    iconPopup.src = util.help[currentInfo].icon;

  } else if ( currentSection === 's_member' ) {
    let currentInfo = document.querySelector('.js-tabs-member li.is-active').getAttribute('data-info');

    titlePopup.innerHTML = util.help[currentInfo].title;
    contentPopup.innerHTML = util.help[currentInfo].content;
    iconPopup.src = util.help[currentInfo].icon;
  }


  
  popup.classList.add('is-show');
  document.body.classList.add('is-open-popup');
  contentPopup.style.height = `calc(100% - ${(titlePopupHeigth.offsetHeight + 1)}px`;
  contentPopup.scroll(0, 0);

  closePopup.forEach(btn => {
    btn.removeEventListener('click', closePopupHelp);
    btn.addEventListener('click', closePopupHelp);
  });

}

export function closePopupHelp() {
  popup.classList.remove('is-show');
  document.body.classList.remove('is-open-popup');
  titlePopup.innerHTML = '';
  contentPopup.innerHTML = '';
  iconPopup.src = '';
  warning.innerHTML = '';
}
