import { householdSearchByNum } from './services';

export default function generateName(num) {

  if ( num === undefined ) {
    num = window.User.rooms[0].current_household;
  }

  let i = householdSearchByNum( num );
  if ( i !== -1 ) {

    let tmp_name = window.User.rooms[0].households[i].h_name;
    
    if ( tmp_name !== null && tmp_name !== 'null' ) {
      return tmp_name;
    } else {
      return 'Домогосподарство ' + num;
    }

  } else {
    return 'Домогосподарство ' + num;
  }

}
