export default function saveInfoForTablet(statusRoom) { // statusRoom - не обезательный параметр
  return new Promise(resolve => {
    let indexPoem = null;
    if ( storage.get('rooms') !== null ) { // если есть квартиры(room) в LocalStorage, берем из него данные и рендерим в формы
      console.log(window.User.rooms[0]);
      const rooms = JSON.parse(storage.get('rooms'));
      window.User.rooms[0].first_time ? window.User.rooms[0].first_time = false : true; // при сохранении данных, если редактировали помещение ставим first_time = false
      const currentRoomId = window.User.rooms[0].r_task_id;
      const currentRoom = {task_id: currentRoomId, room: window.User.rooms[0], room_id: window.User.rooms[0].r_id}; // создаем room c которым работали
      let newRoom = true; // идентификатор для масива rooms


      if (statusRoom) {
        currentRoom.status = statusRoom;
        currentRoom.room.r_finish_time = +new Date();
        console.log(currentRoom.room.r_finish_time);
      } else {
        currentRoom.status = 2; // 2 - статус в работе
      }

      // ищем есть ли room с которым работали в rooms c LocalStorage, если есть перезаписываем
      for (let i = 0; i < rooms.length; i++) {
        if ( +rooms[i].task_id === +currentRoomId && ( !rooms[i].room_id || +rooms[i].room_id === +currentRoom.room.r_id ) ) {

          rooms[i] = currentRoom;
          newRoom = false;
          break;
        }
      }

      if (newRoom) { // если room с которым работали не нашли в rooms c LocalStorage, меняем статусы и записываем его в rooms и в LocalStorage

        // indexPoem = changedStatus(currentRoom, statusRoom);

        changedStatus(currentRoom, statusRoom).then(poem => {
          rooms.push(currentRoom);

          if (statusRoom === 3) {

            for (let i = 0; i < rooms.length; i++) {
              if ( +rooms[i].task_id === +currentRoomId ) {
                // создаю поле m_national_id для бэка
                currentRoom.room.households.forEach(household => {
                  household.members.forEach(member => {
                    member.m_national_id = member.m_national_id_original;
                  });
                });
                window.cryptoWrapper.encrypt(JSON.stringify(currentRoom.room))
                  .then(res => {
                    console.log('encrypt success' + res);
                    rooms[i].room = util.compressToUTF16(res);
                    storage.set('rooms', JSON.stringify(rooms));

                    resolve(poem);
                  })
                  .catch(error => {
                    console.log('encrypt error');
                    popupInfoOk( 'Помилка збереження даних и шифрування: ' + error);
                  });
                break;
              }
            }
          } else {

            for (let i = 0; i < rooms.length; i++) {
              if ( +rooms[i].task_id === +currentRoomId ) {
                let sourceData = JSON.stringify( currentRoom.room );
                let buf = new TextEncoder('utf-8').encode( sourceData );

                window.cryptoWrapper.protect( buf )
                  .then(res => {
                    currentRoom.room = res;
                    rooms[i].room = res;
                    storage.set('rooms', JSON.stringify(rooms));
                    resolve(indexPoem);
                  }).catch(err => {
                    popupInfoOk( 'Помилка збереження даних и шифрування: ' + err);
                    return;
                  });
                break;
              }
            }

          }
        });


      } else { // иначе меняем статусы и записываем отредактированый rooms в LocalStorage

        changedStatus(currentRoom, statusRoom).then(poem => {
          if (statusRoom === 3) {

            for (let i = 0; i < rooms.length; i++) {
              if ( +rooms[i].task_id === +currentRoomId && ( !rooms[i].room_id || +rooms[i].room_id === +currentRoom.room.r_id ) ) {
                // создаю поле m_national_id для бэка
                currentRoom.room.households.forEach(household => {
                  household.members.forEach(member => {
                    member.m_national_id = member.m_national_id_original;
                  });
                });
                window.cryptoWrapper.encrypt(JSON.stringify(currentRoom.room))
                  .then(res => {
                    console.log('crypt - ' + res);
                    rooms[i].room = util.compressToUTF16(res);
                    storage.set('rooms', JSON.stringify(rooms));
                    resolve(poem);
                  }).catch(err => {
                    popupInfoOk( 'Помилка збереження даних и шифрування: ' + err);
                    return;
                  });
                break;
              }
            }
          } else {

            for (let i = 0; i < rooms.length; i++) {
              if ( +rooms[i].task_id === +currentRoomId && ( !rooms[i].room_id || +rooms[i].room_id === +currentRoom.room.r_id ) ) {
                let sourceData = JSON.stringify( currentRoom.room );
                let buf = new TextEncoder('utf-8').encode( sourceData );
                window.cryptoWrapper.protect( buf ).then(res => {
                  currentRoom.room = res;
                  console.log(res);
                  rooms[i].room = res;
                  storage.set('rooms', JSON.stringify(rooms));
                  resolve(poem);
                }).catch(err => {
                  popupInfoOk( 'Помилка збереження даних и шифрування: ' + err);
                  return;
                });
                break;
              }
            }

          }
        });

      }
      // чистим табы от признаков ошибки ( кружочки - красный, желтый, зеленый )
      document.querySelectorAll('li.circle').forEach(li => {
        li.classList.remove('error', 'warning', 'ok');
      });

    } else { // иначе если нет rooms в LocalStorage, создаем room, меняем статусы и записываем rooms с первым room в LocalStorage
      window.User.rooms[0].first_time ? window.User.rooms[0].first_time = false : true; // при сохранении данных, если редактировали помещение ставим first_time = false
      const currentRoom = {task_id: window.User.rooms[0].r_task_id, room: window.User.rooms[0]};

      if (statusRoom) {
        currentRoom.status = statusRoom;
        currentRoom.room.r_finish_time = +new Date();
        console.log(currentRoom.room.r_finish_time);
      } else {
        currentRoom.status = 2;
      }

      // indexPoem = changedStatus(currentRoom, statusRoom);
      changedStatus(currentRoom, statusRoom).then(poem => {
        if (statusRoom === 3) {
          // создаю поле m_national_id для бэка
          currentRoom.room.households.forEach(household => {
            household.members.forEach(member => {
              member.m_national_id = member.m_national_id_original;
            });
          });
          window.cryptoWrapper.encrypt(JSON.stringify(currentRoom.room))
            .then(res => {
              console.log('crypt - ' + res);
              currentRoom.room = util.compressToUTF16(res);
              storage.set('rooms', JSON.stringify([currentRoom]));
              resolve(poem);
            }).catch(err => {
              popupInfoOk( 'Помилка збереження даних и шифрування: ' + err);
              return;
            });

        } else {
          let sourceData = JSON.stringify( currentRoom.room );
          let buf = new TextEncoder('utf-8').encode( sourceData );
          window.cryptoWrapper.protect( buf ).then(res => {
            currentRoom.room = res;
            console.log(res);
            storage.set('rooms', JSON.stringify([currentRoom]));
            resolve(poem);
          }).catch(err => {
            popupInfoOk( 'Помилка збереження даних и шифрування: ' + err);
            return;
          });

        }
      });

    }
  });
}

function changedStatus(currentRoom, statusRoom) { // currentRoom - текущий room с которым работали(обезательный параметр); statusRoom - не обезательный параметр
  return new Promise(resolve => {
    const currentRoomHouseId = currentRoom.room.r_hs_id;
    const currentRoomTaskId = currentRoom.room.r_task_id;
    console.log('currentRoom.room.r_task_id ' + currentRoomTaskId);
    console.log('currentRoom.room.r_task_id ' + currentRoomHouseId);
    let houses = null;
    let currentHouse;
    let currentTask;
    let nameLocalStorage = null;
    let statusHouse = 1;
    const role = +document.body.getAttribute('role');
    let allData = window.allData ? window.allData : JSON.parse(storage.get('allData'));
    let indexPoem = null;

    if ( storage.get('housestatus') ) {
      houses = JSON.parse(storage.get('housestatus'));
      nameLocalStorage = 'housestatus';
    } else if ( storage.get('currentHousestatus') ) {
      houses = JSON.parse(storage.get('currentHousestatus'));
      nameLocalStorage = 'currentHousestatus';
    }

    if ( currentRoom.room.r_datasource === util.CODE_STATIONARY ) {
      houses = JSON.parse(storage.get('stationary'));
      nameLocalStorage = 'stationary';
    }

    console.log(houses);
    console.log(currentRoom);
    console.log(statusRoom);

    for ( let i = 0; i < houses.length; i++ ) {
      if ( +currentRoomHouseId === houses[i].hs_id ) {
        console.log(houses);
        currentHouse = houses[i];
        break;
      }
    }

    for ( let i = 0; i < currentHouse.tasks.length; i++ ) {
      if ( +currentRoomTaskId === currentHouse.tasks[i].t_id ) {
        currentTask = currentHouse.tasks[i];
        break;
      }
    }

    let countMembers = 0;
    let countMan = 0;
    let countWoman = 0;
    let countTempMan = 0;
    let countTempWoman = 0;
    let countTempAbsentMan = 0;
    let countTempAbsentWoman = 0;


    currentRoom.room.households.forEach(household => {
      countMembers += household.members.length;

      household.members.forEach(member => {
        if ( member.m_gender_id === '1' ) {
          countMan++;
          if ( member.m_temp === '3' ) { // 3 - код времено проживающего
            countTempMan++;
          } else if ( member.m_temp === '2' ) {
            countTempAbsentMan++;
          }
        } else {
          countWoman++;
          if ( member.m_temp === '3' ) { // 3 - код времено проживающего
            countTempWoman++;
          } else if ( member.m_temp === '2' ) {
            countTempAbsentWoman++;
          }
        }

      });
    });

    currentRoom.task_created_date = currentTask.t_created_date;
    console.log('currentRoom.task_created_date' + currentRoom.task_created_date);

    if ( statusRoom ) {
      const poems = JSON.parse( storage.get('poems') );

      currentTask.t_status_id = statusRoom;
      currentTask.t_household_count = currentRoom.room.households.length;

      currentTask.t_member_count = countMembers;
      currentTask.t_men_count = countMan;
      currentTask.t_women_count = countWoman;

      currentTask.t_temp_men_count = countTempMan;
      currentTask.t_temp_women_count = countTempWoman;
      currentTask.t_temp_count = +countTempMan + +countTempWoman;

      currentTask.t_tempabsent_men_count = countTempAbsentMan;
      currentTask.t_tempabsent_women_count = countTempAbsentWoman;
      currentTask.t_tempabsent_count = +countTempAbsentMan + +countTempAbsentWoman;

      currentTask.t_finish_datetime = +new Date();

      if ( currentTask.t_datasource === util.CODE_STATIONARY ) {
        indexPoem = Math.floor( Math.random() * poems.length );

        currentRoom.poem_code = poems[indexPoem].poem_id;
      }

    } else {

      if ( +currentTask.t_status_id !== 7 ) {
        currentTask.t_status_id = 2; // 2 - статус в работе
      }
    }
    console.log(currentTask);
    let prevStatus = null;
    for ( let i = 0; i < currentHouse.tasks.length; i++ ) {
      if ( currentHouse.tasks[i].t_flat_num !== null ) {
        if ( +currentHouse.tasks[i].t_status_id === 1 ) {
          statusHouse = 1;

          if ( statusHouse !== prevStatus && prevStatus !== null ) {
            statusHouse = 2;
            break;

          } else {
            prevStatus = 1;
          }

        } else if ( +currentHouse.tasks[i].t_status_id === 2 ) { // 2 - статус в работе

          statusHouse = 2;
          break;

        } else if ( +currentHouse.tasks[i].t_status_id >= 3 ) {
          if ( prevStatus === 1 ) {
            statusHouse = 2;
            break;
          } else {
            statusHouse = 3;
            prevStatus = 3;
          }
        } // end else if
      }
    } // end for
    console.log(statusHouse);
    if ( currentHouse.hs_status_id !== 6 ) {
      currentHouse.hs_status_id = statusHouse;
    }

    if ( statusHouse >= 3 && currentHouse.hs_finish_datetime === null ) {
      currentHouse.hs_finish_datetime = +new Date();
    }
    console.log(currentHouse);
    console.log(houses);

    if ( nameLocalStorage === 'stationary' || nameLocalStorage === 'currentHousestatus' ) {
      storage.set(nameLocalStorage, JSON.stringify( houses ));
    } else if ( nameLocalStorage === 'housestatus' ) {
      storage.set(nameLocalStorage, JSON.stringify( houses ) );
    }

    if ( (role === util.CODE_INSTRUCTOR || role === util.CODE_MANAGER) && nameLocalStorage === 'stationary' ) {
      storage.set('currentHousestatus', JSON.stringify( houses ));
      let currentInstructor = JSON.parse(storage.get('currentInstructor'));
      let counting = currentInstructor.staffusers_counting;
      for ( let i = 0; i < counting.length; i++ ) {
        if ( counting[i].t_datasource === util.CODE_STATIONARY ) {
          counting[i].housestatus = houses;
          break;
        }
      }
      console.log(currentInstructor);
      storage.set('currentInstructor', JSON.stringify( currentInstructor ));
    }
    if ( currentRoom.room.r_datasource === util.CODE_CONTROL_TASK ) {
      storage.set('controlTasks', JSON.stringify( houses ));
      let currentInstructor = JSON.parse(storage.get('currentInstructor'));
      let counting = currentInstructor.staffusers_counting;
      for ( let i = 0; i < counting.length; i++ ) {
        if ( counting[i].t_datasource === util.CODE_CONTROL_TASK ) {
          counting[i].housestatus = houses;
          break;
        }
      }
      console.log(currentInstructor);
      storage.set('currentInstructor',JSON.stringify( currentInstructor ));
      allData.staffusers_census[0].staffusers_instructor[0] = currentInstructor;
      window.allData = allData;
      storage.set('allData', JSON.stringify( allData ));
    }

    if ( role === util.CODE_MANAGER ) { // 1 - роль заведующего

      for ( let i = 0; i < allData.staffusers_census[0].staffusers_instructor.length; i++ ) {
        if ( allData.staffusers_census[0].staffusers_instructor[i].su_instructor_department === 0 ) {
          allData.staffusers_census[0].staffusers_instructor[i].staffusers_counting[0].housestatus = houses;
          break;
        }
      }

    } else if ( role === util.CODE_INSTRUCTOR ) { // 2 - роль инструктора

      for ( let i = 0; i < allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting.length; i++ ) {
        if ( allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting[i].su_counting_department === 0 &&
           allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting[i].t_datasource === 3 && houses[0].hs_datasource === 3 ) {

          allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting[i].housestatus = houses;
          break;
        }
      }

    } else if ( role === util.CODE_COPYIS ) {
      allData.staffusers_census[0].staffusers_instructor[0].staffusers_counting[0].housestatus = houses;

    }
    window.allData = allData;
    storage.set('allData', JSON.stringify( allData ));
    console.log('indexPoem ' + indexPoem);
    resolve(indexPoem);
  });
}
