export default function loadTerson(mode) {

  return new Promise(function(resolve, reject) {
    let name = mode + 'Dictionary';

    query.getInUTF8(`${util.DOMEN}/data/dict/` + mode + '.json')
      .then(data => {
        console.log(mode + ' ' + data);
        // Передаём справочник в глобальную переменную
        window[name] = JSON.parse(data);
        storage.set('lz_' + name, data);
        resolve( 'Ok' );
      }).catch(res => {
        reject(res);
      }); // fetch


  });

}
