export default class Query {
  constructor() { }


  post(url, bodyString) {
    return new Promise((resolve, reject) => {
      const result = nativeRequest.sendPost(url, bodyString);

      try {
        if (JSON.parse(result).err !== 'Error') {
          resolve(result);
        } else {
          reject(JSON.parse(result).errTrue);
        }
      } catch (e) {
        console.log('query get catch ' + result);
      }

    });
  };

  get(url) {
    return new Promise((resolve, reject) => {
      const result = nativeRequest.sendGet(url);

      try {
        if (JSON.parse(result).err !== 'Error') {
          resolve(result);
        } else {
          reject(JSON.parse(result).errTrue);
        }
      } catch (e) {
        console.log('query get catch ' + result);
        resolve(result);
      }

    });
  };

  getInUTF8(url) {
    return new Promise((resolve, reject) => {
      const result = nativeRequest.sendGetX(url);
      if (JSON.parse(result).err !== 'Error') {
        resolve(result);
      } else {
        reject(JSON.parse(result).errTrue);
      }
    });
  };

}

