import adjustWorkplace from './handlers/adjust-workplace';
import adjustWorkfix from './handlers/adjust-workfix';
import adjustWorksearch from './handlers/adjust-worksearch';
import adjustMigration from './handlers/adjust-migration';
import adjustContinuum from './handlers/adjust-continuum';
import adjustPrev from './handlers/adjust-prev';
import adjustCountry from './handlers/adjust-country';
