import mySelect from './handlers/my-select';
// import User from '../../classes/User';
import adjustHomeless from './handlers/adjust-homeless';
export function setHandlers() {
  let tab;
  let func;

  for ( let i = 1; i <= 20; i++ ) {
    tab = document.querySelector('#s_member-'+i);

    if ( eval('typeof handler'+i) === 'function') {
      tab.querySelectorAll('input').forEach(el => {
        if ( el.type === 'text' && !el.parentElement.classList.contains('selectize-input')) {
          el.addEventListener('keyup', eval('handler'+i));
        } else {
          el.addEventListener('change', eval('handler'+i));
        }
      });
      tab.querySelectorAll('select').forEach(el => {
        util.selectsMember.forEach(select => {
          if ( select.length !== undefined && select[0].name === el.name ) {
            select[0].selectize.on('change', eval('handler'+i));
          } else {
            el.addEventListener('change', eval('handler'+i));
          }
        });
      });
    } // if( eval('typeof handler'+i) === 'function')

  } // for(let i=1; i<3; i++)
}


export function runHandlers() {

  for ( let i = 0; i <= 20; i++ ) {
    if ( eval('typeof handler'+i) === 'function' ) {
      eval( 'handler' + i + '()' );
    } // if( eval('typeof handler'+i) === 'function')
  }
}


export function removeHandlers() {
  let tab;
  let func;

  for(let i=1; i<=20; i++) {
    tab = document.querySelector('#s_member-'+i);

    if( eval('typeof handler'+i) === 'function') {
      tab.querySelectorAll('input').forEach(el => {
        if(el.type === 'text') {
          el.removeEventListener('keyup', eval('handler'+i));
        } else {
          el.removeEventListener('change', eval('handler'+i));
        }
      });

      tab.querySelectorAll('select').forEach(el => {
        util.selectsMember.forEach(select => {
          if ( select.length !== undefined && select[0].name === el.name ) {
            select[0].selectize.off('change', eval('handler'+i));
          }
        });
      });
    } // if( eval('typeof handler'+i) === 'function')

  } // for(let i=1; i<3; i++)
}


