// Таб 1 - категория респондента
function handler1() {
  const tab = document.querySelector('#s_member-1');
  const input = tab.querySelector('input:checked[name=m_temp]');
  let val;

  if(input === null) { // данных ещё нет
	  val = 0;
  } else {
	  val = +input.value;
  }

  // Блокируем поле ввода для причины и срока отсутствия
  if(+val === 2) {
    document.querySelector('input[name=m_absence_reason]').disabled = false;
    document.querySelector('[name=m_absence_period]').disabled = false;
    document.querySelector('[name=m_absence_less_month]').parentElement.classList.remove('is-disabled');
    if(this !== undefined && this.type === 'radio') {
      document.querySelector('input[name=m_absence_reason]').focus();
    }
    document.querySelector('.js-disabled-a1').classList.remove('is-disabled');
  } else {
    document.querySelector('input[name=m_absence_reason]').disabled = true;
    document.querySelector('input[name=m_absence_reason]').value = '';
    document.querySelector('[name=m_absence_period]').disabled = true;
    document.querySelector('[name=m_absence_period]').value = '';
    document.querySelector('[name=m_absence_less_month]').checked = false;
    document.querySelector('[name=m_absence_less_month]').parentElement.classList.add('is-disabled');
    document.querySelector('.js-disabled-a1').classList.add('is-disabled');
  }

  adjustWorking();
  adjustWorkplace();
  adjustWorkfix();
  adjustWorksearch();
  adjustMigration();
  // adjustContinuum(); // Нужно блокировать вкладку А16 "Непрерывно жил здесь"?
  // adjustPrev(); // Нужно блокировать вкладку А17 "Где жил раньше"?
  adjustCountry(); // Нужно блокировать вкладку А18 "Проживал ли в другой стране"?
  adjustLeave();
  adjustRegistration();
}


