// Таб 16 - непрерывность проживания
function handler16() {
  const tab = document.querySelector('#s_member-16');
  let year = parseInt( tab.querySelector( '[name=m_live_settlement_year]' ).value );
  let month = tab.querySelector( 'select[name=m_arrival_month]' );

  if( !isNaN(year) && year === 2018 ) {

    month.parentElement.parentElement.classList.remove('is-disabled');
    [...month.children].forEach(option => {
      if ( +option.value !== 12 ) {
        option.disabled = true;
      } else {
        option.disabled = false;
      }
    });

    if ( this !== undefined && this.name === 'm_live_settlement_year') {
      month.value = '';
    }
    
  } else if ( !isNaN(year) && year === 2019 ) {
    month.parentElement.parentElement.classList.remove('is-disabled');
    [...month.children].forEach(option => {
      option.disabled = false;
      if ( +option.value === 12 ) {
        option.disabled = true;
      }
    });

    if ( this !== undefined && this.name === 'm_live_settlement_year') {
      month.value = '';
    }

  } else {
    document.querySelector("a[href='#s_member-16']").parentElement.classList.add('disabled');
    month.parentElement.parentElement.classList.add('is-disabled');
    month.value = '';
  }

  adjustCountry();
}


// Таб 17 - место предыдущего проживания
function handler17() {
  const tab = document.querySelector('#s_member-17');
  let region = parseInt( tab.querySelector( 'select[name=m_previous_residence_region]' ).value );
  const kiev = '80';
  const rural = '2';
  // Проверяем регион
  if ( !isNaN(region) && +region !== 999 ) {
    tab.querySelectorAll( 'input[name=m_previous_residence_type]' ).forEach(el => {

      if ( +region === +kiev && el.value === rural ) {
        el.disabled = true;
        el.checked = false;
        el.parentElement.classList.add( 'disabled' );
      } else {
        el.disabled = false;
        el.parentElement.classList.remove( 'disabled' );
      }
      
    });
  } else {
    tab.querySelectorAll( 'input[name=m_previous_residence_type]' ).forEach(el => {
      el.disabled = true;
      el.checked = false;
      el.parentElement.classList.add( 'disabled' );
    });
  }

  adjustCountry();
}


// Таб 18 - другая страна
function handler18() {
  const tab = document.querySelector('#s_member-18');
  const input = tab.querySelector( 'input:checked[name=m_is_lived_another_country]' );
  const year = tab.querySelector( '[name=m_lived_another_country_year]' );
  const reason = tab.querySelector( 'input:checked[name=m_reason_another_country]' );
  const other = tab.querySelector( 'input[name=m_reason_another_country_another]' );
  let region = mySelect( 'm_lived_another_country' );

  if ( input !== null && +input.value === 1 ) {
    region[0].selectize.enable();
    year.disabled = false;
    tab.querySelectorAll( 'input[name=m_reason_another_country]' ).forEach(el => {
      el.disabled = false;
      el.parentElement.classList.remove( 'disabled' );
    });

    document.querySelector('.js-lived-another-country').classList.remove('is-disabled');
  } else {
    region[0].selectize.disable();
    year.value = '';
    year.disabled = true;
    tab.querySelectorAll( 'input[name=m_reason_another_country]' ).forEach(el => {
      el.checked = false;
      el.disabled = true;
      el.parentElement.classList.add( 'disabled' );
    });

    document.querySelector('.js-lived-another-country').classList.add('is-disabled');
  } // if(input !== null && +input.value === 1) ... else

  if ( reason !== null && +reason.value === 7 ) {
    other.disabled = false;
    other.focus();
  } else {
    other.value = '';
    other.disabled = true;
  }

  if ( this !== undefined ) {
    if ( this.name === 'm_lived_another_country_year' ) {
      // Убираем не-цифры и заменяем значение
      let str = this.value;
      let r = str.replace(/([^\d]*)/g, '');
      this.value = r;
    }
  }
}


// Таб 19 - причины выезда
function handler19() {
  const tab = document.querySelector('#s_member-19');
  const input = tab.querySelector( 'input:checked[name=m_planning_leave_place]' );
  const reason = tab.querySelector( 'input:checked[name=m_planmigration_reason_id]' );
  const other = tab.querySelector( 'input[name=m_planmigration_reason_another]' );

  if ( input !== null && +input.value === 1 ) {
    tab.querySelectorAll( 'input[name=m_planmigration_reason_id]' ).forEach(el => {
      el.disabled = false;
      el.parentElement.classList.remove( 'disabled' );
    });

    document.querySelector('.js-planmigration').classList.remove('is-disabled');

  } else {

    tab.querySelectorAll( 'input[name=m_planmigration_reason_id]' ).forEach(el => {
      el.checked = false;
      el.disabled = true;
      el.parentElement.classList.add( 'disabled' );
    });

    other.value = '';
    other.disabled = true;

    document.querySelector('.js-planmigration').classList.add('is-disabled');
  } // if(input !== null && +input.value === 1) ... else

  if ( reason !== null && +reason.value === 7 ) {
    other.disabled = false;
    other.focus();
  } else {
    other.value = '';
    other.disabled = true;
  }
}


