import renderTableRooms from './render-tables/render-table-rooms';

export default function popupComment() {
  const popupComment = document.querySelector('.js-popup-indiv-room-comment');
  const openPopupComment = document.querySelectorAll('.js-open-popup-comment');
  const closePopupComment = document.querySelectorAll('.js-popup-indiv-room-comment-close');
  const popupIndivRoom = document.querySelector('.js-popup-indiv-room');

  openPopupComment.forEach(btn => {
    btn.addEventListener('click', function(e) {
      const fieldTitle = popupComment.querySelector('.js-popup-indiv-room-comment-title');
      const fieldTooltip = popupComment.querySelector('.js-popup-indiv-room-comment-tooltip');
      const fieldComment = popupComment.querySelector('[name="comment"]');
      const titleText = this.innerText;
      const tooltipText = this.getAttribute('tooltip');
      const status = this.getAttribute('status');
      const currentHsId = this.parentElement.parentElement.getAttribute('current-hs-id');
      const currentIndexTask = this.parentElement.parentElement.getAttribute('current-index-task');
      const housestatus = storage.get('housestatus') ? JSON.parse(storage.get('housestatus')) : JSON.parse(storage.get('currentHousestatus'));

      fieldTitle.innerText = titleText;
      fieldTooltip.innerText = tooltipText;

      housestatus.forEach(house => {
        // console.log(+house.hs_id)
        // console.log(+currentHsId)
        if ( +house.hs_id === +currentHsId ) {

          const currentTask = house.tasks[+currentIndexTask];
          // console.log(currentTask)
          // console.log(currentTask.t_absence_comment)
          fieldComment.value = currentTask.t_absence_comment ? currentTask.t_absence_comment : '';
        }
      });

      popupComment.setAttribute('current-hs-id', currentHsId);
      popupComment.setAttribute('current-index-task', currentIndexTask);
      popupComment.setAttribute('current-status', status);
      popupComment.classList.add('is-show');

    });
  });

  popupComment.querySelector('form').addEventListener('submit', function(e) {
    e.preventDefault();
    const housestatus = storage.get('housestatus') ? JSON.parse(storage.get('housestatus')) : JSON.parse(storage.getItem('currentHousestatus'));
    let nameLocalStorage = storage.get('housestatus') ? 'housestatus' : 'currentHousestatus';
    const status = +this.parentElement.getAttribute('current-status');
    const fieldComment = this.querySelector('[name="comment"]');

    housestatus.forEach(house => {
      if ( +house.hs_id === +popupComment.getAttribute('current-hs-id') ) {
        const currentTask = house.tasks[+popupComment.getAttribute('current-index-task')];
        currentTask.t_absence_comment = fieldComment.value;
        currentTask.t_status_id = status;
        currentTask.t_start_datetime = +new Date();
        if ( status !== 4 || (status === 4 && currentTask.t_absent_count === util.CODE_ABSENT_COUNT_MAX) ) {
          house.tasks[+popupComment.getAttribute('current-index-task')].t_finish_datetime = +new Date();
        }
        for ( let i = 0; i < house.tasks.length; i++ ) {
          if ( +house.tasks[i].t_status_id <= 2 || (+house.tasks[i].t_status_id === 4 && +house.tasks[i].t_absent_count < util.CODE_ABSENT_COUNT_MAX) ) {
            house.hs_status_id = 2;
            break;

          } else if ( (+house.tasks[i].t_status_id >= 3 && +house.tasks[i].t_status_id !== 4) || (+house.tasks[i].t_status_id === 4 && +house.tasks[i].t_absent_count === util.CODE_ABSENT_COUNT_MAX) ) {
            house.hs_status_id = 3;
            house.hs_finish_datetime = +new Date();
          }
        }
      }
    });

    if ( nameLocalStorage === 'housestatus' ) {
      storage.set(nameLocalStorage, JSON.stringify( housestatus ));
    } else if ( nameLocalStorage === 'currentHousestatus' ) {
      storage.set(nameLocalStorage, JSON.stringify( housestatus ));
    }


    if ( housestatus[0].hs_datasource === util.CODE_CONTROL_TASK ) {
      let currentAllData = window.allData ? window.allData : JSON.parse(storage.get('allData'));
      let currentInstructor = JSON.parse(storage.get('currentInstructor'));
      let counting = currentInstructor.staffusers_counting;

      for ( let i = 0; i < counting.length; i++ ) {
        if ( counting[i].t_datasource === util.CODE_CONTROL_TASK ) {
          counting[i].housestatus = housestatus;
          break;
        }
      }

      currentAllData.staffusers_census[0].staffusers_instructor[0] = currentInstructor;

      console.log(currentInstructor);
      console.log(currentAllData);
      window.allData = currentAllData;
      storage.set('allData', JSON.stringify( currentAllData ));
      storage.set('controlTasks', JSON.stringify( housestatus ));
      storage.set('currentInstructor', JSON.stringify( currentInstructor ));

    } else if ( housestatus[0].hs_datasource === util.CODE_STATIONARY ) {
      let currentAllData = window.allData ? window.allData : JSON.parse(storage.get('allData'));
      let currentInstructor = JSON.parse(storage.get('currentInstructor'));
      let counting = currentInstructor.staffusers_counting;

      for ( let i = 0; i < counting.length; i++ ) {
        if ( counting[i].t_datasource === util.CODE_STATIONARY ) {
          counting[i].housestatus = housestatus;
          break;
        }
      }

      currentAllData.staffusers_census[0].staffusers_instructor[0] = currentInstructor;

      console.log(currentInstructor);
      console.log(currentAllData);
      window.allData = currentAllData;
      storage.set('allData', JSON.stringify( currentAllData ));
      storage.set('currentInstructor', JSON.stringify( currentInstructor ));
    }

    renderTableRooms(popupComment.getAttribute('current-hs-id'));

    popupComment.classList.remove('is-show');
    popupComment.setAttribute('current-hs-id', '');
    popupComment.setAttribute('current-index-task', '');
    popupComment.setAttribute('current-status', '');

    popupIndivRoom.setAttribute('current-room-id', '');
    popupIndivRoom.setAttribute('current-hs-id', '');
    popupIndivRoom.setAttribute('current-index-task', '');
    popupIndivRoom.setAttribute('data-source', '');
    popupIndivRoom.classList.remove('is-show');
    popupIndivRoom.querySelector('.js-id-room-text').innerText = '';

    this.reset();
  });

  // closePopupComment.forEach(close => {
  //   close.addEventListener('click', function(e) {
  //     popupComment.classList.remove('is-show');
  //     popupComment.setAttribute('current-hs-id', '');
  //     popupComment.setAttribute('current-index-task', '');
  //     popupComment.setAttribute('current-status', '');
  //   });
  // });
}
