import myMember from './my-member';

export default function adjustWorkfix( member ) {

  if(member === undefined) {
    member = myMember();
  }

  if(member === null) {
    return;
  }

  let block = false;

  if(member.m_age < 15) {
    block = true;
  } else {
    let input = document.querySelector('input:checked[name=m_temp]');
    if(input !== null && +input.value === 3) {
      block = true;
    } else {
      let working = document.querySelector('input:checked[name=m_have_week_paid_work]');
      if(working !== null && +working.value === 2) {
        block = true;
      }
    }
  } // if(member.m_age < 15) ... else

  // let tab = document.querySelector("a[href='#s_member-13']").parentElement;
  // if( block ) {
  //   tab.classList.remove('ok', 'warning', 'error');
  //   tab.classList.add('disabled');
  //   document.querySelectorAll('input[name=m_joblocation_id]').forEach(el => {
  //     el.checked = false;
  //     el.disabled = true;
  //   });
  // } else {
  //   tab.classList.remove('disabled');
  //   document.querySelectorAll('input[name=m_joblocation_id]').forEach(el => {
  //     el.disabled = false;
  //   });
  // }
}
