export default checkRoom;


function clearClass( n ) {
  // На каком мы табе?
  let tab = document.querySelector("a[href='#room-"+n+"']").parentElement;

  let controlMatrix = null;

  if( User.rooms[0].r_datasource === 1) { 
    controlMatrix = controlMatrixInet; 
  } else {
    controlMatrix = controlMatrixInet;    
  }

  
  // Определяем критичность ошибки
  let err;
  if(controlMatrix[n] === 0) {
    err = {code:1, class:'warning'};
  } else {
    err = {code:2, class:'error'};
  }
  // Очищаем стили с текущего таба
  tab.classList.remove('ok', 'warning', 'error');

  return err;
}

function controlTemplate(num, errorText) {
  let tab = document.querySelector("a[href='#room-"+num+"']").parentElement;
  let err = clearClass( num );

  // Если этот таб отключен, значение проверять не нужно
  if( tab.classList.contains('disabled') ) {
    //    tab.classList.add( 'ok' );
    return {code:0, text:'Ok'};
  }

  const room = document.querySelector('#room-'+num);

  // Перебираем все radio, находим отмеченный
  let choice = 0;
  room.querySelectorAll('input[type=radio]').forEach(input => {
    if( input.checked ) {
      choice = +input.value;
    }
  });

  if( choice ) { // Хоть что-то отмечено?
    tab.classList.add( 'ok' );
    return {code:0, text:'Ok'};
  } else {
    tab.classList.add( err.class );
    return {code:err.code, text:errorText};
  } // if( choice ) ... else
}


function getRadioVal( name ) {
  const element = document.querySelector('input:checked[name='+name+']');
  if( element ) {
	  return element.value;
  } else {
	  return null;
  }
}
