export default function getStatuses() {

  query.getInUTF8(`${util.DOMEN}/data/t_statuses.json`)
    .then(statuses => {
      storage.set('r_status', statuses);
    })
    .catch(error => {
      console.log(error);
    });

  query.getInUTF8(`${util.DOMEN}/data/h_statuses.json`)
    .then(statuses => {
      storage.set('h_status', statuses);
    })
    .catch(error => {
      console.log(error);
    });
}
