import { Base64 } from 'js-base64';
import renderTableHouses from './render-tables/render-table-houses';
import renderTableInstructor from './render-tables/render-table-instructor';
import renderTableManager from './render-tables/render-table-manager';
import SimpleCrypto from 'simple-crypto-js';
import handlerAddRoom from './handler-add-room';
import initdictionaryList from '../dictionary/init-dictionaries';
import { ServerResponse } from 'http';
import handlerBtnHomeless from './handler-btn-homeless';
import cryptoWrapper from '../crypto/cryptoAndroid/crypto-control';

export default function authTablet() {
  document.querySelector('.js-auth-tablet').addEventListener('submit', function(e) {
    e.preventDefault();
    const instructorSection = document.querySelector('.js-indiv-instructor');
    const managerSection = document.querySelector('.js-indiv-manager');
    const houseSection = document.querySelector('.js-indiv-houses');
    let btnSubmit = this.querySelector('button[type="submit"]');

    // Блокируем кнопку логина
    onLoaderSubmit(btnSubmit);

    if ( navigator.onLine ) {
      let i = 0;
      let indexCurrentServer;
      let servers = JSON.parse(storage.get('servers'));
      if ( servers ) {
        for ( let i = 0; i < servers.length; i++ ) {
          if ( servers[i] === location.origin ) {
            indexCurrentServer = i;
            break;
          }
        }
        servers.unshift(...servers.splice(indexCurrentServer, 1));
      }

      let redirect = (auth, arr, i) => {
        auth('.').then(
          resolve => {
            offLoaderSubmit(btnSubmit);
          },
          reject => {
            alert('Сервер тимчасово недоступний. Будь ласка, спробуйте пізніше!');
            offLoaderSubmit(btnSubmit);
          }).catch(error => {
          alert('Сервер тимчасово недоступний. Будь ласка, спробуйте пізніше!');
          offLoaderSubmit(btnSubmit);
        });
      };

      redirect(authorization, servers, i);

    } else {
      const loginLocalStorage = storage.get('login');
      const login = document.querySelector('[name="login-auth"]').value;
      const _secretKey = document.querySelector('[name="pass-auth"]').value;
      const simpleCrypto = new SimpleCrypto(_secretKey);
      const passLocalStorage = storage.get('pass');
      const dechipherPass = simpleCrypto.decrypt(passLocalStorage);
      const role = window.allData ? window.allData.user.su_sr_id : JSON.parse(storage.get('allData')).user.su_sr_id;
      const outParentBlock = document.querySelector('.out');
      const preloader = document.querySelector('.preloader');
      // записываю пароль в библиотеку для промежуточного шифрования
      window.cryptoWrapper.password = dechipherPass;

      if ( _secretKey === dechipherPass && loginLocalStorage === login && _secretKey !== '' ) {
        offLoaderSubmit(btnSubmit);
        if ( ( (role === util.CODE_MANAGER || role === util.CODE_INSTRUCTOR ) && storage.get('allData') === null ) ) {
          document.querySelector('.js-refresh-indiv-data').click();
        } else if ( role === util.CODE_COPYIS && storage.get('housestatus') === null ) {
          document.querySelector('.js-refresh-indiv-data').click();
        }

        document.querySelector('.js-indiv-login').classList.remove('is-show');
        document.body.setAttribute('role', role);

        if ( storage.get('allData') !== null ) {

          switch (role) {
            case util.CODE_MANAGER:

              // показываем прелоадер пока грузим справочники
              preloader.classList.add('is-show');
              outParentBlock.classList.add('preloader-show');
              initdictionaryList().then(res => {

                renderTableManager(role);
                managerSection.removeAttribute('hidden');
                document.querySelector('.js-add-room').classList.add('is-show');
                handlerAddRoom();
                // скрываем прелоадер после загрузки справочников
                preloader.classList.remove('is-show');
                outParentBlock.classList.remove('preloader-show');
              });

              break;
            case util.CODE_INSTRUCTOR:

              // показываем прелоадер пока грузим справочники
              preloader.classList.add('is-show');
              outParentBlock.classList.add('preloader-show');
              initdictionaryList().then(res => {

                renderTableInstructor(role);
                instructorSection.removeAttribute('hidden');
                document.querySelector('.js-add-room').classList.add('is-show');
                handlerAddRoom();
                // скрываем прелоадер после загрузки справочников
                preloader.classList.remove('is-show');
                outParentBlock.classList.remove('preloader-show');
              });
              break;
            case util.CODE_COPYIS:
              document.querySelector('.js-homeless').classList.add('is-show');
              handlerBtnHomeless();
              if ( storage.get('housestatus') !== null ) {
                renderTableHouses();
                houseSection.removeAttribute('hidden');
              }
              break;
          }
        }
      } else {
        // alert(`Поточний користувач системи <${loginLocalStorage}>.\nДля авторизації іншого користувача приєднайтесь до мережі інтернет.`);
        alert('Невірній пароль!');
        offLoaderSubmit(btnSubmit);
      }
    }
  });
}

if ( location.pathname === '/registration.html' ) {
  authTablet();
}

function onLoaderSubmit(btn) {
  let btnSpin = btn.querySelector('.js-login-loader');
  let btnText = btn.querySelector('.js-login-text');

  btn.classList.add( 'is-disabled' );
  btnText.style.display = 'none';
  btnSpin.style.display = 'block';
}

function offLoaderSubmit(btn) {
  let btnSpin = btn.querySelector('.js-login-loader');
  let btnText = btn.querySelector('.js-login-text');

  btn.classList.remove( 'is-disabled' );
  btnText.style.display = 'block';
  btnSpin.style.display = 'none';
}

function authorization(domen) {
  return new Promise((resolve, reject) => {
    const login = document.querySelector('[name="login-auth"]');
    const pass = document.querySelector('[name="pass-auth"]');
    const instructorSection = document.querySelector('.js-indiv-instructor');
    const managerSection = document.querySelector('.js-indiv-manager');
    const houseSection = document.querySelector('.js-indiv-houses');

    try {
      query.post(`${util.DOMEN}/reg/synchro`, Base64.encode( JSON.stringify({
        userid: login.value,
        content: JSON.stringify({
          type: 'f_auth',
          pass: pass.value

        })
      }))
      ).then(res => {
        console.log('auth responce !!!!!!' + res);
        const obj = JSON.parse(res);
        if ( obj.code === 0 ) {
          // записываю пароль в библиотеку для промежуточного шифрования
          window.cryptoWrapper.password = pass.value;
          encryptRoom();
          const role = JSON.parse(obj.info).user.su_sr_id;
          const changePass = JSON.parse(obj.info).user.su_change_password;
          const detectChangePass = JSON.parse(obj.info).user.su_change_password;
          const loginForm = document.querySelectorAll('.js-login-content');

          if ( !detectChangePass ) {
            [...loginForm].forEach(form => {
              if (form.classList.contains('js-auth-tablet')) {
                form.classList.remove('is-show');
              } else {
                form.classList.add('is-show');
              }
            });
            return false;
          }
          if ( location.pathname === '/registration.html' ) {
            window.location.pathname = './install.html';
          } else if ( changePass ) {
            const loginLocalStorage = storage.get('login');

            if ( loginLocalStorage !== login.value ) {

              storage.remove('login');
              storage.remove('pass');
              storage.remove('allData');
              storage.remove('stationary');

              if ( storage.get('currentInstructor')) {
                storage.remove('currentInstructor');
              }

              if ( storage.get('userData')) {
                storage.remove('userData');
              }

              if ( storage.get('currentDepartaments')) {
                storage.remove('currentDepartaments');
              }

              if ( storage.get('housestatus')) {
                storage.remove('housestatus');
              }

              if ( storage.get('currentHousestatus')) {
                storage.remove('currentHousestatus');
              }


              const _secretKey = pass.value;
              const simpleCrypto = new SimpleCrypto(_secretKey);
              const passNewUser = pass.value;
              const chipherPass = simpleCrypto.encrypt(passNewUser);
              storage.set('pass', chipherPass);
              storage.set('login', login.value);

            } else {
              storage.remove('login');
              storage.remove('pass');
              const _secretKey = pass.value;
              const simpleCrypto = new SimpleCrypto(_secretKey);
              const passNewUser = pass.value;
              const chipherPass = simpleCrypto.encrypt(passNewUser);
              storage.set('pass', chipherPass);
              storage.set('login', login.value);
            }


            if ( ( (role === util.CODE_MANAGER || role === util.CODE_INSTRUCTOR ) && storage.get('allData') === null ) ) {
              document.querySelector('.js-refresh-indiv-data').click();
            } else if ( role === util.CODE_COPYIS && storage.get('housestatus') === null ) {
              document.querySelector('.js-refresh-indiv-data').click();
            }


            document.querySelector('.js-indiv-login').classList.remove('is-show');
            document.body.setAttribute('role', role);
            const outParentBlock = document.querySelector('.out');
            const preloader = document.querySelector('.preloader');

            if ( storage.get('allData') !== null ) {

              let data = window.allData ? util.removeCoordinateInTasks(window.allData, role) : util.removeCoordinateInTasks(JSON.parse(storage.get('allData')), role);
              // console.log(data);

              const nameDataLocalStorage = ['stationary','currentInstructor','currentDepartaments','currentHousestatus'];
              let currentInstructor = null;
              nameDataLocalStorage.forEach(name => {
                if ( storage.get(name) ) {
                  if ( Array.isArray(storage.get(name)) ) {
                    storage.remove(name);
                  }
                }
              });

              if ( role === util.CODE_MANAGER ) {
                data.staffusers_census[0].staffusers_instructor.forEach(instructor => {
                  if ( instructor.su_instructor_department === 0 && data.user.su_id === instructor.su_id ) {
                    console.log( instructor.staffusers_counting[0].housestatus );
                    currentInstructor = instructor;
                    if ( role !== util.CODE_COPYIS && currentInstructor !== null ) {
                      storage.set('stationary', JSON.stringify( currentInstructor.staffusers_counting[0].housestatus ));
                      storage.set('currentInstructor', JSON.stringify( currentInstructor ));
                      storage.set('currentDepartaments', JSON.stringify( currentInstructor.staffusers_counting[0] ));
                    }
                  }
                });
              } else if ( role === util.CODE_INSTRUCTOR ) {
                data.staffusers_census[0].staffusers_instructor[0].staffusers_counting.forEach(counting => {
                  if ( counting.su_counting_department === 0 && counting.t_datasource === 3 ) {
                    console.log( counting.housestatus );
                    currentInstructor = data.staffusers_census[0].staffusers_instructor[0];
                    if ( role !== util.CODE_COPYIS && currentInstructor !== null ) {
                      storage.set('stationary', JSON.stringify( currentInstructor.staffusers_counting[0].housestatus ));
                      storage.set('currentInstructor', JSON.stringify( currentInstructor ));
                      storage.set('currentDepartaments', JSON.stringify( counting ));
                    }
                  }
                });
              }
              window.allData = data;
              storage.set('allData', JSON.stringify(data));
              switch(role) {
                case util.CODE_MANAGER:
                  // document.querySelector('.js-update-tasks').classList.add('disabled');
                  // показываем прелоадер пока грузим справочники
                  preloader.classList.add('is-show');
                  outParentBlock.classList.add('preloader-show');
                  initdictionaryList().then(res => {
                    renderTableManager(role);
                    managerSection.removeAttribute('hidden');
                    document.querySelector('.js-add-room').classList.add('is-show');
                    handlerAddRoom();
                    // скрываем прелоадер после загрузки справочников
                    preloader.classList.remove('is-show');
                    outParentBlock.classList.remove('preloader-show');
                  });
                  break;
                case util.CODE_INSTRUCTOR:
                  // document.querySelector('.js-update-tasks').classList.add('disabled');
                  // показываем прелоадер пока грузим справочники
                  preloader.classList.add('is-show');
                  outParentBlock.classList.add('preloader-show');
                  initdictionaryList().then(res => {

                    renderTableInstructor(role);
                    instructorSection.removeAttribute('hidden');
                    document.querySelector('.js-add-room').classList.add('is-show');
                    handlerAddRoom();
                    // скрываем прелоадер после загрузки справочников
                    preloader.classList.remove('is-show');
                    outParentBlock.classList.remove('preloader-show');
                  });
                  break;
                case util.CODE_COPYIS:
                  document.querySelector('.js-homeless').classList.add('is-show');
                  handlerBtnHomeless();
                  if ( storage.get('housestatus') !== null ) {
                    renderTableHouses();
                    houseSection.removeAttribute('hidden');
                  }
                  break;
              }
            } else if ( obj.code === -1 ) {
              alert('Невірний логін або пароль!');
            } else if ( obj.code === -2 ) {
              alert('Користувач заблокований!');

            }

          } else {
            const formChangePass = document.querySelector('.js-change-pass-form');
            const formAuth = document.querySelector('.js-auth-tablet');

            formChangePass.classList.add('is-show');
            formAuth.classList.remove('is-show');
          }

        } else if ( obj.code === -1 ) {
          alert('Невірний логін або пароль!');
        } else if ( obj.code === -2 ) {
          alert('Користувач заблокований!');
        }
        resolve('ок connect');
      })
        .catch(error => {
          console.log('auth error' + error);
          reject(true);
        });
    } catch (error) {
      console.log(error);
      reject(true);
    }

  });
}

function encryptRoom() {
  let rooms = storage.get('rooms') ? JSON.parse(storage.get('rooms')) : [];

  for ( let i = 0; i < rooms.length; i++ ) {
    if ( rooms[i].room instanceof Object && +rooms[i].status === 3 ) {
      console.log('JSON.stringify(rooms[i].room) ' + JSON.stringify(rooms[i].room));
      window.cryptoWrapper.encrypt(JSON.stringify(rooms[i].room))
        .then(res => {
          console.log('encryptRoom - 3' + res);
          rooms[i].room = util.compressToUTF16(res);
          storage.set('rooms', JSON.stringify(rooms));
          encryptRoom();
        }).catch(err => {
          popupInfoOk( 'Помилка збереження даних и шифрування: encryptRoom' + err);
          return;
        });
      break;
    } else if ( rooms[i].room instanceof Object && (+rooms[i].status === 2 || +rooms[i].status === 1) ) {
      let sourceData = JSON.stringify( rooms[i].room );
      let buf = new TextEncoder('utf-8').encode( sourceData );
      window.cryptoWrapper.protect( buf ).then(res => {
        console.log('encryptRoom - 2' + res);
        rooms[i].room = res;
        storage.set('rooms', JSON.stringify(rooms));
        encryptRoom();
      }).catch(err => {
        popupInfoOk( 'Помилка збереження даних и шифрування: ' + err);
        return;
      });
      break;
    }
  }
}
