import initTabs from './helpers/tabs';
import './helpers/util';
import { closeFormCreateMember } from './components/member/form-create-member/open-form-create-member';
import { closePopupMember } from './components/member/open-popup-selection-form';
import { handlerClickWithoutPopupMember } from './components/member/open-popup-selection-form';
import saveInfo from './handlers/save-info';
import { initPopupHelp, closePopupHelp } from './components/popup/popup-help';
import  './lib/selectize.js';
import initNavigationWizard from './handlers/navigation-wizard';
import { initNavigation } from './components/core/navigation';
import initTablet from './components/tablet/init-tablet';
import getCryptoCert from './helpers/get-crypto-files';
import removeDictionarys from './helpers/remove-dictionarys';
import Query from './classes/Query';
import Location from './classes/Location';

import cryptoWrapper from './components/crypto/cryptoAndroid/crypto-control';
import Storage from './classes/Storage';


window.query = new Query();
window.storage = new Storage();
window.location = new Location();

let checkLoadCryptoLib = () => {
  return new Promise(resolve => {
    const interval = setInterval(() => {
      if (window.cryptoCheck) {
        clearInterval(interval);
        console.log('checkLoadCryptoLib resolve' + window.cryptoCheck);
        resolve(true);
      }
    }, 500);
  });
};



initTabs(); // инициализируем табы
initNavigation(); // инициализируем навигацию по кнопкам (помещение, домогосподарство, сохранить, проверить/подать ведомости)
initNavigationWizard(); // инициализируем навигацию по кнопкам "Дальше"

window.addEventListener('keyup', (e) => {
  if ( !document.body.classList.contains('warning') ) {
    util.isEscEvent(e, closeFormCreateMember);
    util.isEscEvent(e, closePopupMember);
    util.isEscEvent(e, closePopupHelp);
  }
});
// прелоадер: автор проекта
setTimeout(() => {
  document.querySelector('.preloader-copyright').classList.remove('is-show');
}, 2000);

document.addEventListener('click', function(e) {
  handlerClickWithoutPopupMember(e);
});

window.addEventListener('beforeunload', saveInfo);

const openPopupHelp = document.querySelectorAll('.js-open-popup-help');

openPopupHelp.forEach(btn => {
  btn.addEventListener('click', function() {
    initPopupHelp(this);
    if ( this.getAttribute('data') === 'info' ) {
      this.classList.remove('is-active');
    }
  });
});

// версия
query.get(`${util.DOMEN}/ver.txt`).then(res => {
  document.querySelector('.js-ver').innerText = 'v. ' +res;

  if ( !storage.get('version') ) {
    storage.set('version', JSON.stringify(+res));
  }

  if ( +storage.get('version') !== +res ) {
    removeDictionarys();
    storage.remove('version');
    storage.remove('poems');
  }
  if ( !storage.get('poems') ) {
    query.get(`${util.DOMEN}/data/poems.json`)
      .then(poems => {
        storage.set('poems', poems);
      })
      .catch(error => {
        console.log(error);
      });
  } else {
    console.log('poems');
  }

});


getCryptoCert()
  .then(resolve => {
    console.log('getCryptoCert ок');
    const downloadCert = document.querySelector('.js-cert-form');
    downloadCert.style.display = 'none';
    console.log('cryptoFiles ' + storage.get('cryptoFiles'));
    const cryptoCert = JSON.parse(storage.get('cryptoFiles'));
    const cert1 = util.base64ToArrayBuffer( `${cryptoCert.cert1}` );
    const cert2 = util.base64ToArrayBuffer( `${cryptoCert.cert2}` );
    const publicCert = util.base64ToArrayBuffer( `${cryptoCert.public}` );

    checkLoadCryptoLib().then(res => {
      window.cryptoWrapper = new cryptoWrapper(EUSignCP);
      window.cryptoWrapper.serverCert = publicCert;
      window.cryptoWrapper.localCerts = [cert1, cert2];
      window.cryptoWrapper.pass = cryptoCert.psw;
      window.cryptoWrapper.key = util.base64ToArrayBuffer( `${cryptoCert.key}` );

      window.cryptoWrapper.initialize()
        .then(res => {
          console.log('cryptoWrapper.initialize success');
          initTablet();
          document.querySelector('.preloader').classList.remove('is-show');
          document.querySelector('.out').classList.remove('preloader-show');
        })
        .catch(error => {
          console.log('cryptoWrapper.initialize error' + error);
        });

    });
  });

if ( storage.get( 'userData' ) === null ) {
  document.querySelector('.js-open-popup-help[data="census"]').click();
}


// Перезагрузка планшета если не его не юзать delay минут я у себя сейчас запущу 1 минут+а проверю
let delay = 1000 * 60 * 20; // 20 минут
let reloadPage = () => {
  location.reload();
};
let removePassCrypto = setTimeout(reloadPage, delay);
let updateRemovePassCrypto = (e) => {
  clearTimeout(removePassCrypto);
  removePassCrypto = setTimeout(reloadPage, delay);
};

document.addEventListener('touchstart', updateRemovePassCrypto);
