export function setHandlers() {
  document.querySelector('#h_temp_residents').addEventListener('change', handler1);
  document.querySelector('#h_household_room_parts').addEventListener('change', handler2);
}


export function runHandlers() {
  handler1();
  handler2();
}


function handler1() {
  const bInput = document.querySelector('input:checked[name=r_building_id]');
  let bType;

  if ( bInput === null ) { // данных ещё нет
    bType = 0;
  } else {
    bType = +bInput.value;
  }

  const temp = document.querySelector('input[name=h_temp_residents]').checked;

  // Блокируем зависимые поля
  if ( temp || bType > 8 ) {
    // Кол-во занятых комнат
    document.querySelectorAll('input[name=h_household_rooms]').forEach(el => {
	    el.disabled = true;
      el.checked = false;
    });
    document.querySelector('.js-household-rooms-text').disabled = true;
    // Часть комнаты
    document.querySelector('input[name=h_household_room_parts]').checked = false;
    document.querySelector('input[name=h_household_room_parts]').disabled = true;
    // Тип собственности
    document.querySelectorAll('input[name=h_ownership_id]').forEach(el => {
	    el.disabled = true;
      el.checked = false;
      // устанавливаем дефолтное значение для (Тип володіння житловим приміщeнням h_ownership_id)
      let householdIndex = util.household();
      window.User.rooms[0].households[householdIndex].h_ownership_id = '99';
    });
    document.querySelector('.js-household-disabled').classList.add('is-disabled');
    // если бездомный блокируем поле
    if ( bType === 13 ) {
      document.querySelector('#h_temp_residents').checked = false;
      document.querySelector('#h_temp_residents').disabled = true;
      document.querySelector('#h_temp_residents').parentElement.parentElement.classList.add('is-disabled');
    } else {
      document.querySelector('#h_temp_residents').disabled = false;
      document.querySelector('#h_temp_residents').parentElement.parentElement.classList.remove('is-disabled');
    }
  } else { // if(temp || bType > 8)
    // Кол-во занятых комнат
    document.querySelectorAll('input[name=h_household_rooms]').forEach(el => {
	    el.disabled = false;
    });
    document.querySelector('.js-household-rooms-text').disabled = false;
    // Часть комнаты
    document.querySelector('input[name=h_household_room_parts]').disabled = false;
    // Тип собственности
    document.querySelectorAll('input[name=h_ownership_id]').forEach(el => {
	    el.disabled = false;
    });
    document.querySelector('.js-household-disabled').classList.remove('is-disabled');
    document.querySelector('#h_temp_residents').disabled = false;
    document.querySelector('#h_temp_residents').parentElement.parentElement.classList.remove('is-disabled');
  } // if(temp || bType > 8) ... else

  // Продолжаем блокировать зависимые поля
  if( temp ) {
    document.querySelectorAll('input[name=h_improve_internet_id]').forEach(el => {
      el.disabled = true;
      el.checked = false;
      // устанавливаем дефолтное значение для (Доступ домогосподарства до мережі Інтернет h_improve_internet_id)
      let householdIndex = util.household();
      window.User.rooms[0].households[householdIndex].h_improve_internet_id = '99';
    });
    document.querySelector('.js-household-internet').classList.add('is-disabled');
  } else { // if( temp )
    document.querySelectorAll('input[name=h_improve_internet_id]').forEach(el => {
      el.disabled = false;
    });
    document.querySelector('.js-household-internet').classList.remove('is-disabled');
  } // if( temp ) ... else
}


function handler2() {
  let room_part = document.querySelector('#h_household_room_parts').checked;
  if ( room_part ) { // указана часть комнаты
    // устанавливаем кол-во комнат в null
    document.querySelectorAll('input[name=h_household_rooms]').forEach(radio => {
      radio.checked = false;
    });
    document.querySelector('.js-household-rooms-text').value = ''; // очищаем поле ввода "Інше"

    // Сохраняем новое кол-во комнат для текущего домохозяйства
    let i = util.household();
    if ( i !== -1 ) {
      window.User.rooms[0].households[i].h_household_rooms = null;
    }

  } // if( room_part )
}
