import Choices from 'choices.js';
// import myMember from '../../handlers/my-member';

function createSelect([placeholder, name, className, wrap], arrValue, multiCode = false, optionSelectedText = false ) {
  // создание select
  let customSelect;
  //   let currentMember = myMember( document.querySelector('.js-user-block .js-member').getAttribute('data-id') );
  const select = document.createElement('select');
  select.name = name;
  select.classList.add(className);
  wrap.appendChild(select);


  select.multiple = true;
  multiCode.forEach((value, i) => {
    const option = new Option();
    option.setAttribute('value', value);
    option.innerText = optionSelectedText[i];
    option.selected = true;
    select.appendChild(option);

  });

  // Инициализация выпадающего списка multi с кнопкой удаления
  customSelect = new Choices(`.${className}`, {
    placeholder: true,
    placeholderValue: placeholder,
    noResultsText: 'Нічого не знайдено',
    itemSelectText: 'Натисніть, щоб вибрати',
    noChoicesText: 'Нема з кого вибрати',
    removeItemButton: true,
    //   maxItemCount: 3,
    searchResultLimit: 30,
    //   maxItemText: (maxItemCount) => {
    //     return `Не бiльше ${maxItemCount}-х мов`;
    //   },
  });

  customSelect.setChoices(arrValue, 'value', 'label', false);

  if ( arrValue.length || optionSelectedText.length ) {
    document.querySelector('[data-class="js-select-children"]').style.pointerEvents = 'auto';
  } else {
    document.querySelector('[data-class="js-select-children"]').style.pointerEvents = 'none';
  }
    
  window.selectChildren = customSelect;
}

export default function initSelectChildren(formMember, currentMember, age, codeReletionship ) {
  const select = formMember.querySelector('[data-select-children-multi]');
  // создаю выпадающий список для языков
  const selectName = select.getAttribute('data-select-children-multi');
  const selectClass = select.getAttribute('data-class');
  const selectPlaceholder = select.getAttribute('data-placeholder');
  const dataForSelect = [selectPlaceholder, selectName, selectClass, select];
  const catalogChildren = [];
  const optionSelectedText = [];
  const codeFirst = '1';
  const codeWifeOrHasband = '2';
  const codeChildren = '3';
  const codeParents = '4';
  const codeBrotherOrSister = '5';
  const codeChildrenInLaw = '6';
  const codeParentsInLaw = '7';
  const codeGrandParents = '8';
  const codeGrandChildren = '9';
  const codeOther = '10';
  const codeUnrelated = '11';


  members.forEach(member => {
    if ( +member.m_age < +age ) {

      if ( codeReletionship === codeFirst && member.m_sib_id === codeChildren ) { // если первый, добавляем только сын/дочь
        catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});

      } else if ( codeReletionship === codeWifeOrHasband ) { // если жена/муж добавляем сын/дочь/зать/невестка/другой/несвязаный
        if ( member.m_sib_id === codeChildren || member.m_sib_id === codeChildrenInLaw || member.m_sib_id === codeOther || member.m_sib_id === codeUnrelated ) {
          catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});
        }

      } else if ( codeReletionship === codeChildren ) { // если сын/дочь добавляем внук/внучка/другой/несвязаный
        if ( member.m_sib_id === codeGrandChildren || member.m_sib_id === codeOther || member.m_sib_id === codeUnrelated ) {
          catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});
        }

      } else if ( codeReletionship === codeParents ) { // если папа/мама добавляем первого/брата/сестру/другой/несвязаный
        if ( member.m_sib_id === codeFirst || member.m_sib_id === codeBrotherOrSister || member.m_sib_id === codeOther || member.m_sib_id === codeUnrelated ) {
          catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});
        }

      } else if ( codeReletionship === codeBrotherOrSister ) { // если брат/сестра добавляем другой/несвязаный
        if ( member.m_sib_id === codeOther || member.m_sib_id === codeUnrelated ) {
          catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});
        }

      } else if ( codeReletionship === codeChildrenInLaw ) { // если зять/невестка добавляем внук/внучка/другой/несвязаный
        if ( member.m_sib_id === codeGrandChildren || member.m_sib_id === codeOther || member.m_sib_id === codeUnrelated ) {
          catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});
        }

      } else if ( codeReletionship === codeParentsInLaw ) { // если свекр/свекруха/теща/тесть добавляем муж/жена/другой/несвязаный
        if ( member.m_sib_id === codeWifeOrHasband || member.m_sib_id === codeOther || member.m_sib_id === codeUnrelated ) {
          catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});
        }

      } else if ( codeReletionship === codeGrandParents ) { // если дед/баба добавляем мама/папа/другой/несвязаный
        if ( member.m_sib_id === codeParents || member.m_sib_id === codeOther || member.m_sib_id === codeUnrelated ) {
          catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});
        }

      } else if ( codeReletionship === codeGrandChildren ) { // если внук/внучка добавляем другой/несвязаный
        if ( member.m_sib_id === codeOther || member.m_sib_id === codeUnrelated ) {
          catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});
        }

      } else if ( codeReletionship === codeOther || codeReletionship === codeUnrelated ) { // если другой/несвязаный добавляем другой/несвязаный/дед/баба/свекр/свекруха/теща/тесть/брат/сестра
        if ( member.m_sib_id === codeOther || member.m_sib_id === codeUnrelated || member.m_sib_id === codeGrandParents || member.m_sib_id === codeParentsInLaw || member.m_sib_id === codeBrotherOrSister ) {
          catalogChildren.push( {value: member.m_id, label: member.m_name + ` (${member.m_age})`, selected: false});
        }
      }
      
    }

    if ( currentMember && currentMember[selectName].length > 0 ) {
      currentMember[selectName].forEach(childId => {
        if ( childId === member.m_id ) {
          optionSelectedText.push(member.m_name + ` (${member.m_age})`);
          let removeIndex = null;
          catalogChildren.forEach((removeItem, index) => {
            if ( removeItem.value === childId ) {
              removeIndex = index;
            }
          });

          catalogChildren.splice(removeIndex, 1);
        }
      });
    }
  });

  if ( currentMember ) {
    createSelect( dataForSelect, catalogChildren, currentMember[selectName], optionSelectedText );
  } else {
    createSelect( dataForSelect, catalogChildren, [], optionSelectedText );

  }

  
}
