import checkHouseholdObj from './check-household-obj';

export default function updateIconErrorHousehold() {
  const tabHousehold = document.querySelector('.js-members li[data="household"]');
  const btnsHouseholds = document.querySelectorAll('.js-member-household-tabs li');
  const households = window.User.rooms[0].households;
  let checkHousehold = checkHouseholdObj();
  let currentHouseholdNum = +window.User.rooms[0].current_household;

  households.forEach((household, i) => {

    let errorCode = checkHousehold[i].content[0].code;
    let errorClass;
    
    switch(errorCode) {
      case 0:
        errorClass = 'ok';
        break;
      case 1:
        errorClass = 'warning';
        break;
      case 2: 
        errorClass = 'error';
        break;
    }
    console.log(errorClass);
    console.log(checkHousehold[i].content);
    // если уже редактировали домогосподарство, навешиваю класс ошибки
    if ( household.edit ) {
      btnsHouseholds[i].classList.remove('ok', 'warning', 'error');
      btnsHouseholds[i].classList.add(errorClass);
      household.h_error_class = errorClass;

      if ( household.h_household_number === currentHouseholdNum ) {
        tabHousehold.classList.remove('ok', 'warning', 'error');
        tabHousehold.classList.add(errorClass);
      }
    }

  });
  
}
