import popupControlAll from '../components/popup/popup-control-all';
import checkRoomObj from '../components/room/check-room-obj';
import checkHouseholdObj from '../components/household/check-household-obj';
import checkMemberObj from '../components/member/check-member-obj';
import initPopupMembersCount from '../components/popup/popup-members-count';
import linksControl from './links-control';

export default function checkAllData() {
  return new Promise(function(resolve, reject) {

    const percentFilling = {
      room: 1,
      households: 0,
      members: 0
    };

    window.User.rooms[0].households.forEach(household => {
      percentFilling.households += 1;
      household.members.forEach(member => {
        percentFilling.members += 1;
      });
    });

    // Первая проверка - совпадает ли число созданных членов семей указанному для каждого домохозяйства
    // if( ! initPopupMembersCount() ) {
    //   reject( 2 );
    //   return;
    // }

    if ( window.User.rooms[0].households.length === 0 ) {
      alert('Ви не додали жодного домогосподарства!');
      return;
    }

    const codeOk = 0;
    const codeWarning = 1;
    const codeError = 2;

    // Проверяем все помещения, домохозяйства и членов.
    let rooms = checkRoomObj();
    let households = checkHouseholdObj();
    let members = checkMemberObj();
    console.log(members);

    // Подсчитываем найденные ошибки
    let res = 0;
    let text = '';
    let type;
    let countAllErrors = [];
    let countAllWarning = [];


    rooms.forEach(room => {
      let errorsRoom = [];
      let countErrors = [];
      let countWarning = [];

      room.content.forEach(error => {
        if ( error.code !== codeOk ) {

          if ( error.code === codeError ) {
            countErrors.push(error.code);
            countAllErrors.push(error.code);
          } else {
            countWarning.push(error.code);
            countAllWarning.push(error.code);
          }
          if ( !~error.text.indexOf('Ok') && error.code === codeError ) {
            errorsRoom.push( `<li class="error-icon"><a class="js-link-control" href="#" data-section="room" data-number="${error.number}">${error.text} (П${error.number})</a></li>`);
          } else if ( !~error.text.indexOf('Ok') && error.code === codeWarning ) {
            errorsRoom.push( `<li class="warning-icon"><a class="js-link-control" href="#" data-section="room" data-number="${error.number}">${error.text} (П${error.number})</a></li>`);
          }

          if ( error.code > res ) {
            res = error.code;
          }
        }
      });
      if ( countErrors.length && countWarning.length ) {
        text += `<h3 class="popup-control-all__subtitle">Приміщення: <span class="error">помилок: ${countErrors.length}</span>, <span class="warning">попереджень: ${countWarning.length}</span></h3>`;
        text += `<ol>${errorsRoom.join('')}</ol>`;
      } else if ( countErrors.length ) {
        text += `<h3 class="popup-control-all__subtitle">Приміщення: <span class="error">помилок: ${countErrors.length}</span></h3>`;
        text += `<ol>${errorsRoom.join('')}</ol>`;
      } else if ( countWarning.length ) {
        text += `<h3 class="popup-control-all__subtitle">Приміщення: <span class="warning">попереджень: ${countWarning.length}</span></h3>`;
        text += `<ol>${errorsRoom.join('')}</ol>`;
      }
      
    });
    console.log(households);
    households.forEach(house => {
      let countErrorsHouseAndAllMembers = [];
      let countWarningHouseAndAllMemners = [];

      // проверяю и собираю ошибки по текущему домохозяйству
      house.content.forEach(error => {

        if ( error.code > res ) {
          res = error.code;
        }

        if ( error.code === codeError ) {
          countErrorsHouseAndAllMembers.push(error.code);
          countAllErrors.push(error.code);
        } else if ( error.code === codeWarning ) {
          countWarningHouseAndAllMemners.push(error.code);
          countAllWarning.push(error.code);
        }
      });
      // проверяю и собираю ошибки по всем членам текущего домохозяйства
      members.forEach(member => {
        console.log(house.id);
        console.log(member.m_h_id);
        console.log(member.errors);
        if ( member.errors.length && house.id === member.m_h_id ) {
          member.errors.forEach(error => {

            if ( error.code > res ) {
              res = error.code;
            }

            if ( error.code === codeError ) {
              countErrorsHouseAndAllMembers.push(error.code);
              countAllErrors.push(error.code);
            } else if ( error.code === codeWarning ) {
              countWarningHouseAndAllMemners.push(error.code);
              countAllWarning.push(error.code);
            }
          });
          console.log(countErrorsHouseAndAllMembers);
          console.log(countWarningHouseAndAllMemners);
        }
      });

      // создаю заголовок и количество всех ошибок/предупреждений по текущему домохозяйству
      if ( countErrorsHouseAndAllMembers.length && countWarningHouseAndAllMemners.length ) {
        text += `<h3 class="popup-control-all__subtitle">Домогосподарство: ${house.title}: <span class="error">помилок: ${countErrorsHouseAndAllMembers.length}</span>, <span class="warning">попереджень: ${countWarningHouseAndAllMemners.length}</span></h3>`;
      } else if ( countErrorsHouseAndAllMembers.length ) {
        text += `<h3 class="popup-control-all__subtitle">Домогосподарство: ${house.title}: <span class="error">помилок: ${countErrorsHouseAndAllMembers.length}</span></h3>`;
      } else if ( countWarningHouseAndAllMemners.length ) {
        text += `<h3 class="popup-control-all__subtitle">Домогосподарство: ${house.title}: <span class="warning">попереджень: ${countWarningHouseAndAllMemners.length}</span></h3>`;
      }

      // текст ошибок по текущему домохозяйству
      let errorsHousehold = [];
      house.content.forEach(error => {
        console.log(error);
        if ( error.number === 15 ) { // вкладка П15
          errorsHousehold.push( `<li class="error-icon"><a class="js-link-control" href="#" data-section="room" data-number="${error.number}">${error.text}</a></li>`);
        } else if ( !~error.text.indexOf('Ok') && error.code === codeError ) {
          errorsHousehold.push( `<li class="error-icon"><a class="js-link-control" href="#" data-section="family" data-name-household="${house.title}">${error.text}</a></li>`);
        } else if ( !~error.text.indexOf('Ok') && error.code === codeWarning ) {
          errorsHousehold.push( `<li class="warning-icon"><a class="js-link-control" href="#" data-section="family" data-name-household="${house.title}">${error.text}</a></li>`);
        }
      });

      if ( errorsHousehold.length && countErrorsHouseAndAllMembers.length || errorsHousehold.length && countWarningHouseAndAllMemners.length ) {
        text += '<p><b>Щодо самого домогосподарства:</b></p>';
        text += `<ol>${errorsHousehold.join('')}</ol>`;
      } else if ( countErrorsHouseAndAllMembers.length || countWarningHouseAndAllMemners.length ) {
        text += '<p><b>Щодо самого домогосподарства:</b> немає попереджень і помилок</p>';
      }
      

      // проверяю членов текущего домохозяйства на ошибки/предупреждения
      members.forEach(member => {
        let errorsMember = [];
        if ( house.id === member.m_h_id ) {
          // собираю ошибки/предупреждения текущего члена
          member.errors.forEach(error => {

            if ( error.code !== codeOk && error.code === codeError ) {
              errorsMember.push( `<li class="error-icon"><a class="js-link-control" href="#" 
                                                                                    data-section="s_member" 
                                                                                    data-number="${error.number}" 
                                                                                    data-id="${member.m_id}" 
                                                                                    data-name-household="${house.title}">${error.text} (A${error.number})</a></li>` );
            } else if ( error.code !== codeOk && error.code === codeWarning ) {
              errorsMember.push( `<li class="warning-icon"><a class="js-link-control" href="#" 
                                                                                      data-section="s_member" 
                                                                                      data-number="${error.number}" 
                                                                                      data-id="${member.m_id}" 
                                                                                      data-name-household="${house.title}">${error.text} (A${error.number})</a></li>` );
            }
          });
          // если есть ошибки, вывожу их
          if ( errorsMember.length ) {
            text += `<p><b>${member.m_name}</b></p>`;
            text += `<ol>${errorsMember.join('')}</ol>`;
          }
          
        }
      });  
      
    });
    
    // заполняю процент ответов для всего помещения
    let allAsk = (percentFilling.room * 14) + (percentFilling.households * 4) + (percentFilling.members * 20);
    let allErrors = countAllErrors.length + countAllWarning.length;
    window.User.rooms[0].r_percent_filling = Math.floor((allAsk - allErrors)*100/allAsk);
    
    if ( res === codeOk ) {
      resolve( codeOk );
    } else {
      
      // заголовок всех ошибок/предупреждений
      let content; 
      if ( countAllErrors.length && countAllWarning.length ) {
        content = `<h3>ВСЬОГО: <span class="error">помилок: ${countAllErrors.length}</span>, <span class="warning">попереджень: ${countAllWarning.length}</span></h3>` + text;
      } else if ( countAllErrors.length ) {
        content = `<h3>ВСЬОГО: <span class="error">помилок: ${countAllErrors.length}</span></h3>` + text;
      } else if ( countAllWarning.length ) {
        content = `<h3>ВСЬОГО: <span class="warning">попереджень: ${countAllWarning.length}</span></h3>` + text;
      }

      if ( countAllErrors.length ) {
        document.querySelector('.js-popup-control-all').classList.add('is-error');
      } else {
        document.querySelector('.js-popup-control-all').classList.remove('is-error');
      }
      // открываем popup
      popupControlAll(content, resolve, reject);
      // закрываю попап после записи поля percent_filling
      if ( document.querySelector('.js-popup-control-all').getAttribute('percent-filling') ) {
        document.querySelector('.js-popup-control-all').removeAttribute('percent-filling');
        document.querySelector('.js-popup-control-all').click();
      }

      // навешиваем оброботчик на сылки контроля
      linksControl();
    }

  }); // new Promise()
}

