export default function promisePopupGetCoordinate() {
  return new Promise((resole, reject) => {
    const popupGetCoordinate = document.querySelector('.js-popup-get-coordinate');
    const closePopupGetCoordinate = document.querySelectorAll('.js-popup-get-coordinate-close');
    const btnNotHouse = document.querySelector('.js-not-house');

    popupGetCoordinate.querySelector('form').removeEventListener('submit', handlerSubmit);
    popupGetCoordinate.querySelector('form').addEventListener('submit', handlerSubmit);

    closePopupGetCoordinate.forEach(close => {
      close.removeEventListener('click', handlerClosePopup);
      close.addEventListener('click', handlerClosePopup);
    });

    btnNotHouse.removeEventListener('click', handlerNotHouse);
    btnNotHouse.addEventListener('click', handlerNotHouse);

    function handlerClosePopup() {
      reject(false);
    }
        
    function handlerSubmit(e) {
      e.preventDefault();
      resole(true);
    }

    function handlerNotHouse(e) {
      resole('not-house');
    }

  });
    
}
