import mySelect from './my-select';

export default function adjustRegistration() {

  let block = false;
  let input = document.querySelector('input:checked[name=m_temp]');
  let tab = document.querySelector("a[href='#s_member-20']").parentElement;

  if ( input !== null && +input.value === 3 || +User.rooms[0].r_building_id === 13 ) {
    block = true;
  }

  if ( block ) {
    // const other = mySelect( 'm_registration_another' );
    tab.classList.remove('ok', 'warning', 'error');
    tab.classList.add('disabled');

    document.querySelectorAll('input[name=m_place_coincide]').forEach(el => {
      el.disabled = true;
      el.checked = false;
    });
    // document.querySelectorAll('input[name=m_registration_id]').forEach(el => {
    //   el.disabled = true;
    //   el.checked = false;
    // });

    // if (other !== null ) {
    //   other[0].selectize.clear();
    //   other[0].selectize.disable();
    // }
  } else {
    tab.classList.remove('disabled');
    document.querySelectorAll('input[name=m_place_coincide]').forEach(el => {
      el.disabled = false;
    });
  }

}
