export default class cryptoWrapper {
  constructor(EUSignCP) {
    this.euSign = EUSignCP() || null;

    this.serverCert = null;
    this.localCerts = null;
    this.pass = null;
    this.key = null;
    this.password = null;
  }

  initialize() {
    return new Promise((resolve, reject) => {
      try {
        this.euSign.Initialize();
        this.euSign.SetJavaStringCompliant(true);
        this.euSign.SetCharset('UTF-8');
        this.setSettings().then(ok => {
          console.log('setSettings ok ' + this.euSign.DoesNeedSetSettings());
          this.loadCACerts().then(res => {
            resolve( 'Ok' );
          });

        });

      } catch (e) {
        console.log( e );
        reject( e.message );
      }
    });

  }


  setSettings() {
    return new Promise((resolve, reject) => {
      try {
        this.euSign.SetXMLHTTPProxyService('/proxy_handler' );

        var settings = this.euSign.CreateFileStoreSettings();
        settings.SetPath( '' );
        settings.SetSaveLoadedCerts( true );
        this.euSign.SetFileStoreSettings( settings );

        settings = this.euSign.CreateProxySettings();
        this.euSign.SetProxySettings( settings );

        settings = this.euSign.CreateTSPSettings();
        this.euSign.SetTSPSettings( settings );

        settings = this.euSign.CreateOCSPSettings();
        settings.SetUseOCSP( true );
        settings.SetBeforeStore( true );
        settings.SetAddress( '' );
        settings.SetPort( 80 );
        this.euSign.SetOCSPSettings( settings );

        settings = this.euSign.CreateCMPSettings();
        this.euSign.SetCMPSettings( settings );

        settings = this.euSign.CreateLDAPSettings();
        this.euSign.SetLDAPSettings( settings );

        settings = this.euSign.CreateModeSettings();
        settings.offlineMode = true;
        this.euSign.SetModeSettings(settings);

        resolve( 'Ok' );

      } catch (e) {
        console.log( e );
        reject( e.message );
      }

    }); // new Promise
  }

  base64ToArrayBuffer( base64 ) {
    var binary_string = window.atob(base64);
    var len = binary_string.length;
    var bytes = new Uint8Array( len );
    for (var i = 0; i < len; i++) {
      bytes[i] = binary_string.charCodeAt(i);
    }
    return new Uint8Array( bytes.buffer );
  }


  loadCACerts() {
    return new Promise(resolve => {
      query.get(`${util.DOMEN}/data/key/CACertificates.p7b`)
        .then(res => {
          for (let i = 0; i < this.localCerts.length; i++) {
            this.euSign.SaveCertificate(this.localCerts[i]);
          }
          this.euSign.SaveCertificates(new Uint8Array(this.base64ToArrayBuffer(btoa(res))));
          this.euSign.ReadPrivateKeyBinary(this.key, this.pass);
          console.log('loadCACerts success');
          resolve(true);
        });
    });

  }

  signIntern(string) {
    return new Promise((resolve, reject) => {
      try {
        let res = this.euSign.SignDataInternal(true, string, true);
        resolve(res);
      }catch (e) {
        reject(e);
      }
    });
  }

  encrypt(data) {
    return new Promise((resolve, reject) => {
      try {
        let res = this.euSign.EnvelopDataToRecipientsWithDynamicKey([this.serverCert], true, true, data, true);
        resolve( res );
      } catch (e) {
        reject(e);
      }
    });
  }
  protect( data ) {
    return new Promise((resolve, reject) => {
      try {
        let res = this.euSign.ProtectDataByPassword(data, this.password, true);
        resolve( res );
      } catch ( e ) {
        reject( e );
      }

    });
  }

  decrypt() {
    return new Promise((resolve, reject) => {
      try {
        let res = this.euSign.DevelopData(data);
        console.log('DevelopData ok');
        resolve( res );
      } catch( e ) {
        console.log(e);
        reject( e );
      }
    }); // new Promise
  }
  unprotect( data ) {
    return new Promise((resolve, reject) => {
      try {
        let res = this.euSign.UnprotectDataByPassword(data, this.password, false);
        resolve( res );
      } catch( e ) {
        reject( e );
      }
    }); // new Promise
  }

}
