import myMember from './my-member';

export default function adjustWorking( member ) {

  if(member === undefined) {
    member = myMember();
  }

  if(member === null) {
    return;
  }

  let block = false;

  if(member.m_age < 15) {
    block = true;
  } else {
    let input = document.querySelector('input:checked[name=m_temp]');
    if(input !== null && +input.value === 3) {
      block = true;
    }
  }

  let another =document.querySelector('input[name=m_another_employment]');
  let tab = document.querySelector("a[href='#s_member-11']").parentElement;
  if( block ) {
    tab.classList.remove('ok', 'warning', 'error');
    tab.classList.add('disabled');
    document.querySelectorAll('input[name=m_have_week_paid_work]').forEach(el => {
      el.checked = false;
      el.disabled = true;
    });
    document.querySelectorAll('input[name=m_employ_id]').forEach(el => {
      el.checked = false;
      el.disabled = true;
    });
    another.disabled = true;
    another.value = '';
  } else {
    tab.classList.remove('disabled');
    document.querySelectorAll('input[name=m_have_week_paid_work]').forEach(el => {
      el.disabled = false;
    });
    document.querySelectorAll('input[name=m_employ_id]').forEach(el => {
      el.disabled = false;
    });
    another.disabled = false;
  }
}
