import renderTableHouses from './render-tables/render-table-houses';
import { Base64 } from 'js-base64';
import SimpleCrypto from 'simple-crypto-js';
import renderTableInstructor from './render-tables/render-table-instructor';
import renderTableManager from './render-tables/render-table-manager';
import handlerAddRoom from './handler-add-room';
import handlerBtnHomeless from './handler-btn-homeless';
import popupInfoOk from '../popup/popup-info-ok';
import cryptoWrapper from '../crypto/cryptoAndroid/crypto-control';

export default function refreshData() {
  const popupGetPass = document.querySelector('.js-popup-get-pass');
  const synchroIndivData = document.querySelector('.js-refresh-indiv-data');
  const sidebar = document.querySelector('.js-sidebar');
  const burger = document.querySelector('.js-burger');
  const instructorSection = document.querySelector('.js-indiv-instructor');
  const managerSection = document.querySelector('.js-indiv-manager');
  const houseSection = document.querySelector('.js-indiv-houses');
  const allIndivSection = document.querySelectorAll('.js-indiv-section');
  const outParentBlock = document.querySelector('.out');
  const preloader = document.querySelector('.preloader');
  const inputPass = popupGetPass.querySelector('[name="get-pass"]');

  popupGetPass.addEventListener('submit', function(e) {
    e.preventDefault();

    if ( navigator.onLine ) {
      let login = storage.get('login');
      const crypto = new SimpleCrypto(inputPass.value);
      const decipherPass = crypto.decrypt(storage.get('pass'));
      let housestatus = null;
      const role = +document.body.getAttribute('role');
      let controlTasks = null;
      let data = '';

      if ( storage.get('housestatus') ) {
        housestatus = JSON.parse( storage.get('housestatus'));
      } else if ( storage.get('currentHousestatus') ) {
        housestatus = JSON.parse(storage.getItem('currentHousestatus'));
      }

      if ( housestatus ) {
        if ( role !== util.CODE_COPYIS && storage.get('stationary') ) {
          housestatus = JSON.parse(storage.getItem('stationary'));
        }
        let responds = [];
        const rooms = JSON.parse( storage.get('rooms') );

        if ( rooms ) {
          rooms.forEach(currentRoom => {
            if ( currentRoom.status === 3 ) {
              responds.push({
                resp_t_id: currentRoom.task_id,
                resp_idkep: currentRoom.task_id,
                resp_file: currentRoom.task_id,
                resp_code: currentRoom.poem_code,
                resp_data: currentRoom.status === 3 ? util.decompressFromUTF16(currentRoom.room) : currentRoom.room,
                resp_date: currentRoom.task_created_date
              });
            }
          });
        }

        if ( !responds.length ) {
          responds.push('');
        }

        if ( role === util.CODE_INSTRUCTOR ) {
          controlTasks = JSON.parse( storage.get('controlTasks') );
        }

        data = {};

        if ( controlTasks !== null ) {
          if ( housestatus !== null ) {
            data.housestatus = housestatus.concat(controlTasks);
          } else {
            data.housestatus = controlTasks;
          }

        } else {
          data.housestatus = housestatus;
        }

        data.respondents = responds;
      }

      window.cryptoWrapper.signIntern(JSON.stringify(data)).then(cypher => {
        console.log('signIntern ок - ' + cypher);
        sendSignData(cypher);

      }).catch(err => {
        console.log('signIntern error' + err);
      });


      function sendSignData(data) {
        console.log('sendSignData' + data);
        // показываем прелоадер
        preloader.classList.add('is-show');
        outParentBlock.classList.add('preloader-show');

        let i = 0;
        let indexCurrentServer;
        let servers = JSON.parse(storage.get('servers'));

        if ( servers ) {
          for ( let i = 0; i < servers.length; i++ ) {
            if ( servers[i] === location.origin ) {
              indexCurrentServer = i;
              break;
            }
          }
          servers.unshift(...servers.splice(indexCurrentServer,1));
          console.log(servers);
        }

        let redirect = (auth, arr, i) => {

          if ( Array.isArray(arr) && arr.length && i <= arr.length ) {
            let obj = {
              domen: arr[i],
              login: login,
              pass: decipherPass,
              data: data
            };
            auth(obj).then(
              resolve => {
                console.log(resolve);
              },
              reject => {
                console.log(i);
                console.log(arr);
                i++;
                console.log(i);
                if ( i >= arr.length) {
                  console.log('finish');
                } else {
                  console.log(arr);
                  console.log(i);
                  redirect(auth, arr, i);
                }
              });
          } else {
            let obj = {
              domen: '.',
              login: login,
              pass: decipherPass,
              data: data
            };
            synchro(obj);
          }
        };

        redirect(synchro, servers, i);
      }
    } else {
      alert('Для синхронізації приєднайтесь до мережі інтернет.');
    }
  });

  function synchro(obj) {
    return new Promise((resolve, reject) => {
      console.log('update data');
      try {
        console.log(obj.domen);
        console.log('obj.data' + obj.data);
        console.log('obj.login' + obj.login);
        console.log('obj.pass' + obj.pass);
        query.post(`${util.DOMEN}/reg/synchro`, Base64.encode( JSON.stringify({
          userid: obj.login,
          content: JSON.stringify({
            type: 'synchro',
            pass: obj.pass,
            data: obj.data
          })
        }))
        )
          .then(res => {
            console.log('1111111111111111111111 ' + res);
            const result = JSON.parse(res);
            // console.log(res.body);
            if ( result.code === -1 ) {
              // скрываем прелоадер
              preloader.classList.remove('is-show');
              outParentBlock.classList.remove('preloader-show');
              alert('Невірний пароль!');
            } else {


              storage.set('servers', result.data.servers);
              if ( result.info ) {

                const allData = util.removeCoordinateInTasks(JSON.parse(result.info), JSON.parse(result.info).user.su_sr_id);
                const role = allData.user.su_sr_id;
                // console.log(result.info);
                // console.log(allData);
                window.allData = allData;
                storage.set('allData', JSON.stringify(allData));

                // скрываем прелоадер
                preloader.classList.remove('is-show');
                outParentBlock.classList.remove('preloader-show');

                if ( role === util.CODE_COPYIS ) {
                  const housestatus = JSON.parse(result.info).staffusers_census[0].staffusers_instructor[0].staffusers_counting[0].housestatus;
                  storage.set('housestatus', JSON.stringify(housestatus) );
                  console.log(housestatus);
                  let countSlot = 0;
                  changeStatusRooms(housestatus);
                  for ( let i = 0; i < housestatus.length; i++ ) {
                    if ( housestatus[i].hs_datasource === 0 ) {
                      for ( let j = 0; j < housestatus[i].tasks.length; j++ ) {
                        if ( housestatus[i].tasks[j].t_status_id === 1 ) {
                          countSlot++;
                        }
                      }
                      break;
                    }
                  }
                  console.log(countSlot);
                  if ( !countSlot ) {
                    popupInfoOk('Рекомендуємо додатково натиснути кнопку "Оновити" через 5 хвилин для збільшення ліміту кількості переписаних на переписній дільниці дільниці');
                  }
                  renderTableHouses();
                  allIndivSection.forEach(section => {
                    section.setAttribute('hidden', true);
                  });
                  houseSection.removeAttribute('hidden');
                  document.querySelector('.js-not-house').classList.remove('is-show'); // скрываем кнопку (нет дома)
                  document.querySelector('.js-homeless').classList.add('is-show');
                  handlerBtnHomeless();

                } else if ( role === util.CODE_INSTRUCTOR ) {
                  const staffusersCounting = JSON.parse(result.info).staffusers_census[0].staffusers_instructor[0].staffusers_counting;
                  let countSlotHouses = 0;
                  let countSlotTasks = 0;
                  let stationaryHouses = null;
                  let controlTasks = null;

                  for (let i = 0; i < staffusersCounting.length; i++) {
                    if ( staffusersCounting[i].su_counting_department === 0 && staffusersCounting[i].t_datasource === util.CODE_STATIONARY ) {
                      stationaryHouses = staffusersCounting[i].housestatus;
                    } else if ( staffusersCounting[i].su_counting_department === 0 && staffusersCounting[i].t_datasource === util.CODE_CONTROL_TASK ) {
                      controlTasks = staffusersCounting[i].housestatus;
                    }
                  }

                  for ( let i = 0; i < stationaryHouses.length; i++ ) {
                    if ( stationaryHouses[i].hs_house_id === null ) {
                      const tasks = stationaryHouses[i].tasks;
                      countSlotHouses++;

                      for ( let j = 0; j < tasks.length; j++ ) {
                        if ( tasks[j].t_flat_num === null ) {
                          countSlotTasks++;
                        }
                      }
                    }
                  }
                  changeStatusRooms(stationaryHouses);
                  if (controlTasks) {
                    changeStatusRooms(controlTasks);
                  }
                  if ( !countSlotTasks || !countSlotHouses ) {
                    popupInfoOk('Рекомендуємо додатково натиснути кнопку "Оновити" через 5 хвилин для збільшення ліміту кількості переписаних на переписній дільниці дільниці');
                  }

                  renderTableInstructor(role);
                  allIndivSection.forEach(section => {
                    section.setAttribute('hidden', true);
                  });
                  instructorSection.removeAttribute('hidden');
                  document.querySelector('.js-not-house').classList.remove('is-show'); // скрываем кнопку (нет дома)
                  document.querySelector('.js-add-room').classList.add('is-show');
                  document.querySelector('.js-homeless').classList.remove('is-show');
                  handlerAddRoom();

                } else if ( role === util.CODE_MANAGER ) {

                  const staffusersInstructor = JSON.parse(result.info).staffusers_census[0].staffusers_instructor;
                  let countSlotHouses = 0;
                  let countSlotTasks = 0;
                  let stationaryHouses = null;

                  for (let i = 0; i < staffusersInstructor.length; i++) {
                    if ( staffusersInstructor[i].su_instructor_department === 0 && JSON.parse(result.info).user.su_id === staffusersInstructor[i].su_id ) {
                      stationaryHouses = staffusersInstructor[i].staffusers_counting[0].housestatus;
                    }
                  }

                  for ( let i = 0; i < stationaryHouses.length; i++ ) {
                    if ( stationaryHouses[i].hs_house_id === null ) {
                      const tasks = stationaryHouses[i].tasks;
                      countSlotHouses++;

                      for ( let j = 0; j < tasks.length; j++ ) {
                        if ( tasks[j].t_flat_num === null ) {
                          countSlotTasks++;
                        }
                      }
                    }
                  }
                  changeStatusRooms(stationaryHouses);
                  if ( !countSlotTasks || !countSlotHouses ) {
                    popupInfoOk('Рекомендуємо додатково натиснути кнопку "Оновити" через 5 хвилин для збільшення ліміту кількості переписаних на переписній дільниці дільниці');
                  }

                  renderTableManager(role);
                  allIndivSection.forEach(section => {
                    section.setAttribute('hidden', true);
                  });
                  managerSection.removeAttribute('hidden');
                  document.querySelector('.js-not-house').classList.remove('is-show'); // скрываем кнопку (нет дома)
                  document.querySelector('.js-add-room').classList.add('is-show');
                  document.querySelector('.js-homeless').classList.remove('is-show');
                  handlerAddRoom();
                }

                popupGetPass.reset();
                popupGetPass.classList.remove('is-show');

                if ( sidebar.classList.contains('is-show') ) {
                  sidebar.classList.remove('is-show');
                  burger.classList.remove('is-active');
                }
                // });


              } else {
                console.log(res);
                console.log(result);
              }
            }

            resolve('ok synchro');
          })
          .catch(error => {
            reject(true);
            console.log('update data fetch catch ' + obj);
            // // скрываем прелоадер
            // preloader.classList.remove('is-show');
            // outParentBlock.classList.remove('preloader-show');
            console.log(error);
          });
      } catch (error) {
        console.log('update data try catch2 ' + obj);
        reject(true);
      }

    });
  }
  function changeStatusRooms(housestatus) {
    const rooms = storage.get('rooms') ? JSON.parse(storage.get('rooms')) : [];
    console.log(rooms);
    housestatus.forEach(house => {
      house.tasks.forEach(task => {
        if ( task.t_status_id === 7 ) {
          for ( let i = 0; i < rooms.length; i++ ) {
            if ( +rooms[i].task_id === +task.t_id ) {
              rooms[i].status = 7;
              break;
            }
          }
        }
      });
    });
    console.log(rooms);
    console.log(housestatus);
    storage.set('rooms', JSON.stringify(rooms));
  }

  synchroIndivData.addEventListener('click', (e) => {
    const popupGetPass = document.querySelector('.js-popup-get-pass');
    const closePopupGetPass = document.querySelector('.js-close-popup-get-pass');

    popupGetPass.classList.add('is-show');

    closePopupGetPass.addEventListener('click', (e) => {
      popupGetPass.classList.remove('is-show');
    });

  });

}


