import popupInfoOk from '../components/popup/popup-info-ok';
import saveInfoForTablet from '../components/tablet/save-info-for-tablet';
//import { userPass } from '../components/crypto/read-key';

export default function saveInfo(withOutPopup) {

  // Определяем, в какой мы секции
  const root = document.querySelector( '.js-root' );
  console.log(root.getAttribute('data-section'));
  console.log(root);
  if ( root.getAttribute('data-section') === 'indiv' || root.getAttribute('data-section') === null ) {
    console.log('return');
    return;
  }
  let userPass;
  if ( window.cryptoWrapper ) {
    const crypto = window.cryptoWrapper;
    userPass = crypto.pass;
  }


  switch ( root.dataset.section ) {
    case 'auth':
    		return;

    case 'room':
      util.setValues(window.User.rooms[0], document.querySelector('.js-form-room'));
      break;

    case 'family':
      // форма редактирования household, сохраняем данные с ней в current_household
      let hIndex = util.household();
      util.setValues(window.User.rooms[0].households[hIndex], document.querySelector('.js-household-form'));

      break;

    case 's_member':
      let currentMemberId = document.querySelector('.js-user-block .js-member').getAttribute('data-id');
      let currentMember;
      let fomrMember =  document.querySelector('.js-form-member');
      let householdIndex = util.household();

      window.User.rooms[0].households[householdIndex].members.forEach(member => {
        if ( currentMemberId === member.m_id ) {
          currentMember = member;
        }
      });

      util.setValues(currentMember, fomrMember);
      break;
  }

  // Отмечаем, когда прекратили редактировать
  // if ( window.User.history !== undefined ) {
  //   let last = window.User.history.length - 1;
  //   window.User.history[last][1] = + new Date();
  // }
  window.User.rooms[0].r_member_count = 0;
  window.User.rooms[0].households.forEach(household => {
    window.User.rooms[0].r_member_count += household.members.length;
  });

  let sourceData = JSON.stringify( User );

  if ( userPass ) { // если пользователь входил с использованием пароля, шифруем данные
    let buf = new TextEncoder('utf-8').encode( sourceData );
    //let encryptedData = euSign.ProtectDataByPassword(buf, userPass, true);
    //localStorage.setItem('userData', encryptedData);
    crypto.ProtectDataByPassword( buf ).then(res => {
      storage.set('userData', res);
    }).catch(err => {
      popupInfoOk( 'Помилка збереження даних: ' + err);
      return;
    });
  } else { // если пароля не было, сохраняем в открытом виде (?)
    storage.set('userData', sourceData);
  }


  if ( withOutPopup !== true ) {

    saveInfoForTablet().then(indexPoem => {
      document.querySelector('.js-popup-info-ok-close').click();
      popupInfoOk( 'Дані збережено');
    });

  }

}
