import { disabledGrandFatherAndMother } from '../helpers-form-create-member'; 
import changedTextForValidity from './changed-text-for-validity';
export default function changeGender() {
  const form = document.querySelector('.js-form-create-member');
  const inputName = form.querySelector('[name="m_name"]');
  const selectRelationship = form.querySelector('select[name="m_sib_id"]');
  const selectSpouse = form.querySelector('select[name="m_spouse_id"]');
  const allRadioGender = form.querySelectorAll('[name="m_gender_id"]');
  const optionTemplate = document.createElement('option');
  const man = '1';
  const woman = '2';
  const wifeOrHusband = '2';
  const momOrDad = '4';
  const motherOrFatherInLaw = '7';
  const grandfatherOrGrandmother = '8';
  

  
    
  // слежу за изменением пола
  [...allRadioGender].forEach(radio => {
    radio.addEventListener('change', function(e) {
      changedTextForValidity();
      let radioValue = this.value;
      let haveWifeOrHusband = false; // по умолчанию нет муж/жена
      // проверяю есть ли муж/жена
      members.forEach(member => {
        if ( member.m_sib_id === wifeOrHusband ) {
          haveWifeOrHusband = true;
        }
      });
      
      // разблокировал поле ввода имени
      inputName.classList.remove('is-disabled');
      inputName.parentElement.parentElement.classList.remove('is-disabled');

      // наполняю женские/мужские связи
      [...selectRelationship.children].forEach(option => {
        if ( option.hasAttribute(radioValue) ) {
          option.innerText = option.getAttribute(radioValue);
        }
        
        // проверяю если есть первый член домохозяйства
        if ( members.length > 0 ) {
        // блокирию выбор муж/жена если одинаковый пол с первым 
          if ( members[0].m_gender_id === radioValue ) {
            if ( option.value === wifeOrHusband ) {
              option.disabled = true;
            }
          // блокирию выбор муж/жена если муж/жена уже есть
          } else if ( haveWifeOrHusband ) {
            if ( option.value === wifeOrHusband ) {
              option.disabled = true;
            }
          } else {
            if ( option.value === wifeOrHusband ) {
              option.disabled = false;
            }
          }

          // блокирую мама/папа если уже есть
          for ( let i = 0; i < members.length; i++ ) {
            if ( members[i].m_sib_id === momOrDad && members[i].m_gender_id === radioValue && option.value === momOrDad ) {
              option.disabled = true;
              break;
            } else {
              if ( option.value === momOrDad ) {
                option.disabled = false;
              }
            }
          }

          // блокирую свекруха, свекор, теща, тесть если уже есть
          for ( let i = 0; i < members.length; i++ ) {
            if ( members[i].m_sib_id === motherOrFatherInLaw && members[i].m_gender_id === radioValue && option.value === motherOrFatherInLaw ) {
              option.disabled = true;
              break;
            } else {
              if ( option.value === motherOrFatherInLaw ) {
                option.disabled = false;
              }
            }
          }

          // блокирую дед, баба если уже есть
          disabledGrandFatherAndMother();

          // при смене пола обнуляю выбор родственых связей
          if ( option.value === '' ) {
            option.selected = true;
          }


        }
      });

      
      // обнуляю список муж/жена
      [...selectSpouse.children].forEach(option => {
        if ( option.value === '' ) {
          option.selected = true;
        }
      });
      
      // если выбран пол женский
      if ( radioValue === woman ) {

        // обнуляю список муж/жена
        [...selectSpouse.children].forEach(option => {
          if ( option.value !== '' ) {
            option.parentNode.removeChild(option);
          }
        });

        // наполняю список выбора мужей
        members.forEach(member => {

          if ( member.m_gender_id === man && member.m_age > util.MIN_AGE_FOR_MARRIAGE && member !== members[0] ) {
            let option = optionTemplate.cloneNode();
            option.setAttribute('value', member.m_id);
            option.innerText = member.m_name;
            selectSpouse.appendChild(option);
          }
        });

      // если выбран пол мужской 
      } else {
        // обнуляю список муж/жена
        [...selectSpouse.children].forEach(option => {
          if ( option.value !== '' ) {
            option.parentNode.removeChild(option);
          }
        });
        // наполняю список выбора жен
        members.forEach(member => {
          if ( member.m_gender_id === woman && member.m_age > util.MIN_AGE_FOR_MARRIAGE && member !== members[0] ) {
            let option = optionTemplate.cloneNode();
            option.setAttribute('value', member.m_id);
            option.innerText = member.m_name;
            selectSpouse.appendChild(option);
          }
        });
      }
      
    });
  });
}
