import openPopupWarn from '../components/popup/popup-warn';
import checkAllData from './check-all-data';
import saveInfo from './save-info';
import saveInfoForTablet from '../components/tablet/save-info-for-tablet';
import renderTableRooms from '../components/tablet/render-tables/render-table-rooms';

export default function sendData() {
  saveInfo(true);

  checkAllData().then(res => {

    console.log('save ok');
    document.querySelector('.js-indiv-address').setAttribute('hidden', true);

    return warn();
  }).then(ok => {
    document.querySelector('.preloader').classList.add('is-show');
    console.log('start preloader');
    document.querySelector('.out').classList.add('preloader-show');
    return saveInfoForTablet(3); // 3 - статус виконанно
  }).then(indexPoem => {
    console.log(indexPoem);
    return finishPage(indexPoem);
  }).then(done => {
    // переход на квартиры (rooms)
    const root = document.querySelector('.js-root');
    const allSections = root.querySelectorAll('[data=section]');
    const houseId = +window.User.rooms[0].r_hs_id;
    document.querySelector('.js-close-user-block').click();
    allSections.forEach(el => {
      if ( el.classList.contains('indiv') ) {
        el.removeAttribute('hidden');
      } else {
        el.setAttribute('hidden', true);
      }
    });
    root.setAttribute('data-section', 'indiv');
    renderTableRooms(houseId);
    document.querySelector('.js-indiv-rooms').removeAttribute('hidden');

  }).catch(err => {
    console.log('catch from checkAllData ' + err);
    return;
  });
}


function warn() {
  return new Promise((resolve, reject) => {
    openPopupWarn('<center><p><span class="popup-check__subtitle">Подання відомостей є кінцевою операцією для респондента.</span></p>' +
          '<p><span class="popup-check__subtitle">Надіслані дані не можна буде виправити.</span></p>' +
          '<p><span class="popup-check__subtitle">Будь ласка, переконайтеся в тому, що введені Вами дані точні й не містять помилок.</span></p>' +
          '<p><span class="popup-check__subtitle">Ви впевнені в тому, що готові подати відомості?</span></p></center>', resolve, reject);
  }); // new Promise
}

function finishPage(indexPoem) {
  console.log(indexPoem);
  return new Promise(resolve => {
    if ( indexPoem !== null ) {
      const closeFinishPage = document.querySelector('.js-finish-page');
      const poems = JSON.parse( storage.get('poems') );

      document.querySelector('.js-user-code').innerHTML = `${poems[indexPoem].poem_name}. ${poems[indexPoem].poem_author}`;
      document.body.classList.add( 'is-finish' );

      closeFinishPage.addEventListener('click', (e) => {
        document.body.classList.remove( 'is-finish' );
        document.querySelector('.preloader').classList.remove('is-show');
        document.querySelector('.out').classList.remove('preloader-show');
        resolve(true);
      });
    } else {
      document.querySelector('.preloader').classList.remove('is-show');
      document.querySelector('.out').classList.remove('preloader-show');
      resolve(true);
    }

  });
}
