import myMember from './my-member';
import mySelect from './my-select';

export default function adjustWorkplace( member ) {

  if(member === undefined) {
    member = myMember();
  }

  if(member === null) {
    return;
  }

  let block = false;

  if(member.m_age < 15) {
    block = true;
  } else {
    let input = document.querySelector('input:checked[name=m_temp]');
    if(input !== null && +input.value === 3) {
      block = true;
    } else {
      let working = document.querySelector('input:checked[name=m_have_week_paid_work]');
      if(working !== null && +working.value === 2) {
        block = true;
      }
    }
  } // if(member.m_age < 15) ... else

  let country = mySelect( 'm_main_job_location_another_country' );
  let region = mySelect( 'm_job_region' );
  let city = mySelect( 'm_job_settlement' );
  // let another = document.querySelector('input[name=m_remotework_another]');
  let tab = document.querySelector("a[href='#s_member-12']").parentElement;
  if( block ) {
    tab.classList.remove('ok', 'warning', 'error');
    tab.classList.add('disabled');
    document.querySelectorAll('input[name=m_native_job_location]').forEach(el => {
      el.checked = false;
      el.disabled = true;
    });
    // document.querySelectorAll('input[name=m_remotework_id]').forEach(el => {
    //   el.checked = false;
    //   el.disabled = true;
    // });
    // another.disabled = true;
    // another.value = '';

    if(country !== null) {
      country[0].selectize.clear();
      country[0].selectize.disable();
    }
    if(region !== null) {
      region[0].selectize.clear();
      region[0].selectize.disable();
    }
    if(city !== null) {
      city[0].selectize.clear();
      city[0].selectize.disable();
    }
  } else { // if( block )
    tab.classList.remove('disabled');
    document.querySelectorAll('input[name=m_native_job_location]').forEach(el => {
      el.disabled = false;
    });
    document.querySelectorAll('input[name=m_native_job_location]').forEach(el => {
      el.disabled = false;
    });
  } // if( block ) ... else

}
