    this.r_street_id = arg.r_street_id || null; // Назва вулиці тощо
    this.r_house_number_id = arg.r_house_number_id || null; // № будинку
    this.r_flat_number = arg.r_flat_number || ''; // № квартири
    this.r_flat_without_number = arg.r_flat_without_number || false; // № квартири
    // П1
    this.r_building_id = arg.r_building_id || '99'; // Тип будівлі та житлового приміщення
    this.r_rolling_stock = arg.r_rolling_stock || null; // Якщо r_building_id був вибраний варіант 11
    this.r_build_type_another = arg.r_build_type_another || null; // Якщо r_building_id був вибраний варіант 12
