    this.r_improve_water_id = arg.r_improve_water_id || '99'; // Водопостачання
    // П4
    this.r_improve_toilet_id = arg.r_improve_toilet_id || '99'; // Туалет
    this.r_toilet_other_households = arg.r_toilet_other_households || '99'; // Якщо r_improve_toilet_id був вибраний варіант 11 «чи користуєтесь Ви цим туалетом разом з іншими домогосподарствами»
    // П5
    this.r_improve_bath_id = arg.r_improve_bath_id || '99'; // Ванна та (або) душ
    this.r_improve_bathhouse_id = arg.r_improve_bathhouse_id || '99'; // Лазня, сауна
