    this.r_improve_hotwater_id = arg.r_improve_hotwater_id || '99'; // Гаряче водопостачання
    // П7
    this.r_improve_sewage_id = arg.r_improve_sewage_id || '99'; // Водовідведення (каналізація)
    // П8
    this.r_improve_kitchen_id = arg.r_improve_kitchen_id || '99'; // Кухня або кухонний куток
    // П9
    this.r_improve_heating_id = arg.r_improve_heating_id || '99'; // Опалення
    // П10а
    this.r_improve_electricity_id = arg.r_improve_electricity_id || '99'; // Електропостачання
    // П10б
    this.r_improve_electric_stove_id = arg.r_improve_electric_stove_id || '99'; // Електрична плита стаціонарна
    // П11
    this.r_improve_gas_id = arg.r_improve_gas_id || '99'; // Газ
