    this.r_improve_garbage_id = arg.r_improve_garbage_id || '99'; // Видалення побутових відходів
    this.r_garbage_another = arg.r_garbage_another || null; // Якщо r_improve_garbage_id був вибраний варіант 65
    // П13
    this.r_period_id = arg.r_period_id || '99'; // Період побудови будівлі
    // П14
    this.r_composition_id = arg.r_composition_id || '99'; // Матеріал зовнішніх стін будівлі
    this.r_composition_another = arg.r_composition_another || null; // Якщо r_composition_id був вибраний варіант 6


    //this.r_region_district             = arg.r_region_district || null; // Район
    //this.r_united_community            = arg.r_united_community || null; // Об’єднана територіальна громада
    //this.r_city_district               = arg.r_city_district || null; // Район міста
    //this.r_village_council             = arg.r_village_council || null; // Сільська рада
    //this.r_village                     = arg.r_village || null; // Сільський населений пункт
    //this.r_road_network_type           = arg.r_road_network_type || null; //Тип елементу вулично-дорожньої мережі
    //this.r_boarding_building_type = arg.r_boarding_building_type || null; // Якщо r_building_type був вибраний варіант ?????
