    this.current_tab = '1';
    this.current_household = null;
    this.first_time = true;
    this.r_hs_id = arg.r_hs_id || null;
    this.r_datasource = arg.r_datasource || 2; // 2 - признак планшетной переписи
    this.r_member_count = arg.r_member_count || 0;
    this.r_terson_id_distr = arg.r_terson_id_distr || null;
    this.r_indicators = arg.r_indicators || '';
    this.r_percent_filling = arg.r_percent_filling || 0;
    this.r_status_id = this.r_status_id || 7;
