import householdMembersNumChange from './members-num-change';

let initialised = false;

export default function memberCountBinds() {

  if ( initialised ) {
    return; // уже инициализировано
  }
  
  initialised = true;

  // Привязываем обработчики изменения кол-ва членов домохозяйства
  document.querySelectorAll('.js-household-member-count2').forEach(el => { // на все радиокнопки
    el.addEventListener('change', householdMembersNumChange); // defined in components/room/household-num-change.js
  });
  document.querySelector('.js-household-member-count2-text').addEventListener('keyup', householdMembersNumChange); // на текстовый ввод

}
